import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { getIdFromNodesData, getNodesData, getParentIdFromNodesData } from './utils/pm-tree-nodes.utils';
import { PmMeasurement } from '../models/measurement.interface';
import { saveAs } from 'file-saver';
import { ButtonTheme } from '../../button/model/button-theme.enum';
import { ButtonLength } from '../../button/model/button-length.enum';
import { ButtonModel } from '../../button/model/button-model.definition';
import { MenuAction } from '../../button-with-menu/model/menu-action';
import { CounterNode } from '@oam/pm-shared/lib/measurement-store/models/counters-selection.interface';
import { ToastMessage } from '@oam/core/lib/actions/toast-message';
import {
    IMPORT_SUCCESS_MESSAGE,
    EXPORT_SUCCESS_MESSAGE,
    IMPORT_WARNING_PARTIALLY_MESSAGE,
    IMPORT_ERROR_FILE_CORRUPTED_MESSAGE,
    IMPORT_ERROR_NO_COMPATIBLE_COUNTERS_MESSAGE,
    IMPORT_ERROR_NO_COUNTERS_MESSAGE,
    IMPORT_ERROR_READING_FILE_MESSAGE
} from './utils/pm-tree-toast-messages';
import { DynamicTreeComponent } from './mat-tree/dynamic-tree.component';
import { isEqual } from 'lodash';
import { ExportService } from '@oam/core/lib/services';
import { ButtonOptions } from '../../button/model/button-options.definition';
import { OverlayMenuBarComponent } from '../../overlay-menu-bar/components/overlay-menu-bar/overlay-menu-bar.component';
import { TreeNodeData } from './models/pm-tree.interface';

export interface CountersFilter {
    distName: string;
    measurementAbbreviation: string;
    counterId: string;
}

@Component({
    selector: 'ui-webem-pm-tree',
    templateUrl: './pm-tree.component.html',
    styleUrls: ['./pm-tree.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class UiWebemPmTreeComponent implements OnChanges {
    @Input() pmMeasurements: PmMeasurement[] = [];
    @Input() selectedCounters: CounterNode[] = [];
    @Input() expandedNodes: string[] = [];
    @Input() filter: CountersFilter;
    @Input() hideTreePanel: boolean;
    @Output() filterChange: EventEmitter<CountersFilter> = new EventEmitter();
    @Output() selectionChange: EventEmitter<TreeNodeData[]> = new EventEmitter();
    @Output() expansionChange: EventEmitter<string[]> = new EventEmitter();
    @Output() importSelectionToaster: EventEmitter<{ payload: ToastMessage }> = new EventEmitter();
    @ViewChild('tree') treeComponent: DynamicTreeComponent;
    @ViewChild('importSelectionInput') importSelectionInput: ElementRef;
    @ViewChild(OverlayMenuBarComponent) overlayPanel: OverlayMenuBarComponent;

    shouldDisableButtons = true;
    readonly optionModel: ButtonModel = {
        title: 'Operations',
        icon: 'mdi mdi-16px mdi-dots-vertical',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long
    };

    readonly filterButton = {
        model: {
            icon: 'mdi mdi-16px mdi-filter-menu',
            title: 'Filter by',
            theme: ButtonTheme.Secondary,
            length: ButtonLength.Long
        } as ButtonModel,
        options: {
            minWidth: '32px',
            minHeight: '26px'
        }
    };
    readonly buttonOptions: ButtonOptions = { forceWidth: '70px' };
    readonly clearAllButton: ButtonModel = {
        label: 'Clear All'
    };
    readonly createButton: ButtonModel = {
        label: 'Ok',
        theme: ButtonTheme.Accent
    };
    readonly cancelButton: ButtonModel = {
        label: 'Cancel'
    };
    filterContent = {
        distName: '',
        measurementAbbreviation: '',
        counterId: ''
    };
    isFilterEmpty = true;
    isSavedFilterActive = false;
    isClearAllAction = false;
    pmTreeMenuItems: MenuAction[];
    importedFile: File;
    newNodesMap: Map<string, string[]>;

    constructor(private readonly exportService: ExportService) {
    }

    setPmTreeMenuItems(): void {
        this.pmTreeMenuItems = [
            {
                label: 'Clear selection',
                icon: 'mdi mdi-eraser',
                command: () => this.clearSelection(),
                disabled: !this.areCountersSelected() || this.shouldDisableButtons
            },
            {
                label: 'Export selection',
                icon: 'mdi mdi-file-export',
                command: () => this.onExportCountersSelection(),
                disabled: !this.areCountersSelected() || this.shouldDisableButtons
            },
            {
                label: 'Import selection',
                icon: 'mdi mdi-file-import',
                command: () => this.importSelectionInput.nativeElement.click(),
                disabled: this.shouldDisableButtons
            }];
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.pmMeasurements) {
            this.updateNodes();
            this.setPmTreeMenuItems();
        }

        if (changes.filter) {
            this.filterContent = { ...this.filter };
            this.setFilterState();
        }
    }

    clearSelection() {
        if (this.treeComponent) {
            this.treeComponent.clearSelection();
        }
        this.selectedCounters = [];
        this.selectionChange.emit([]);
        this.setPmTreeMenuItems();
    }

    collapseAll() {
        if (this.treeComponent) {
            this.treeComponent.collapseAll();
        }
    }

    selectCounters(counters: TreeNodeData[]) {
        this.selectionChange.emit(counters);
        this.selectedCounters = counters as CounterNode[];
        this.setPmTreeMenuItems();
    }

    expandNodes(counters: string[]) {
        this.expansionChange.emit(counters);
    }

    changeFilter(filter: CountersFilter) {
        this.filter = filter;
        this.filterChange.emit(filter);
    }

    onExportCountersSelection() {
        const blob: Blob = new Blob([JSON.stringify(this.selectedCounters)], { type: 'application/json' });
        saveAs(blob, this.exportService.getExportFilenameWithExtension('Counters_Selection', 'json'));
        this.fireToastMessage(EXPORT_SUCCESS_MESSAGE);
    }

    onImportCountersSelection($event: any): void {
        this.importedFile = $event.currentTarget.files[0];
        if (this.importedFile.size === 0) {
            this.fireToastMessage(IMPORT_ERROR_NO_COUNTERS_MESSAGE);

            return;
        }
        this.readFileContent(this.importedFile, this.setCountersSelection.bind(this));
        this.importSelectionInput.nativeElement.value = null;
    }

    private updateNodes() {
        const newMeasurements = getNodesData(this.pmMeasurements);
        if (!isEqual(newMeasurements, this.newNodesMap)) {
            this.shouldDisableButtons = !newMeasurements.size;
            if (newMeasurements.size) {
                this.rectifySelection(newMeasurements);
                this.rectifyExpandedState(newMeasurements);
            }
            this.newNodesMap = newMeasurements;
            this.setPmTreeMenuItems();
        }
    }

    private rectifySelection(map: Map<string, string[]>) {
        if (this.selectedCounters && this.selectedCounters.length) {
            const existingSelectedCounters = this.filterExistingCounters(this.selectedCounters, map);
            if (existingSelectedCounters.length !== this.selectedCounters.length) {
                if (this.treeComponent) {
                    this.treeComponent.clearSelection();
                }
                this.selectedCounters = existingSelectedCounters;
                setTimeout(() => this.selectionChange.emit(this.selectedCounters), 5);
            }
        }
    }

    private rectifyExpandedState(map: Map<string, string[]>) {
        if (this.expandedNodes && this.expandedNodes.length) {
            const existingExpandedNodes = this.expandedNodes
                .filter((nodeId: string) => map.has(nodeId));
            if (existingExpandedNodes.length !== this.expandedNodes.length) {
                this.expandedNodes = existingExpandedNodes;
                setTimeout(() => this.expansionChange.emit(this.expandedNodes), 5);
            }
        }
    }

    private readFileContent(file: File | undefined, processingFunction: any): void {
        if (file) {
            const reader = new FileReader();
            reader.onerror = () => {
                reader.abort();
                this.fireToastMessage(IMPORT_ERROR_READING_FILE_MESSAGE);
            };
            reader.onload = processingFunction;

            reader.readAsText(file);
        }
    }

    private setCountersSelection($event: ProgressEvent): void {
        const target: any = $event.target;
        if (target && target.result) {
            const fileContent = target.result as string;
            let selectedCounters: CounterNode[] = [];
            try {
                selectedCounters = JSON.parse(fileContent);
            } catch (e) {
                this.fireToastMessage(IMPORT_ERROR_FILE_CORRUPTED_MESSAGE, this.importedFile.name);

                return;
            }
            this.setCountersOnTree(selectedCounters);
            this.setPmTreeMenuItems();
        }
    }

    private setCountersOnTree(selectedCounters: CounterNode[]) {
        let existingSelectedCounters: CounterNode[] = [];
        if (selectedCounters.length) {
            existingSelectedCounters = this.filterExistingCounters(selectedCounters, this.newNodesMap);
            if (existingSelectedCounters.length) {
                this.treeComponent.clearSelection();
                this.treeComponent.collapseAll();
                this.selectedCounters = existingSelectedCounters.slice(0, this.treeComponent.getSelectionLimit());
                this.selectionChange.emit(this.selectedCounters);
                if (selectedCounters.length <= existingSelectedCounters.length &&
                    existingSelectedCounters.length <= this.treeComponent.getSelectionLimit()) {
                    this.fireToastMessage(IMPORT_SUCCESS_MESSAGE);
                } else {
                    this.fireToastMessage(IMPORT_WARNING_PARTIALLY_MESSAGE);
                }
            } else {
                this.fireToastMessage(IMPORT_ERROR_NO_COMPATIBLE_COUNTERS_MESSAGE);
            }
        } else {
            this.fireToastMessage(IMPORT_ERROR_NO_COUNTERS_MESSAGE);
        }
    }

    private fireToastMessage(message: ToastMessage, fileName?: string) {
        this.importSelectionToaster.emit({
            payload: {
                detail: fileName ? `${fileName}${message.detail}` : message.detail,
                severity: message.severity,
                summary: message.summary
            } as ToastMessage
        });
    }

    private areCountersSelected(): boolean {
        return this.selectedCounters && !!this.selectedCounters.length;
    }

    private filterExistingCounters(countersFromImportedFile: CounterNode[], map: Map<string, string[]>): CounterNode[] {
        return countersFromImportedFile.filter((counter: CounterNode) => {
            const counterParentId = getParentIdFromNodesData(counter);
            const counterToBeChecked = getIdFromNodesData(counter);

            return map.has(counterParentId) && map.get(counterParentId)?.find((counterName) => counterName.includes(counterToBeChecked));
        });
    }

    setFilterDistName({ value }: { value: string }) {
        this.filterContent = { ...this.filterContent, distName: value };
        this.setFilterState();
    }

    setFilterMeasurementAbbreviation({ value }: { value: string }) {
        this.filterContent = { ...this.filterContent, measurementAbbreviation: value };
        this.setFilterState();
    }

    setFilterCounterId({ value }: { value: string }) {
        this.filterContent = { ...this.filterContent, counterId: value };
        this.setFilterState();
    }

    submitAction() {
        this.filterChange.emit(this.filterContent);
        this.isClearAllAction = false;
        this.overlayPanel.hide();
    }

    cancelAction() {
        this.filterContent = { ...this.filter };
        this.setFilterState();
        this.isClearAllAction = false;
        this.overlayPanel.hide();
    }

    clearAllAction() {
        this.filterContent = {
            distName: '',
            measurementAbbreviation: '',
            counterId: ''
        };
        this.isClearAllAction = true;
        this.setFilterState();
    }

    setFilterState() {
        this.isSavedFilterActive = this.filter && Object.values(this.filter).some(item => !!item);
        this.isFilterEmpty = Object.values(this.filterContent).every(item => !item);
    }

    isSubmitButtonDisabled(): boolean {
        return (this.isSavedFilterActive && isEqual(this.filter, this.filterContent)) ||
            (this.isFilterEmpty && isEqual(this.filter, this.filterContent)) &&
            !this.isClearAllAction;
    }

    visibilityChange(isVisible: boolean) {
        if (!isVisible) {
            this.filterContent = {
                distName: '',
                measurementAbbreviation: '',
                counterId: ''
            };
        } else {
            this.filterContent = { ...this.filter };
        }

        this.setFilterState();
    }
}
