import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import {
    ssoLoginStarted,
    login,
    loginFailed,
    loginInProgress,
    loginStarted,
    loginSuccessful,
    removeLastSession,
    removeLastSessionSuccessfull,
    setLoginInitialValues,
    tokenRenewalSuccessful,
    loginLater
} from '../actions/login.actions';
import {
    catchError,
    delayWhen,
    filter,
    map,
    mergeMap,
    switchMap,
    takeUntil,
    tap,
    withLatestFrom
} from 'rxjs/operators';
import { of, timer } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { LoginResponse, RefreshTokenResponse, TokenInfo } from '@oam/cu-api-gw-api/models';
import { TokenType, UserInformation } from '@oam/core/lib/store/user-state';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import {
    handleLoginRelatedError,
    removeToken,
    renewSessionToken,
    setLastSessionToken,
    setToken,
    setUsernameForPasswordExpired,
    updateUser,
    updateUserInformation,
    updateLoginResponse,
    updateDowngradedPrivilegesDialogPopupFlag
} from '@oam/core/lib/actions/user.actions';
import { getUser, selectLastSessionToken, selectLonginResponse } from '@oam/core/lib/user/core/reducers/user-reducer';
import { select, Store } from '@ngrx/store';
import { AuthenticationService } from '@oam/cu-api-gw-api/services/authentication.service';
import { SessionMgmtService } from '@oam/cu-api-gw-api/services/session-mgmt.service';
import { EnforceLogoutResponse } from '@oam/cu-api-gw-api/models/enforce-logout-response';
import { DateTime } from '@oam/core';
import { ROLE_NAME_MAPPING, OFFLINE_USER_MODE } from '@oam/shared/lib/constants/role-access-constants';
import { vnfcTypeSessionKey, VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { StatusService } from '@oam/cu-asm-api/services/status.service';
import { getVnfcHeader } from '@oam/vnfc-shared/lib/vnfc-store/functions/get-vnfc-header';
import {
    getLegalNoticeMessage,
    openLegalNoticeModal,
    showLegalNoticeModal,
    submitLegalNoticeModal } from '@oam/cu-ui/lib/legal-notice-modal/core/actions/legal-notice-modal.actions';
import { CuLayout, VmCuLayout } from '../../../../../../cu-dashboard/src/lib/core/models/ui-settings';
import { validateAndClearLayout } from '@oam/core/lib/functions/validate-and-clean-layout-settings';
import { VduLayout } from '../../../../../../vdu-dashboard/src/lib/core/models/ui-settings';

@Injectable()
export class LoginEffects {
    private readonly authorizationType = {
        BASIC: 'Basic',
        BEARER: 'Bearer'
    };

    // As agreed with API-GW, adding extra 2s before retry login.
    private LOGIN_RETRY_BUFFER = 2000;

    constructor(private readonly actions$: Actions,
                private readonly store: Store,
                private readonly secService: AuthenticationService,
                private readonly sessionMgmtService: SessionMgmtService,
                private readonly statusService: StatusService,
                private readonly vnfcSharedService: VnfcSharedService,
                private readonly activatedRoute: ActivatedRoute) {
    }

    cancelLogin$ = createEffect(() =>
        this.actions$
            .pipe(
                ofType(setLoginInitialValues)
            ), { dispatch: false }
    );

    login$ = createEffect(() =>
        this.actions$.pipe(
            ofType(login),
            mergeMap(({ payload: { username, password, readOnlyChecked } }) => {
                return [
                    loginStarted({ payload: { username, password, readOnlyChecked } })
                ];
            }))
    );

    loginInProgress$ = createEffect(() =>
        this.actions$.pipe(
            ofType(login, renewSessionToken),
            mergeMap(() => of(loginInProgress()))
        ));

    loginStarted$ = createEffect(() =>
        this.actions$.pipe(
            ofType(loginStarted),
            mergeMap(({ payload: { username, password, readOnlyChecked } }) => {
                const encodedAuthorizationString = window.btoa(`${username}:${password}`);
                const authorization = `${this.authorizationType.BASIC} ${encodedAuthorizationString}`;

                const loginPostObservable = readOnlyChecked ? this.secService.loginPost({
                    Authorization: authorization, body: {
                        ReadOnlyMode: readOnlyChecked
                    }
                }) : this.secService.loginPost({
                    Authorization: authorization
                });

                return loginPostObservable.pipe(
                    takeUntil(this.cancelLogin$),
                    tap((response: LoginResponse) => {
                        sessionStorage.setItem(TokenType.BEARER, response.Token);
                    }),
                    switchMap((loginResponse: LoginResponse) =>
                        this.statusService.getVnfcs$Response().pipe(
                            mergeMap(vnfcsResponse => {
                                const vnfcType = getVnfcHeader(vnfcsResponse);
                                sessionStorage.setItem(vnfcTypeSessionKey, vnfcType);
                                this.vnfcSharedService.setVnfcType(vnfcType);

                                return of(loginResponse);
                            }),
                            catchError(err => {
                                console.log(`\n${StatusService.GetVnfcsPath} failed, ${err.message}\n`, err);

                                return of(loginResponse);
                            }),
                        )
                    ),
                    mergeMap((response: LoginResponse) => {
                        if ( !this.vnfcSharedService.isVmcu()) {
                            if (!ROLE_NAME_MAPPING[response.Role]) return of(loginFailed({ payload: 'Invalid user profile' }));

                            return [
                                updateLoginResponse({ payload: { response, username } }),
                                openLegalNoticeModal()
                            ];
                        }

                        return this.loginResponseMap(response, username || '');
                    }),
                    catchError((error: HttpErrorResponse) => of(handleLoginRelatedError({ error, username, password,  readOnlyChecked})))
                );
            })
        )
    );

    ssoLoginStarted$ = createEffect(() => this.actions$.pipe(
        ofType(ssoLoginStarted),
        mergeMap((action) => this.sessionMgmtService.getTokenInfo().pipe(
            takeUntil(this.cancelLogin$),
            switchMap((response: TokenInfo) =>
                this.statusService.getVnfcs$Response().pipe(
                    mergeMap(vnfcsResponse => {
                        const vnfcType = getVnfcHeader(vnfcsResponse);
                        sessionStorage.setItem(vnfcTypeSessionKey, vnfcType);
                        this.vnfcSharedService.setVnfcType(vnfcType);

                        return of(response);
                    }),
                    catchError(err => {
                        console.log(`\n${StatusService.GetVnfcsPath} failed, ${err.message}\n`, err);

                        return of(response);
                    }),
                )
            ),
            mergeMap((response: TokenInfo) => {
                if (!this.vnfcSharedService.isVmcu()) {
                    if (!ROLE_NAME_MAPPING[response.Role]) return this.handleSsoNormalError();

                    const actions = [updateLoginResponse({ payload: { response, username: response.User } })];

                    if (response.Role !== OFFLINE_USER_MODE) {
                        actions.push(openLegalNoticeModal() as any);
                    } else {
                        this.loginResponseMap(response, response.User || '').forEach((action) => {
                            actions.push(action as any);
                        });
                    }

                    return actions;
                }

                return this.loginResponseMap(response, response.User || '');
            }),
            catchError((error: HttpErrorResponse) => {
                if (error.status === 403) {
                    return of(
                        removeToken({ payload: TokenType.BEARER }),
                        setToken({ payload: { type: TokenType.PASSWORD_CHANGE, value: action.payload } }),
                        setUsernameForPasswordExpired({ payload: error.error.User }),
                        redirect({ routerLink: RouterLink.passwordExpired, activatedRoute: this.activatedRoute })
                    );
                } else {
                    return this.handleSsoNormalError();
                }
            })
        )))
    );

    handleLoginRelatedError$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(handleLoginRelatedError),
            mergeMap(({ error, username, password, readOnlyChecked }) => {
                if (error.status === 403) {
                    return of(
                        setToken({ payload: { type: TokenType.PASSWORD_CHANGE, value: error.error.Token } }),
                        setUsernameForPasswordExpired({ payload: username || '' }),
                        redirect({
                            routerLink: RouterLink.passwordExpired,
                            activatedRoute: this.activatedRoute
                        })
                    );
                } else if (error.status === 401) {
                    if(error.error.additionalStatus && error.error.additionalStatus === 106){
                        return of(loginFailed({ payload: 'This role is deactivated' }));
                    }

                    return of(typeof error.error === 'object' ? loginFailed({ payload: error.error.title }):loginFailed({ payload: error.error }));
                } else if (error.status === 460) {
                    return of(loginFailed({ payload: 'Restricted access feature is activated' }));
                } else if (error.status === 461) {
                    return of(loginFailed({ payload: 'Maximum session number is reached' }));
                } else if (error.status === 462) {
                    const errorResponse = error.error as unknown as EnforceLogoutResponse;

                    return of(loginLater({
                        message: `Session will be created after ${DateTime.convertSecondToHourMinuteSecond(errorResponse.EnforceLogoutDelay)}.
                        Restricted access is activated.`,
                        enforcedLogoutDelay: errorResponse.EnforceLogoutDelay,
                        username: username,
                        password: password,
                        readOnlyChecked: readOnlyChecked
                    }));
                } else if (error.status === 503)  {
                    return of(loginFailed({ payload: 'Due to service temporarily unavailable, please try later' }));
                } else return of(loginFailed({ payload: 'Login failed. Please try again' }));
            })
        );
    });

    loginLater$ = createEffect(() => this.actions$.pipe(
        ofType(loginLater),
        mergeMap(( loginLaterAction) => {
            return of(loginLaterAction).pipe(
                delayWhen( ({ enforcedLogoutDelay }) => timer(enforcedLogoutDelay * 1000 + this.LOGIN_RETRY_BUFFER)),
                takeUntil(this.cancelLogin$),
                mergeMap(({ username, password, readOnlyChecked }) => {
                    return [
                        loginStarted({ payload: { username, password, readOnlyChecked } })
                    ];
                })
            );
        })
    ));

    renewSessionToken$ = createEffect(() =>
        this.actions$.pipe(
            ofType(renewSessionToken),
            withLatestFrom(this.store.pipe(select(getUser))),
            mergeMap(([{ username, password, shouldStartOperation }, userInformation]) => {
                const encodedAuthorizationString = window.btoa(`${username}:${password}`);
                const authorization = `${this.authorizationType.BASIC} ${encodedAuthorizationString}`;
                const authToken = sessionStorage.getItem(`${TokenType.BEARER}`);

                if (this.vnfcSharedService.isVmcu()) {
                    return this.secService.loginPost({
                            Authorization: authorization, body: {
                                ReadOnlyMode: !!userInformation?.information?.readOnlyMode
                            }
                        }
                    ).pipe(
                        takeUntil(this.cancelLogin$),
                        mergeMap((response: LoginResponse) => [
                            setLastSessionToken({ authToken }),
                            setToken({ payload: { type: TokenType.BEARER, value: response.Token } }),
                            ...this.renewTokenResponseMap(response, username || '', shouldStartOperation)
                        ]),
                        catchError((error: HttpErrorResponse) => of(handleLoginRelatedError({ error, username, password })))
                    );
                }

                return this.sessionMgmtService.refreshTokenPatch({ Authorization: authorization,body: {
                        Token: authToken as string,
                        Type: TokenType.BEARER
                    } })
                    .pipe(
                        mergeMap((response: RefreshTokenResponse) => this.onRefreshTokenSucceeded(response, shouldStartOperation, authToken)),
                        catchError((error: HttpErrorResponse) => {
                            return [400, 404, 500].includes(error.status) ?
                                of(loginFailed({ payload: error.error })) :
                                of(loginFailed({ payload: 'Service is temporarily unavailable' }));
                        })
                    );
            })
        )
    );

    removeLastSession$ = createEffect(() =>
        this.actions$.pipe(
            ofType(removeLastSession),
            withLatestFrom(this.store.select(selectLastSessionToken)),
            mergeMap(([, token]) => this.secService.logoutPost({
                Authorization: `${this.authorizationType.BEARER} ${token}`
            }).pipe(
                map(() => removeLastSessionSuccessfull())
            ))
        ));

    openLegalNoticeModal$ = createEffect(() =>
        this.actions$.pipe(
            ofType(openLegalNoticeModal),
            filter(() => !this.vnfcSharedService.isVmcu()),
            switchMap(() => of (
                showLegalNoticeModal(),
                getLegalNoticeMessage()))
        )
    );

    submitLegalNoticeModal$ = createEffect(() =>
        this.actions$.pipe(
            ofType(submitLegalNoticeModal),
            withLatestFrom(this.store.select(selectLonginResponse)),
            mergeMap(([, response]) => this.loginResponseMap(response.response, response.username))
        )
    );

    loginResponseMap(loginResponse: LoginResponse | TokenInfo, user: string) {
        const userInfo = {
            username: user,
            expires: loginResponse.Expires,
            failedLoginAttempts: loginResponse.FailedLoginAttempts,
            lastFailedLogin: loginResponse.LastFailedLogin,
            lastLogin: loginResponse.LastLogin,
            passwordAboutToExpireMessage: loginResponse.AdditionalText,
            readOnlyMode: loginResponse.ReadOnlyMode,
            role: loginResponse.Role
        } as UserInformation;

        const layout = this.vnfcSharedService.getVnfcType() === 'vDu' ? VduLayout : (this.vnfcSharedService.getVnfcType()  === 'vCu' ? CuLayout : VmCuLayout);
        validateAndClearLayout(this.store, layout);

        const { redirectUrl } = this.activatedRoute.snapshot.queryParams;

        return [
            loginSuccessful(),
            updateDowngradedPrivilegesDialogPopupFlag({ payload: userInfo.readOnlyMode }),
            updateUserInformation({ payload: userInfo }),
            updateUser({ payload: userInfo }),
            redirect({ routerLink: redirectUrl || RouterLink.dashboard, activatedRoute: this.activatedRoute })
        ];
    }

    renewTokenResponseMap(loginResponse: LoginResponse | TokenInfo, user: string, shouldStartOperation: boolean) {
        const userInfo = {
            username: user,
            expires: loginResponse.Expires,
            failedLoginAttempts: loginResponse.FailedLoginAttempts,
            lastFailedLogin: loginResponse.LastFailedLogin,
            lastLogin: loginResponse.LastLogin,
            passwordAboutToExpireMessage: loginResponse.AdditionalText,
            role: loginResponse.Role
        } as UserInformation;


        return [
            removeLastSession(),
            tokenRenewalSuccessful({ shouldStartOperation }),
            updateUserInformation({ payload: userInfo }),
            updateUser({ payload: userInfo })
        ];
    }

    onRefreshTokenSucceeded(response: RefreshTokenResponse, shouldStartOperation: boolean, authToken: string | null) {
        const userInfo = {
            expires: response.Expires
        } as UserInformation;

        return [
            setLastSessionToken({ authToken }),
            setToken({ payload: { type: response.Type, value: response.Token } }),
            tokenRenewalSuccessful({ shouldStartOperation }),
            updateUserInformation({ payload: userInfo })
        ];
    }

    handleSsoNormalError() {
        return of(
            removeToken({ payload: TokenType.BEARER }),
            redirect({ routerLink: RouterLink.login, activatedRoute: this.activatedRoute }),
            loginFailed({ payload: 'Login failed. Please try again' })
        );
    }
}
