import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';
import { Directive, Injectable } from '@angular/core';
import { chain } from 'lodash';
import { MenuItem } from 'primeng/api';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';

@Directive()
export abstract class NavigationSubmenuDirective {

    constructor(private navigationService: NavigationService) {
    }

    menuItemsMap: { [key: string]: MenuItem };

    protected setMenuItemsMap(tabRouterLinks: RouterLink[]): void {

        this.menuItemsMap = chain(this.navigationService
            .getAvailableNavigations())
            .filter(navigation =>
                tabRouterLinks.some(routerLink => navigation.routerLink === routerLink))
            .map(({ label, routerLink }) => ({
                id: routerLink,
                label: label,
                routerLink: routerLink
            }))
            .keyBy('routerLink')
            .value();
    }
}
