import { createAction, props } from '@ngrx/store';

export enum LoginActionsTypes {
    LOGIN = '[Auth] Login user',
    LOGIN_SUCCESSFUL = '[User] [Auth] Login successful',
    TOKEN_SUCCESSFUL = '[User] [Auth] Token renewal successful',
    RESET_OPERATION_STATUS = '[Auth] Reset operation status',
    LOGIN_FAILED = '[User] [Auth] Login failed',
    REMOVE_LAST_SESSION = '[User] [Auth] Remove last session after token renewal',
    REMOVE_LAST_SESSION_SUCCESFULL = '[User] [Auth] Remove last session after token renewal succesfull',
    LOGIN_IN_PROGRESS = '[Auth] Login in progress',
    LOGIN_INITIAL_VALUES = '[User] [Auth] Reset to inital values',
    LOGIN_STARTED = '[Auth] Start login process',
    START_SSO_LOGIN = '[Auth] Start Sso login',
    LOGIN_LATER = '[Auth] Login later',
    SESSION_UNINTENTIONALLY_TERMINATED = '[Auth] Session unintentionally terminated'
}

export const login = createAction(
    LoginActionsTypes.LOGIN,
    props<{ payload: {username: string, password: string, readOnlyChecked: boolean}}>()
);

export const loginSuccessful = createAction(
    LoginActionsTypes.LOGIN_SUCCESSFUL
);

export const tokenRenewalSuccessful = createAction(
    LoginActionsTypes.TOKEN_SUCCESSFUL,
    props<{shouldStartOperation?: boolean}>()
);

export const removeLastSession = createAction(
    LoginActionsTypes.REMOVE_LAST_SESSION
);
export const removeLastSessionSuccessfull = createAction(
    LoginActionsTypes.REMOVE_LAST_SESSION_SUCCESFULL
);

export const resetOperationStatus = createAction(
    LoginActionsTypes.RESET_OPERATION_STATUS
);

export const loginFailed = createAction(
    LoginActionsTypes.LOGIN_FAILED,
    props<{ payload: string}>()
);

export const loginInProgress = createAction(
    LoginActionsTypes.LOGIN_IN_PROGRESS
);

export const setLoginInitialValues = createAction(
    LoginActionsTypes.LOGIN_INITIAL_VALUES
);

export const loginStarted = createAction(
    LoginActionsTypes.LOGIN_STARTED,
    props<{ payload: {username: string, password: string, readOnlyChecked?: boolean}}>()
);

export const ssoLoginStarted = createAction(
    LoginActionsTypes.START_SSO_LOGIN,
    props<{ payload: string}>()
);

export const loginLater = createAction(
    LoginActionsTypes.LOGIN_LATER,
    props<{ message: string, enforcedLogoutDelay: number, username: string, password: string, readOnlyChecked?: boolean}>()
);

export const sessionUnintentionallyTerminated = createAction(
    LoginActionsTypes.SESSION_UNINTENTIONALLY_TERMINATED
);
