import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { SPLIT_VIEW_LOCATION } from '../models';
import { OutletUrlPathService } from '@oam/core/lib/services/outlet-url-path.service';
import { SplitViewRestrictionsService } from './split-view-restrictions.service';


@Injectable({
    providedIn: 'root'
})
export class SplitViewGuardService implements CanActivate {

    constructor(private readonly outletUrlPathService: OutletUrlPathService,
                private readonly router: Router,
                private readonly splitViewRestrictionsService: SplitViewRestrictionsService) {
    }

    canActivate( route: ActivatedRouteSnapshot,
                 state: RouterStateSnapshot): boolean | UrlTree {
        const urlTopView = this.outletUrlPathService.getOutletWebemRoutePath(state.url, SPLIT_VIEW_LOCATION.TOP);
        const urlBottomView = this.outletUrlPathService.getOutletWebemRoutePath(state.url, SPLIT_VIEW_LOCATION.BOTTOM);
        const isUrlTopViewInRestrictedGroup = this.splitViewRestrictionsService.isUrlInRestrictionGroup(urlTopView);
        const isUrlBottomViewInRestrictedGroup = this.splitViewRestrictionsService.isUrlInRestrictionGroup(urlBottomView);
        if (urlTopView === urlBottomView) return false;

        if (isUrlTopViewInRestrictedGroup && isUrlBottomViewInRestrictedGroup) {
            return this.router.parseUrl(this.splitViewRestrictionsService.splitViewConfig.DEFAULT_REDIRECTION_AFTER_FAILED_RESTRICTION_CHECKED);
        }

        return true;
    }
}
