import { createEntityAdapter, EntityAdapter } from '@ngrx/entity';
import { setCountersInfoPanelState, unsetCountersInfoPanelState } from '../actions/counters-info.actions';
import { CountersDetailsState } from '../store/counters-details.state';
import { Action, createReducer, on } from '@ngrx/store';

export const countersDetailsPanelAdapter: EntityAdapter<string> = createEntityAdapter<string>({
    selectId: (counter: string) => counter
});

const countersDetailsPanelInitialState: CountersDetailsState = countersDetailsPanelAdapter.getInitialState();

const reducer = createReducer(
    countersDetailsPanelInitialState,
    on(setCountersInfoPanelState, (state, { id }) => countersDetailsPanelAdapter.addOne(id, state)),
    on(unsetCountersInfoPanelState, (state, { id }) => countersDetailsPanelAdapter.removeOne(id, state))
);

export function countersDetailsPanelReducer(
    state: CountersDetailsState | undefined,
    action: Action
): CountersDetailsState {
    return reducer(state, action);
}
