import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    HostListener,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import Chart, { ChartConfiguration, ChartData, ChartDataSets, ChartOptions, ChartType } from 'chart.js';
import { noop } from 'rxjs';
import { WebemChartData } from '../../model/webem-chart.data';
import { ChartDataSetConfig } from '../../model/chart-data-set.type';
import { FileSaverService } from 'ngx-filesaver';
import ChartDataLabels from 'chartjs-plugin-datalabels';

export interface GraphDrawEvent {
    context: CanvasRenderingContext2D;
    element: HTMLCanvasElement;
}

@Component({
    selector: 'ui-webem-chart',
    templateUrl: './chart.component.html',
    styleUrls: ['./chart.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ChartComponent implements OnInit, OnChanges {
    @Input() type: ChartType;
    @Input() dataSetConfigs: Array<ChartDataSetConfig>;
    @Input() data?: WebemChartData[];
    @Input() labels: Array<string | string[]>;
    @Input() options: ChartOptions;

    @Output() drawGraph = new EventEmitter<GraphDrawEvent>();

    configuration: ChartConfiguration;
    context: CanvasRenderingContext2D | null;
    chart?: Chart;

    @ViewChild('chart', { static: true }) canvasElement: ElementRef<HTMLCanvasElement>;

    emitChange = noop;

    constructor(private readonly fileSaver: FileSaverService) {
    }

    ngOnInit(): void {
        const chartDataSets = this.data ? this.createDataSets(this.data) : [];
        const chartData: ChartData = { labels: this.labels, datasets: chartDataSets };
        const configuration = { plugins: [ChartDataLabels], type: this.type, data: chartData, options: this.options };
        this.context = this.canvasElement.nativeElement.getContext('2d');
        if (this.context) {
            this.emitChange = () => this.drawGraph.emit({
                element: this.canvasElement.nativeElement,
                context: this.context as CanvasRenderingContext2D
            });

            this.emitChange();
            this.chart = new Chart(this.context, configuration);
        }
    }

    @HostListener('window:resize', [])
    onResize(): void {
        if (this.chart) {
            this.emitChange();
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (this.chart) {
            if (changes.data) {
                this.updateData(this.chart.data, changes.data.currentValue);
            }

            if (changes.labels) {
                this.updateLabels(this.chart.data, changes.labels.currentValue);
            }

            if (changes.dataSetConfigs) {
                this.updateDataSetConfig(this.chart.data, changes.dataSetConfigs.currentValue);
            }

            if (changes.options) {
                this.updateOptions(this.chart.options, changes.options.currentValue);
            }

            if (changes.data || changes.labels || changes.dataSetConfigs || changes.options) {
                this.chart.update({ lazy: true });
            }
        }
    }

    updateData(chartData: ChartData, source: WebemChartData[] | null): void {
        if (!chartData.datasets || !source ||
            chartData.datasets.length === 0 || chartData.datasets.length !== source.length) {
            chartData.datasets = source ? this.createDataSets(source) : [];
        } else if (chartData.datasets && source) {
            this.updateDataSets(chartData.datasets, source);
        }
    }

    updateOptions(options: ChartOptions, source: ChartOptions | null): void {
        Object.assign(options, source);
    }

    updateLabels(chartData: ChartData, labels: Array<string | string[]>) {
        chartData.labels = labels;
    }

    updateDataSetConfig(chartData: ChartData, dataSetConfigs: Array<ChartDataSetConfig>): void {
        if (chartData.datasets) {
            chartData.datasets.forEach((dataSet: ChartDataSets, index: number) => {
                Object.assign(dataSet, dataSetConfigs[index]);
            });
        }
    }

    updateDataSets(chartDataSets: ChartDataSets[], source: WebemChartData[]): void {
        source.forEach((chartData: WebemChartData, i: number) => {
            chartDataSets[i].data = chartData.data;
        });
    }


    createDataSets(source: WebemChartData[]): ChartDataSets[] {
        return source.map(
            (chartData: WebemChartData, index: number) => ({
                ...this.dataSetConfigs ? this.dataSetConfigs[index] : {},
                data: chartData.data
            } as ChartDataSets)
        );
    }

    saveChartAsPicture(fileName: string) {
        if (this.context) {
            this.context.save();
            this.context.globalCompositeOperation = 'destination-over';
            this.context.fillStyle = '#EDF2F5';
            this.context.fillRect(0, 0, this.canvasElement.nativeElement.width, this.canvasElement.nativeElement.height);
            this.canvasElement.nativeElement.toBlob((blob) => {
                if (blob) {
                    this.fileSaver.save(blob, fileName+'.png');
                    if (this.context) {
                        this.context.restore();
                    }
                }
            }, 'image/png', 0.95);
        }
    }
}
