import { DeltaPlanInfoResponse, PlanInfoResponse } from '@oam/cu-asm-api/models';
import { PlanOrigin } from './index';
export interface PlansInfo extends PlanInfoResponse {
  from: PlanOrigin
}

export interface DeltaPlansInfo extends DeltaPlanInfoResponse {
  from: PlanOrigin
}

export type AllPlansInfo = PlansInfo | DeltaPlansInfo;

export function isDeltaPlan(plan: AllPlansInfo): plan is DeltaPlansInfo {
    return  plan.from === PlanOrigin.DELTA;
}
