import { Component, Input, OnInit } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { Router } from '@angular/router';

@Component({
  selector: 'ui-webem-submenu',
  templateUrl: './submenu.component.html',
  styleUrls: ['./submenu.component.scss']
})
export class SubmenuComponent implements OnInit {
    @Input() menuItemsMap: { [key: string]: MenuItem };
    menuItems: MenuItem[];

    constructor(private readonly router: Router) {
    }

    ngOnInit() {
        this.menuItems = Object.values(this.menuItemsMap);

        this.selectCurrentItem(this.router.url);
    }

    selectCurrentItem(href: string): void {
        this.clearPreviousSelected();

        const currentItem = this.menuItemsMap[href];

        if (currentItem) {
            currentItem.styleClass = 'active-menu-item';
        }
    }

    private clearPreviousSelected() {
        this.menuItems.forEach((item) => delete item.styleClass);
    }

}
