import { createEntityAdapter, EntityAdapter } from '@ngrx/entity';
import { CounterInfo } from '@oam/cu-pmv2-api/models/counter-info';
import {
    clearCountersInfo,
    setCountersInfo,
    retrievingCounterInfoFailed,
    retrievingCounterInfoSucceed
} from '../actions/counters-info.actions';
import { CountersInfoState } from '../store/counters-info.state';
import { Action, createReducer, on } from '@ngrx/store';

export const countersInfoAdapter: EntityAdapter<CounterInfo> = createEntityAdapter<CounterInfo>({
    selectId: (counter: CounterInfo) => counter.id
});

const countersInfoInitialState: CountersInfoState = {
    info: countersInfoAdapter.getInitialState(),
    failedToRetrieveCountersInfo: false
};

const reducer = createReducer(
    countersInfoInitialState,
    on(setCountersInfo, (state, { info }) => {
        return ({ ...state, info: countersInfoAdapter.setAll(info, state.info) });
    }),
    on(clearCountersInfo, (state) => {
        return { ...state, info: countersInfoAdapter.removeAll(state.info) };
    }),
    on(retrievingCounterInfoFailed, (state) => ({ ...state, failedToRetrieveCountersInfo: true })),
    on(retrievingCounterInfoSucceed, (state) => ({ ...state, failedToRetrieveCountersInfo: false })),
);

export function countersInfoReducer(
    state: CountersInfoState | undefined,
    action: Action
): CountersInfoState {
    return reducer(state, action);
}
