import { Action, createReducer, on } from '@ngrx/store';
import { NavigationSearchState } from '../models/navigation-search-state';
import { setNavigationSearchOverlayClosed, setNavigationSearchOverlayOpened } from '../actions/navigation-search.actions';


export const navigationSearchStateFeatureKey = 'navigationSearch';

export const initialState: NavigationSearchState = {
    isNavigationSearchOverlayOpened: false
};

const setNavigationSearchReducer = createReducer(
    initialState,
    on(setNavigationSearchOverlayOpened, () => {
        return { isNavigationSearchOverlayOpened: true } as NavigationSearchState;
    }),
    on(setNavigationSearchOverlayClosed, () => {
        return { isNavigationSearchOverlayOpened: false } as NavigationSearchState;
    })
);

export function reducer(state: NavigationSearchState | undefined, action: Action) {
    return setNavigationSearchReducer(state, action);
}
