import { Action, createReducer, on } from '@ngrx/store';
import { toggleKeyboardShortcutsAvailability } from '../actions/keyboard-shortcuts.actions';

export const KEYBOARD_SHORTCUTS = 'KeyboardShortcuts';

export interface KeyboardShortcutsState {
    [key: string]: boolean;
    enabled: boolean;
}

export const initialState: KeyboardShortcutsState = {
    enabled: true
};

const keyboardShortcutsReducer = createReducer(
    initialState,
    on(toggleKeyboardShortcutsAvailability, (state, { payload: enabled }) => {
        return { ...state, enabled };
    }),
);

export function reducer(state: KeyboardShortcutsState | undefined, action: Action) {
    return keyboardShortcutsReducer(state, action);
}
