import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { PageNotFoundRedirectPaths, TextBlockModel } from '../../core/models';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { Router } from '@angular/router';
import { isUserAuthenticated } from '../../core/functions/page-not-found.utils';

@Component({
    selector: 'ui-webem-page-not-found',
    templateUrl: './page-not-found.component.html',
    styleUrls: ['./page-not-found.component.scss']
})
export class PageNotFoundComponent implements OnInit {
    @ViewChild('textBlock', { static: true }) textTemplateRef: TemplateRef<HTMLSpanElement>;
    @ViewChild('separatorBlock', { static: true }) separatorTemplateRef: TemplateRef<HTMLHRElement>;
    @ViewChild('buttonBlock', { static: true }) buttonTemplateRef: TemplateRef<HTMLElement>;

    blocks: TextBlockModel[];

    constructor(private readonly router: Router) {
    }

    ngOnInit(): void {
        const isUserLogged = isUserAuthenticated();

        this.blocks = [
            {
                text: '404',
                className: 'header-404',
                template: this.textTemplateRef
            },
            {
                text: 'Page does not exist',
                className: 'header-description',
                template: this.textTemplateRef
            },
            {
                className: 'separator',
                template: this.separatorTemplateRef
            },
            {
                text: 'Sorry, we can\'t find this page',
                className: 'description-title',
                template: this.textTemplateRef
            },
            {
                text: 'You\'re here, because probably:',
                className: 'description-text-bold',
                template: this.textTemplateRef
            },
            {
                text: '- the page has incorrect URL',
                className: 'description-text',
                template: this.textTemplateRef
            },
            {
                text: '- the page has been moved',
                className: 'description-text',
                template: this.textTemplateRef
            },
            {
                label: isUserLogged ? 'Go to Dashboard Page' : 'Go to Login Page',
                url: isUserLogged ? PageNotFoundRedirectPaths.Dashboard : PageNotFoundRedirectPaths.Login,
                className: 'redirect-button',
                theme: ButtonTheme.Accent,
                template: this.buttonTemplateRef
            }
        ];
    }

    async redirectToPage(url: string): Promise<void> {
        await this.router.navigateByUrl(url);
    }
}
