import { PlanUploadOptions } from '@oam/cu-asm-api/models/plan-upload-options';
import { UploadRequest } from '@oam/cu-cm-api/models/upload-request';
import { ConfigurationSaveVariant } from '@oam/core/lib/models';

export function getAsmUploadRequestBody(saveVariant: ConfigurationSaveVariant): PlanUploadOptions {
    return {
        deltaChangeOnly: saveVariant === ConfigurationSaveVariant.DeltaConfigurationOnly
    };
}

export function getCmUploadRequestBody(saveVariant: ConfigurationSaveVariant): UploadRequest {
    return {
        includeRuntimeData: saveVariant === ConfigurationSaveVariant.FullRuntimeConfiguration
    };
}
