import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { FeatureFlagGuardRule } from '@oam/ng-feature-flag';

export function mapToFeatureFlagGuardRules(navs: Navigation[]): Required<FeatureFlagGuardRule>[] {
  return navs
    .filter(({ featureFlag }) => !!featureFlag)
    .map(({ featureFlag, routerLink }) => {
      return {
        path: routerLink.replace(/^\//, ''),
        featureFlag
      } as Required<FeatureFlagGuardRule>;
    });
}
