import { Injectable } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { take } from 'rxjs/operators';
import { Cloud } from '@oam/shared/lib/core/models/cloud.type';
import { selectCloud } from '../selectors/cloud.selectors';

@Injectable({
    providedIn: 'root'
})

export class CloudInfoService {
    cloudName: string;
    cloudId: string;

    constructor(private readonly store: Store) { }

    public getCloudName(): string {
        this.store
            .pipe(
                select(selectCloud),
                take(1)
            ).subscribe((data: Cloud) => {
                this.cloudName = data.cloudName as string;
            });

        return this.cloudName;
    }

    public getCloudId(): string {
        this.store
            .pipe(
                select(selectCloud),
                take(1)
            ).subscribe((data: Cloud) => {
                this.cloudId = data.cloudId as string;
            });

        return this.cloudId;
    }
}
