import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { LoginPageComponent } from './components/login/login-page.component';
import { TokenInfoDialogComponent } from './components/token-info/token-info-dialog.component';
import { SsoLoginPageComponent } from './components/sso-login/sso-login-page.component';
import * as loginState from './core/reducers/login.reducer';
import * as logoutState from './core/reducers/logout.reducer';
import * as tokenInfoReducer from './core/reducers/token-info.reducer';
import { EffectsModule } from '@ngrx/effects';
import { LoginEffects } from './core/effects/login.effects';
import { LogoutEffects } from './core/effects/logout.effects';
import { TokenInfoEffects } from './core/effects/token-info.effects';
import { CardModule } from 'primeng/card';
import { ProgressBarModule } from 'primeng/progressbar';
import { AutoFocusDirective } from './components/login/directives/auto-focus.directive';
import { WebemButtonModule, ShowPasswordButtonModule, TrackCapsLockModule } from '@oam/cu-ui';
import { CheckboxModule } from 'primeng/checkbox';

@NgModule({
    declarations: [
        LoginPageComponent,
        SsoLoginPageComponent,
        TokenInfoDialogComponent,
        AutoFocusDirective,
    ],
    imports: [
        CommonModule,
        StoreModule.forFeature(loginState.featureKey, loginState.reducer),
        StoreModule.forFeature(logoutState.featureKey, logoutState.reducer),
        StoreModule.forFeature(tokenInfoReducer.tokenInfoFeatureKey, tokenInfoReducer.reducer),
        EffectsModule.forFeature([LoginEffects, LogoutEffects, TokenInfoEffects]),
        CardModule,
        WebemButtonModule,
        FormsModule,
        ReactiveFormsModule,
        ProgressBarModule,
        TrackCapsLockModule,
        ShowPasswordButtonModule,
        CheckboxModule
    ],
    exports: [
        LoginPageComponent,
        SsoLoginPageComponent,
        TokenInfoDialogComponent,
        StoreModule,
        CheckboxModule
    ]
})
export class AuthModule {
}
