import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import {
    confirmDialog,
    openDialog,
    showMessage,
    closeDialogsByName,
    closeAllDialogs
} from '../actions/overlay.actions';
import { WebemOverlayService } from '../services/webem-overlay.service';
import { tap } from 'rxjs/operators';
import { DynamicMessageDialogConfig } from '../models';
import { Confirmation } from 'primeng/api';
import { Action } from '@ngrx/store';

@Injectable()
export class OverlayEffects {
    constructor(private readonly actions$: Actions, private readonly overlayService: WebemOverlayService) {
    }

    openDialog$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(openDialog),
            tap((action) => {
                this.overlayService.openDialog(action.componentType, action.config);
            })
        );
    }, { dispatch: false });

    closeDialogsByName$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(closeDialogsByName),
            tap((action) => {
                this.overlayService.closeDialogsByName(action.closedComponent);
            })
        );
    }, { dispatch: false });

    closeAllDialogs$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(closeAllDialogs),
            tap(() => {
                this.overlayService.closeAllDialogs();
            })
        );
    }, { dispatch: false });

    showMessage$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(showMessage),
            tap((action) => {
                const config: DynamicMessageDialogConfig = {
                    header: action.header,
                    message: action.message
                };
                if (action.severity) config.severity = action.severity;

                this.overlayService.showMessage(config);
            })
        );
    }, { dispatch: false });

    confirmDialog$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(confirmDialog),
            tap((action) => {
                const config: Action = {...action};
                // @ts-ignore
                delete config.type;
                this.overlayService.confirm(config as Confirmation);
            })
        );
    }, { dispatch: false });

}
