import { Component, HostListener, Input, OnDestroy, OnInit } from '@angular/core';
import { forceRedirect } from '@oam/core/lib/actions/router.actions';
import { select, Store } from '@ngrx/store';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { StatusModelBar } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { TokenInfoDialogComponent } from '@oam/auth/lib/auth/components/token-info/token-info-dialog.component';
import { getTokenInfo } from '@oam/auth/lib/auth/core/selectors/token-info.selectors';
import { openDialog, closeDialogsByName } from '@oam/cu-ui';
import { Subject } from 'rxjs';
import { filter, pairwise, take, takeUntil } from 'rxjs/operators';
import { DateTime } from '@oam/core';
import _ from 'lodash';
import { TokenInfo } from '@oam/cu-api-gw-api';
import { AvailableFeatureFlagsEnum } from '@oam/core/lib/environment-variables/core/models';
import { FeatureFlag } from '@oam/feature-flag';
import { UserOperationService } from '@oam/core/lib/services/user-operation.service';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import { cleanUiSettingsCache } from '@oam/core/lib/actions/app-settings.actions';
import { selectHasCleanUiSettingsState } from '@oam/core/lib/reducers/app-settings.reducer';
import { RoleAccessService } from '@oam/role-access-core';
import { OFFLINE_USER_MODE } from '@oam/shared/lib/constants/role-access-constants';

@Component({
    selector: 'webem-page-header',
    templateUrl: './page-header.component.html',
    styleUrls: ['./page-header.component.scss']
})
export class PageHeaderComponent implements OnInit, OnDestroy {
    @Input() statusBarModel: StatusModelBar;
    @Input() productName: string;
    @Input() helpFilePath: string;
    @Input() helpFileName: string;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    constructor(private readonly store: Store,
                private readonly featureFlag: FeatureFlag,
                private userOperationService: UserOperationService,
                private readonly roleAccessService: RoleAccessService) { }

    @HostListener('window:keydown', ['$event'])
    @HostListener('window:keyup', ['$event'])
    @HostListener('window:resize', ['$event'])
    @HostListener('window:scroll', ['$event'])
    @HostListener('window:mousemove', ['$event'])
    @HostListener('window:touchend', ['$event'])
    @HostListener('window:mouseup', ['$event'])
    @HostListener('window:wheel', ['$event'])
    onEvent(event: any): void {
        this.userOperationService.setFlag(true, event.type);
    }

    isNotOffline() {
        return this.roleAccessService.currentRole !== OFFLINE_USER_MODE;
    }

    ngOnInit(): void {
        this.store.pipe(
            take(1),
            select(selectHasCleanUiSettingsState),
            filter(shouldNotifyUISettingGotRestored => shouldNotifyUISettingGotRestored),
        ).subscribe( () => {
            this.store.dispatch(cleanUiSettingsCache({payload: false}));
            this.store.dispatch(
                addNotification({
                    payload: {
                        detail: 'Legacy layout setting is incompatable, fallen back to the default one',
                        severity: 'warning',
                        summary: 'LayoutSettings'
                    }
                })
            );
        });

        this.store.dispatch(this.statusBarModel.subscribeModelWatch());
        // disabled until CB007962/RCP3024 completed
        if (this.featureFlag.isFeatureEnabled(AvailableFeatureFlagsEnum.WIP_CB007962_TOKENINFO)) {
            this.store.dispatch(this.statusBarModel.subscribeUserOperationWatch());
            this.store.dispatch(this.statusBarModel.subscribeTokenInfoWatch());
            this.store.select(getTokenInfo)
                .pipe(
                    pairwise(),
                    takeUntil(this.ngUnsubscribe),
                ).subscribe(
                ([pre, now]) => {
                    this.showTokenInfoDialog(pre, now);
                }
            );
        }
    }

    ngOnDestroy(): void {
        this.store.dispatch(this.statusBarModel.unsubscribeModelWatch());

        if (this.featureFlag.isFeatureEnabled(AvailableFeatureFlagsEnum.WIP_CB007962_TOKENINFO)) {
            this.store.dispatch(this.statusBarModel.unsubscribeUserOperationWatch());
            this.store.dispatch(this.statusBarModel.unsubscribeTokenInfoWatch());
            this.ngUnsubscribe.next();
            this.ngUnsubscribe.complete();
        }
    }

    navigateToHomePage() {
        this.store.dispatch(forceRedirect({routerLink: RouterLink.root}));
    }

    showTokenInfoDialog(pre: TokenInfo, now: TokenInfo): void {
        if (now?.User === '') return;
        let dialogMsg = '';
        const preEnforceLogout = pre.EnforceLogout;
        const nowEnforceLogout = now?.EnforceLogout;
        if (!preEnforceLogout && nowEnforceLogout) {
            const delayTime = DateTime.convertSecondToHourMinuteSecond(now?.EnforceLogoutDelay);
            dialogMsg = `You will be disconnected in ${delayTime}. ${this.formatLogoutReason(now?.EnforceLogoutReason)}.`;
        }
        if (preEnforceLogout && !nowEnforceLogout) {
            dialogMsg = 'You will not be disconnected. The restricted access feature is deactivated.';
        }
        if (!_.isEmpty(dialogMsg)) {
            this.store.dispatch(closeDialogsByName({closedComponent: TokenInfoDialogComponent}));
            this.store.dispatch(openDialog({
                componentType: TokenInfoDialogComponent,
                config: {
                    header: 'Access restrictions alert',
                    closable: false,
                    data: {
                        alertMessage: dialogMsg,
                    }
                }
            }));
        }
    }

    formatLogoutReason(logoutReason: string | undefined): string {
        if (!logoutReason) return '';
        if (logoutReason.startsWith('1')) {
            return 'The restricted access feature is activated';
        } else if (logoutReason.startsWith('2')) {
            return 'High priority user connected';
        } else {
            return '';
        }
    }
}
