import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'formatNodeName'
})
export class FormatNodeNamePipe implements PipeTransform {
    transform(value: any, isCounter = false): string {
        let nodeName = '';
        if (value) {
            if (value.includes('/') && !value.includes('*-*')) {
                const splitedValues: string[] = value.split('/');
                nodeName = splitedValues[splitedValues.length - 1];
            } else {
                const splitedValues: string[] = value.split('*-*');
                nodeName = splitedValues[splitedValues.length - 1];
                if (nodeName.includes('__')) {
                    const abbreviationNameValues: string[] = nodeName.split('__');
                    const lastIndexOfName = abbreviationNameValues.length - 1;
                    if (isCounter) {
                        nodeName = `${abbreviationNameValues[lastIndexOfName - 1]} (${abbreviationNameValues[lastIndexOfName]})`;
                    } else {
                        nodeName = abbreviationNameValues[lastIndexOfName];
                    }
                }
            }
        }

        return nodeName;
    }
}
