import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { PollingService } from '@oam/core';
import { asyncScheduler } from 'rxjs';
import { exhaustMap, map, mergeMap, takeUntil, tap, withLatestFrom } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { selectUserTokenExpirationTime } from '@oam/core/lib/user/core/reducers/user-reducer';
import { TokenExpirationTimeService } from '@oam/core/lib/services/token-expiration-time.service';
import {
    setRemainingSeconds,
    setRemainingTime,
    startCountingSeconds,
    startCountingSessionTime, stopCountingSeconds
} from '../actions/session-token-time.actions';

@Injectable()
export class SessionTokenTimeEffects {
    remainingSeconds = 60;

    stopCountingSeconds$ = createEffect(() =>
        this.actions$.pipe(
            ofType(stopCountingSeconds),
            tap(() => this.remainingSeconds = 60)
        ), { dispatch: false }
    );

    setRemainingTime$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCountingSessionTime),
            withLatestFrom(this.store.pipe(select(selectUserTokenExpirationTime))),
            mergeMap(() => this.pollingService.getTimer(scheduler).pipe(
                map(() => {
                    this.tokenExpirationService.calculateRemainingTime();
                    const remainingTime = this.tokenExpirationService.getTokenExpirationTime();

                    return setRemainingTime({ remainingTime });
                })
            ))
        )
    );

    startCountingSeconds$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCountingSeconds),
            exhaustMap(() => this.pollingService.getTimer(scheduler, 0, 1000).pipe(
                takeUntil(this.stopCountingSeconds$),
                map(() => {
                    if (this.remainingSeconds === 0) {
                        return setRemainingSeconds({ remainingSeconds: null });
                    }

                    return setRemainingSeconds({ remainingSeconds: `${this.remainingSeconds--} sec` });
                })
            ))
        )
    );


    constructor(
        private readonly actions$: Actions,
        private readonly pollingService: PollingService,
        private readonly store: Store,
        private readonly tokenExpirationService: TokenExpirationTimeService) {
    }

}
