import { FormGroup, ValidationErrors } from '@angular/forms';

const MISSING_MANDATORY_FIELD = {
    missingMandatoryField: true
};

export function oneOfControlsMandatoryValidator(form: FormGroup): ValidationErrors | null {
    const password = form.get('password')?.value;
    const skipRenewal = form.get('skipRenewal')?.value;

    if (skipRenewal) return null;
    if (!password) return MISSING_MANDATORY_FIELD;

    return null;
}
