import { Component, OnDestroy, OnInit } from '@angular/core';
import { DetailFiles, ISnapshotOperationState } from '../../core/models';
import { select, Store } from '@ngrx/store';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
    selectCollectingSnapshotAvailability,
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot-server-status.selector';
import {
    selectCollectingSnapshotFilesChanged
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot.selector';
import {
    cleanServerStatusIfOperationIsNotOngoing,
    startCheckingSnapshotServerStatus,
    startGettingRemoteSnapshotNeedToBeTransferred
} from '@oam/diagnostic-snapshot/core/actions/snapshot.action';
import {
    selectRemoteSnapshotFileName
} from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';
import { FeatureFlag } from '@oam/feature-flag';
import { AvailableFeatureFlagsEnum } from '@oam/core/lib/environment-variables/core/models';

@Component({
    selector: 'diagnostic-snapshot-home',
    templateUrl: './diagnostic-snapshot-home.component.html',
    styleUrls: ['./diagnostic-snapshot-home.component.scss']
})
export class DiagnosticSnapshotHomeComponent implements OnInit, OnDestroy {
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    collectingSnapshotFiles$: Observable<DetailFiles[]>;
    isSnapshotIdle$: Observable<boolean>;
    remoteSnapshotFileName$: Observable<string | undefined>;

    constructor(
        private readonly store: Store,
        private readonly featureFlag: FeatureFlag
    ) {
    }

    ngOnInit() {
        if (this.featureFlag.isFeatureEnabled(AvailableFeatureFlagsEnum.PRODUCT_SNAPSHOT_TRANSFER)) {
            this.store.dispatch(startGettingRemoteSnapshotNeedToBeTransferred());
        }

        this.isSnapshotIdle$ = this.store.pipe(
            select(selectCollectingSnapshotAvailability),
            takeUntil(this.ngUnsubscribe)
        );

        this.collectingSnapshotFiles$ = this.store.pipe(
            select(selectCollectingSnapshotFilesChanged),
            takeUntil(this.ngUnsubscribe)
        );

        this.remoteSnapshotFileName$ = this.store.pipe(
            select(selectRemoteSnapshotFileName),
            takeUntil(this.ngUnsubscribe)
        );

        this.store.dispatch(startCheckingSnapshotServerStatus());
    }

    ngOnDestroy() {
        this.store.dispatch(cleanServerStatusIfOperationIsNotOngoing());
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
