import { format } from 'date-fns';
import { DateTimeFormats } from '../models';

/* eslint-disable webem/no-direct-date-use */
export class DateTime extends Date {
    constructor(dateTime?: number | string) {
        super(dateTime || new Date());
    }

    static get currentDate(): string {
        return format(new Date(), DateTimeFormats.Date);
    }

    static get currentTime(): string {
        return format(new Date(), DateTimeFormats.Time);
    }

    static get currentDateTime(): string {
        return format(new Date(), DateTimeFormats.DateTime);
    }

    static fromData(year: number, month: number, date = 0, hours = 0, minutes = 0, seconds = 0, ms = 0): DateTime {
        return new DateTime(new Date(year, month, date, hours, minutes, seconds, ms).toString());
    }

    static toFormat(date: Date, dateTimeFormat: DateTimeFormats): string {
        return format(date, dateTimeFormat);
    }

    static convertSecondToHourMinuteSecond(seconds: number): String {
        const h = Math.floor(seconds / 3600);
        const m = Math.floor((seconds / 60 % 60));
        const s = Math.floor((seconds % 60));

        const hour = h === 0 ? '' : `${h} ${ h > 1 ? 'hours ' : 'hour '}`;
        const min = m === 0 ? '' : `${m} ${ m > 1 ? 'minutes ' : 'minute '}`;
        const sec = s === 0 ? '' : `${s} ${ s > 1 ? 'seconds ' : 'second '}`;

        return `${hour}${min}${sec}`.trim();
    }

    toFormattedDate(): string {
        return DateTime.toFormat(this, DateTimeFormats.Date);
    }

    toFormattedTime(): string {
        return DateTime.toFormat(this, DateTimeFormats.Time);
    }

    toFormattedDateTime(timezone = false): string {
        return DateTime.toFormat(this, timezone ? DateTimeFormats.DateTime : DateTimeFormats.DateTimeNoTimezone);
    }

    toFormattedDateTimeWithTSeparator(): string {
        return DateTime.toFormat(this,  DateTimeFormats.DateTimeWithTSeparator);
    }

    toFormattedFileDateTime(): string {
        return DateTime.toFormat(this, DateTimeFormats.FileDateTime);
    }

    toFormattedChartDateTime(): string {
        return DateTime.toFormat(this, DateTimeFormats.ChartDateTime);
    }
}
