import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Notification } from '@oam/core/lib/models/notification.type';
import { MILI_SECONDS_IN_MINUTE, MILI_SECONDS_IN_HOUR } from './notification-const';

type SinceOccurred = {
    h: number;
    min: number;
};

@Component({
  selector: 'ui-webem-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements OnChanges {
    @Input() notification: Notification;
    @Input() openDate: Date;
    icon: string;
    sinceOccurred: SinceOccurred = {
        h: 0,
        min: 0
    };

    constructor() {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.openDate && changes.openDate.currentValue) {
            const difference = Math.max(changes.openDate.currentValue.getTime() - this.notification.date.getTime(), 0);
            this.sinceOccurred = {
                h: Math.floor(difference / MILI_SECONDS_IN_HOUR),
                min: Math.floor(difference % MILI_SECONDS_IN_HOUR / MILI_SECONDS_IN_MINUTE)
            };
        }
    }
}
