import { upperFirst } from 'lodash';
import { Component, Input, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Observable } from 'rxjs';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { StatusModelBar, ModelState } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { ActivatedRoute } from '@angular/router';
import { FormatMrbtsIdPipe } from '@oam/layouts/lib/modules/page-header/pipes/format-mrbts-id.pipe';
import { nullableValueFormat } from '@oam/shared/lib/functions/nullable-value-format';


const formatMrbtsId = new FormatMrbtsIdPipe();
@Component({
    selector: 'webem-general-status-info',
    templateUrl: './general-status-info.component.html',
    styleUrls: ['./general-status-info.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class GeneralStatusInfoComponent implements OnInit {
    @Input() model: StatusModelBar;
    modelState$: Observable<ModelState>;
    mrbtsIdAndNameView: boolean;
    mrbtsNameView: boolean;
    operationatStateView: boolean;

    constructor(
        private readonly store: Store,
        private readonly activatedRoute: ActivatedRoute
    ) { }

    ngOnInit() {
        this.modelState$ = this.store.pipe(
            select(this.model.selectModelState)
        );

        this.operationatStateView = this.model.operationatStateView;
    }

    navigateToStatus() {
        this.store.dispatch(redirect({routerLink: this.model.statusPropertiesLink, activatedRoute: this.activatedRoute}));
    }

    getStatusTitle(modelState: ModelState): string {
        const btsName = nullableValueFormat(this.model.name);

        return modelState.mrbtsId !== this.model.initialState.mrbtsId && modelState.btsName !== this.model.initialState.btsName
            ? this.getStatusTitleWithOperationalState(modelState)
            : btsName;
    }

    getStatusTitleWithOperationalState(modelState: ModelState): string {
        const operationalStatePart = modelState.operationalState ? `: ${upperFirst(modelState.operationalState)}` : '';
        const mrbtsId = nullableValueFormat(modelState.mrbtsId);
        const btsName = nullableValueFormat(modelState.btsName);

        return `${formatMrbtsId.transform(mrbtsId)} / ${btsName}${operationalStatePart}`;
    }

    getSoftwareVersionTitle(softwareVersion: string | null): string {
        return !!softwareVersion && softwareVersion !== this.model.initialState.softwareVersion
            ? `${this.model.name} software version: ${softwareVersion}`
            : `${this.model.name} software version`;
    }

    isEnabledState(operationalState: string | null): boolean {
        return operationalState === this.model.operationalState.Enabled
            || operationalState === this.model.operationalState.OnAir;
    }

    isTDSState(operationalState: string | null): boolean {
        return operationalState === this.model.operationalState.Test;
    }

    getStyleByOperationalStatus(operationalState: string | null): string {
        let style = '';
        if (this.isTDSState(operationalState)) {
            style = 'tds';
        } else if (operationalState === this.model.operationalState.OnAir) {
            style = 'enabled';
        }

        return style;
    }

    getStateMessage(operationalState: string | null): string | null {
        return this.isTDSState(operationalState)? 'TDS' : operationalState;
    }
}
