import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { asyncScheduler, of } from 'rxjs';
import {
    catchError,
    exhaustMap,
    map,
    mergeMap,
    takeUntil,
    withLatestFrom,
    filter
} from 'rxjs/operators';
import { Store, select } from '@ngrx/store';
import {
    cleanTransferSnapshotState,
    cleanTransferStateIfTransferIsNotOngoing,
    setRemoteSnapshotFileNameAndOperationId,
    startGettingRemoteSnapshotError,
    startGettingRemoteSnapshotNeedToBeTransferred,
    startTransferSnapshotToNMS,
    stopGettingRemoteSnapshotNeedToBeTransferred,
    transferSnapshotFailed,
    transferSnapshotToNMSIsFinished
} from '@oam/diagnostic-snapshot/core/actions/snapshot.action';
import { SnapshotStatus } from '@oam/cu-asm-api';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import { PollingService } from '@oam/core';
import { SnapshotFetchFilesService } from '@oam/diagnostic-snapshot/core/services/snapshot-fetch-files.service';
import { SnapshotService } from '@oam/cu-asm-api/services';
import {
    selectOperationIdForRemoteSnapshot,
    selectTransferSnapshotIsOngoing
} from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';


@Injectable()
export class TransferSnapshotToNmsEffects {
    transferSnapshotFailed$ = createEffect(() =>
        this.actions$.pipe(
            ofType(transferSnapshotFailed),
            map(({ error }) => addNotification({
                payload: {
                    detail: error.content,
                    severity: 'error',
                    summary: 'Snapshot Transfer Failed'
                }
            }))
        )
    );

    stopGettingRemoteSnapshotNeedToBeTransferred$ = createEffect(() =>
        this.actions$.pipe(
            ofType(stopGettingRemoteSnapshotNeedToBeTransferred)
        ), { dispatch: false }
    );

    startGettingRemoteSnapshotNeedToBeTransferred$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startGettingRemoteSnapshotNeedToBeTransferred),
            exhaustMap(() => {
                return this.pollingService.getTimer(scheduler).pipe(
                    takeUntil(this.stopGettingRemoteSnapshotNeedToBeTransferred$),
                    mergeMap(() => this.snapshotService.getRemoteSnapshotOperations({status: SnapshotStatus.Failed})
                        .pipe(
                            map(response => setRemoteSnapshotFileNameAndOperationId({
                                remoteSnapshotFileName: response[0]?.remoteName,
                                operationId: response[0]?.operationId
                            })),
                            catchError(error => of(startGettingRemoteSnapshotError(error)))
                        )
                    )
                );
            })
        )
    );

    addNotificationForTransferSnapshot$ = createEffect(() =>
        this.actions$.pipe(
            ofType(startTransferSnapshotToNMS),
            map(() => addNotification({
                payload: {
                    detail: 'Transfer snapshot to nms started',
                    severity: 'info',
                    summary: 'Snapshot Transfer Started'
                }
            }))
        )
    );

    startTransferSnapshotToNMS$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startTransferSnapshotToNMS),
            withLatestFrom(this.store.select(selectOperationIdForRemoteSnapshot)),
            exhaustMap(([, operationId]) =>
                this.snapshotService.uploadRemoteSnapshot({ operationId: operationId as string })
                    .pipe(
                        mergeMap(() => [
                            addNotification({
                                payload: {
                                    detail: 'Transfer snapshot to nms succeeded',
                                    severity: 'success',
                                    summary: 'Snapshot Transfer Succeeded'
                                }
                            }),
                            transferSnapshotToNMSIsFinished()
                        ]),
                        catchError(( {error} ) => {
                            return of(transferSnapshotFailed({ error: JSON.parse(error) }));
                        })
                    )
            )
        )
    );

    cleanTransferStateIfTransferIsNotOngoing$ = createEffect(() =>
        this.actions$.pipe(
            ofType(cleanTransferStateIfTransferIsNotOngoing),
            withLatestFrom(
                this.store.pipe(select(selectTransferSnapshotIsOngoing))
            ),
            filter(([, isOngoing]) => !isOngoing),
            mergeMap(() => [cleanTransferSnapshotState(), stopGettingRemoteSnapshotNeedToBeTransferred()])
        )
    );

    constructor(private readonly actions$: Actions,
                private readonly pollingService: PollingService,
                private readonly snapshotFetchFilesService: SnapshotFetchFilesService,
                private readonly snapshotService: SnapshotService,
                private readonly store: Store) {}
}
