import { createAction, props } from '@ngrx/store';

export enum PageNavigationActionTypes {
    changeMainMenuExpandState = '[User] [Main menu] Change expand state'
}

export interface ChangeExpandStateProps {
    payload: { isExpanded: boolean }
}

export const changeMainMenuExpandState = createAction(PageNavigationActionTypes.changeMainMenuExpandState, props<ChangeExpandStateProps>());
