import { ModuleWithProviders, NgModule } from '@angular/core';
import { ErrorsTabConfig } from '@oam/errors/lib/core/services/errors-tab-config.service';

@NgModule({})
export class ErrorsTabConfigModule {
    static forRoot(additionalData: ErrorsTabConfig): ModuleWithProviders<ErrorsTabConfigModule> {
        return {
            ngModule: ErrorsTabConfigModule,
            providers: [
                {
                    provide: ErrorsTabConfig,
                    useValue: additionalData
                }
            ]
        };
    }
}
