import { createAction } from '@ngrx/store';

enum CommissioningActionTypes {
    startCommissioning = '[EFFECT] commissioning in progress',
    stopCommissioning = '[EFFECT] commissioning finished'
}

export const startCommissioning = createAction(CommissioningActionTypes.startCommissioning);
export const stopCommissioning = createAction(CommissioningActionTypes.stopCommissioning);
