import { Injectable } from '@angular/core';
import { PeService } from '@oam/cu-asm-api/services';
import { NotSupportedPlanOriginError } from '../../../lib/core/model';
import {
    UpdateParametersActionRequestEndpoint,
    UpdateParametersRequestEndpoint
} from '@oam/configuration-shared/lib/core/model/update-parameters-endpoints';
import { Observable, throwError } from 'rxjs';
import {
    AddStructuresActionRequestEndpoint,
    AddStructuresRequestEndpoint,
    RemoveStructuresActionRequestEndpoint,
    RemoveStructuresRequestEndpoint,
    RevertStructuresActionRequestEndpoint,
    RevertStructuresRequestEndpoint
} from '@oam/configuration-shared/lib/core/model/update-structures-endpoints';
import { PlanOrigin } from '@oam/configuration-shared/lib/core/model';

@Injectable()
export class PlanApiUpdateParametersEndpointsService {
    constructor(private readonly peService: PeService) {
    }

    getUpdateParametersRequestEndpoint(planOrigin: PlanOrigin): UpdateParametersRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getUpdateParametersActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): UpdateParametersActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteUpdateParameterAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    getAddSimpleListElementRequestEndpoint(planOrigin: PlanOrigin): AddStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getAddSimpleListElementActionRequestEndpoint(
        planId: string, planOrigin: PlanOrigin): AddStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteAddSimpleListElementAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    getRemoveSimpleListElementRequestEndpoint(planOrigin: PlanOrigin): RemoveStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getRemoveSimpleListElementActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): RemoveStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    getRevertSimpleListElementRequestEndpoint(planOrigin: PlanOrigin): RevertStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getRevertSimpleListElementActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): RevertStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteRevertSimpleListElementAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    deleteRemoveSimpleListElementAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    getAddStructureRequestEndpoint(planOrigin: PlanOrigin): AddStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getAddStructuresActionRequestEndpoint(
        planId: string, planOrigin: PlanOrigin): AddStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteAddStructureAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    getRemoveStructureRequestEndpoint(planOrigin: PlanOrigin): RemoveStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getRemoveStructureActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): RemoveStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteRemoveStructureAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    getRevertStructureRequestEndpoint(planOrigin: PlanOrigin): RemoveStructuresRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersRequestEndpoint(planOrigin);
    }

    getRevertStructureActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): RemoveStructuresActionRequestEndpoint | Observable<never> {
        return this.getDefaultUpdateParametersActionRequestEndpoint(planId, planOrigin);
    }

    deleteRevertStructureAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        return this.getDefaultDeleteUpdateParameterAction(planId, actionId, planOrigin);
    }

    private getDefaultUpdateParametersRequestEndpoint(planOrigin: PlanOrigin): UpdateParametersRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.updateParameters(request);
            case PlanOrigin.DELTA:
                return request => this.peService.updateParametersOfDeltaPlan(request);
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    private getDefaultUpdateParametersActionRequestEndpoint(planId: string, planOrigin: PlanOrigin): UpdateParametersActionRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.getUpdateParametersAction({
                    planId,
                    actionId: request.actionId
                });
            case PlanOrigin.DELTA:
                return request => this.peService.getUpdateParametersOfDeltaPlanAction({
                    planId,
                    actionId: request.actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    private getDefaultDeleteUpdateParameterAction(planId: string, actionId: string, planOrigin: PlanOrigin): Observable<void> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteUpdateParametersAction({ planId, actionId });
            case PlanOrigin.DELTA:
                return this.peService.deleteUpdateParametersOfDeltaPlanAction({ planId, actionId });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }
}
