import {
    provideSplitViewConfigService,
    SPLIT_VIEW_CONFIG,
} from './core/services/split-view-config-token';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { SplitViewRestrictionsService } from './core/services/split-view-restrictions.service';
import { SplitViewConfig } from '@oam/split-view/lib/core/models';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';

@NgModule({})
export class SplitViewRestrictionsModule {
    static forRoot(splitViewConfig: SplitViewConfig): ModuleWithProviders<SplitViewRestrictionsModule> {

        return {
            ngModule: SplitViewRestrictionsModule,
            providers: [
                {
                    provide: SPLIT_VIEW_CONFIG,
                    useValue: splitViewConfig
                },
                {
                    provide: SplitViewRestrictionsService,
                    useFactory: provideSplitViewConfigService,
                    deps: [ SPLIT_VIEW_CONFIG, NavigationService ]
                },
                {
                    provide: VnfcSharedService
                }
            ]
        };
    }
}
