import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import * as fromCompatibilityBrowser from './reducers/compatibility-browser.reducer';
import { CommonModule } from '@angular/common';
import { WebemButtonModule } from '../../lib/button/button.module';
import { BrowserIncompatibilityBarComponent } from './components/browser-incompatibility-bar/browser-incompatibility-bar.component';

@NgModule({
    declarations: [BrowserIncompatibilityBarComponent],
    imports: [
        CommonModule,
        WebemButtonModule,
        StoreModule.forFeature('browserCompatibility', fromCompatibilityBrowser.reducer)
    ],
    providers: [],
    exports: [BrowserIncompatibilityBarComponent]
})
export class BrowserCompatibilityModule {
}
