import { createFeatureSelector, createSelector } from '@ngrx/store';
import { RouterReducerState } from '@ngrx/router-store';
import { RouterStateUrl } from '@oam/core/lib/models/router-state-url';

export type RouterState = RouterReducerState<RouterStateUrl>;

export const ROUTER_STATE = 'router';

const selectRouterState = createFeatureSelector<RouterState>(ROUTER_STATE);

export const selectUrl = createSelector(selectRouterState, (routerState): (string | undefined) => {
    return routerState?.state?.url;
});

export const selectUrlWithoutParams = createSelector(selectRouterState, (routerState) => {
    let url = routerState.state.url;
    Object.values(routerState.state.params).forEach(param => {
        url = url.replace(param, '');
    });

    return url.substring(url.length - 1, url.length) === '/' ? url.slice(0, -1) : url;
});
