import { InjectionToken } from '@angular/core';
import { DetailsPanelConfigService } from '@oam/details-panel/lib/core/services/details-panel-config.service';
import { DetailsPanelConfig } from '@oam/details-panel/lib/core/model/details-panel-config';


export const DETAILS_PANEL_CONFIG = new InjectionToken<DetailsPanelConfig>(
    'DetailsPanelConfig'
);


export function provideDetailsPanelConfigService(detailsPanelConfig: any) {
    const detailsPanelConfigService = new DetailsPanelConfigService();
    detailsPanelConfigService.config = detailsPanelConfig;

    return (detailsPanelConfigService);
}
