import { Injectable } from '@angular/core';
import { MenuItemMap } from '@oam/core/lib/models/menuu-item-map.type';

export class ErrorsTabConfig {
    routes: MenuItemMap;
}

@Injectable({
    providedIn: 'root'
})
export class ErrorsTabConfigService {
    private readonly routes: MenuItemMap;

    constructor(config: ErrorsTabConfig) {
        this.routes = config.routes;
    }

    get availableRoutes(): MenuItemMap {
        return this.routes;
    }
}
