import { Directive, EventEmitter, HostListener, Output } from '@angular/core';

@Directive(
    { selector: '[uiWebemCapsLock]' }
)

export class TrackCapsLockDirective {
    @Output('uiWebemCapsLock') capsLock = new EventEmitter<boolean>();

    capsLockKeyCode = 'CapsLock';

    @HostListener('window:keydown', ['$event'])
    onKeyDown(event: KeyboardEvent): void {
        if (event.code === this.capsLockKeyCode) {
            this.capsLock.emit(event.getModifierState && event.getModifierState('CapsLock'));
        }
    }
    @HostListener('window:keyup', ['$event'])
    onKeyUp(event: KeyboardEvent): void {
        if (event.code === this.capsLockKeyCode) {
            this.capsLock.emit(event.getModifierState && event.getModifierState('CapsLock'));
        }
    }
}
