import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import * as fromDetailsPanel from './core/reducers/detail-panel.reducer';
import { StoreModule } from '@ngrx/store';
import { DetailsPanelComponent } from './components/details-panel/details-panel.component';
import { DetailsPanelToggleButtonComponent } from './components/details-panel-toggle-button/details-panel-toggle-button.component';
import { ButtonModule } from 'primeng/button';
import { TabViewModule } from 'primeng/tabview';
import { ParametersTreeModule } from 'libs/ui-webem/src/lib/parameters-tree/parameters-tree.module';
import { ChooseObjectMenuModule } from 'libs/ui-webem/src/lib/choose-object-menu/choose-object-menu.module';
import { FilterInputModule } from 'libs/ui-webem/src/lib/filter-input/filter-input.module';
import { ParametersContentDirective } from '@oam/details-panel/lib/directives/parameters-content.directive';
import { OverviewContentDirective } from '@oam/details-panel/lib/directives/overview-content.directive';
import { SeparateContentDirective } from '@oam/details-panel/lib/directives/separate-content.directive';
import { StatusTabbedPanelComponent } from '@oam/details-panel/lib/components/status-tabbed-panel/status-tabbed-panel.component';
import { WeightFactorDetailTabContentDirective } from './directives/weight-factor-detail-tab-content.directive';
