import { Action, createFeatureSelector, createReducer, createSelector, on, State } from '@ngrx/store';
import { changeMainMenuExpandState } from '../actions/main-menu.actions';

export const MAIN_MENU_KEY = 'mainMenu';

export interface MainMenuState {
    isExpanded: boolean;
}

const initialState: MainMenuState = {
    isExpanded: false
};

const onChangeMainMenuExpandState = on(changeMainMenuExpandState, (state: MainMenuState, { payload: { isExpanded } }) => {
    return { ...state, isExpanded };
});

const mainMenuReducer = createReducer(initialState, onChangeMainMenuExpandState);

// AOT require to have decorator createReducer wrapped in function.
export function reducer(state: MainMenuState, action: Action) {
    return mainMenuReducer(state, action);
}
export const selectMainMenuState = createFeatureSelector<MainMenuState>(MAIN_MENU_KEY);
export const selectIsExpandedMainMenu = createSelector(selectMainMenuState, (state) => state.isExpanded);
