import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { map } from 'rxjs/operators';
import { forceRedirect, redirect } from '@oam/core/lib/actions/router.actions';
import { SplitViewRestrictionsService } from '@oam/split-view/lib/core/services/split-view-restrictions.service';
import { getSplitViewOutlets } from '../functions/get-outlets-from-router-link';


@Injectable()
export class RouterEffects {

    redirect$ = createEffect(() =>
        this.actions$.pipe(
            ofType(redirect),
            map((action) => {
                const navigationExtras: NavigationExtras = {};
                if (action.data) {
                    navigationExtras['state'] = {
                        data: action.data
                    };
                }

                const rootPath = '/';
                navigationExtras['queryParams'] = action.redirectUrl && action.redirectUrl !== rootPath ?
                    { redirectUrl: action.redirectUrl } : null;
                const splitViewActiveRoute = action.activatedRoute.pathFromRoot.find(route => route.outlet === 'topView' || route.outlet === 'bottomView');
                const isSplitViewEnabled = !!splitViewActiveRoute;
                const curUrlTree = this.router.parseUrl(this.router.url);
                const topViewUrl = curUrlTree?.root?.children?.primary?.children['topView']?.segments
                    .map((outlet) => outlet.path).join('/');
                const bottomViewUrl = curUrlTree?.root?.children?.primary?.children['bottomView']?.segments
                    .map((outlet) => outlet.path).join('/');
                const redirectUrl = action.routerLink.split('/').filter(url => !!url);
                const isRedirectToSplitView = redirectUrl[0] === 'split-view' && redirectUrl.length > 1;
                if (isSplitViewEnabled && this.splitViewRestrictionsService.notInSplitViews(redirectUrl)){
                    return this.router.navigate([action.routerLink], navigationExtras);
                }
                if (isSplitViewEnabled && splitViewActiveRoute?.outlet === 'topView') {
                    if (this.splitViewRestrictionsService.isSplitBottomView(redirectUrl.join('/'))) {
                        return this.router.navigate(
                            ['split-view', getRouteToken(),{
                                outlets: {
                                    ['bottomView']: redirectUrl,
                                    ['topView']: topViewUrl
                                }
                            }],
                            navigationExtras
                        );
                    } else if (bottomViewUrl) {
                        return this.router.navigate(
                            ['split-view', {
                                outlets: {
                                    ['bottomView']: bottomViewUrl,
                                    ['topView']: redirectUrl
                                }
                            }],
                            navigationExtras
                        );
                    } else {
                        return this.router.navigate(
                            ['split-view', {
                                outlets: {
                                    [splitViewActiveRoute.outlet]: redirectUrl
                                }
                            }],
                            navigationExtras
                        );
                    }
                } else if (!isSplitViewEnabled && isRedirectToSplitView) {
                    const outlets = getSplitViewOutlets(action.routerLink);

                    return this.router.navigate(
                        ['split-view', outlets]
                    );
                } else if (!isSplitViewEnabled) {
                    return this.router.navigate([action.routerLink], navigationExtras);
                }
            })
        ), { dispatch: false }
    );

    forceRedirect$ = createEffect(() =>
        this.actions$.pipe(
            ofType(forceRedirect),
            map((action) => {
                const rootPath = '/';

                return this.router.navigate([action.routerLink], {
                    queryParams: action.redirectUrl && action.redirectUrl !== rootPath ? { redirectUrl: action.redirectUrl } : null
                });
            })
        ), { dispatch: false }
    );

    constructor(private readonly actions$: Actions, private readonly router: Router,
                private readonly splitViewRestrictionsService: SplitViewRestrictionsService) {
    }
}

function getRouteToken() {
  return { routeToken: Math.random() };
}
