import { TreeNodeData } from '../models/pm-tree.interface';
import { PmMeasurement } from '../../models/measurement.interface';
import { CounterNode } from '@oam/pm-shared/lib/measurement-store/models/counters-selection.interface';

const DELIMITER = '*-*';
const ABBREV_DELIMITER = '__';
const DISTNAME_DELIMITER = '/';

export interface CounterIdElements {
    id: string,
    dimensionDistName: string,
    measurementId: string
}
export type CounterObjectNode = {
    id: string,
    parentId?: string;
    object?: CounterNode,
    children: CounterObjectNode[],
    name: string,
    isExpanded: boolean,
    isGroup: boolean,
};

export function getNodesData(pmMeasurements: PmMeasurement[]): Map<string, string[]> {
    const nodesMap = new Map<string, string[]>();

    pmMeasurements.forEach((measurement: PmMeasurement) => {
        const parentObjectDN = measurement.dimensionDistName;

        if (parentObjectDN) {
            const measurementNodeData: TreeNodeData = {
                dimensionDistName: parentObjectDN,
                measurementId: measurement.id,
                measurementAbbreviation: measurement.abbreviation
            };
            const measurementId = getIdFromNodesData(measurementNodeData);
            addDistnameNodes(parentObjectDN, nodesMap, measurementId);

            measurement.counters.forEach((counter) => {
                const counterNodeData: TreeNodeData = {
                    dimensionDistName: parentObjectDN,
                    measurementId: measurement.id,
                    measurementAbbreviation: measurement.abbreviation,
                    counterId: counter.counterId,
                    counterAbbreviation: counter.counterAbbreviation
                };
                const counterNodeId = getIdFromNodesData(counterNodeData);
                const measurementIds = nodesMap.get(measurementId);

                nodesMap.set(measurementId, !measurementIds ?
                    [counterNodeId] : [...measurementIds, counterNodeId]);
            });
        }
    });

    return nodesMap;
}

function addDistnameNodes(parentObjectDN: string, nodesMap: Map<string, string[]>, measurementId: string) {
    let parentDistname = parentObjectDN;
    let current = parentDistname.split(DISTNAME_DELIMITER)[0];

    while (parentDistname.includes(DISTNAME_DELIMITER)) {
        const parts = parentDistname.split(DISTNAME_DELIMITER);
        const newCurrent = current + DISTNAME_DELIMITER + parts[1];
        const currentNodeChildren = nodesMap.get(current);
        if (currentNodeChildren) {
            if (!currentNodeChildren.includes(newCurrent)) {
                nodesMap.set(current, [...currentNodeChildren, newCurrent]);
            }
        } else {
            nodesMap.set(current, [newCurrent]);
        }

        current = newCurrent;
        parentDistname = parentDistname.substring(parentDistname.indexOf(DISTNAME_DELIMITER) + 1);
    }

    const currentNodes = nodesMap.get(current);
    nodesMap.set(current, !currentNodes ?
        [measurementId] : [...currentNodes, measurementId]);
}

export function hasSelectChild(children: CounterObjectNode[], selectedCounters: CounterNode[]): boolean {
    return children?.some((child: { id: string; children: any; }) => {
        return selectedCounters.some(selectedCounter => {
            return getCounterUniqueId(selectedCounter) === child.id;
        }) || hasSelectChild(child.children, selectedCounters);
    });
}

export function getCounterUniqueId(counter: CounterNode): string {
    return `${counter.counterId}:${counter.dimensionDistName}`;
}

export function getCounterIdElementsFromId(id: string): CounterIdElements {
    const [counterId, dimensionDistName] = id.split(':');
    const measurementId = counterId.split('C')[0].slice(1);

    return {
        id: counterId,
        dimensionDistName,
        measurementId
    };
}

export function getIdFromNodesData(nodeData: TreeNodeData): string {
    const counterId = nodeData.counterId ? `${DELIMITER}${nodeData.counterId}` : '';
    const counterPart = nodeData.counterAbbreviation ? `${counterId}${ABBREV_DELIMITER}${nodeData.counterAbbreviation}` : counterId;
    const measurementId = nodeData.measurementId ? `${DELIMITER}${nodeData.measurementId}` : '';
    const measurementPart = nodeData.measurementAbbreviation ?
        `${measurementId}${ABBREV_DELIMITER}${nodeData.measurementAbbreviation}` : measurementId;

    return `${nodeData.dimensionDistName}${measurementPart}${counterPart}`;
}

export function getParentIdFromNodesData(nodeData: TreeNodeData): string {
    const measurementId = nodeData.measurementId ? `${DELIMITER}${nodeData.measurementId}` : '';
    const measurementPart = nodeData.measurementAbbreviation ?
        `${measurementId}${ABBREV_DELIMITER}${nodeData.measurementAbbreviation}` : measurementId;

    return `${nodeData.dimensionDistName}${measurementPart}`;
}
