import { Action, createReducer, on } from '@ngrx/store';
import {
    startCommissioning,
    stopCommissioning
} from '../actions/commissioning-flag.actions';

export const commissioningFlagFeatureKey = 'commissioningFlag';

export interface FlagState {
    isCommissioningInProgress: boolean
}

export const initialState: FlagState = {
    isCommissioningInProgress: false
};

const onStartCommissioning = on(startCommissioning, (state: FlagState) => ({ ...state, isCommissioningInProgress: true }));
const onStopCommissioning = on(stopCommissioning, (state: FlagState) => ({ ...state, isCommissioningInProgress: false }));

const commissioningFlagReducer = createReducer(initialState, onStartCommissioning, onStopCommissioning);

export function reducer(state: FlagState, action: Action) {
    return commissioningFlagReducer(state, action);
}

