import { Action, createReducer, on } from '@ngrx/store';
import { SnapshotServerStatus } from '../models/snapshot-server-status';
import {
    setSnapshotServerStatus,
    checkSnapshotServerStatusError,
    cleanSnapshotServerStatus
} from '../actions/snapshot.action';

export const collectingSnapshotServerStatusFeatureKey = 'collectingSnapshotServerStatus';
export interface ICollectingSnapshotServerStatus {
    snapshotServerStatus?: SnapshotServerStatus | 'server error' | string;
}
export const initialState: ICollectingSnapshotServerStatus = {snapshotServerStatus: undefined};

export const collectingSnapshotServerStatusReducer = createReducer(initialState,
    on(setSnapshotServerStatus, (state, { snapshotServerStatus } ) => ({...state, snapshotServerStatus})),
    on(checkSnapshotServerStatusError, (state) => ({...state, snapshotServerStatus: 'server error'})),
    on(cleanSnapshotServerStatus, () => initialState),
);

export function reducer(state: ICollectingSnapshotServerStatus | undefined, action: Action) {
    return collectingSnapshotServerStatusReducer(state, action);
}
