import { createAction, props } from '@ngrx/store';
import { CounterInfo } from '@oam/cu-pmv2-api/models';
import { HttpErrorResponse } from '@angular/common/http';

export enum CountersInfoActionTypes {
    SetCountersInfo = '[PMV2] [PM Counters Info] Set counters info',
    ClearCountersInfo = '[PMV2] [PM Counters Info] Clear counters info',
    CountersInfoFetchingError = '[PMV2] [PM Counters Info] Counters info fetching error',
    SetCountersInfoPanelState = '[PMV2] [PM Counters Info] Set counters info panel state',
    UnsetCountersInfoPanelState = '[PMV2] [PM Counters Info] Unset counters info panel state',
    RetrievingCounterInfoFailed = '[PMV2] [PM Counters Info] Failed to retrieve counter info from server',
    RetrievingCounterInfoSucceed = '[PMV2] [PM Counters Info] Counter info successfully retrieved from the server'
}

export const setCountersInfo = createAction(
    CountersInfoActionTypes.SetCountersInfo,
    props<{info: CounterInfo[]}>()
);

export const clearCountersInfo = createAction(
    CountersInfoActionTypes.ClearCountersInfo,
);

export const setCountersInfoPanelState = createAction(
    CountersInfoActionTypes.SetCountersInfoPanelState,
    props<{id: string}>()
);

export const unsetCountersInfoPanelState = createAction(
    CountersInfoActionTypes.UnsetCountersInfoPanelState,
    props<{id: string}>()
);

export const countersInfoFetchingError = createAction(
    CountersInfoActionTypes.CountersInfoFetchingError,
    props<{error: HttpErrorResponse}>()
);

export const retrievingCounterInfoFailed = createAction(
    CountersInfoActionTypes.RetrievingCounterInfoFailed
);

export const retrievingCounterInfoSucceed = createAction(
    CountersInfoActionTypes.RetrievingCounterInfoSucceed
);

