import Dexie from 'dexie';
import { Injectable } from '@angular/core';
import { NGXLogInterface } from 'ngx-logger';
import { INDEXED_DB_ENV } from '../config';
import { isNumber } from 'lodash';


@Injectable({
    providedIn: 'root'
})

export class ConsoleLogsDatabaseService extends Dexie {
    private readonly session: number;
    private readonly dbSchema: { [key: string]: string | null };

    constructor() {
        super(INDEXED_DB_ENV.DB_NAME);
        this.dbSchema = {};
        this.session = Date.now();
        this.createStore();
    }

    async clearStore(): Promise<void> {
        this.openDbConnection();
        await this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .clear();
    }

    async addLog(newLog: ConsoleLogInterface): Promise<void> {
        this.openDbConnection();
        if (await this.isStoreLimitExceeded()) {
            await this.deleteOldLogs();
        }
        newLog.session = this.session;
        await this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .add(newLog);
    }

    async getBlobLogs(): Promise<BlobLogs> {
        const allLogs = await this.getAllLogs().then((logs) => logs);

        return {
            name: 'BROWSER_LOGS.json',
            blob: new Blob([JSON.stringify(allLogs, null, 2)], { type: 'blob' })
        };
    }

    private async getAllLogs(): Promise<Array<ConsoleLogInterface>> {
        this.openDbConnection();

        return this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .toArray()
            .then( logs => logs)
            .catch(error => {
                Error(error);

                return [];
            });
    }

    private async deleteOldLogs(): Promise<void> {
        const latestLogId = await this.getLatestLog().then((element) => element.id);

        await this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .where('id')
            .belowOrEqual(latestLogId - INDEXED_DB_ENV.DB_STORE_MIN_THRESHOLD)
            .delete();
    }

    private getLatestLog(): Dexie.Promise<any | undefined> {
        return this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .orderBy('id')
            .last();
    }

    private getCount(): Dexie.Promise<number> {
        return this.table(INDEXED_DB_ENV.DB_STORE_NAME)
            .count();
    }

    private async isStoreLimitExceeded(): Promise<boolean> {
        const totalLogsNumber = await this.getCount().then(num => num);

        return (isNumber(totalLogsNumber) && totalLogsNumber > INDEXED_DB_ENV.DB_STORE_MAX_THRESHOLD);
    }

    private openDbConnection(): void {
        if (!this.isOpen()) {
            this.open()
                .catch(err => {
                    console.error(`Open failed: ${err.stack}`);
                    this.createStore();
                });
        }
    }

    private createStore(): void {
        this.dbSchema[INDEXED_DB_ENV.DB_STORE_NAME] = INDEXED_DB_ENV.DB_STORE_SCHEMA;
        this.version(INDEXED_DB_ENV.DB_VERSION)
            .stores(this.dbSchema);
    }
}

interface ConsoleLogInterface extends NGXLogInterface {
    session?: number,
    id?: number
}

interface BlobLogs {
    name: string,
    blob: any
}
