import { Action, createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import { uiRefresh, cleanUiSettingsCache } from '@oam/core/lib/actions/app-settings.actions';

export const appSettingsFeatureKey = 'appSettings';
export interface AppSettingsState {
    refreshCount: number,
    shouldNotifyUISettingGotRestored: boolean
}
export const initialState: AppSettingsState = {
    refreshCount: 0,
    shouldNotifyUISettingGotRestored: false
};

const onRefreshUi = on(uiRefresh, (state: AppSettingsState) => {
    return {
        ...state,
        refreshCount: state.refreshCount + 1
    };
});

const onCleanUiSettingsCache = on(cleanUiSettingsCache, (state: AppSettingsState, action) => {
    return {
        ...state,
        shouldNotifyUISettingGotRestored: action.payload
    };
});

const appSettingsReducer = createReducer(
    initialState,
    onRefreshUi,
    onCleanUiSettingsCache
);

export function reducer(state: AppSettingsState, action: Action) {
    return appSettingsReducer(state, action);
}

export const selectAppSettingsState = createFeatureSelector<AppSettingsState>(appSettingsFeatureKey);
export const selectAppSettingsRefresh = createSelector(selectAppSettingsState, (state: AppSettingsState) => state.refreshCount);
export const selectHasCleanUiSettingsState = createSelector(selectAppSettingsState, (state: AppSettingsState) => state.shouldNotifyUISettingGotRestored);
