import { createAction, props } from '@ngrx/store';

export const startCountingSessionTime = createAction(
    '[SessionTokenTime] Start counting session time'
);

export const startCountingSeconds = createAction(
    '[SessionTokenTime] Start counting seconds'
);

export const sessionTokenExpired = createAction(
    '[User] [SessionTokenTime] Session token has expired'
);

export const stopCountingSeconds = createAction(
    '[SessionTokenTime] Stop counting seconds'
);

export const setRemainingTime = createAction(
    '[SessionTokenTime] Set remaining session time',
    props<{ remainingTime?: number }>()
);

export const setRemainingSeconds = createAction(
    '[SessionTokenTime] Set remaining seconds session time',
    props<{ remainingSeconds: string | null }>()
);
