import { CollectionViewer, DataSource } from '@angular/cdk/collections';
import { DynamicFlatNode } from './dynamic-flat-node';
import { BehaviorSubject, merge, Observable } from 'rxjs';
import { DynamicDatabase } from './dynamic-database';
import { map } from 'rxjs/operators';

export class DynamicDataSource implements DataSource<DynamicFlatNode> {
    dataChange = new BehaviorSubject<DynamicFlatNode[]>([]);

    get data(): DynamicFlatNode[] {
        return this.dataChange.value;
    }

    set data(value: DynamicFlatNode[]) {
        this.dataChange.next(value);
    }

    constructor(private readonly _database: DynamicDatabase) {
    }

    unsubscribe() {
        this.dataChange.unsubscribe();
    }

    connect(collectionViewer: CollectionViewer): Observable<DynamicFlatNode[]> {
        return merge(collectionViewer.viewChange, this.dataChange).pipe(map(() => this.data));
    }

    disconnect() {
        this.dataChange.unsubscribe();
    }

    toggleNode(node: DynamicFlatNode): DynamicFlatNode[] {
        const children = this._database.getChildren(node.item);
        const index = this.data.indexOf(node);

        if (!children || index < 0) return [];

        node.isExpanded = !node.isExpanded;
        let nodesAdded: DynamicFlatNode[] = [];
        if (node.isExpanded) {
            const nodes = children.map(name =>
                new DynamicFlatNode(name, node.level + 1, this._database.isExpandable(name)));
            this.data.splice(index + 1, 0, ...nodes);

            nodesAdded = nodes;
        } else {
            let count = 0;
            for (let i = index + 1; i < this.data.length
            && this.data[i].level > node.level; i++) {
                count++;
            }
            this.data.splice(index + 1, count);
        }

        this.dataChange.next(this.data);

        return nodesAdded;
    }

    collapseAll() {
        this.data = this.data.filter(node => {
            if (node.level !== 0) return false;

            node.isExpanded = false;

            return true;
        });

        this.dataChange.next(this.data);
    }

    getNodeById(nodeId: string): DynamicFlatNode | undefined {
        return this.data.find((node: DynamicFlatNode) => node.item === nodeId);
    }
}
