import { createAction, props } from '@ngrx/store';
import { Token, UserInformation } from '@oam/core/lib/store/user-state';
import { LocalUserPropertie } from '@oam/cu-asm-api';
import { HttpErrorResponse } from '@angular/common/http';
import { LoginResponse, TokenInfo } from '@oam/cu-api-gw-api';

enum UserActionTypes {
    UPDATE_USER = '[User] Update user',
    REMOVE_USER = '[User] Remove user',
    UPDATE_USER_INFORMATION = '[User] Update user information',
    UPDATE_DOWNGRADED_PRIVILEGES_DIALOG_POPUP_FLAG = '[User] update downgraded privileges dialog popup flag',
    REMOVE_FROM_USER_INFORMATION = '[User] Remove from user information',
    SET_TOKEN = '[User] Set a new token',
    SET_LAST_SESSION_TOKEN = 'Set last session token',
    CLEAR_TOKENS = '[User] Clear tokens',
    REMOVE_TOKEN = '[User] Remove token',
    RENEW_TOKEN = '[Auth]Renew token',
    DISPLAY_PASSWORD_EXPIRATION_WARNING = '[User] Display password expiration warning',
    CLEAR_PASSWORD_EXPIRATION_WARNING = '[User] Clear password expiration warning',
    SET_USERNAME_FOR_PASSWORD_EXPIRED = '[User] Set username for password expiration',
    LOGIN_RELATED_ERROR = '[User] Login related error',
    UPDATE_LOGIN_RESPONSE = '[User] Update Login Response',
    SET_CURRENT_ACCOUNT_ACTIVATE_STATUS = '[Local Account] Set account activate status'
}

export const updateUser = createAction(
    UserActionTypes.UPDATE_USER,
    props<{ payload: UserInformation }>()
);

export const setToken = createAction(
    UserActionTypes.SET_TOKEN,
    props<{ payload: Token }>()
);

export const setLastSessionToken = createAction(
    UserActionTypes.SET_LAST_SESSION_TOKEN,
    props<{ authToken: string | null }>()
);

export const updateDowngradedPrivilegesDialogPopupFlag = createAction(
    UserActionTypes.UPDATE_DOWNGRADED_PRIVILEGES_DIALOG_POPUP_FLAG,
    props<{ payload: boolean }>()
);

export const updateUserInformation = createAction(
    UserActionTypes.UPDATE_USER_INFORMATION,
    props<{ payload: UserInformation }>()
);

export const updateLoginResponse = createAction(
    UserActionTypes.UPDATE_LOGIN_RESPONSE,
    props<{ payload: {response: LoginResponse | TokenInfo, username: string} }>()
);

export const removeFromUserInformation = createAction(
    UserActionTypes.REMOVE_FROM_USER_INFORMATION,
    props<{ payload: string }>()
);

export const removeUser = createAction(UserActionTypes.REMOVE_USER);
export const clearTokens = createAction(UserActionTypes.CLEAR_TOKENS);
export const removeToken = createAction(UserActionTypes.REMOVE_TOKEN, props<{ payload: string }>());
export const renewSessionToken = createAction(UserActionTypes.RENEW_TOKEN,
    props<{username: string, password: string, shouldStartOperation: boolean}>()
);
export const displayPasswordExpirationWarning = createAction(UserActionTypes.DISPLAY_PASSWORD_EXPIRATION_WARNING);
export const clearPasswordExpirationWarning = createAction(UserActionTypes.CLEAR_PASSWORD_EXPIRATION_WARNING);

export const handleLoginRelatedError = createAction(UserActionTypes.LOGIN_RELATED_ERROR,
    props<{error: HttpErrorResponse, username: string, password: string, readOnlyChecked?: boolean}>()
);
export const setUsernameForPasswordExpired = createAction(
    UserActionTypes.SET_USERNAME_FOR_PASSWORD_EXPIRED,
    props<{ payload: string }>()
);
export const setCurrentAccountActivatedStatus = createAction(
    UserActionTypes.SET_CURRENT_ACCOUNT_ACTIVATE_STATUS,
    props<{ payload: LocalUserPropertie[] }>()
);

