import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';

export enum ViewType {
    TILE = 'Tile',
    TABLE = 'Table'
}

export interface ViewToggleInput {
    getDataSource: (store: Store<{}>) => Observable<ViewType>;
    toggleView: (value: ViewType, store: Store<{}>) => void;
}
