import { Component, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import {
    sessionTokenExpired,
    startCountingSeconds,
    startCountingSessionTime,
    stopCountingSeconds
} from './core/actions/session-token-time.actions';
import {
    selectRemainingSeconds,
    selectRemainingTime
} from '@oam/cu-ui/lib/user-menu/components/session-time/core/reducers/session-token-time.reducer';
import { filter, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { isNil } from 'lodash';

@Component({
    selector: 'ui-webem-app-session-time',
    templateUrl: './session-time.component.html',
    styleUrls: ['./session-time.component.scss']
})
export class SessionTimeComponent implements OnInit, OnDestroy {
    private readonly ngUnsubscribe = new Subject<void>();
    remainingMinutes?: string | null;
    remainingSeconds?: string | null;
    badgeStyle: string;

    constructor(private readonly store: Store) {
    }

    ngOnInit(): void {
        this.store.dispatch(startCountingSessionTime());
        this.countMinutes();
    }

    countMinutes() {
        this.store.select(selectRemainingTime).pipe(
            takeUntil(this.ngUnsubscribe),
            filter(time => !!time)
        ).subscribe((time) => {
                if (!time || time < 0) return;
                if (time > 30) this.remainingMinutes = null;
                if (time <= 30) {
                    this.remainingMinutes = `${time} min`;
                    this.badgeStyle = '#000000'; // $nokiaUiBlackColor
                }
                if (time <= 10) this.badgeStyle = '#DA1914'; // $nokiaAlarmRedColor
                if (time > 1) this.store.dispatch(stopCountingSeconds());
                if (time <= 1) {
                    this.remainingMinutes = undefined;
                    this.store.dispatch(startCountingSeconds());
                    this.countSeconds();
                }
            }
        );
    }

    countSeconds() {
        this.store.select(selectRemainingSeconds)
            .pipe(
                takeUntil(this.ngUnsubscribe))
            .subscribe(seconds => {
                if (seconds === null) this.store.dispatch(sessionTokenExpired());
                if (isNil(seconds)) return this.remainingSeconds = null;

                this.remainingSeconds = seconds;
            });

    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
