import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import {
    catchError,
    concatMap,
    exhaustMap,
    filter,
    map,
    mergeMap,
    takeUntil,
    throttleTime,
    withLatestFrom,
    first
} from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { asyncScheduler, of, combineLatest } from 'rxjs';
import { PollingService } from '@oam/core/lib/services/polling.service';
import { SnapshotService } from '@oam/cu-asm-api/services';
import { SnapshotStatus } from '@oam/cu-asm-api/models';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import {
    addCollectedFilesToStore,
    cleanSnapshotState,
    cleanSnapshotStateIfCollectingIsNotOngoing,
    cleanUpSnapshotOnServerFinished,
    collectingLocalSnapshotIsFailed,
    collectingLocalSnapshotIsSucceeded,
    collectingLocalSnapshotOngoing,
    collectingRemoteSnapshotIsFailed,
    collectingRemoteSnapshotIsSucceeded,
    collectingRemoteSnapshotOngoing,
    downloadingFilesCompleted,
    startCollectingLocalSnapshot,
    startCollectingLocalSnapshotHearbeat,
    startCollectingRemoteSnapshot,
    startDownloadingFiles,
    saveFilesLocal,
    cancellingCollectionIsOngoing,
    cancellingSnapshotIsFailed,
    deleteLocalSnapshotAfterSuccess,
    startCancellingCollectionLocalSnapshot,
    startCancellingCollectionRemoteSnapshot,
} from '../actions/snapshot.action';
import {
    selectCollectingSnapshotFiles,
    selectCollectingSnapshotFileStates,
    selectCollectingSnapshotIsOngoingOrCancelling,
    selectCollectingSnapshotOperationId,
    selectCollectingSnapshotState,
    downloadFilesCollectingSuccessSelector,
    selectstartHeartBeatStatus
} from '../selectors/collecting-snapshot.selector';
import { FilesDataResponse } from '../models';
import { SnapshotFetchFilesService } from '../services/snapshot-fetch-files.service';
import { isEmpty } from 'lodash';


@Injectable()
export class CollectingSnapshotEffects {
    cancelSnapshotIsFailed$ = createEffect(() =>
        this.actions$.pipe(
            ofType(cancellingSnapshotIsFailed),
            map(({ error }) => addNotification({
                payload: {
                    detail: error.content,
                    severity: 'error',
                    summary: 'Snapshot Cancellation Failed'
                }
            }))
        )
    );

    addCancelNotification$ = createEffect(() =>
        this.actions$.pipe(
            ofType(startCancellingCollectionLocalSnapshot, startCancellingCollectionRemoteSnapshot),
            map(() => addNotification({
                    payload: {
                        detail: 'Cancel snapshot operation started',
                        severity: 'info',
                        summary: 'Cancelling Snapshot'
                    }
                })
            )
        )
    );

    startCancellingCollectionLocalSnapshot$ = createEffect(() =>
        this.actions$.pipe(
            ofType(startCancellingCollectionLocalSnapshot),
            throttleTime(1000),
            withLatestFrom(this.store.select(selectCollectingSnapshotOperationId)),
            exhaustMap(([, operationId]) =>
                this.snapshotService.deleteLocalSnapshotOperation({ operationId: operationId as string })
                    .pipe(
                        map(() => cancellingCollectionIsOngoing()),
                        catchError(({ error }) => of(cancellingSnapshotIsFailed({ error: JSON.parse(error) })))
                    )
            )
        )
    );

    startCancellingCollectionRemoteSnapshot$ = createEffect(() =>
        this.actions$.pipe(
            ofType(startCancellingCollectionRemoteSnapshot),
            throttleTime(1000),
            withLatestFrom(this.store.pipe(select(selectCollectingSnapshotOperationId))),
            exhaustMap(([, operationId]) =>
                this.snapshotService.deleteRemoteSnapshotOperation({ operationId: operationId as string }).pipe(
                    map(() => cancellingCollectionIsOngoing()),
                    catchError(({ error }) => of(cancellingSnapshotIsFailed({ error: JSON.parse(error) })))
                )
            )
        )
    );

    startCollectingLocalSnapshot$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCollectingLocalSnapshot),
            throttleTime(1000),
            withLatestFrom(this.store.select(selectCollectingSnapshotState)),
            exhaustMap(([, { coverage, filterRegx, reason }]) => this.snapshotService.triggerLocalSnapshot({
                    body: {
                        coverage,
                        filterRegx,
                        reason,
                        heartbeat: true,
                        originator: 'EM'
                    }
                }).pipe(
                mergeMap(response => [
                    collectingLocalSnapshotOngoing({
                        operationId: response.operationId as string,
                        files: response.files as FilesDataResponse[],
                        startedAt: Date.now()
                    }),
                    addNotification({
                        payload: {
                            detail: 'Snapshot collecting operation started',
                            severity: 'info',
                            summary: 'Collecting Snapshot'
                        }
                    })
                ]),
                catchError(error => of(collectingLocalSnapshotIsFailed(error)))
                )
            )
        )
    );

    startCollectingRemoteSnapshot$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCollectingRemoteSnapshot),
            throttleTime(1000),
            withLatestFrom(this.store.select(selectCollectingSnapshotState)),
            exhaustMap(([, { coverage, filterRegx, reason }]) => this.snapshotService.triggerRemoteSnapshot({
                    body: {
                        coverage,
                        filterRegx,
                        reason,
                        originator: 'EM'
                    }
                }).pipe(
                mergeMap(response => [
                    collectingRemoteSnapshotOngoing({
                        operationId: response.operationId as string,
                        startedAt: Date.now()
                    }),
                    addNotification({
                        payload: {
                            detail: 'Snapshot collecting operation started',
                            severity: 'info',
                            summary: 'Collecting Snapshot'
                        }
                    })
                ]),
                catchError(error => of(collectingRemoteSnapshotIsFailed(error)))
                )
            )
        )
    );

    collectingLocalSnapshotIsSucceeded$ = createEffect(() =>
        this.actions$.pipe(
            ofType(collectingLocalSnapshotIsSucceeded),
            mergeMap(action =>
                combineLatest([
                    of(action),
                    this.store.pipe(
                        select(downloadFilesCollectingSuccessSelector),
                        first((fileStates) =>
                            fileStates === true
                        ),
                    )
                ])
            ),
            map(() =>
                saveFilesLocal()
            )
        ));

    sendNotificationCollectingRemoteSnapshotIsSucceeded$ = createEffect(() =>
        this.actions$.pipe(
            ofType(collectingRemoteSnapshotIsSucceeded),
            map(() => addNotification({
                payload: {
                    detail: 'Snapshot collection operation succeeded',
                    severity: 'success',
                    summary: 'Snapshot Collected'
                }
            }))
        )
    );

    downloadingFilesCompleted$ = createEffect(() =>
        this.actions$.pipe(
            ofType(downloadingFilesCompleted),
            mergeMap((action) => [
                addNotification({
                    payload: {
                        detail: 'Snapshot collection operation succeeded',
                        severity: 'success',
                        summary: 'Snapshot Collected'
                    }
                }),
                deleteLocalSnapshotAfterSuccess({ operationId: action.operationId })
            ])
        )
    );

    deleteLocalSnapshotAfterSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(deleteLocalSnapshotAfterSuccess),
            mergeMap((action) => {
                return this.snapshotService.deleteLocalSnapshotOperation({
                    operationId: action.operationId
                }).pipe(
                    map(() => cleanUpSnapshotOnServerFinished())
                );
            })
        )
    );

    deleteRemoteSnapshotAfterSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(collectingRemoteSnapshotIsSucceeded),
            mergeMap((action) => this.snapshotService.deleteRemoteSnapshotOperation({
                operationId: action.operationId
            }).pipe(
                map(() => cleanUpSnapshotOnServerFinished())
            ))
        )
    );

    downloadingFiles$ = createEffect(() =>
        this.actions$.pipe(
            ofType(saveFilesLocal),
            withLatestFrom(
                this.store.pipe(select(selectCollectingSnapshotFiles)),
                this.store.pipe(select(selectCollectingSnapshotOperationId))
            ),
            map(([, , operationId]: any): any => {
                this.snapshotFetchFilesService.saveFilesLocal(operationId);

            })
        ), { dispatch: false }
    );

    startDownloadingFiles$ = createEffect(() =>
        this.actions$.pipe(
            ofType(startDownloadingFiles),
            withLatestFrom(
                this.store.pipe(select(selectCollectingSnapshotOperationId)),
                this.store.pipe(select(selectCollectingSnapshotFileStates)),
                this.store.pipe(select(selectstartHeartBeatStatus)),
            ),
            map(([,operationId,files,startHeartBeatStatus]: any): any => {
                this.snapshotFetchFilesService.startDownloadingFiles(files as FilesDataResponse[], operationId);
                if (startHeartBeatStatus !== true){
                    this.store.dispatch(startCollectingLocalSnapshotHearbeat({operationId, startHeartBeatStatus: true }));
                }

            })
        ), { dispatch: false }
    );
    collectingSnapshotIsFailed$ = createEffect(() =>
        this.actions$.pipe(
            ofType(collectingLocalSnapshotIsFailed, collectingRemoteSnapshotIsFailed),
            map(({ error }) => addNotification({
                payload: {
                    detail: error.content,
                    severity: 'error',
                    summary: 'Collecting Snapshot Failed'
                }
            }))
        )
    );

    collectingLocalSnapshotOngoing$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(collectingLocalSnapshotOngoing),
            mergeMap((action) =>
                this.pollingService.getTimer(scheduler).pipe(
                    takeUntil(this.actions$.pipe(ofType(cancellingCollectionIsOngoing))),
                    takeUntil(this.actions$.pipe(ofType(collectingLocalSnapshotIsSucceeded))),
                    takeUntil(this.actions$.pipe(ofType(collectingLocalSnapshotIsFailed))),
                    concatMap(() => {
                        return this.snapshotService.getLocalSnapshotOperation({ operationId: action.operationId as string })
                            .pipe(
                                concatMap(response => {
                                    const actions = [];
                                    if (!isEmpty(response.files)) {
                                        actions.push(addCollectedFilesToStore({ files: response.files as FilesDataResponse[] }));
                                        actions.push(startDownloadingFiles({ operationId: action.operationId as string, files: action.files as FilesDataResponse[] }));
                                        if (response.status === SnapshotStatus.Succeeded) {
                                            actions.push(collectingLocalSnapshotIsSucceeded({ operationId: action.operationId as string }));
                                        }
                                    }
                                    if (response.status === SnapshotStatus.Failed) {
                                        const failureInfo = response.failureInfo as string;
                                        actions.push(collectingLocalSnapshotIsFailed({
                                            error: {
                                                summary: failureInfo,
                                                content: failureInfo
                                            },
                                            operationId: action.operationId as string
                                        }));
                                    }

                                    return actions;
                                }),
                                catchError(httpError => of(collectingLocalSnapshotIsFailed({
                                        error: httpError.error,
                                        operationId: action.operationId as string
                                    }))
                                )
                            );
                    })
                )
            )
        )
    );

    startCollectingLocalSnapshotHearbeat$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCollectingLocalSnapshotHearbeat),
            mergeMap((action) =>
                this.pollingService.getTimer(scheduler).pipe(
                    takeUntil(this.actions$.pipe(ofType(downloadingFilesCompleted))),
                    takeUntil(this.actions$.pipe(ofType(cancellingCollectionIsOngoing))),
                    mergeMap(() => this.snapshotService.keepSnapshotAlive({ operationId: action.operationId })),
                    catchError(() => of())
                )
            )
        ), { dispatch: false }
    );

    collectingRemoteSnapshotOngoing$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(collectingRemoteSnapshotOngoing),
            mergeMap((action) =>
                this.pollingService.getTimer(scheduler).pipe(
                    takeUntil(this.actions$.pipe(ofType(cancellingCollectionIsOngoing))),
                    takeUntil(this.actions$.pipe(ofType(collectingRemoteSnapshotIsSucceeded))),
                    takeUntil(this.actions$.pipe(ofType(collectingRemoteSnapshotIsFailed))),
                    concatMap(() => {
                        return this.snapshotService.getRemoteSnapshotOperation({ operationId: action.operationId as string })
                            .pipe(
                                concatMap(response => {
                                    const actions = [];
                                    if (response.status === SnapshotStatus.Succeeded) {
                                        actions.push(collectingRemoteSnapshotIsSucceeded(
                                            { operationId: action.operationId as string }
                                        ));
                                    }
                                    if (response.status === SnapshotStatus.Failed) {
                                        const failureInfo = response.failureInfo as string;
                                        actions.push(collectingRemoteSnapshotIsFailed({
                                            error: {
                                                summary: failureInfo,
                                                content: failureInfo
                                            },
                                            operationId: action.operationId as string
                                        }));
                                    }

                                    return actions;
                                }),
                                catchError(httpError => of(collectingRemoteSnapshotIsFailed({
                                        error: httpError.error,
                                        operationId: action.operationId as string
                                    }))
                                )
                            );
                    })
                )
            )
        )
    );

    snapshotCollectingIsOngoing$ = createEffect(() =>
        this.actions$.pipe(
            ofType(cleanSnapshotStateIfCollectingIsNotOngoing),
            withLatestFrom(
                this.store.pipe(select(selectCollectingSnapshotIsOngoingOrCancelling))
            ),
            filter(([, isOngoing]) => !isOngoing),
            map(() => cleanSnapshotState())
        )
    );

    constructor(private readonly actions$: Actions,
                private readonly pollingService: PollingService,
                private readonly snapshotFetchFilesService: SnapshotFetchFilesService,
                private readonly snapshotService: SnapshotService,
                private readonly store: Store) {
    }
}
