import { Pipe, PipeTransform } from '@angular/core';
import { SplitAreaDirective } from '../directives/split-area/split-area.directive';
import { CurledUpAreaMap } from '../model/curled-up-area-map-type';

@Pipe({
    name: 'sizeBiggerThanMinSize',
    pure: false
})
export class SizeBiggerThanMinSizePipe implements PipeTransform {
    transform(marker: SplitAreaDirective, curledUpAreaMap: CurledUpAreaMap): boolean {
        return marker.context.size === '*' || marker.context.size > marker.context.minSize || !curledUpAreaMap[marker.context.id];
    }
}
