import { SPLIT_VIEW_LOCATION } from '@oam/split-view';

export function getSplitViewOutlets(routerLink: string): SplitViewLocationOutlets {
    const routerOutlets: SplitViewLocationOutlets = {
        outlets : {}
    };
    routerLink
        .slice(0, -1)
        .split('//')
        .forEach(partOfRouter => {
        const outlet = partOfRouter.split(':')[1];
        const outletLocation = partOfRouter.includes(SPLIT_VIEW_LOCATION.TOP);

        if (outletLocation) {
            routerOutlets.outlets[SPLIT_VIEW_LOCATION.TOP] = outlet;
        } else {
            routerOutlets.outlets[SPLIT_VIEW_LOCATION.BOTTOM] = outlet;
        }
    });

    return routerOutlets;
}

interface SplitViewLocationOutlets {
    outlets: {
        topView?: string,
        bottomView?: string
    }
}
