import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ToastMessageState } from '../models/toast-message-state.type';
import { featureKey } from '../reducers/toast-message.reducer';

export const selectToastMessageState = createFeatureSelector<ToastMessageState>(featureKey);
export const selectToastIds = createSelector(selectToastMessageState, (state: ToastMessageState) => state.toastIds);
export const selectNotifications = createSelector(selectToastMessageState, (state: ToastMessageState) => state.notifications);
