import { RoleAccessGuardRule } from '@oam/role-access-ng';

type NavWithRoleAccessFlag = { roleAccessFlag?: string, routerLink: string };

export function mapToRoleAccessGuardRules(navs: NavWithRoleAccessFlag[]): Required<RoleAccessGuardRule>[] {
    return navs
        .filter(({ roleAccessFlag }) => !!roleAccessFlag)
        .map(({ roleAccessFlag, routerLink }) => {
            return {
                path: routerLink.replace(/^\//, ''),
                roleAccessFlag
            } as Required<RoleAccessGuardRule>;
        });
}
