import { Injectable, Inject, NgZone } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Injectable({
    providedIn: 'root'
})
export class AdjustUiDropdownService {
    constructor(@Inject(DOCUMENT) private readonly document: Document,
                private readonly ngZone: NgZone) {}

    public adjustDropDown() {
        const documentElem = this.document.documentElement;
        if (documentElem.scrollHeight <= documentElem.clientHeight) {
            documentElem.style.overflow = 'hidden';
            this.ngZone.runOutsideAngular(() => {
                setTimeout(() => {
                    documentElem.style.overflow = 'auto';
                });
            });
        }
    }
}
