import { Action, createReducer, on } from '@ngrx/store';
import { setTokenInfo, setTokenInfoInitialValues } from '../actions/token-info.actions';
import { TokenInfo } from '@oam/cu-api-gw-api';

export const tokenInfoFeatureKey = 'tokenInfoGet';

export const initialState: TokenInfo = {
    Role: '',
    EnforceLogout: false,
    EnforceLogoutDelay: 0,
    Expires: '',
    FailedLoginAttempts: '',
    LastFailedLogin: '',
    LastLogin: '',
    PasswordExpirationDate: '',
    ReadOnlyMode: false,
    Type: '',
    User: ''
};

export const tokenInfoReducer = createReducer(
    initialState,
    on(setTokenInfo, (state, {tokenInfo}) => ({ ...state, ...tokenInfo })),
    on(setTokenInfoInitialValues, () => initialState),
);

export function reducer(state: TokenInfo | undefined, action: Action) {
    return tokenInfoReducer(state, action);
}
