import { Pipe, PipeTransform } from '@angular/core';
import { isEmpty } from 'lodash';
import { SideBarMenuGroup } from '@oam/cu-ui';
import { navigationRoot } from '../models/navigation-group.model';
import { NavGroup, Navigation } from '../models/navigation.interface';
import { IconTypes } from '@oam/cu-ui/lib/side-bar-menu/model/side-bar-menu-model';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { groupNavigations } from '@oam/navigation/lib/navigation/core/utility/navigation-transformer';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';


@Pipe({ name: 'sideBarMenuGroups', pure: true })
export class SideBarMenuGroupsPipe implements PipeTransform {
    constructor(private navigationService: NavigationService) {
    }

    transform(currentRoute: string | null, navigations: Navigation[]): SideBarMenuGroup[] | null {
        if (!currentRoute) return null;

        return toMenuGroupList(this.navigationService.getAvailableNavigations(), currentRoute);
    }
}

function setActive(groups: SideBarMenuGroup[], currentRoute: string) {
    for (const group of groups) {
        group.items.forEach((item: any) => {
            if (!isEmpty(item?.children)) {
                const activeChildIndex = item.children.findIndex((child: any) => child.data === currentRoute);
                if (activeChildIndex !== -1) {
                    item.children[activeChildIndex].isActive = true;
                    item.isActive = true;
                    group.isActive = true;
                }
            } else {
                const activeItem = group.items.find(item => item?.data === currentRoute);
                if (!activeItem) return;

                activeItem.isActive = true;
                group.isActive = true;
            }
        });

        if (group.isActive) break;
    }
}

export function toMenuGroupList(navigations: Navigation[], currentRoute: string) {

    const groups =  groupNavigations<Navigation, NavGroup, SideBarMenuGroup>
    (navigations, toSideBarModel, exclude2ndLevelErrorsTab);

    setActive(groups, currentRoute);

    return groups;

    function exclude2ndLevelErrorsTab(navigation: Navigation) {
        return [
            RouterLink.errorsMissingMandatoryObjects,
            RouterLink.errorsObjectsQuantityErrors,
            RouterLink.errorsGlobalRelationErrors,
            RouterLink.errorsParameterErrors,
            RouterLink.errorsHardwareErrors,
            RouterLink.errorsParameterRelationErrors
        ].some(routerLinkConst => routerLinkConst === navigation.routerLink);
    }

    function toSideBarModel(x: { isGroup: boolean; isItem: boolean; nav: Navigation | NavGroup; }) {
        const { isGroup, isItem, nav } = x;
        const isRootNavigation = nav?.navigationGroup === navigationRoot;

        const obj = isGroup ?
            {
                icon: isRootNavigation ? (nav?.icon || '') : nav?.navigationGroup.icon,
                items: nav['items'],
                label: isRootNavigation ? nav?.label : (nav?.navigationGroup?.label || nav?.navigationGroup?.id),
                iconType: nav?.navigationGroup.iconType ?
                    nav?.navigationGroup.iconType :
                    IconTypes.MATERIAL_ICON
            } : getContent();

        function getContent() {
            return isItem ?
                {
                    keyboardShortcut: nav?.keyboardShortcut,
                    label: nav?.label,
                    children: nav?.children
                } : {
                    keyboardShortcut: nav?.keyboardShortcut,
                    label: nav?.label
                };
        }

        return {
            ...obj,
            data: nav?.routerLink
        } as SideBarMenuGroup;
    }
}

