/* tslint:disable */
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BaseService } from '../base-service';
import { ApiConfiguration } from '../api-configuration';
import { StrictHttpResponse } from '../strict-http-response';
import { RequestBuilder } from '../request-builder';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

import { NrbtsRInfo } from '../models/nrbts-r-info';
import { NrduRInfo } from '../models/nrdu-r-info';

@Injectable({
  providedIn: 'root',
})
export class TransportService extends BaseService {
  constructor(
    config: ApiConfiguration,
    http: HttpClient
  ) {
    super(config, http);
  }

  /**
   * Path part for operation transportUpdatePatch
   */
  static readonly TransportUpdatePatchPath = '/transport/update';

  /**
   * Information received from transport to be updated.
   *
   *
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `transportUpdatePatch()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  transportUpdatePatch$Response(params: {

    /**
     * Name of className that needs update
     */
    className?: 'NRBTS_R' | 'NRDU_R';
  
    /**
     * Update NRBTS_A or NRDU_A objects
     */
    body: NrbtsRInfo | NrduRInfo
  }): Observable<StrictHttpResponse<void>> {

    const rb = new RequestBuilder(this.rootUrl, TransportService.TransportUpdatePatchPath, 'patch');
    if (params) {

      rb.query('className', params.className, {});

      rb.body(params.body, 'application/json');
    }
    return this.http.request(rb.build({
      responseType: 'text',
      accept: '*/*'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return (r as HttpResponse<any>).clone({ body: undefined }) as StrictHttpResponse<void>;
      })
    );
  }

  /**
   * Information received from transport to be updated.
   *
   *
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `transportUpdatePatch$Response()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  transportUpdatePatch(params: {

    /**
     * Name of className that needs update
     */
    className?: 'NRBTS_R' | 'NRDU_R';
  
    /**
     * Update NRBTS_A or NRDU_A objects
     */
    body: NrbtsRInfo | NrduRInfo
  }): Observable<void> {

    return this.transportUpdatePatch$Response(params).pipe(
      map((r: StrictHttpResponse<void>) => r.body as void)
    );
  }

}
