import {
    cleanTransferSnapshotResultState,
    cleanTransferSnapshotState,
    setRemoteSnapshotFileNameAndOperationId,
    startCollectingLocalSnapshot,
    startCollectingRemoteSnapshot,
    startGettingRemoteSnapshotError,
    startTransferSnapshotToNMS,
    transferSnapshotFailed,
    transferSnapshotToNMSIsFinished
} from '@oam/diagnostic-snapshot/core/actions/snapshot.action';
import { Action, createReducer, on } from '@ngrx/store';
import { ITransferSnapshotToNMS, TRANSFER_SNAPSHOT_STATUS } from '@oam/diagnostic-snapshot/core/models';

export const transferSnapshotFeatureKey = 'transferSnapshotToNMS';

export const initialState: ITransferSnapshotToNMS = {
    remoteSnapshotFileName: undefined,
    operationId: undefined,
    transferStatus: undefined
};

export const transferSnapshotToNMSReducer = createReducer(
    initialState,
    on(setRemoteSnapshotFileNameAndOperationId, (state, { remoteSnapshotFileName, operationId }) => {
        return {
            ...state,
            remoteSnapshotFileName,
            operationId
        };
    }),
    on(startGettingRemoteSnapshotError, (state) => {
        return {
            ...state,
            remoteSnapshotFileName: undefined
        };
    }),
    on(transferSnapshotFailed, (state, { error }) => {
        return {
            ...state,
            transferStatus: TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_FAILED,
            errorMessage: error,
            finishedAt: Date.now()
        };
    }),
    on(startTransferSnapshotToNMS, (state) => {
        return {
            ...state,
            transferStatus: TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_ONGOING,
            startedAt: Date.now(),
            errorMessage: undefined
        };
    }),
    on(transferSnapshotToNMSIsFinished, (state) => {
        return {
            ...state,
            transferStatus: TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_SUCCEEDED,
            finishedAt: Date.now()
        };
    }),
    on(cleanTransferSnapshotResultState, startCollectingRemoteSnapshot, startCollectingLocalSnapshot, (state) => {
        return {
            ...state,
            transferStatus: undefined,
            errorMessage: undefined,
            finishedAt: undefined,
            startedAt: undefined
        };
    }),
    on(cleanTransferSnapshotState, () => {
        return {
            ...initialState
        };
    })
);

export function reducer(state: ITransferSnapshotToNMS | undefined, action: Action) {
    return transferSnapshotToNMSReducer(state, action);
}
