import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import {
    subscribeTokenInfoWatch,
    unsubscribeTokenInfoWatch,
    subscribeTokenInfoWatchError,
    tokenInfoRequest,
    setTokenInfo,
    PostTokenInfo,
    SubscribeUserOperationWatch,
    UnsubscribeUserOperationWatch,
    RenewOperationedAction,
    ExpiredPollingErrorAction
} from '@oam/auth/lib/auth/core/actions/token-info.actions';
import { asyncScheduler, of } from 'rxjs';
import {
    catchError,
    map,
    mergeMap,
    takeUntil,
    tap,
    timeout
} from 'rxjs/operators';
import { PollingService } from '@oam/core/lib/services/polling.service';
import { SessionMgmtService } from '@oam/cu-api-gw-api/services/session-mgmt.service';
import { UserOperationService } from '@oam/core/lib/services/user-operation.service';

@Injectable()
export class TokenInfoEffects {

  constructor(private actions$: Actions,
              private readonly pollingService: PollingService,
              private readonly sessionMgmtService: SessionMgmtService,
              private readonly userOperationService: UserOperationService
              ) {}

  subscribeTokenInfoWatch$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(subscribeTokenInfoWatch),
            mergeMap(() => this.pollingService.getTimer(scheduler).pipe(
                takeUntil(this.unsubscribeTokenInfoWatch$),
                map(() => tokenInfoRequest())
            ))
        )
    );

  tokenInfoRequest$ = createEffect(() =>
        this.actions$.pipe(
            ofType(tokenInfoRequest),
            mergeMap(() => this.sessionMgmtService.getTokenInfo().pipe(
                map((tokenInfo) => setTokenInfo({tokenInfo})),
                catchError(err => of(subscribeTokenInfoWatchError(err)))
                )
            )
        ));

  unsubscribeTokenInfoWatch$ = createEffect(() =>
            this.actions$.pipe(
                ofType(unsubscribeTokenInfoWatch)
            ),
        { dispatch: false }
    );

    postTokenInfo$ = createEffect(() =>
        this.actions$.pipe(
            ofType(PostTokenInfo),
            mergeMap(() =>
                this.sessionMgmtService.tokenInfoPost({body: {OperationTimestamp: this.userOperationService.getFlag()}}
                ).pipe(
                    timeout(this.pollingService.REQUEST_TIMEOUT_MS),
                    tap(() => this.userOperationService.setFlag(false)),
                    map(() => RenewOperationedAction({OperationTimestamp: false}) ),
                    catchError(err => {
                        this.userOperationService.setFlag(false);

                        return of(ExpiredPollingErrorAction(err));
                    })
                ))
        )
    );

    subscribeUserOperationWatch$ = createEffect(() => ({scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(SubscribeUserOperationWatch),
            mergeMap(() => this.pollingService.getTimer(scheduler).pipe(
                takeUntil(this.unsubscribeUserOperationWatch$),
                map(() => PostTokenInfo())
            ))
        )
    );

    unsubscribeUserOperationWatch$ = createEffect(() =>
            this.actions$.pipe(
                ofType(UnsubscribeUserOperationWatch)
            ),
        { dispatch: false }
    );

}
