import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { getIsPasswordAgingPresent } from '@oam/core/lib/reducers/user.reducer';
import { UserInformation } from '@oam/core/lib/store/user-state';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { clearPasswordExpirationWarning } from '@oam/core/lib/actions/user.actions';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { ActivatedRoute } from '@angular/router';
import { RoleAccessService } from '@oam/role-access-core';
import { OFFLINE_USER_MODE } from '@oam/shared/lib/constants/role-access-constants';

@Component({
    selector: 'webem-password-expiration-warning',
    templateUrl: './password-expiration-warning.html',
    styleUrls: ['./password-expiration-warning.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PasswordExpirationWarningComponent implements OnInit, OnDestroy {
    passwordAboutToExpireMessage?: string;
    isPasswordAboutToExpire$: Observable<boolean>;

    constructor(
        private readonly store: Store,
        private readonly activatedRoute: ActivatedRoute,
        private readonly roleAccessService: RoleAccessService) {
    }

    isNotOffline() {
        return this.roleAccessService.currentRole !== OFFLINE_USER_MODE;
    }

    ngOnInit(): void {
        this.isPasswordAboutToExpire$ = this.store.pipe(select(getIsPasswordAgingPresent));
        const userInfoFomSession = sessionStorage.getItem('userInfo');
        if (userInfoFomSession) {
            const userInfo: UserInformation = JSON.parse(userInfoFomSession);
            this.passwordAboutToExpireMessage = userInfo.passwordAboutToExpireMessage;
        }
    }

    goToPasswordChange() {
        this.store.dispatch(redirect({routerLink: RouterLink.localUsersAccount, activatedRoute: this.activatedRoute}));
        this.closeInformation();
    }

    closeInformation() {
        this.store.dispatch(clearPasswordExpirationWarning());
    }

    ngOnDestroy(): void {
    }
}
