import {
    ChangeDetectionStrategy, ChangeDetectorRef,
    Component,
    Input,
    OnChanges,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { ChartOptions } from 'chart.js';
import { getAllTimestamps } from '../utils/timestamp.utils';
import { PM_CHART_CONSTANTS } from '../pm-chart/pm-chart-constants';
import { PmCounter } from '../models/counters.interface';
import { WebemChartData } from '../../chart/model/webem-chart.data';
import { ChartDataSetConfig } from '../../chart/model/chart-data-set.type';
import { sortPmCounterValuesByStartTime, compareStartTimesAscendant } from '../utils/pm-data.utils';
import { LineChartComponent } from '../../line-chart/components/line-chart/line-chart.component';

@Component({
    selector: 'ui-webem-pm-chart',
    templateUrl: './pm-chart.component.html',
    styleUrls: ['./pm-chart.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PmChartComponent implements  OnChanges {
    @Input() pmCounters: PmCounter[];
    @Input() selectCounterDialogStatus: boolean;
    @Input() loading: boolean;
    @ViewChild('lineChart') lineChart: LineChartComponent;
    options: ChartOptions = PM_CHART_CONSTANTS.OPTIONS;
    chartData: WebemChartData[];
    timestamps: string[];
    dataSetConfigs: ChartDataSetConfig[] = [];

    constructor(public readonly _changeDetectorRef: ChangeDetectorRef) {}

    ngOnChanges(changes: SimpleChanges): void {
        if(changes.selectCounterDialogStatus && !changes.selectCounterDialogStatus.firstChange){
            changes.selectCounterDialogStatus?.currentValue? this._changeDetectorRef.detach(): this._changeDetectorRef.reattach();
        }
        if (changes.pmCounters) {
            this.updateChartInfo();
        }
    }

    private updateChartInfo(): void {
        this.dataSetConfigs = [];
        this.chartData = [];
        const sortedPmData = sortPmCounterValuesByStartTime('asc', this.pmCounters);
        this.chartData = sortedPmData.map((counter: PmCounter, index: number) => {
            const label = `${counter.dimensionDistName}/${counter.measurementAbbreviation}/${counter.counterId} (${counter.counterAbbreviation})`;
            this.addDataSetConfigForCounter(index, label);

            return this.computeChartDataForCounter(counter);
        });
        this.timestamps = getAllTimestamps(sortedPmData).sort(compareStartTimesAscendant);
    }

    private addDataSetConfigForCounter(index: number, label: string): void {
        const nbOfColorsDefined = PM_CHART_CONSTANTS.COLORS.length;
        const colorToBeUsed = PM_CHART_CONSTANTS.COLORS[index % nbOfColorsDefined];
        const dataSetConfig: ChartDataSetConfig = {
            borderColor: colorToBeUsed,
            backgroundColor: colorToBeUsed,
            label
        };
        this.dataSetConfigs.push(dataSetConfig);
    }

    private computeChartDataForCounter(counter: PmCounter):  WebemChartData {
        const dataForChart = counter.counterValues.map(counterValue => {
            return {
                x: counterValue.startTime,
                y: counterValue.value
            };
        });

        return { data: dataForChart };
    }

    savePmChartAsPicture(fileName: string) {
        this.lineChart.savePmChartAsPicture(fileName);
    }
}
