export interface PasswordValidationOptions {
    minLength: number;
    maxLength: number;
    minNumberOfDigits: number;
    minNonAlphaNumerical: number;
}
export interface UsernameValidationOptions {
    minLength: number;
    maxLength: number;
}

export type ValidationStatus = {
    password: {
        [key in PasswordValidationRulesType]?: boolean;
    };
    passwordOptions: PasswordValidationOptions,
    username: {
        [key in UsernameValidationRulesType]?: boolean;
    };
    usernameOptions: UsernameValidationOptions;
};

export interface UserInformationChange {
    username: string;
    password: string;
    newUsername: string;
    newPassword: string;
    activeStatus?: 'active' | 'deactive';
    roleType?: 'systemUserAccessMode' | 'secUserAccessMode' | 'appUserAccessMode' | 'readOnlyUserAccessMode';
}

export interface UserInformationChangeStatus {
    status: InformationChangeStatusTypes,
    reason: string
}

export enum InformationChangeStatusTypes {
    IN_PROGRESS = 'IN_PROGRESS',
    SUCCESSFUL = 'SUCCESSFUL',
    FAILED = 'FAILED',
    NONE = 'NONE'
}

export enum ValidationRules {
    PASSWORDS_MATCH = 'PASSWORDS_MATCH',
    PASSWORD_NOT_SAME_AS_USERNAME = 'PASSWORD_NOT_SAME_AS_USERNAME',
    PASSWORD_NOT_CONTAIN_USERNAME = 'PASSWORD_NOT_CONTAIN_USERNAME',
    MAX_LENGTH = 'MAX_LENGTH',
    MIN_LENGTH = 'MIN_LENGTH',
    CONSECUTIVE_CHAR = 'CONSECUTIVE_CHAR',
    DIGIT_NUMBER = 'DIGIT_NUMBER',
    LOWER_UPPER_CASE = 'LOWER_UPPER_CASE',
    UPPER_CASE = 'UPPER_CASE',
    LOWER_CASE = 'LOWER_CASE',
    DISALLOWED_ASCII = 'DISALLOWED_ASCII',
    NON_ALPHA_NUMBER = 'NON_ALPHA_NUMBER',
}

export enum FormParameters {
    USERNAME = 'username',
    PASSWORD = 'password',
    NEW_PASSWORD = 'newPassword',
    ACTIVATION_STATUS = 'activationStatus',
    ROLE_NAME = 'roleName',
    CONFIRM_PASSWORD = 'confirmPassword'
}

export enum UserInformationChangeOptions {
    PASSWORD_ONLY = 'Password',
    USERNAME_ONLY = 'Username',
    NO_OPTION_SELECTED = 'No Option Selected',
    PASSWORD_AND_USERNAME = 'Password and Username',
    PASSWORD_EXPIRATION_CHANGE = 'Expired password',
    PASSWORD_SERVICE = 'Password Service',
    ACTIVE_STATUS = 'Active status'
}

export enum AccountActiveOptions {
    systemUserAccessMode = 'BTS System Administrator',
    secUserAccessMode = 'BTS Security Administrator',
    appUserAccessMode = 'BTS Application Administrator',
    readOnlyUserAccessMode = 'BTS Read-Only user'
}

export enum ActiveStatusChangeOptions {
    ACTIVE = 'active',
    INACTIVE = 'inactive'
}

type UsernameValidationRulesType = ValidationRules.MAX_LENGTH |
    ValidationRules.MIN_LENGTH |
    ValidationRules.DISALLOWED_ASCII;

type PasswordValidationRulesType = ValidationRules.MAX_LENGTH |
    ValidationRules.MIN_LENGTH |
    ValidationRules.CONSECUTIVE_CHAR |
    ValidationRules.DIGIT_NUMBER |
    ValidationRules.LOWER_UPPER_CASE |
    ValidationRules.UPPER_CASE |
    ValidationRules.LOWER_CASE |
    ValidationRules.DISALLOWED_ASCII |
    ValidationRules.NON_ALPHA_NUMBER |
    ValidationRules.PASSWORD_NOT_SAME_AS_USERNAME |
    ValidationRules.PASSWORD_NOT_CONTAIN_USERNAME;
