import { Component, OnInit } from '@angular/core';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { DiagnosticContentMenuConfigService } from './diagnostic-content-menu-config.service';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';
import { NavigationSubmenuDirective } from '@oam/cu-ui/lib/submenu/components/submenu/navigation-submenu.directive';

@Component({
    selector: 'oam-shared-diagnostic-content-menu',
    templateUrl: './diagnostic-content-menu.component.html'
})
export class DiagnosticContentMenuComponent extends NavigationSubmenuDirective implements OnInit {

    constructor(private readonly diagnosticContentMenuConfigService: DiagnosticContentMenuConfigService,
                private readonly vnfcSharedService: VnfcSharedService, navigationService: NavigationService) {
        super(navigationService);
    }

    ngOnInit() {
        this.setMenuItemsMap([
            RouterLink.snapshot,
            ...this.getShouldShowEntries(),
            RouterLink.ipConnectivity,
            RouterLink.pmtuDiscovery,
            RouterLink.ipsecAssociations
        ]);
    }

    private getShouldShowEntries() {
        if (this.diagnosticContentMenuConfigService.getMenuConfig) {
            return this.vnfcSharedService.isVcu() ? [RouterLink.uplane] : [RouterLink.uplane, RouterLink.cplane];
        } else {
            return [RouterLink.tracing, RouterLink.cplane, RouterLink.sfpMonitoring];
        }
    }
}
