import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { ButtonModel } from '../../../button/model/button-model.definition';

@Component({
    selector: 'ui-webem-filter-input',
    templateUrl: './filter-input.component.html',
    styleUrls: ['./filter-input.component.scss']
})
export class FilterInputComponent implements OnInit, OnDestroy {
    @Output()
    valueChange: EventEmitter<string> = new EventEmitter<string>();
    @Input()
    value = '';
    @Input()
    disabled = false;
    @Input()
    placeholder = '';
    @Input()
    debounceTime = 200;
    @Input()
    id = '';

    changeValueSubject: Subject<void>;
    filterIconModel: ButtonModel;

    constructor() {
    }

    ngOnInit(): void {
        this.changeValueSubject = new Subject<void>();
        this.changeValueSubject
            .pipe(debounceTime(this.debounceTime))
            .subscribe(() => this.valueChange.emit(this.value));
        this.filterIconModel = {
            icon: 'mdi mdi-filter'
        };
    }

    onValueChange(): void {
        this.changeValueSubject.next();
    }

    resetValue(event: MouseEvent) {
        this.value = '';
        this.changeValueSubject.next();
        event.stopPropagation();
    }

    ngOnDestroy(): void {
        this.changeValueSubject.complete();
    }
}
