import { Pipe, PipeTransform } from '@angular/core';
import { GroupableTableViewColumn } from '../models/table-view-column.definition';
import { ParameterVisibilityMap } from '../../core/models/parameter-visibility-map.definition';

@Pipe({
    name: 'filterInvisibleColumns',
    pure: true
})
export class FilterInvisibleColumnsPipe implements PipeTransform {
    transform<T extends string = string>(
        columns: GroupableTableViewColumn<T>[],
        visibleParameters?: ParameterVisibilityMap<T> | null
    ): GroupableTableViewColumn<T>[] {
        if (!visibleParameters) return columns;

        return columns.filter(column =>
            !column.parameterGroup || !!visibleParameters[column.parameterGroup]
        );
    }
}
