import { ManagedObjectData, ParameterRelationError, ParameterRelationErrorDetail } from '@oam/cu-asm-api';
import { combineLatest, forkJoin, Subject } from 'rxjs';
import { selectSelectedPlan } from '@oam/configuration-shared/lib/core/selectors/selected-plan.selectors';
import { distinctUntilChanged, filter, map, shareReplay, switchMap, takeUntil } from 'rxjs/operators';
import { flattenDeep, get } from 'lodash';
import { PlanOrigin, PlansInfo } from '@oam/configuration-shared/lib/core/model';
import { Injectable } from '@angular/core';
import { PlanApiEndpointsService } from '@oam/configuration-shared/lib/core/services/plan-api-endpoints.service';
import { Selector, Store } from '@ngrx/store';


@Injectable()
export class GetRelationErrorService {
    constructor(public readonly store: Store, public readonly planApiEndpointsService: PlanApiEndpointsService) {

    }

    public getRelationErrorObservable(managedObjectSelector: Selector<ManagedObjectData, unknown>, destroy: Subject<void>) {
        return combineLatest([
            this.store.select(managedObjectSelector),
            this.store.select(selectSelectedPlan)
        ]).pipe(
            takeUntil(destroy),
            filter(([selectedMo, plan]) => {
                const planOrigin = get(plan, 'from');

                return !!get(selectedMo, 'relationErrors.length') &&
                    (planOrigin === PlanOrigin.DELTA || planOrigin === PlanOrigin.STAGE);
            }),
            distinctUntilChanged((previous, current) => {
                const [previousMo, previousPlan] = previous as [ManagedObjectData, PlansInfo];
                const [currentMo, currentPlan] = current as [ManagedObjectData, PlansInfo];

                return previousPlan.planId === currentPlan.planId &&
                    previousPlan.revision === currentPlan.revision &&
                    previousMo.moUuid === currentMo.moUuid &&
                    previousMo.revision === currentMo.revision;
            }),
            switchMap(([selectedMo, planInfo]: [ManagedObjectData, PlansInfo]) =>
                forkJoin((selectedMo.relationErrors as ParameterRelationError[]).map(({ errorIndex }) =>
                    this.planApiEndpointsService.getRelationErrorsDetailsObject(planInfo, errorIndex)))
                    .pipe(
                        filter((relationErrorsList: ParameterRelationErrorDetail[][] | undefined) => !!relationErrorsList),
                        map((relationErrorsList: ParameterRelationErrorDetail[][]) => {
                                return flattenDeep(relationErrorsList)
                                    .filter(relationErrorDetails => relationErrorDetails.parentMoUuid === selectedMo.moUuid);
                            }
                        )
                    )
            ),
            shareReplay()
        );
    }
}


