import { Directive, Output, EventEmitter, HostListener } from '@angular/core';
import { get } from 'lodash';
import {
    SIZE_LIMIT,
    FILE_NOT_SUPPORTED,
    MULTIPLE_FILES_NOT_ALLOWED,
    EMPTY_FILE
} from '../model/drop-errors-messages';
import { SUPPORTED_FILES, SupportedFile } from '../model/files-supported-by-drop';
import { KILOBYTE_BINARY } from '@oam/shared/lib/core/models/math-constants';


@Directive({
    selector: '[uiWebemDragDropFile]'
})
export class DragDropUploadDirective {

    @Output() fileDroppedAction = new EventEmitter<any>();

    @HostListener('dragenter', ['$event']) onDragEnter(event: DragEvent) {
        event.preventDefault();
        event.stopPropagation();
    }

    @HostListener('dragover', ['$event']) onDragOver(event: DragEvent) {
        event.preventDefault();
        event.stopPropagation();
    }

    @HostListener('dragleave', ['$event']) onDragLeave(event: DragEvent) {
        event.preventDefault();
        event.stopPropagation();
    }

    @HostListener('drop', ['$event']) onDrop(event: DragEvent) {
        event.preventDefault();
        event.stopPropagation();

        const files: FileList = get(event, 'dataTransfer.files');

        if (files) this.handleFiles(files);
    }

    handleFiles(files: FileList): void {
        switch (true) {
            case files.length > 1 :
                this.emitErrorMultipleFiles();
                break;
            case files.length === 1 : {
                const file = files[0];
                if (file) this.handleFile(file);
                break;
            }
        }
    }

    handleFile(file: File) {

        const supported: SupportedFile | undefined = SUPPORTED_FILES.find((supportedFile) => file.name.endsWith(supportedFile.extension));

        if (!supported) {
            this.emitErrorNotSupported();

            return;
        }

        if (!file.size) {
            this.emitErrorEmptySize();

            return;
        }

        file.size > supported.maxSizeKB * KILOBYTE_BINARY ? this.emitErrorToBig(supported) : this.emitFile(file);

    }

    emitFile(file: File): void {
        this.fileDroppedAction.emit(file);
    }

    emitErrorToBig(supported: SupportedFile): void {
        const errorMessage = `${SIZE_LIMIT} for ${supported.type} ${supported.maxSizeKB} KB`;
        this.fileDroppedAction.emit(Error(errorMessage));
    }

    emitErrorNotSupported(): void {
        const errorMessage = `${FILE_NOT_SUPPORTED}`;
        this.fileDroppedAction.emit(Error(errorMessage));
    }

    emitErrorMultipleFiles(): void {
        const errorMessage = `${MULTIPLE_FILES_NOT_ALLOWED}`;
        this.fileDroppedAction.emit(Error(errorMessage));
    }

    emitErrorEmptySize(): void {
        const errorMessage = `${EMPTY_FILE}`;
        this.fileDroppedAction.emit(Error(errorMessage));
    }

}
