import { Component, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Store } from '@ngrx/store';
import { NavigationEnd, Router } from '@angular/router';
import { includes } from 'lodash';
import { RoutedMenuItem, SplitPanelConfiguration } from '@oam/cu-ui';
import { DroppedItemService } from '@oam/core/lib/services/dropped-item.service';
import { StatusModelBar } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { QuickMenuAlarms } from '@oam/navigation/lib/navigation/core/models/quick-menu-alarms.interface';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { filter, takeUntil } from 'rxjs/operators';
import { LayoutsConfigService } from '@oam/layouts/lib/layouts-config.service';
import { isDetailPanelVisible } from '@oam/details-panel/lib/core/selectors/detail-panel.selectors';
import { DetailsPanelConfigService } from '@oam/details-panel/lib/core/services/details-panel-config.service';
import { setDetailsPanelResizingFlag } from '@oam/details-panel/lib/core/actions/detail-panel.actions';

@Component({
    selector: 'webem-page-layout',
    templateUrl: './page-layout.component.html',
    styleUrls: ['./page-layout.component.scss']
})
export class PageLayoutComponent implements OnInit, OnDestroy {
    currentRoute = '';
    navigationMenu: Navigation[];
    quickMenuItems: RoutedMenuItem[];
    statusBarModel: StatusModelBar;
    quickMenuAlarmsModel: QuickMenuAlarms;
    detailPanelAnimationOngoing = false;

    isDetailsPanelExpanded$: Observable<boolean>;
    contentPanelConfiguration: SplitPanelConfiguration = { size: '*' };
    detailsPanelConfiguration: SplitPanelConfiguration = {
        minSize: 270,
        maxSize: 370,
        size: 328,
        style: {
            'box-shadow': '-2px 0px 10px rgba(0,0,0,0.15)',
        }
    };

    private currentRoute$: Observable<NavigationEnd>;
    private readonly ngUnsubscribe = new Subject();

    constructor(
        private readonly store: Store,
        private readonly router: Router,
        private readonly droppedItemService: DroppedItemService,
        private readonly detailsPanelConfigService: DetailsPanelConfigService,
        private readonly layoutsConfigService: LayoutsConfigService
    ) {}

    ngOnInit() {
        this.navigationMenu = this.layoutsConfigService.getNavigations;
        this.quickMenuItems = this.layoutsConfigService.getQuickMenuItems;
        this.statusBarModel = this.layoutsConfigService.getStatusBarModel;
        this.quickMenuAlarmsModel = this.layoutsConfigService.getQuickMenuAlarmsModel;

        this.currentRoute$ = this.router.events.pipe(
            filter<NavigationEnd>(event => event instanceof NavigationEnd)
        );
        this.isDetailsPanelExpanded$ = this.store.select(isDetailPanelVisible);
        this.watchCurrentRoute();
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    showDetailPanelButton(): boolean {
        const currentRouteWithoutSlash = this.currentRoute.substring(1);
        const urlsWithDetailPanel = this.detailsPanelConfigService.getUrlsWithDetailsPanel();

        return includes(urlsWithDetailPanel, currentRouteWithoutSlash);
    }

    handleDroppedItem(item: File | Error): void {
        this.droppedItemService.handleItem(item);
    }

    setDetailsPanelResizingFlag(isBeingResized: boolean) {
        this.store.dispatch(setDetailsPanelResizingFlag({ payload: isBeingResized }));
        this.detailPanelAnimationOngoing = false;
    }

    handleDetailPanelToggleButtonClick(): void {
        this.detailPanelAnimationOngoing = true;
    }

    private watchCurrentRoute() {
        this.currentRoute$.pipe(
            takeUntil(this.ngUnsubscribe),
        ).subscribe((event: NavigationEnd) => {
            this.currentRoute = event.url;
            this.setDetailsPanelResizingFlag(false);
        });

        this.currentRoute = this.router.url;
    }
}
