import { CountersSelectionState } from '../store/counters-selection.state';
import {
    CounterNode,
} from '@oam/pm-shared/lib/measurement-store/models/counters-selection.interface';
import {
    clearCountersSelection,
    selectCounters
} from '../actions/counters-selection.actions';
import { createEntityAdapter, EntityAdapter } from '@ngrx/entity';
import { getCounterUniqueId } from '@oam/cu-ui';
import { Action, createReducer, on } from '@ngrx/store';

export const checkedNodesAdapter: EntityAdapter<CounterNode> = createEntityAdapter<CounterNode>({
    selectId: (counter: CounterNode) => getCounterUniqueId(counter)
});

export const countersSelectionInitialState: CountersSelectionState = {
    checkedNodes: checkedNodesAdapter.getInitialState(),
    counterStartTime: '',
    timeFrame: 6
};

const reducer = createReducer(
    countersSelectionInitialState,
    on(selectCounters, (state, { nodes, counterStartTime, timeFrame }) => ({
        ...state,
        checkedNodes: checkedNodesAdapter.setAll(nodes, state.checkedNodes),
        counterStartTime,
        timeFrame
    })),
    on(clearCountersSelection, (state) => ({
        ...state,
        checkedNodes: checkedNodesAdapter.removeAll(state.checkedNodes),
        counterStartTime: countersSelectionInitialState.counterStartTime,
        timeFrame: countersSelectionInitialState.timeFrame
    }))
);

export function countersSelectionReducer(
    state: CountersSelectionState | undefined,
    action: Action
): CountersSelectionState {
    return reducer(state, action);
}
