import { NGXLoggerMonitor, NGXLogInterface } from 'ngx-logger';
import { ConsoleLogsDatabaseService } from '@oam/core';

export class ConsoleLoggerMonitor implements NGXLoggerMonitor {
    constructor(private readonly logsDatabase: ConsoleLogsDatabaseService) {

    }

    async onLog(log: NGXLogInterface) {
        await this.logsDatabase.addLog(log);
    }
}
