import { Alarm } from '@oam/cu-asm-api/models';
import { AlarmSeverities } from '@oam/cu-ui/lib/alarm-severity-icon/model/alarm-severities.definition';

type AggregatedSeverities = {
    [k: string]: string
};

export function findHighestSeverity(alarms: Alarm[]): AlarmSeverities | undefined {
    const severities = alarms.reduce((aggregatedSeverities: AggregatedSeverities, alarm: Alarm) => {
        aggregatedSeverities[alarm.severity] = alarm.severity;

        return aggregatedSeverities;
    }, {} as AggregatedSeverities);

    return [
        AlarmSeverities.critical,
        AlarmSeverities.major,
        AlarmSeverities.minor,
        AlarmSeverities.warning
    ].find((type: AlarmSeverities) => !!severities[type]) || undefined;
}
