import {  createAction, props } from '@ngrx/store';
import { PlansInfo } from '../model';

export enum PlansInfoActionTypes {
    UpdateStagedPlans = '[PlansInfo] Update staged plans',
    StagedPlansFetchSuccess = '[PlansInfo] Staged plans fetched',
    StagedPlansFetchError = '[PlansInfo] Staged plans fetching failed',
    UpdateDeltaPlans = '[PlansInfo] Update delta plans',
    DeltaPlansFetchSuccess = '[PlansInfo] Delta plans fetched',
    DeltaPlansFetchError = '[PlansInfo] Delta plans fetching failed',
    UpdateInactivePlans = '[PlansInfo] Update inactive plans',
    InactivePlansFetchSuccess = '[PlansInfo] Inactive plans fetched',
    InactivePlansFetchError = '[PlansInfo] Inactive plans fetching failed',
    UpdateActivePlans = '[PlansInfo] Update active plans',
    ActivePlansFetchSuccess = '[PlansInfo] Active plans fetched',
    ActivePlansFetchError = '[PlansInfo] Active plans fetching failed',
    AllPlansInfoRequested = '[PlansInfo] All plans info requested',
    SubscribePlansInfoError = '[PlansInfo] Subscribe plans info error',
    SubscribePlansInfoChange = '[User] [PlansInfo] Subscribe plans info change',
    UnsubscribePlansInfoChange = '[User] [PlansInfo] Unsubscribe plans info change',
}

export const stagedPlansFetchError = createAction(
    PlansInfoActionTypes.StagedPlansFetchError
);

export const deltaPlansFetchError = createAction(
    PlansInfoActionTypes.DeltaPlansFetchError
);

export const inactivePlansFetchError = createAction(
    PlansInfoActionTypes.InactivePlansFetchError
);

export const activePlansFetchError = createAction(
    PlansInfoActionTypes.ActivePlansFetchError
);

export const stagedPlansFetchSuccess = createAction(
    PlansInfoActionTypes.StagedPlansFetchSuccess,
    props<{ plans: PlansInfo[] }>()
);

export const deltaPlansFetchSuccess = createAction(
    PlansInfoActionTypes.DeltaPlansFetchSuccess,
    props<{ plans: PlansInfo[] }>()
);

export const inactivePlansFetchSuccess = createAction(
    PlansInfoActionTypes.InactivePlansFetchSuccess,
    props<{ plans: PlansInfo[] }>()
);

export const activePlansFetchSuccess = createAction(
    PlansInfoActionTypes.ActivePlansFetchSuccess,
    props<{ plans: PlansInfo[] }>()
);

export const updateStagedPlans = createAction(
    PlansInfoActionTypes.UpdateStagedPlans,
    props<{ plans: PlansInfo[] }>()
);

export const updateDeltaPlans = createAction(
    PlansInfoActionTypes.UpdateDeltaPlans,
    props<{ plans: PlansInfo[] }>()
);

export const updateInactivePlans = createAction(
    PlansInfoActionTypes.UpdateInactivePlans,
    props<{ plans: PlansInfo[] }>()
);

export const updateActivePlans = createAction(
    PlansInfoActionTypes.UpdateActivePlans,
    props<{ plans: PlansInfo[] }>()
);

export const loadAllPlansInfoFromServer = createAction(
    PlansInfoActionTypes.AllPlansInfoRequested
);

export const subscribeOnPlansInfoChanges = createAction(
    PlansInfoActionTypes.SubscribePlansInfoChange
);

export const unsubscribeFromPlansInfoChanges = createAction(
    PlansInfoActionTypes.UnsubscribePlansInfoChange
);

export const subscribePlansInfoError = createAction(
    PlansInfoActionTypes.SubscribePlansInfoError,
    props<{error: any}>()
);

