import { ModuleWithProviders, NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { KEYBOARD_SHORTCUTS, reducer } from './core/reducers/keyboard-shortcuts.reducer';
import { KeyboardShortcutsConfig } from './core/services/keyboard-shortcuts-config';


@NgModule({
    declarations: [],
    imports: [
        StoreModule.forFeature(KEYBOARD_SHORTCUTS, reducer)
    ],
    exports: []
})
export class KeyboardShortcutsModule {
    static forRoot(config: KeyboardShortcutsConfig): ModuleWithProviders<KeyboardShortcutsModule> {
        return {
            ngModule: KeyboardShortcutsModule,
            providers: [
                {
                    provide: KeyboardShortcutsConfig,
                    useValue: config,
                }
            ]
        };
    }
}
