import { Action, createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import { RunningTestModels, RunningTestModel } from '@oam/cu-cp-config-api';
import {
    clearTestModels,
    updateRunningTestModels
} from '@oam/diagnostic-shared/lib/core/actions/running-test-models.actions';
import { unionWith, isEmpty, isEqual } from 'lodash';
import { logoutSuccessful } from '@oam/auth/lib/auth/core/actions/logout.actions';

export const featureKey = 'RunningTestModels';

export interface RunningTestModelsState {
    runningTestModels: RunningTestModels,
    testModels: Array<RunningAndStopTestModel>
}

export interface RunningAndStopTestModel extends RunningTestModel {
    isStopped?: boolean
}

export const initialState: RunningTestModelsState = {
    runningTestModels: [],
    testModels: []
};

function updateTestModels(state: RunningTestModelsState, runningTestModels: RunningTestModels) {
    if (isEmpty(state.testModels)) return runningTestModels;
    if (isEqual(state.runningTestModels, runningTestModels)) return state.testModels;

    const testModels = state.testModels.map(testModel => {
        if (!testModel.isStopped && !runningTestModels.find(item => isEqualTestModel(testModel, item))) {

            return {...testModel, isStopped: true};
        }

        return testModel;
    });

    return unionWith(testModels, runningTestModels, isRunningEqualTestModel);
}

function isRunningEqualTestModel(testModel: RunningAndStopTestModel, newTestModel: RunningAndStopTestModel) {
    return !testModel.isStopped && !newTestModel.isStopped && isEqualTestModel(testModel, newTestModel);
}

function isEqualTestModel(testModel: RunningAndStopTestModel, newTestModel: RunningAndStopTestModel) {
    return testModel.nrCellId === newTestModel.nrCellId && testModel.testType === newTestModel.testType;
}

const runningTestModelsReducer = createReducer(
    initialState,
    on(updateRunningTestModels, (state, { payload: runningTestModels }) => {
        return {
            runningTestModels,
            testModels: updateTestModels(state, runningTestModels)
        };
    }),
    on(logoutSuccessful, () => initialState),
    on(clearTestModels, () => initialState)
);

export function reducer(state: RunningTestModelsState | undefined, action: Action) {
    return runningTestModelsReducer(state, action);
}

export const diagnosticTestModelsStore = createFeatureSelector<RunningTestModelsState>(featureKey);
export const getRunningTestModelsState = createSelector(diagnosticTestModelsStore,
    (state) => state.testModels);
