import { Component, Input } from '@angular/core';

export enum MessageDisplay {
    SHOW_MESSAGE = 'showMessage',
    HIDE_MESSAGE_SHOW_PADDING = 'hideMessageShowPadding',
    HIDE_MESSAGE_AND_PADDING = 'hideMessageAndPadding',
    HIDE_MESSAGE_AND_GROUP = 'hideMessageAndGroup'
}

@Component({
    selector: 'ui-webem-form-validation-message',
    templateUrl: './form-validation-message.component.html',
    styleUrls: ['./form-validation-message.component.scss']
})
export class FormValidationMessageComponent {
    @Input() message: string;
    @Input() displayOption: MessageDisplay = MessageDisplay.SHOW_MESSAGE;

    getClass() {
        return {
            [MessageDisplay.SHOW_MESSAGE]: 'validation-message',
            [MessageDisplay.HIDE_MESSAGE_SHOW_PADDING]: 'no-validation-message',
            [MessageDisplay.HIDE_MESSAGE_AND_GROUP]: 'no-validation-message-grouped',
            [MessageDisplay.HIDE_MESSAGE_AND_PADDING]: ' '
        } [this.displayOption] || 'validation-message';
    }
}
