import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { RoutedMenuItem } from '../../model/routed-menu-item.definition';
import { Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { QuickMenuAlarms } from '@oam/navigation/lib/navigation/core/models/quick-menu-alarms.interface';

@Component({
    selector: 'ui-webem-routed-menu',
    templateUrl: './routed-menu.component.html',
    styleUrls: ['./routed-menu.component.scss']
})
export class RoutedMenuComponent implements OnInit, OnDestroy {
    @Input() items: RoutedMenuItem[];
    @Output() menuAction = new EventEmitter<RouterLink>();
    @Output() navigateToAlarms = new EventEmitter<void>();
    @Input() model: QuickMenuAlarms;
    @Input() numberOfAlarms: number;

    clickAction = new Subject<RouterLink>();
    clickActionSubscription: Subscription;

    constructor() { }

    ngOnInit() {
        this.clickActionSubscription = this.clickAction.pipe(
            debounceTime(120)
        ).subscribe((route) => this.menuAction.emit(route));
    }

    itemClickHandler(route: RouterLink) {
        this.clickAction.next(route);
    }

    navigateToAlarmsHandler() {
        this.navigateToAlarms.next();
    }

    getAlarmsTitle(): string {
        return this.numberOfAlarms !== null &&
            this.numberOfAlarms >= 0 &&
            this.numberOfAlarms !== this.model.initialState.numberOfAlarms
                ? `Active alarms: ${this.numberOfAlarms}`
                : 'Active alarms';
    }

    ngOnDestroy() {
        this.clickActionSubscription.unsubscribe();
    }
}
