import { Directive, Input, TemplateRef } from '@angular/core';

export interface SplitPanelConfiguration {
    minSize?: number;
    maxSize?: number;
    style?: any;
    size: any;
    id?: number;
}

const DEFAULT_CONFIGURATION: SplitPanelConfiguration = {
    size: '*',
};

@Directive({
    selector: '[uiWebemSplitPanel]'
})
export class SplitPanelDirective {
    @Input() visible = true;
    @Input() set uiWebemSplitPanel(configuration: SplitPanelConfiguration) {
        this.configuration = configuration;
    }
    configuration = DEFAULT_CONFIGURATION;

    constructor(
        public template: TemplateRef<any>,
    ) {}
}
