import { Component, Input, OnChanges, OnInit, ChangeDetectionStrategy, SimpleChanges } from '@angular/core';
import { WebemChartData } from '../../../chart/model/webem-chart.data';
import { ChartDataSetConfig } from '../../../chart/model/chart-data-set.type';
import { chartBarOptions } from './chart-bar-options';
import { cloneDeep } from 'lodash';

@Component({
    selector: 'ui-webem-chart-bar-status-panel',
    templateUrl: './chart-bar-status-panel.component.html',
    styleUrls: ['./chart-bar-status-panel.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ChartBarStatusPanelComponent implements OnChanges, OnInit {
    @Input() title: string;
    @Input() data: WebemChartData[];
    @Input() labels: string[] | string[][];
    @Input() dataSetConfigs: ChartDataSetConfig[];

    options = cloneDeep(chartBarOptions);
    mappedDataSetConfigs: ChartDataSetConfig[];

    constructor() { }

    ngOnInit(): void {
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.title && this.options.title) this.options.title.text = this.title;
        if (changes.dataSetConfigs) this.mappedDataSetConfigs = this.dataSetConfigs.map(this.mapDataSetConfigs);
    }

    private mapDataSetConfigs(dataSetConfig: ChartDataSetConfig) {
        return (dataSetConfig.backgroundColor && !dataSetConfig.hoverBackgroundColor)
            ? { ...dataSetConfig, hoverBackgroundColor: dataSetConfig.backgroundColor }
            : dataSetConfig;
    }
}
