import {
    Component,
    Input,
    OnInit,
    ContentChild,
    TemplateRef,
    Output,
    EventEmitter,
    SimpleChanges,
    OnChanges,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    ViewChild, ElementRef, OnDestroy, AfterViewInit, NgZone
} from '@angular/core';
import { Table } from 'primeng/table';
import { TableViewColumn, GroupableTableViewColumn } from '../../core/models/table-view-column.definition';
import { ParameterVisibilityMap } from '../../core/models/parameter-visibility-map.definition';
import { SPINNER_CLASS } from '@oam/cu-ui/lib/spinner/models/spinner-class-constants';
import ResizeObserver from 'resize-observer-polyfill';
import { get, isEmpty } from 'lodash';

@Component({
    selector: 'ui-webem-table-view',
    templateUrl: './table-view.component.html',
    styleUrls: ['./table-view.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TableViewComponent implements OnInit, OnChanges, OnDestroy, AfterViewInit {
    @ViewChild('pTable') pTable: Table;
    @ContentChild('row', { static: true }) row: TemplateRef<any>;

    private _selectedItem?: any;
    get selectedItem(): any | null {
        return this._selectedItem || null;
    }

    set selectedItem(item: any | null) {
        this._selectedItem = item;
        this.selectedChange.emit(item);
    }

    @Input() items: any[] = [];
    @Input() columns: (TableViewColumn | GroupableTableViewColumn<any>)[];
    @Input() visibleParameterGroupColumns?: ParameterVisibilityMap<any>;
    @Input() minBuffer = 40;
    @Input() dataKey?: string;
    @Input() selected?: any;
    @Input() pSelectableRowDisabled = false;
    @Input() virtual = false;
    @Input() loading: boolean;
    @Input() supportNoDataShow = false;
    @Input() scrollable = true;
    @Input() virtualScroll = true;
    @Input() noDataMessage = 'No Data';
    @Output() selectedChange = new EventEmitter<any>();
    private observer: ResizeObserver;

    itemsSlice: any[] = [];
    scrollHeight = '';
    tableScrollHeight: any;
    readonly SPINNER_CLASS = SPINNER_CLASS;

    constructor(private readonly cdr: ChangeDetectorRef, public readonly elementRef: ElementRef,private ngZone: NgZone) {
    }

    ngOnInit() {
    }

    ngAfterViewInit() {
        this.observer = new ResizeObserver((entries: any) => {

            this.ngZone.runOutsideAngular(() => {
                this.tableScrollHeight = get(entries,'[0].contentRect.height');

                const newHeight = this.calculateScrollHeight(this.items);

                if (newHeight === this.scrollHeight) return;

                this.scrollHeight = newHeight;

                this.cdr.detectChanges();
            });
        });

        this.observer.observe(this.elementRef.nativeElement);
    }

    ngOnDestroy() {
        this.observer.unobserve(this.elementRef.nativeElement);
    }

    isTableEmpty(): boolean {
        return isEmpty(this.items);
    }

    calculateScrollHeight(items: any[]) {
        if (!items || !this.tableScrollHeight) return '0px';

        const offsetWidth = document.documentElement.offsetWidth;
        const scrollWidth = document.documentElement.scrollWidth;

        const pTableBottomPadding = 15;
        const browserScrollbarHeight = 17;
        const pTableHeaderHeight = 27;
        const pTableBodyHeight = this.tableScrollHeight - pTableHeaderHeight - pTableBottomPadding;

        const height = (offsetWidth === scrollWidth) ? pTableBodyHeight : (pTableBodyHeight - browserScrollbarHeight);

        const rowsHeight = items.length * 27;
        let rowsHeightWithScrollbar = rowsHeight;
        const virtualScrollElement = this.elementRef.nativeElement.getElementsByTagName('cdk-virtual-scroll-viewport')[0];
        if (virtualScrollElement?.offsetWidth < virtualScrollElement?.scrollWidth) {
            rowsHeightWithScrollbar = rowsHeight + browserScrollbarHeight;
        }
        const finalHeight = rowsHeight > height ? height : rowsHeightWithScrollbar;

        return `${finalHeight}px`;
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.selected?.currentValue) this._selectedItem = this.selected;
    }

    ngAfterContentChecked() {
        this.scrollHeight = this.calculateScrollHeight(this.items);
    }

    rowTrackBy = (index: number, item: any): any => {
        return this.dataKey ? get(item, this.dataKey) : item;
    };
}
