import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { SplitAreaDirectiveContext } from '@oam/cu-ui';
import { select, Store } from '@ngrx/store';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { DroppedItemService } from '@oam/core/lib/services/dropped-item.service';
import { isDetailPanelVisible } from '@oam/details-panel/lib/core/selectors/detail-panel.selectors';
import { filter, takeUntil } from 'rxjs/operators';
import { SPLIT_VIEW_LOCATION } from '../../core/models/split-view-location-type';
import { DetailsPanelConfigService } from '@oam/details-panel/lib/core/services/details-panel-config.service';
import { OutletUrlPathService } from '@oam/core/lib/services/outlet-url-path.service';


@Component({
  selector: 'webem-split-view-single-column-layout',
  templateUrl: './split-view-single-column-page-layout.component.html',
  styleUrls: ['./split-view-single-column-page-layout.component.scss']
})
export class SplitViewSingleColumnPageLayoutComponent implements OnInit, OnDestroy {

    isDetailPanelExpanded$: Observable<boolean>;
    currentRoute: string;

    defaultDetailPanelSize: SplitAreaDirectiveContext = {
        minSize: 270,
        size: 300,
        maxSize: 370,
        id: 2,
        hideGutter: false
    };
    splitViewRouterOutlet: string;
    showDetailToggleButton: boolean;
    topView?: string;
    bottomView?: string;
    detailPanelAnimationOngoing = false;

    hiddenDetailPanelSize: SplitAreaDirectiveContext = { minSize: 0, size: 0, maxSize: 0, id: 2, hideGutter: true };
    contentDefaultSize: SplitAreaDirectiveContext = { size: '*',  id: 1 };
    detailsPanelSize: SplitAreaDirectiveContext;
    contentSize: SplitAreaDirectiveContext | null;
    readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    private readonly detailPanelVisibilityUnsubscribe: Subject<void> = new Subject<void>();

    constructor(
        private readonly store: Store,
        private readonly router: Router,
        private readonly droppedItemService: DroppedItemService,
        private readonly actRoute: ActivatedRoute,
        private readonly cd: ChangeDetectorRef,
        private readonly outletService: OutletUrlPathService,
        private readonly detailsPanelConfigService: DetailsPanelConfigService
    ) {}

    ngOnInit() {
        this.splitViewRouterOutlet = this.actRoute.snapshot.outlet;
        this.currentRoute = this.router.url;
        this.showDetailToggleButton = this.showDetailPanelButton();
        this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP);
        this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM);
        this.subscribeForDetailPanelVisibility(this.topView, this.bottomView);

        this.router.events.pipe(
            filter(event => event instanceof NavigationEnd),
            takeUntil(this.ngUnsubscribe)
        ).subscribe((event: NavigationEnd) => {
            const splitRouterOutlet = this.actRoute.snapshot.outlet;

            this.currentRoute = event.url;
            this.splitViewRouterOutlet = splitRouterOutlet === SPLIT_VIEW_LOCATION.TOP
                ? SPLIT_VIEW_LOCATION.TOP : SPLIT_VIEW_LOCATION.BOTTOM;
            this.showDetailToggleButton = this.showDetailPanelButton();
            this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP );
            this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM );

            this.detailPanelVisibilityUnsubscribe.next();
            this.detailPanelVisibilityUnsubscribe.complete();
            this.subscribeForDetailPanelVisibility(this.topView, this.bottomView);

        });
    }

    subscribeForDetailPanelVisibility(topView?: string, bottomView?: string) {
        this.isDetailPanelExpanded$ = this.store.pipe(
            select(isDetailPanelVisible, {
                outlet: this.splitViewRouterOutlet,
                topView,
                bottomView}
            ),
            takeUntil(this.ngUnsubscribe),
            takeUntil(this.detailPanelVisibilityUnsubscribe)
        );

        this.isDetailPanelExpanded$.subscribe((toggleDetailPanel) => {
            if (toggleDetailPanel) {
                this.detailsPanelSize = this.defaultDetailPanelSize;
                this.contentSize = this.contentDefaultSize;
            } else {
                this.contentSize = null;
                this.detailsPanelSize = this.hiddenDetailPanelSize;
            }
        });
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    showDetailPanelButton(): boolean {
        const urlsWithDetailPanel = this.detailsPanelConfigService.getUrlsWithDetailsPanel();

        this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP);
        this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM);

        if (this.splitViewRouterOutlet === SPLIT_VIEW_LOCATION.TOP && this.topView !== undefined) {
            return urlsWithDetailPanel.some( route => (this.topView as string).includes(route));
        }

        if (this.splitViewRouterOutlet === SPLIT_VIEW_LOCATION.BOTTOM && this.bottomView !== undefined) {
            return urlsWithDetailPanel.some( route => (this.bottomView as string).includes(route));
        }

        return false;
    }

    handleDroppedItem(item: File | Error): void {
        this.droppedItemService.handleItem(item);
    }

    handleDetailPanelToggleButtonClick(): void {
        this.detailPanelAnimationOngoing = true;
    }

    handleAnimationEnd(): void {
        this.detailPanelAnimationOngoing = false;
    }
}
