import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { map, pluck } from 'rxjs/operators';
import { NGXLogger } from 'ngx-logger';
import { GetHelp } from '@oam/core/lib/actions/help.actions';
import { DatetimeService } from '@oam/core/lib/services/datetime.service';
import { UserInformation } from '@oam/core/lib/store/user-state';
import { getUser, selectDowngradedPrivilegesDialogPopupFlag } from '@oam/core/lib/user/core/reducers/user-reducer';
import { logout } from '@oam/auth/lib/auth/core/actions/logout.actions';
import { openDialog } from '@oam/cu-ui';
import { DowngradedPrivilegesDialogComponent } from '@oam/cu-ui/lib/user-menu/components/downgraded-privileges-dialog/downgraded-privileges-dialog.component';
import { Userrole } from '@oam/cu-asm-api/models';
import { UserInformationService } from '@oam/core/lib/services/user-information.service';

const INVALID = 'Invalid';

@Component({
    selector: 'webem-user-menu',
    templateUrl: './user-menu.component.html',
    styleUrls: ['./user-menu.component.scss']
})
export class UserMenuSmartComponent implements OnInit, OnDestroy {
    @Input() helpFilePath: string;
    @Input() helpFileName: string;

    userInformation$: Observable<UserInformation>;

    downgradedPrivilegesDialogPopupFlag$: Observable<any>;
    downgradedPrivilegesDialogPopupFlagSubscription: any;

    constructor(
        private readonly store: Store,
        private readonly logger: NGXLogger,
        private readonly datetimeService: DatetimeService,
        private readonly userInformationService: UserInformationService
    ) {}

    ngOnDestroy(): void {
        this.downgradedPrivilegesDialogPopupFlagSubscription.unsubscribe();
    }

    ngOnInit() {
        const role = this.userInformationService.getRole();
        this.downgradedPrivilegesDialogPopupFlag$ = this.store.pipe(select(selectDowngradedPrivilegesDialogPopupFlag));

        this.downgradedPrivilegesDialogPopupFlagSubscription = this.downgradedPrivilegesDialogPopupFlag$.subscribe((shouldDowngradedPrivilegesDialogPopup) => {
            if (shouldDowngradedPrivilegesDialogPopup && role !== Userrole.ReadOnlyUserAccessMode) {
                this.store.dispatch(openDialog({
                    componentType: DowngradedPrivilegesDialogComponent,
                    config: {
                        header: 'Downgraded privileges',
                        closable: false
                    }
                }));
            }
        });

        this.userInformation$ = this.store.pipe(select(getUser),
            pluck('information'),
            map((user: UserInformation) => {
                const passwordExpirationDate = this.getPasswordExpirationFromApiDirectly(user.passwordExpirationDate);
                const lastLogin =  user.lastLogin && this.formatLoginInfoDate(user.lastLogin);
                const expires =  user.expires && this.formatLoginInfoDate(user.expires);

                return {
                    ...user,
                    passwordExpirationDate,
                    lastLogin: lastLogin?.includes(INVALID) ? user.lastLogin : lastLogin,
                    expires
                };
            }));
    }

    formatLoginInfoDate(lastLogin: string) {
        const separator = 'from';
        if (lastLogin.includes(separator)) {
            lastLogin = lastLogin.split(separator)[0].trim();
        }
        lastLogin = lastLogin.replace(/\s[A-Z]+(?=\s[0-9]+$)/, '');

        return this.datetimeService.getFormattedDateTime(lastLogin, false);
    }

    getPasswordExpirationFromApiDirectly(rawDateOrMessage = ''): string {
        return rawDateOrMessage === '' ? 'N/A' : rawDateOrMessage;
    }

    getPasswordExpirationInfo(rawDateOrMessage = ''): string {
        const formattedDate = this.datetimeService.getFormattedDateTime(rawDateOrMessage, true);

        return formattedDate.includes(INVALID) ? rawDateOrMessage : formattedDate;
    }

    onHelpClick() {
        this.logger.trace('[UserMenuComponent] Event helpClick has been caught. Action getHelp is emitting...');
        this.store.dispatch(new GetHelp({ filePath: this.helpFilePath, fileName: this.helpFileName }));
    }

    onLogOutClick() {
        this.store.dispatch(logout());
    }
}
