/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { FmService } from './services/fm.service';
import { StatusService } from './services/status.service';
import { CuFmClientService } from './services/cu-fm-client.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    FmService,
    StatusService,
    CuFmClientService,
    ApiConfiguration
  ],
})
export class CuFmClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuFmClientModule> {
    return {
      ngModule: CuFmClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuFmClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuFmClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
