import {
    Component,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ChangeDetectionStrategy,
    Output,
    EventEmitter,
    OnDestroy,
    ChangeDetectorRef
} from '@angular/core';
import { FlatTreeControl } from '@angular/cdk/tree';
import { take } from 'rxjs/operators';
import { isArray, uniq } from 'lodash';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { TreeNodeData } from '../models/pm-tree.interface';
import { getIdFromNodesData } from '../utils/pm-tree-nodes.utils';
import { DynamicFlatNode } from '../utils/dynamic-flat-node';
import { DynamicDatabase } from '../utils/dynamic-database';
import { DynamicDataSource } from '../utils/dynamic-data-source';
import { CountersFilter } from '../pm-tree.component';

@Component({
    selector: 'ui-webem-dynamic-tree',
    templateUrl: 'dynamic-tree.component.html',
    styleUrls: ['dynamic-tree.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class DynamicTreeComponent implements OnInit, OnChanges, OnDestroy {
    @Input() data: Map<string, string[]>;
    @Input() filter: CountersFilter;
    @Input() selectedCounters: TreeNodeData[] = [];
    @Input() expandedNodes: string[] = [];

    @Output() selectionChange: EventEmitter<TreeNodeData[]> = new EventEmitter();
    @Output() expansionChange: EventEmitter<string[]> = new EventEmitter();

    dataSource: DynamicDataSource;
    isLoading$: Observable<boolean>;
    private database: DynamicDatabase;
    private treeControl: FlatTreeControl<DynamicFlatNode>;
    private readonly treeLoading$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);
    private readonly rawExpandedNodes = new Set<string>();
    private readonly checkListSelection = new Set<string>();
    private readonly DELIMITER = '*-*';
    private readonly ABBREV_DELIMITER = '__';
    private readonly MAXIMUM_SELECTED_NODES = 20;
    readonly disabledMessage = `Maximum limit of ${this.MAXIMUM_SELECTED_NODES} selected counters has been reached`;
    isLoading = true;
    private counterLoadingSubscription: Subscription;
    constructor(private readonly cd: ChangeDetectorRef) {}

    ngOnInit() {
        this.counterLoadingSubscription = this.treeLoading$.subscribe(value => {
            this.isLoading = value;
            this.cd.markForCheck();
        });

        this.executeWithLoading(() => this.setup(), true);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.data && this.dataSource) {
            this.executeWithLoading(() => this.initializeTree(), changes.data.firstChange);
        }

        if (changes.filter && this.dataSource) {
            this.executeWithLoading(() => this.filterNodes(this.filter), changes.filter.firstChange);
        }

        if (changes.selectedCounters && this.dataSource) {
            const nodesToSelect: string[] = changes.selectedCounters.currentValue
                .map((counter: TreeNodeData) => getIdFromNodesData(counter))
                .filter((counterId: string) => !this.checkListSelection.has(counterId));

            if (nodesToSelect.length) {
                this.selectNode(nodesToSelect, false);
                this.applySelection();
                this.makeNodesVisibile(nodesToSelect);
            }
        }
    }

    ngOnDestroy() {
        this.expansionChange.emit(Array.from(this.rawExpandedNodes));

        if (this.counterLoadingSubscription) {
            this.counterLoadingSubscription.unsubscribe();
        }
    }

    getSelectionLimit() {
        return this.MAXIMUM_SELECTED_NODES;
    }

    clearSelection() {
        this.dataSource.data.forEach((node: DynamicFlatNode) => {
            node.isChecked = false;
            node.isIndeterminate = false;
        });
        this.checkListSelection.clear();
    }

    collapseAll() {
        this.dataSource.collapseAll();
        this.rawExpandedNodes.clear();
        this.expansionChange.emit([]);
    }

    hasChildren(node: string) {
        return !!this.database.getChildren(node);
    }

    toggle(node: DynamicFlatNode) {
        this.expandOrCollapseNode(node.item);
        this.toggleNodeAndSetState(node);
    }

    isExpanded(node: DynamicFlatNode) {
        return node.isExpanded;
    }

    leafItemSelectionToggle(node: DynamicFlatNode): void {
        if (this.checkIsSelected(node.item)) {
            node.isChecked = false;
            this.deselectNode(node.item);
        } else {
            node.isChecked = true;
            this.selectNode(node.item);
        }

        this.applySelectionToParents(node);
    }

    parentItemSelectionToggle(node: DynamicFlatNode): void {
        if (!this.isExpanded(node)) this.toggle(node);

        this.dataSource.dataChange.pipe(take(1)).subscribe(() => {
            const directDescendants = this.getRawDirectChildren(node);

            if (!directDescendants || !directDescendants.length) return;

            if (node.expandable && this.descendantsPartiallySelected(node.item) || this.descendantsAllSelected(node.item)) {
                this.deselectParentNodeAndDirectDescendants(node, directDescendants);
                this.applySelectionToParents(node);
                this.changeSelection();
            } else {
                const numberOfLeafsSelected = this.checkListSelection.size;

                if (numberOfLeafsSelected < this.MAXIMUM_SELECTED_NODES) {
                    const remainingItemsToSelect = this.MAXIMUM_SELECTED_NODES - numberOfLeafsSelected;
                    this.selectLeafTypeDescendants(directDescendants, remainingItemsToSelect);
                    this.applySelectionToParents(node);
                } else {
                    return;
                }

                this.toggleParentTypeDescendants(directDescendants);
            }

            this.setStateForNode(node);
        });
    }

    isParentNodeDisabled(node: DynamicFlatNode): boolean {
        return this.isMaxLimitReached() && !this.descendantsPartiallySelected(node.item) && !this.descendantsAllSelected(node.item);
    }

    isLeafNodeDisabled(node: DynamicFlatNode): boolean {
        return !this.checkIsSelected(node.item) && this.isMaxLimitReached();
    }

    private isMaxLimitReached(): boolean {
        return this.checkListSelection.size >= this.MAXIMUM_SELECTED_NODES;
    }

    private readonly getLevel = (node: DynamicFlatNode) => node.level;

    private readonly isExpandable = (node: DynamicFlatNode) => node.expandable;

    private makeNodesVisibile(nodesList: string[]) {
        const parentsList: string[] = [];
        nodesList.forEach((nodeId: string) => {
            let parentId = this.database.getParentId(nodeId);
            while (parentId) {
                parentsList.push(parentId);
                parentId = this.database.getParentId(parentId);
            }
        });
        this.expandedNodes = uniq(parentsList).sort();
        this.initializeExpandedNodes();
    }

    private checkIsExpanded(node: string) {
        return this.rawExpandedNodes.has(node);
    }

    private selectNode(node: string | string[], notify = true) {
        if (isArray(node)) {
            node.forEach(n => {
                this.checkListSelection.add(n);
            });
        } else {
            this.checkListSelection.add(node);
        }

        if (notify) this.changeSelection();
    }

    private deselectNode(node: string | string[], notify = true) {
        if (isArray(node)) {
            node.forEach(n => {
                this.checkListSelection.delete(n);
            });
        } else {
            this.checkListSelection.delete(node);
        }

        if (notify) this.changeSelection();
    }

    private checkIsSelected(node: string) {
        return this.checkListSelection.has(node);
    }

    private expandNode(node: string) {
        this.rawExpandedNodes.add(node);
    }

    private collapseNode(node: string) {
        this.rawExpandedNodes.delete(node);
    }

    private expandOrCollapseNode(node: string) {
        if (this.checkIsExpanded(node)) {
            this.collapseNode(node);
        } else {
            this.expandNode(node);
        }
    }

    private executeWithLoading(longAction: () => void, firstLoading = false) {
        this.treeLoading$.next(true);
        setTimeout(() => {
            longAction();
            if (!firstLoading || this.data.size) {
                this.treeLoading$.next(false);
            }
        }, 10);
    }
    private setup() {
        this.treeControl = new FlatTreeControl<DynamicFlatNode>(this.getLevel, this.isExpandable);
        this.database = new DynamicDatabase();
        this.dataSource = new DynamicDataSource(this.database);

        if (this.data.size) {
            this.initializeTree();
        }
    }

    private initializeExpandedNodes() {
        this.rawExpandedNodes.clear();

        if (this.expandedNodes.length && this.dataSource.data.length) {
            this.expandedNodes.forEach((nodeToExpand: string) => {
                const node = this.dataSource.data.find((n: DynamicFlatNode) => n.item === nodeToExpand);
                if (node) {
                    this.toggleNodeAndSetState(node);
                    this.rawExpandedNodes.add(node.item);
                }
            });
        }
    }

    private changeSelection() {
        const nodesToEmit: TreeNodeData[] = Array.from(this.checkListSelection).map(item => this.mapNodeRawToNodeData(item));
        this.selectionChange.emit(nodesToEmit);
    }

    private mapNodeRawToNodeData(node: string): TreeNodeData {
        const nodeParts = node.split(this.DELIMITER);
        const measurementParts = nodeParts[1].includes(this.ABBREV_DELIMITER) ? nodeParts[1].split(this.ABBREV_DELIMITER) : [nodeParts[1]];
        const counterParts = nodeParts[2].includes(this.ABBREV_DELIMITER) ? nodeParts[2].split(this.ABBREV_DELIMITER) : [nodeParts[2]];

        return {
            dimensionDistName: nodeParts[0] || '',
            measurementId: measurementParts[0] || '',
            measurementAbbreviation: measurementParts[1] || '',
            counterId: counterParts[0] || '',
            counterAbbreviation: counterParts[1] || ''
        };
    }

    private applySelection() {
        this.dataSource.data.forEach((node: DynamicFlatNode) => {
            this.setStateForNode(node);
        });
    }

    private isChildOfNode(node: DynamicFlatNode, child: DynamicFlatNode): boolean {
        return this.treeControl.getLevel(node) < this.treeControl.getLevel(child);
    }

    private initializeTree() {
        const selectedRawCounters = this.selectedCounters.map(
            (counter: TreeNodeData) => getIdFromNodesData(counter));
        this.selectNode(selectedRawCounters, false);
        if (this.filter && Object.values(this.filter).some(item => !!item)) {
            this.filterNodes(this.filter);
        } else {
            this.database.setDatabase(this.data);
            this.dataSource.data = this.database.initialData();
            this.applySelection();
            this.initializeExpandedNodes();
        }
    }

    private setStateForNode(node: DynamicFlatNode): void {
        if (node.expandable) {
            node.isChecked = this.descendantsAllSelected(node.item);
            node.isIndeterminate = this.descendantsPartiallySelected(node.item);
        } else {
            node.isChecked = this.checkIsSelected(node.item);
        }
    }

    private descendantsPartiallySelected(node: string): boolean {
        const descendants: string[] = this.database.dataMap.get(node) || [];
        const result = descendants.some(child => {
            return this.checkIsSelected(child)
                || this.descendantsPartiallySelected(child)
                || this.descendantsAllSelected(child);
        });

        return result && !this.descendantsAllSelected(node);
    }

    private descendantsAllSelected(node: string): boolean {
        const descendants: string[] = this.database.dataMap.get(node) || [];

        if (!descendants || !descendants.length) return false;

        return descendants.every(child =>
            this.checkIsSelected(child) || this.descendantsAllSelected(child));
    }

    private filterNodes(filterValue: CountersFilter) {
        if (Object.values(filterValue).some(item => !!item)) {
            this.database.setDatabase(this.filterMap(this.data, filterValue));
            this.dataSource.data = this.database.initialDataExtendedByFilter(this.filter);
            this.applySelection();
        } else {
            this.database.setDatabase(this.data);
            this.dataSource.data = this.database.initialData();
            this.applySelection();

            const nodesToSelect: string[] = this.selectedCounters
                .map((counter: TreeNodeData) => getIdFromNodesData(counter));

            this.makeNodesVisibile(nodesToSelect);
        }
    }

    private filterMap(map: Map<string, string[]>, filter: CountersFilter): Map<string, string[]> {
        const filteredMap = new Map<string, string[]>();
        for (const [key, value] of map) {
            const children = value.filter((child) => {
                return this.isRawNodeMatchingFilter(child, filter);
            });

            if (children.length) {
                filteredMap.set(key, children);
                this.includeParents(key, filteredMap);
            }
        }

        return filteredMap;
    }

    private isRawNodeMatchingFilter(node: string, filterValue: CountersFilter): boolean {
        const nodeParts = node.toLowerCase().split(this.DELIMITER);

        const nodeObject = {
            distName: nodeParts[0] || null,
            measurementAbbreviation: nodeParts[1] || null,
            counterId: nodeParts[2] || null
        };

        return Object.entries(nodeObject).every(item => {
            if (filterValue[item[0]] && item[1]) {
                return item[1].includes(filterValue[item[0]].toLowerCase());
            } else return !!(!filterValue[item[0]] && item[1]);
        });
    }

    private includeParents(node: string, map: Map<string, string[]>) {
        let child = node;
        let parentId = this.database.getParentId(node);
        while (parentId) {
            const nodeSiblings = map.get(parentId) || [];
            map.set(parentId, uniq([...nodeSiblings, child]));
            child = parentId;
            parentId = this.database.getParentId(parentId);
        }
    }

    private applySelectionToParents(node: DynamicFlatNode): void {
        let parent = this.getParentNode(node);

        while (parent) {
            this.setStateForNode(parent);
            parent = this.getParentNode(parent);
        }
    }

    private getParentNode(node: DynamicFlatNode): DynamicFlatNode | null {
        if (node.level < 1) return null;

        const parentId = this.database.getParentId(node.item);
        if (!parentId) return null;

        return this.dataSource.getNodeById(parentId) || null;
    }

    private toggleNodeAndSetState(node: DynamicFlatNode) {
        const nodesAdded = this.dataSource.toggleNode(node);
        nodesAdded.forEach((nodeObject: DynamicFlatNode) => {
            this.setStateForNode(nodeObject);
        });
    }

    private selectLeafTypeDescendants(descendents: DynamicFlatNode[], numberToSelect: number): void {
        const leafDescendants = descendents.filter(n => !n.expandable).slice(0, numberToSelect);
        this.selectNode(leafDescendants.map((leaf: DynamicFlatNode) => leaf.item));
        leafDescendants.forEach(n => {
            n.isChecked = true;
        });
    }

    private deselectParentNodeAndDirectDescendants(node: DynamicFlatNode, descendents: DynamicFlatNode[]): void {
        node.isChecked = false;
        node.isIndeterminate = false;
        const directDescendentsLeafs = descendents.filter(n => !n.expandable);
        this.deselectNode(directDescendentsLeafs.map(n => n.item), false);
        directDescendentsLeafs.forEach(n => {
            n.isChecked = false;
        });

        descendents.forEach((descendant: DynamicFlatNode) => {
            if (descendant.expandable && (descendant.isChecked || descendant.isIndeterminate)) {
                if (!this.isExpanded(descendant)) this.toggle(descendant);
                const directDescendants = this.getRawDirectChildren(descendant);
                this.deselectParentNodeAndDirectDescendants(descendant, directDescendants);
            }
        });
    }

    private toggleParentTypeDescendants(descendents: DynamicFlatNode[]): void {
        descendents.forEach((descendant: DynamicFlatNode) => {
            if (descendant.expandable && this.checkListSelection.size < this.MAXIMUM_SELECTED_NODES) {
                this.parentItemSelectionToggle(descendant);
            }
        });
    }

    private getRawDirectChildren(node: DynamicFlatNode) {
        const startIndex = this.dataSource.data.indexOf(node);
        const results = [];

        for (let i = startIndex + 1; i < this.dataSource.data.length && this.isChildOfNode(node, this.dataSource.data[i]); i++) {
            if (this.treeControl.getLevel(node) === this.treeControl.getLevel(this.dataSource.data[i]) - 1) {
                results.push(this.dataSource.data[i]);
            }
        }

        return results;
    }
}
