import {
    AfterViewInit,
    ChangeDetectorRef,
    Component,
    Input,
    NgZone,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { DetailFiles } from '../../core/models';
import { Table } from 'primeng/table';

@Component({
    selector: 'diagnostic-snapshot-details',
    templateUrl: './diagnostic-snapsthot-details.component.html',
    styleUrls: ['./diagnostic-snapsthot-details.component.scss']
})

export class DiagnosticSnapsthotDetailsComponent implements OnInit, OnChanges {
    @Input() detailFiles: DetailFiles[] = [];
    @ViewChild('table') table: Table;
    toolbarHeight = '172px';

    constructor(private readonly ngZone: NgZone) {
    }

    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges) {
        this.scroll();
    }

    scroll() {
        this.ngZone.runOutsideAngular(() => {
            setTimeout(() => {
                this.ngZone.run(() => {
                    if (this.table && this.table.containerViewChild) {
                        const body = this.table.containerViewChild.nativeElement.getElementsByClassName('p-datatable-scrollable-body')[0];
                        body.scrollTop = body.scrollHeight;
                    }
                });
            }, 0);
        });
    }
}
