import { createFeatureSelector, createSelector } from '@ngrx/store';
import { splitViewsStateFeatureKey, SplitViewState } from '../../core/reducers/split-view.reducer';

export const selectSplitViewState = createFeatureSelector<SplitViewState>(splitViewsStateFeatureKey);

export const selectAvailableViews = createSelector(
    selectSplitViewState,
    (state: SplitViewState) => state.availableViews
);

export const selectSplitViewsDetails = createSelector(
    selectSplitViewState,
    (state: SplitViewState) => state
);
