import { createAction, props } from '@ngrx/store';
import { ErrorMessageDetails } from '@oam/core/lib/models/app-settings.model';

enum AppSettingsActionTypes {
    ImportUiSettings = '[User] [AppSettings] Import UI Settings from file',
    ImportUiSettingsFile = '[AppSettings] Import UI Settings file',
    ImportUiSettingsFileSuccess = '[AppSettings] Import UI Settings file success',
    ImportUiSettingsFileError = '[AppSettings] Import UI Settings file error',

    ExportUiSettings = '[User] [AppSettings] Export UI Setting',
    ExportUiSettingsFile = '[AppSettings] Export UI Settings file',
    ExportUiSettingsFileSuccess = '[AppSettings] Export UI Settings file success',
    ExportUiSettingsFileError = '[AppSettings] Export UI Settings file error',

    RestoreDefaultUiSettings = '[User] [AppSettings] Restore Default UI Setting',
    RestoreDefaultUiSettingsSuccess = '[User] [AppSettings] Restore Default UI Setting success',

    RestoreDefaultUiSettingsError = '[User] [AppSettings] Restore Default UI Setting error',
    RestoreDefaultUiSettingsValues = '[User] [AppSettings] Restore Default UI Setting values',

    UiRefresh = '[User] [AppSettings] UI Refresh',
    CleanUiSettingsCache = '[User] [AppSettings] Clean UI Settings Cache'
}

export const importUiSettings = createAction(AppSettingsActionTypes.ImportUiSettings, props<{ file: File }>());
export const exportUiSettings = createAction(AppSettingsActionTypes.ExportUiSettings);
export const restoreDefaultUiSettings = createAction(AppSettingsActionTypes.RestoreDefaultUiSettings);
export const restoreDefaultUiSettingsSuccess = createAction(AppSettingsActionTypes.RestoreDefaultUiSettingsSuccess);
export const restoreDefaultUiSettingsError = createAction(AppSettingsActionTypes.RestoreDefaultUiSettingsError);
export const restoreDefaultUiSettingsValues = createAction(AppSettingsActionTypes.RestoreDefaultUiSettingsValues);
export const importUiSettingsFile = createAction(
    AppSettingsActionTypes.ImportUiSettingsFile,
    props<{file: File}>());
export const importUiSettingsFileSuccess = createAction(AppSettingsActionTypes.ImportUiSettingsFileSuccess);
export const importUiSettingsFileError = createAction(
    AppSettingsActionTypes.ImportUiSettingsFileError,
    props<{errorMessageDetails: ErrorMessageDetails}>());

export const exportUiSettingsFile = createAction(AppSettingsActionTypes.ExportUiSettingsFile);
export const exportUiSettingsFileSuccess = createAction(AppSettingsActionTypes.ExportUiSettingsFileSuccess);
export const exportUiSettingsFileError = createAction(
    AppSettingsActionTypes.ExportUiSettingsFileError,
    props<{errorMessageDetails: ErrorMessageDetails}>());

export const uiRefresh = createAction(AppSettingsActionTypes.UiRefresh);
export const cleanUiSettingsCache = createAction(AppSettingsActionTypes.CleanUiSettingsCache, props<{ payload: boolean}>());

