import { Component, Input } from '@angular/core';
import { Store } from '@ngrx/store';
import { SelectItem } from 'primeng/api';
import { ActivatedRoute, Router } from '@angular/router';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { setNavigationSearchOverlayClosed } from '../../core/actions/navigation-search.actions';


@Component({
    selector: 'webem-navigation-search-routes-list-item',
    templateUrl: './navigation-search-routes-list-item.component.html',
    styleUrls: ['./navigation-search-routes-list-item.component.scss']
})
export class NavigationSearchRoutesListItemComponent {
    @Input() routeItem: SelectItem;
    isActive = false;

    constructor(public readonly router: Router,
                private readonly store: Store,
                public readonly activatedRoute: ActivatedRoute) {
    }

    isDisabled(): boolean {
        return this.routeItem.value === this.router.url;
    }

    setActive(value: boolean): void {
        this.isActive = value;
    }

    redirectToSelectedRoute(): void {
        if (!this.isDisabled()) {
            this.store.dispatch(redirect({ routerLink: this.routeItem.value, activatedRoute: this.activatedRoute }));
            this.store.dispatch(setNavigationSearchOverlayClosed());
        }
    }
}
