import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { asyncScheduler, of } from 'rxjs';
import { catchError, mergeMap, takeUntil, withLatestFrom, exhaustMap, filter } from 'rxjs/operators';
import { PollingService } from '@oam/core/lib/services/polling.service';
import {
    setSnapshotServerStatus,
    setSnapshotStatusToCanceled,
    checkSnapshotServerStatusError,
    startCheckingSnapshotServerStatus,
    stopCheckingSnapshotServerStatus,
    cleanSnapshotServerStatus,
    cleanServerStatusIfOperationIsNotOngoing
} from '../actions/snapshot.action';
import { SnapshotService } from '@oam/cu-asm-api/services';
import { SnapshotServerStatus } from '../models/snapshot-server-status';
import { Store, select } from '@ngrx/store';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import {
    selectCollectingSnapshotIsOngoingOrCancelling,
    selectCollectingSnapshotState
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot.selector';
import { SNAPSHOT_COLLECTING_STATUS } from '@oam/diagnostic-snapshot/core/models/snapshot-progress-status';
import { selectTransferSnapshotIsOngoing } from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';
import { SnapshotFetchFilesService } from '../services/snapshot-fetch-files.service';


@Injectable()
export class CollectingSnapshotServerStatusEffects {
    stopCheckingSnapshotServerStatus$ = createEffect(() =>
        this.actions$.pipe(
            ofType(stopCheckingSnapshotServerStatus)
        ), { dispatch: false }
    );

    startCheckingSnapshotServerStatus$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startCheckingSnapshotServerStatus),
            exhaustMap(() => {
                return this.pollingService.getTimer(scheduler).pipe(
                    takeUntil(this.stopCheckingSnapshotServerStatus$),
                    withLatestFrom(this.store.pipe(select(selectCollectingSnapshotState))),
                    mergeMap(([, {collectStatus}]) => this.snapshotService.checkSnapshotStatus().pipe(
                        mergeMap(response => {
                            const actions = [];
                            if (response.status === SnapshotServerStatus.Idle && collectStatus === SNAPSHOT_COLLECTING_STATUS.CANCELLING) {
                                actions.push(
                                    addNotification({
                                        payload: {
                                            detail: 'Cancel snapshot operation succeeded',
                                            severity: 'success',
                                            summary: 'Snapshot Cancellation Succeeded'
                                        }
                                    }),
                                    setSnapshotStatusToCanceled()
                                );
                                this.snapshotFetchFilesService.cancelCollectingFiles();
                            }
                            actions.push(setSnapshotServerStatus({ snapshotServerStatus: response.status as string }));

                            return actions;
                        }),
                        catchError(error => of(checkSnapshotServerStatusError(error)))
                    ))
                );
            })
        )
    );

    cleanServerStatusIfOperationIsNotOngoing$ = createEffect(() =>
        this.actions$.pipe(
            ofType(cleanServerStatusIfOperationIsNotOngoing),
            withLatestFrom(
                this.store.pipe(select(selectCollectingSnapshotIsOngoingOrCancelling)),
                this.store.pipe(select(selectTransferSnapshotIsOngoing))
            ),
            filter(([, isCollectingSnapshotOngoing, isTransferSnapshotOngoing]) => !isCollectingSnapshotOngoing && !isTransferSnapshotOngoing),
            mergeMap(() => [stopCheckingSnapshotServerStatus(), cleanSnapshotServerStatus()])
        )
    );

    constructor(private readonly actions$: Actions, private readonly pollingService: PollingService,
                private readonly snapshotService: SnapshotService,
                private readonly snapshotFetchFilesService: SnapshotFetchFilesService,
                private readonly store: Store) {
    }

}
