import { Store } from '@ngrx/store';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { BaseDialogComponent } from '@oam/shared/lib/components/base-dialog/base-dialog-component';
import { updateDowngradedPrivilegesDialogPopupFlag } from '@oam/core/lib/actions/user.actions';

@Component({
    selector: 'ui-webem-downgraded-privileges-dialog',
    templateUrl: './downgraded-privileges-dialog.component.html',
    styleUrls: ['./downgraded-privileges-dialog.component.scss']
})
export class DowngradedPrivilegesDialogComponent extends BaseDialogComponent implements OnInit, OnDestroy {
    readonly okButtonModel: ButtonModel = {
        label: 'OK',
        theme: ButtonTheme.Accent
    };


    constructor(protected store: Store,
                public config: DynamicDialogConfig,
                ref: DynamicDialogRef) {
        super(store, ref);
    }

    ngOnInit(): void {
        super.ngOnInit();
    }

    closeDialog(): void {
        super.closeDialog();
        this.store.dispatch(updateDowngradedPrivilegesDialogPopupFlag({ payload: false }));
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
