import { createFeatureSelector, createSelector } from '@ngrx/store';
import {
    ITransferSnapshotToNMS, SERVER_CONNECTION_FAILURE,
    TRANSFER_SNAPSHOT_BTN_TITLE,
    TRANSFER_SNAPSHOT_STATUS
} from '@oam/diagnostic-snapshot/core/models';
import { transferSnapshotFeatureKey } from '@oam/diagnostic-snapshot/core/reducers/transfer-snapshot-to-nms.reducer';
import {
    selectCollectingSnapshotAvailability,
    selectSnapshotServerConnectionFailure
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot-server-status.selector';
import { SNAPSHOT_OPERATION_IS_NOT_AVAILABILITY } from '@oam/diagnostic-snapshot/core/models/snapshot-operation-btn-title.constants';

export const selectTransferSnapshotToNmsState =
    createFeatureSelector<ITransferSnapshotToNMS>(transferSnapshotFeatureKey);

export const selectRemoteSnapshotFileName = createSelector(
    selectTransferSnapshotToNmsState,
    ({ remoteSnapshotFileName }) => remoteSnapshotFileName
);

export const selectOperationIdForRemoteSnapshot = createSelector(
    selectTransferSnapshotToNmsState,
    ({ operationId }) => operationId
);

export const selectSnapshotTransferStatus = createSelector(
    selectTransferSnapshotToNmsState,
    ({ transferStatus }) => transferStatus
);

export const selectTransferSnapshotIsOngoing = createSelector(
    selectSnapshotTransferStatus,
    (transferStatus) => transferStatus === TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_ONGOING
);

export const selectTransferSnapshotDuration = createSelector(
    selectTransferSnapshotToNmsState,
    ({finishedAt, startedAt}) => (finishedAt as number) - (startedAt as number)
);

export const selectTransferSnapshotStartedAt = createSelector(
    selectTransferSnapshotToNmsState,
    ({startedAt}) => startedAt
);

export const selectTransferSnapshotFinished = createSelector(
    selectTransferSnapshotToNmsState,
    ({transferStatus}) => transferStatus === TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_SUCCEEDED
);

export const selectTransferSnapshotFailed = createSelector(
    selectTransferSnapshotToNmsState,
    ({transferStatus}) => transferStatus === TRANSFER_SNAPSHOT_STATUS.TRANSFER_SNAPSHOT_FAILED
);

export const selectTransferSnapshotErrorMessage = createSelector(
    selectTransferSnapshotToNmsState,
    ({errorMessage}) => errorMessage
);

export const selectTransferSnapshotBtnTitle = createSelector(
    selectRemoteSnapshotFileName,
    selectTransferSnapshotIsOngoing,
    selectSnapshotServerConnectionFailure,
    selectCollectingSnapshotAvailability,
    (remoteSnapshotFileName, isTransferSnapshotOngoing, isServerConnectionFailure, isSnapshotOperationAvailability) => {
        if (isTransferSnapshotOngoing) {
            return TRANSFER_SNAPSHOT_BTN_TITLE.TRANSFER_IS_ONGOING;
        } else if(isServerConnectionFailure) {
            return SERVER_CONNECTION_FAILURE;
        } else if (!isSnapshotOperationAvailability) {
            return SNAPSHOT_OPERATION_IS_NOT_AVAILABILITY;
        } else if (!remoteSnapshotFileName) {
            return TRANSFER_SNAPSHOT_BTN_TITLE.NOT_EXISTS_SNAPSHOT_TRANSFER_TO_NMS;
        } else {
            return TRANSFER_SNAPSHOT_BTN_TITLE.DEFAULT;
        }
    }
);

export const selectTransferOperationStatus = createSelector(
    selectTransferSnapshotIsOngoing,
    selectTransferSnapshotFinished,
    selectTransferSnapshotFailed,
    (isTransferSnapshotOngoing, isTransferSnapshotFinished, isTransferSnapshotFailed) => {
        return {isTransferSnapshotOngoing, isTransferSnapshotFinished, isTransferSnapshotFailed};
    }
);
