import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CuResetButtonState } from '../../model/cu-reset-button-state';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';

@Component({
    selector: 'ui-webem-reset-button',
    templateUrl: './reset-button.component.html',
    styleUrls: ['./reset-button.component.scss']
})
export class ResetButtonComponent implements OnInit {
    @Input() enabledButtonTitle: string;
    @Input() ongoingButtonTitle: string;

    @Input() cuResetState: CuResetButtonState;
    @Output() resetClick = new EventEmitter<void>();

    cuResetStateEnum = CuResetButtonState;

    resetButtonEnabledModel: ButtonModel;
    resetButtonOngoingModel: ButtonModel;

    constructor() {
    }

    ngOnInit() {
        this.resetButtonEnabledModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-refresh mdi-flip-h',
            title: this.enabledButtonTitle,
            length: ButtonLength.Long
        };

        this.resetButtonOngoingModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-loading mdi-spin',
            title: this.ongoingButtonTitle,
            length: ButtonLength.Long,
        };
    }

    onResetClick() {
        this.resetClick.emit();
    }
}
