import { Directive, EventEmitter, HostListener, Output } from '@angular/core';

@Directive({
  selector: '[uiWebemToggleTooltipByChildTooltip]'
})
export class ToggleTooltipByChildTooltipDirective {

    @Output() hideTooltip = new EventEmitter();
    @Output() showTooltip = new EventEmitter();

    @HostListener('hideParentTooltip', ['$event']) onHide(event: Event) {
        event.stopPropagation();
        this.hideTooltip.emit();
    }

    @HostListener('showParentTooltip', ['$event']) onShow(event: Event) {
        event.stopPropagation();
        this.showTooltip.emit();
    }

}
