import { Injectable } from '@angular/core';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { FeatureFlag } from '@oam/feature-flag';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { RoleAccessService } from '@oam/role-access-core';

@Injectable()
export class NavigationService {

    private readonly navigations: Navigation[];

    constructor(
        navigations: Navigation[],
        private featureFlag: FeatureFlag,
        private vnfcSharedService: VnfcSharedService,
        private roleAccessService: RoleAccessService
    ) {
        this.navigations = navigations;
    }

    getAvailableNavigations(): Navigation[] {

        return (this.vnfcSharedService
            .filterMenuForVcuVmcu(this.navigations)  as Navigation[])
            .filter(({ featureFlag, roleAccessFlag }) =>
                this.featureFlag.isFeatureEnabled(featureFlag) &&
                !this.roleAccessService.isForbiddenForCurrentRole(roleAccessFlag));
    }
}
