import { createReducer, on, Action, createFeatureSelector, createSelector } from '@ngrx/store';
import { clearPasswordExpirationWarning, displayPasswordExpirationWarning } from '../actions/user.actions';

export const featureKey = 'passwordAging';

export interface PasswordAgingState {
    isPasswordAgingPresent: boolean;
}

export const initialState: PasswordAgingState = {
    isPasswordAgingPresent: false
};

const passwordExpirationReducer = createReducer(
    initialState,
    on(displayPasswordExpirationWarning, (state) => ({
        ...state, isPasswordAgingPresent: true
    })),
    on(clearPasswordExpirationWarning, (state) => ({
        ...state, isPasswordAgingPresent: false
    }))
);

export function reducer(state: PasswordAgingState, action: Action) {
    return passwordExpirationReducer(state, action);
}

export const getPasswordAgingState = createFeatureSelector<PasswordAgingState>(featureKey);
export const getIsPasswordAgingPresent = createSelector(getPasswordAgingState, (state) => state.isPasswordAgingPresent);
