/* tslint:disable */
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BaseService } from '../base-service';
import { ApiConfiguration } from '../api-configuration';
import { StrictHttpResponse } from '../strict-http-response';
import { RequestBuilder } from '../request-builder';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

import { ChannelImpactedReq } from '../models/channel-impacted-req';

@Injectable({
  providedIn: 'root',
})
export class FhConfigService extends BaseService {
  constructor(
    config: ApiConfiguration,
    http: HttpClient
  ) {
    super(config, http);
  }

  /**
   * Path part for operation cellsPatch
   */
  static readonly CellsPatchPath = '/cells';

  /**
   * Information received from fhConfig to deactivate/activate cells.
   *
   *
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `cellsPatch()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  cellsPatch$Response(params: {
  
    /**
     * Deactivate/activate cells for impacted channels
     */
    body: ChannelImpactedReq
  }): Observable<StrictHttpResponse<void>> {

    const rb = new RequestBuilder(this.rootUrl, FhConfigService.CellsPatchPath, 'patch');
    if (params) {


      rb.body(params.body, 'application/json');
    }
    return this.http.request(rb.build({
      responseType: 'text',
      accept: '*/*'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return (r as HttpResponse<any>).clone({ body: undefined }) as StrictHttpResponse<void>;
      })
    );
  }

  /**
   * Information received from fhConfig to deactivate/activate cells.
   *
   *
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `cellsPatch$Response()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  cellsPatch(params: {
  
    /**
     * Deactivate/activate cells for impacted channels
     */
    body: ChannelImpactedReq
  }): Observable<void> {

    return this.cellsPatch$Response(params).pipe(
      map((r: StrictHttpResponse<void>) => r.body as void)
    );
  }

}
