import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AlarmCountBySeverity } from '../alarm-severity-dashboard-panel/AlarmCountBySeverity';

@Component({
    selector: 'ui-webem-alarms-counter-panel',
    templateUrl: './alarms-counter-panel.component.html',
    styleUrls: ['./alarms-counter-panel.component.scss']
})
export class AlarmsCounterPanelComponent implements OnInit {

    @Input() criticalAlarmsNumber: AlarmCountBySeverity;
    @Input() majorAlarmsNumber: AlarmCountBySeverity;
    @Input() minorAlarmsNumber: AlarmCountBySeverity;
    @Input() warningAlarmsNumber: AlarmCountBySeverity;

    @Output() criticalClick = new EventEmitter<void>();
    @Output() majorClick = new EventEmitter<void>();
    @Output() minorClick = new EventEmitter<void>();
    @Output() warningClick = new EventEmitter<void>();

    constructor() {
    }

    ngOnInit() {
    }

}
