import { Component, EventEmitter, Input, OnInit, Output, ViewChild, OnChanges } from '@angular/core';
import { Table } from 'primeng/table';
import { applyPrimeTablePatch } from '../../../prime-table-workaround/prime-table-workaround';
import { SPINNER_CLASS } from '@oam/cu-ui/lib/spinner/models/spinner-class-constants';
import { Observable } from 'rxjs';
import { DatetimeService } from '@oam/core';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';
import { SessionInfo } from '@oam/cu-api-gw-api';
import { isBoolean, sortBy } from 'lodash';

@Component({
    selector: 'ui-webem-sessions-table',
    templateUrl: './sessions-table.component.html',
    styleUrls: ['./sessions-table.component.scss']
})
export class SessionsTableComponent implements OnInit, OnChanges {
    @Input() sessions: SessionInfo[];
    @Input() isLoadingSessionsData$: Observable<boolean>;
    @Input() itemsPerPage: number;

    @ViewChild('table', { static: true }) table: Table;

    @Output()
    PageChanged = new EventEmitter();

    @Output()
    SessionSelected = new EventEmitter<SessionInfo>();

    @Output()
    clearSelection = new EventEmitter();
    readonly SPINNER_CLASS = SPINNER_CLASS;

    constructor(private readonly datetimeService: DatetimeService) {
    }

    ngOnInit() {
        applyPrimeTablePatch(this.table);
    }

    ngOnChanges() {
        this.sessions = sortBy(this.sessions, (a) => this.formatDate(a.CreationTime));
    }

    onSessionSelect({ data }: { data: SessionInfo }) {
        this.SessionSelected.emit(data);
    }

    onRowUnselect() {
        this.clearSelection.emit();
    }

    onLoadNewPage($event: any) {
        this.PageChanged.emit($event);
    }

    isTableEmpty(): boolean {
        return !this.sessions.length;
    }

    formatDate(timestamp: string): string {
        timestamp = timestamp?.replace(/\s[A-Z]+(?=\s[0-9]+$)/, '');

        return timestamp ? this.datetimeService.getFormattedDateTime(timestamp, false) : EMPTY_PLACEHOLDER;
    }

    formatReadOnly(mode: boolean) {

        return isBoolean(mode) ? mode : EMPTY_PLACEHOLDER;
    }
}
