import { Action, createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import {
    login,
    loginFailed,
    loginInProgress,
    loginSuccessful,
    resetOperationStatus,
    setLoginInitialValues,
    tokenRenewalSuccessful,
    loginLater, sessionUnintentionallyTerminated
} from '../actions/login.actions';
import { OperationStatus } from '@oam/core/lib/models/operation.interface';

export const featureKey = 'login';

export const initialState: OperationStatus = {
    inProgress: false,
    successful: false,
    failed: false,
    failedReason: '',
    tryLater: false
};

const loginReducer = createReducer(
    initialState,
    on(loginInProgress, (state) => {
        return { ...state, inProgress: true };
    }),
    on(loginFailed, (state, { payload: failedReason }) => {
        return { ...state, failed: true, inProgress: false, tryLater: false, failedReason };
    }),
    on(loginSuccessful, (state) => {
        return { ...state, failed: false, failedReason: '', successful: true, inProgress: false, tryLater: false };
    }),
    on(tokenRenewalSuccessful, (state, { shouldStartOperation }) => {
        return {
            ...state,
            tokenRenewalSuccessful: {
                status: true,
                shouldStartOperationAfterTokenRenewal: shouldStartOperation
            },
            inProgress: false,
            failed: false,
            failedReason: ''
        };
    }),
    on(resetOperationStatus, (state) => {
        return {
            ...state, tokenRenewalSuccessful: {
                status: false
            },
            inProgress: false
        };
    }),
    on(setLoginInitialValues, (state) => {
        return { ...state, successful: false, inProgress: false, failed: false, failedReason: '', tryLater: false };
    }),
    on(sessionUnintentionallyTerminated, (state) => {
        return { ...state, successful: false, inProgress: false, failed: true, tryLater: false };
    }),
    on(loginLater, (state, { message: failedReason }) => {
        return { ...state, tryLater: true, failedReason};
    })
);

export function reducer(state: OperationStatus | undefined, action: Action) {
    return loginReducer(state, action);
}

export const getLoginStatus = createFeatureSelector<OperationStatus>(featureKey);
export const selectLoginInProgress = createSelector(getLoginStatus, status => status.inProgress);
export const selectTokenRenewalSuccessful = createSelector(getLoginStatus, status => status.tokenRenewalSuccessful);

