import { Injectable } from '@angular/core';
import { Actions, ofType, createEffect } from '@ngrx/effects';
import { asyncScheduler, of } from 'rxjs';
import { mergeMap, map, takeUntil, catchError, tap, exhaustMap } from 'rxjs/operators';
import { StatusService } from '@oam/cu-asm-api/services';
import { PollingService } from '@oam/core/lib/services/polling.service';
import {
    setCu,
    cuRequest,
    subscribeCuError,
    unsubscribeCuWatch,
    subscribeCuWatch
} from '../actions/cu.actions';
import { setCloudAction } from '@oam/shared/lib/core/actions/cloud.actions';
import { WebemStateService } from '@oam/core/lib/services/webem.state.service';

@Injectable()
export class CuEffects {
    constructor(
        private readonly pollingService: PollingService,
        private readonly actions$: Actions,
        private readonly statusService: StatusService,
        private readonly webemStateService: WebemStateService
    ) {
    }

    unsubscribeCuWatch$ = createEffect(() =>
            this.actions$.pipe(
                ofType(unsubscribeCuWatch)
            ),
        { dispatch: false }
    );

    cuRequest$ = createEffect(() =>
        this.actions$.pipe(
            ofType(cuRequest),
            exhaustMap(() => this.statusService.getCu().pipe(
                tap(cu => this.webemStateService.setWebemState(cu)),
                mergeMap(cu => ([
                    setCu({ payload: cu }),
                    setCloudAction({ payload: { cloudName: cu.name, cloudId: cu.id } })
                ])),
                catchError(err => of(subscribeCuError(err)))
            ))
        )
    );

    subscribeCuWatch$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(subscribeCuWatch),
            mergeMap(() => this.pollingService.getTimer(scheduler).pipe(
                takeUntil(this.unsubscribeCuWatch$),
                map(() => cuRequest())
            ))
        )
    );

}
