import {
    PmTableColumn,
    PmTableRow,
    PmTableRowValues
} from '../models/pm-table.interface';
import { mapValues, keyBy } from 'lodash';
import { getAllTimestamps } from '../../utils/timestamp.utils';
import { PmCounter, PmCounterValue } from '../../models/counters.interface';
import { compareStartTimesAscendant, compareStartTimesDescendant } from '../../utils/pm-data.utils';

export function getScrollableColumns(pmCounters: PmCounter[]): PmTableColumn[] {
    return pmCounters.map((pmCounter: PmCounter) => ({
        field: getKeyForCounters(pmCounter),
        header: `${pmCounter.counterId} (${pmCounter.counterAbbreviation})`
    }));
}

export function getFrozenRows(pmCounters: PmCounter[]): PmTableRow[] {
    return [
        { counterId: 'Full Path', ...getAllFullPaths(pmCounters) },
        { counterId: 'Unit', ...getAllUnits(pmCounters) }
    ];
}

export function getScrollableRows(pmCounters: PmCounter[], order: string): PmTableRow[] {
    const timestampsSorter = order === 'asc' ? compareStartTimesAscendant : compareStartTimesDescendant;
    const timestamps = getAllTimestamps(pmCounters).sort(timestampsSorter);

    return timestamps.map((timestamp: string) => ({
        counterId: timestamp,
        ...getAllCountersValueAtTimestamp(pmCounters, timestamp)
    }));
}

function getKeyForCounters(pmCounter: PmCounter) {
    return `${pmCounter.dimensionDistName}/${pmCounter.counterId}/${pmCounter.measurementAbbreviation}`;
}

function getAllFullPaths(pmCounters: PmCounter[]): PmTableRowValues {
    const countersUniqueKeys = keyBy(pmCounters, getKeyForCounters);

    return mapValues(countersUniqueKeys, (pmCounter: PmCounter) =>
        `${pmCounter.dimensionDistName}/${pmCounter.measurementAbbreviation}`
    );
}

function getAllUnits(pmCounters: PmCounter[]): PmTableRowValues  {
    const countersUniqueKeys = keyBy(pmCounters, getKeyForCounters);

    return mapValues(countersUniqueKeys, (pmCounter: PmCounter) =>
        pmCounter.counterUnit
    );
}

function getAllCountersValueAtTimestamp(countersTableModels: PmCounter[], timestamp: string): PmTableRowValues {
    const countersUniqueKeys = keyBy(countersTableModels, getKeyForCounters);

    return mapValues(countersUniqueKeys, (pmCounter: PmCounter) => {
        const timestampsObject = pmCounter.counterValues.find((value: PmCounterValue) => value.startTime === timestamp);

        return timestampsObject && timestampsObject.value || '-';
    });
}
