import { createFeatureSelector, createSelector } from '@ngrx/store';
import { legalNoticeModalFeatureKey, LegalNoticeModalState } from '../reducers/legal-notice-modal.reducer';

export const legalNoticeModalFeatureSelector = createFeatureSelector(legalNoticeModalFeatureKey);

export const securityNoticeSelector = createSelector(
    legalNoticeModalFeatureSelector,
    (state: LegalNoticeModalState) => state.securityNotice
);

export const securityNoticeConfirmButtonVisibleSelector = createSelector(
    legalNoticeModalFeatureSelector,
    (state: LegalNoticeModalState) => !!state.confirmButtonVisible
);

export const securityNoticeErrorSelector = createSelector(
    legalNoticeModalFeatureSelector,
    (state: LegalNoticeModalState) => !!state.okButtonVisible
);

export const isLegalNoticeVisibleSelector = createSelector(
    legalNoticeModalFeatureSelector,
    (state: LegalNoticeModalState) => state.isVisible
);
