import { createFeatureSelector, createSelector } from '@ngrx/store';
import { Data, Params } from '@angular/router';
import { RouterReducerState } from '@ngrx/router-store';


export interface MergedRoute {
    url: string;
    queryParams: Params;
    params: Params;
    data: Data;
}

export const routerStateConfig = {
    stateKey: 'router'
};

export type MergedRouteReducerState = RouterReducerState<MergedRoute>;
export const getRouterReducerState = createFeatureSelector<MergedRouteReducerState>(routerStateConfig.stateKey);

export const getMergedRoute = createSelector(getRouterReducerState, (routerReducerState): MergedRoute | undefined => {
    if (routerReducerState) return routerReducerState.state;

    return;
});

