import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, map, take, filter } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { clearTokens, removeUser } from '@oam/core/lib/actions/user.actions';
import { forceRedirect } from '@oam/core/lib/actions/router.actions';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { setConnectionStatus } from '@oam/server-connection-checker/lib/core/actions/server-connection.actions';
import {
    loginFailed,
    sessionUnintentionallyTerminated
} from '@oam/auth/lib/auth/core/actions/login.actions';
import {
    pmServerDown,
    pmServerAvailable,
    subscribeToPmServiceState
} from '@oam/pm-v2/lib/core/actions/counters-pm-service.actions';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import { selectUserRole } from '@oam/core/lib/user/core/reducers/user-reducer';
import { ROLE_NAME_MAPPING } from '@oam/shared/lib/constants/role-access-constants';
import { selectPmServerDown } from '@oam/pm-v2/lib/core/selectors/counters.selectors';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {

    static readonly CONNECTION_ERR_CODES = [0, 502, 503, 504];
    static readonly PM_SERVICE_DOWN_ERR_CODES = [500, 503];

    isConnectionStable = true;

    isPmServerDown: boolean;

    currentRole: string;

    constructor(private readonly store: Store, private readonly vnfcSharedService: VnfcSharedService) {
        this.store.pipe(select(selectUserRole)).subscribe((role: string) => {
            this.currentRole = role;
        });
    }

    static isConnectionError(error: any): boolean {
        return ErrorInterceptor.CONNECTION_ERR_CODES.includes(error.status);
    }

    static isPmServiceDownError(error: any): boolean {
        return ErrorInterceptor.PM_SERVICE_DOWN_ERR_CODES.includes(error.status);
    }

    intercept(request: HttpRequest<string>, next: HttpHandler): Observable<HttpEvent<string>> {
        return next.handle(request).pipe(
            map((event) => {
                if (event instanceof HttpResponse && !this.isConnectionStable) {
                    this.store.dispatch(setConnectionStatus({ isConnected: true }));
                    this.isConnectionStable = true;
                }

                return event;
            }),
            catchError((err) => {
                if (err.status === 401 && request.url !== '/api/sec/v1/login') {
                    this.store.dispatch(clearTokens());
                    this.store.dispatch(removeUser());
                    this.store.dispatch(sessionUnintentionallyTerminated());
                    this.store.dispatch(forceRedirect({routerLink: RouterLink.login}));
                    if(err.error.additionalStatus && err.error.additionalStatus === 106) {
                        this.store.dispatch(loginFailed({ payload: `${ROLE_NAME_MAPPING[this.currentRole] ? ROLE_NAME_MAPPING[this.currentRole] : 'Role'} is deactivated` }));
                    }
                }

                if (err.status === 405) {
                     this.store.dispatch(addNotification({payload: {
                             severity: 'error',
                             summary: 'Operation Failed',
                             detail: 'No access permission'
                         }}));
                }

                if (ErrorInterceptor.isConnectionError(err) && this.isConnectionStable && !request.url.includes('/api/pm/v2/')) {
                    this.store.dispatch(setConnectionStatus({ isConnected: false }));
                    this.isConnectionStable = false;
                    this.store.dispatch(pmServerAvailable());
                }

                if (ErrorInterceptor.isPmServiceDownError(err) && this.isConnectionStable && request.url.includes('/api/pm/v2/')) {
                    this.store.pipe(
                        select(selectPmServerDown),
                        filter(() => this.vnfcSharedService.isVcu()),
                        take(1)
                    ).subscribe((isPmServerDown: boolean) => {
                        if (!isPmServerDown) {
                            this.store.dispatch(pmServerDown());
                            this.store.dispatch(subscribeToPmServiceState());
                        }
                    });
                }

                return throwError(err);
            })
        );
    }
}
