import { ModuleWithProviders, NgModule } from '@angular/core';
import { Environment } from '../environment-variables/core/models';

@NgModule({
    declarations: [],
    imports: []
})
export class EnvironmentVariablesModule {
    static forRoot(config: Environment): ModuleWithProviders<EnvironmentVariablesModule> {
        return {
            ngModule: EnvironmentVariablesModule,
            providers: [
                {
                    provide: Environment,
                    useValue: config
                }
            ]
        };
    }
}
