import { Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { isEmpty } from 'lodash';
import { Subject } from 'rxjs';
import { SelectItem } from 'primeng/api';
import { select, Store } from '@ngrx/store';
import { takeUntil } from 'rxjs/operators';
import { RoutesList } from '../../models/routes-list';
import { ESCAPE, KEY_F } from '../../constants/keyboard-keys.constants';
import { selectIsNavigationSearchOverlayOpened } from '../../core/selectors/navigation-search.selectors';
import {
    setNavigationSearchOverlayClosed,
    setNavigationSearchOverlayOpened
} from '../../core/actions/navigation-search.actions';
import { ButtonLength, ButtonModel, ButtonTheme, OverlayPanelComponent } from '@oam/cu-ui';
import { NavGroup, Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { navigationRoot } from '@oam/navigation/lib/navigation/core/models/navigation-group.model';
import { groupNavigations } from '@oam/navigation/lib/navigation/core/utility/navigation-transformer';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';


@Component({
    selector: 'webem-navigation-search',
    templateUrl: './navigation-search.component.html',
    styleUrls: ['./navigation-search.component.scss']
})
export class NavigationSearchComponent implements OnInit, OnDestroy {
    isOverlayOpened: boolean;
    allRoutes: SelectItem[];
    routesList: RoutesList;
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    navigationSearchButtonModel: ButtonModel = {
        icon: 'navigation-search-icon mdi mdi-feature-search-outline',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long,
        title: 'Navigation search'
    };
    @ViewChild('overlayPanel') overlayPanel: OverlayPanelComponent;
    @ViewChild('overlayTarget') overlayTarget: ElementRef;

    @HostListener('window:keydown', ['$event'])
    onKeyDown(event: KeyboardEvent): void {
        if ((event.ctrlKey && event.shiftKey && event.code === KEY_F) && !this.isOverlayOpened) {
            this.openOverlay(event);
        }
        if (event.code === ESCAPE) {
            this.hideOverlayIfOpened();
        }
    }

    constructor(private readonly store: Store,
                private navigationService: NavigationService) {
    }

    ngOnInit(): void {
        this.allRoutes = this.getAllRoutesSorted();
        this.setAllRoutesAsFilteredRoutes();
        this.store.pipe(
            takeUntil(this.ngUnsubscribe),
            select(selectIsNavigationSearchOverlayOpened)
        ).subscribe((isOpened: boolean) => {
            this.isOverlayOpened = isOpened;
        });
    }

    toggle(event: Event): void {
        if (!this.isOverlayOpened) {
            this.openOverlay(event);
        } else {
            this.hideOverlayIfOpened();
        }
    }

    hideOverlayIfOpened(): void {
        if (this.isOverlayOpened) {
            this.overlayPanel.hide();
            this.store.dispatch(setNavigationSearchOverlayClosed());
            this.setAllRoutesAsFilteredRoutes();
        }
    }

    filterChanged(searchText: string): void {
        let filteredRoutes: SelectItem[];
        searchText = searchText.toLowerCase();

        if (searchText === '') {
            this.setAllRoutesAsFilteredRoutes();
        } else {
            filteredRoutes = this.allRoutes.filter((view: SelectItem) =>
                view.label && view.label.toLowerCase().includes(searchText)
            );
            this.routesList = { filteredRoutes, searchText };
        }
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    openOverlay(event: Event): void {
        this.overlayPanel.show(event, this.overlayTarget.nativeElement);
        this.store.dispatch(setNavigationSearchOverlayOpened());
    }

    private getAllRoutesSorted(): SelectItem[] {

        return this.transFormToRouteSelectItems(this.navigationService.getAvailableNavigations())
            .sort(this.sortRoutes);
    }

    private sortRoutes(a: SelectItem, b: SelectItem): number {
        if (!a.label || !b.label) return 0;
        const firstLabel = a.label.toLowerCase();
        const secondLabel = b.label.toLowerCase();
        if (firstLabel < secondLabel) return -1;
        if (firstLabel > secondLabel) return 1;

        return 0;
    }

    private setAllRoutesAsFilteredRoutes(): void {
        this.routesList = {
            filteredRoutes: this.allRoutes,
            searchText: ''
        };
    }

    private transFormToRouteSelectItems(navigations: Navigation[]): SelectItem[] {

        return groupNavigations<Navigation, NavGroup>(navigations, prepareModel, filter)
            .map((group: NavGroup) => {
                return group.items.reduce((searchItemGroup: any, item: Navigation) => {
                    const searchItem = {
                        label: getLabel(item, group),
                        value: item.routerLink
                    };
                    if (!isEmpty(item?.children)) {
                        searchItemGroup.push(...(item.children as any).map((child: any) => ({
                            label: `${searchItem.label} / ${child.label}`,
                            value: child.routerLink
                        })));
                    } else {
                        searchItemGroup.push(searchItem);
                    }

                    return searchItemGroup;
                }, []);
            })
            .flat();

        function prepareModel({ isGroup, nav }: { isGroup: boolean, isItem: boolean, nav: NavGroup | Navigation }) {

            return {
                label: isGroup ? (nav?.navigationGroup?.label || nav?.navigationGroup?.id) : nav?.label,
                items: nav['items'],
                navigationGroup: nav?.navigationGroup,
                routerLink: nav?.routerLink,
                children: nav?.children
            };
        }

        function filter(nav: Navigation) {

            return nav.routerLink === RouterLink.errors;
        }

        function getLabel(item: Navigation, group: NavGroup) {

            return group?.items.length === 1 || group?.navigationGroup?.id === navigationRoot.id
                ? item?.label :
                `${group.label} / ${item.label}`;
        }
    }
}
