import { Component, ContentChild, OnInit, TemplateRef, Input, Output, EventEmitter } from '@angular/core';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { Store } from '@ngrx/store';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonThickness } from '../../../button/model/button-thickness.enum';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'ui-webem-rich-panel',
    templateUrl: './rich-panel.component.html',
    styleUrls: ['./rich-panel.component.scss']
})
export class RichPanelComponent implements OnInit {
    @Input() loading = false;
    @Input() redirectLink?: RouterLink;
    @Output() beforeRedirect = new EventEmitter<void>();
    @ContentChild('title', { static: true }) templateTitle: TemplateRef<any>;
    @ContentChild('content', { static: true }) templateContent: TemplateRef<any>;
    @ContentChild('operations', { static: true }) templateOperations: TemplateRef<any>;

    redirectButtonModel: ButtonModel = {
        icon: 'mdi mdi-logout-variant',
        title: 'Redirect to view',
        theme: ButtonTheme.Secondary,
        thickness: ButtonThickness.Thin
    };

    constructor(private readonly store: Store, private readonly activatedRoute: ActivatedRoute) { }

    ngOnInit() { }

    redirect(link: RouterLink) {
        this.beforeRedirect.emit();

        this.store.dispatch(redirect({routerLink: link, activatedRoute: this.activatedRoute}));
    }
}
