import { Injectable } from '@angular/core';
import { AlarmKeys } from './model';


type ParameterNameMapper = {
    [k in AlarmKeys]: string
};

const parameterNames = {
    severity: 'Severity',
    faultName: 'Fault name',
    faultId: 'Fault ID',
    appearTime: 'Appeared',
    alarmingObject: 'Alarming object',
    eventTypeName: 'Event type',
    probableCauseName: 'Cause name',
    lastUpdatedTime: 'Last updated',
    alarmId: 'Alarm ID',
    faultyCells: 'Number of faulty cells',
    degradedCells: 'Number of degraded cells',
    affectedCells: 'Number of affected cells',
    clearedTime: 'Cleared time',
    alarmName: 'Alarm name',
    additionalInfo: 'Additional information'
} as unknown as ParameterNameMapper;

@Injectable({
    providedIn: 'root'
})
export class UiAlarmParametersMapperService {
    constructor() {
    }

    getUiName(abbreviationName: AlarmKeys): string | undefined {
        return parameterNames[abbreviationName];
    }
}
