import { Directive, Input, TemplateRef } from '@angular/core';

export class CurledUpAreaContext {
    forId: number;
}

@Directive({
    selector: '[uiWebemCurledUpArea]'
})
export class CurledUpAreaDirective {

    context = new CurledUpAreaContext();

    constructor(public template: TemplateRef<any>) {
    }

    @Input()
    set uiWebemCurledUpArea(value: CurledUpAreaContext) {
        Object.assign(this.context, value);
    }
}
