/* tslint:disable */
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BaseService } from '../base-service';
import { ApiConfiguration } from '../api-configuration';
import { StrictHttpResponse } from '../strict-http-response';
import { RequestBuilder } from '../request-builder';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

import { OperationStatus } from '../models/operation-status';

@Injectable({
  providedIn: 'root',
})
export class AsyncOperationsService extends BaseService {
  constructor(
    config: ApiConfiguration,
    http: HttpClient
  ) {
    super(config, http);
  }

  /**
   * Path part for operation operationsIdGet
   */
  static readonly OperationsIdGetPath = '/operations/{id}';

  /**
   * Get operation status by id.
   *
   *
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `operationsIdGet()` instead.
   *
   * This method doesn't expect any request body.
   */
  operationsIdGet$Response(params: {

    /**
     * Resource id
     */
    id: string;

  }): Observable<StrictHttpResponse<OperationStatus>> {

    const rb = new RequestBuilder(this.rootUrl, AsyncOperationsService.OperationsIdGetPath, 'get');
    if (params) {

      rb.path('id', params.id, {});

    }
    return this.http.request(rb.build({
      responseType: 'json',
      accept: 'application/json'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return r as StrictHttpResponse<OperationStatus>;
      })
    );
  }

  /**
   * Get operation status by id.
   *
   *
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `operationsIdGet$Response()` instead.
   *
   * This method doesn't expect any request body.
   */
  operationsIdGet(params: {

    /**
     * Resource id
     */
    id: string;

  }): Observable<OperationStatus> {

    return this.operationsIdGet$Response(params).pipe(
      map((r: StrictHttpResponse<OperationStatus>) => r.body as OperationStatus)
    );
  }

}
