import { Component, Input, OnInit } from '@angular/core';
import { redirect, forceRedirect } from '@oam/core/lib/actions/router.actions';
import { RoutedMenuItem } from '@oam/cu-ui';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { select, Store } from '@ngrx/store';
import { ModelState, QuickMenuAlarms } from '@oam/navigation/lib/navigation/core/models/quick-menu-alarms.interface';
import { Observable } from 'rxjs';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'webem-quick-menu',
    templateUrl: './quick-menu.component.html',
    styleUrls: ['./quick-menu.component.scss']
})
export class QuickMenuComponent implements OnInit {
    @Input() items: RoutedMenuItem[];
    @Input() model: QuickMenuAlarms;
    modelState$: Observable<ModelState>;

    constructor(private readonly store: Store, private readonly activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.modelState$ = this.store.pipe(
            select(this.model.selectModelState)
        );
    }

    menuAction(route: RouterLink) {
        this.store.dispatch(forceRedirect({ routerLink: route }));
    }

    navigateToAlarms() {
        this.store.dispatch(redirect({routerLink: this.model.activeAlarmsLink, activatedRoute: this.activatedRoute}));
    }
}
