import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { WebemRoutesPaths } from '@oam/cu-shared';
import { AuthGuardService } from '@oam/core/lib/services/auth-guard-service.service';
import { SingleColumnLayoutComponent } from '@oam/layouts/lib/components/single-column-layout/single-column-layout.component';
import { SingleColumnLayoutWithActionsBarComponent } from '@oam/layouts/lib/components/single-column-layout-with-actions-bar/single-column-layout-with-actions-bar.component';
import { LayoutsConfigModule } from '@oam/layouts/lib/layouts-config.module';
import { LayoutsConfig } from '@oam/layouts/lib/layouts-config.service';
import { quickMenuItems } from '../models/quick-menu-items.model';
import { statusBarModel } from '..//models/status-bar-info.model';
import { quickMenuAlarmsModel } from '../models/quick-menu-alarms.model';
import { navigations } from '../models/navigations.model';
import { LayoutsModule } from '@oam/layouts/lib/layouts.module';
import { SplitViewLayoutComponent } from '@oam/split-view/lib/components/split-view-layout/split-view-layout.component';
import { TracingConfigModule } from '@oam/tracing/lib/tracing-config.module';
import { CU_TARGET_COMPONENTS } from '@oam/webem/app/core/constants/cu-target-components';
import { PageNotFoundComponent, PageNotFoundModule } from '@oam/cu-ui';
import { LoginPageComponent } from '@oam/auth/lib/auth/components/login/login-page.component';
import { SsoLoginPageComponent } from '@oam/auth/lib/auth/components/sso-login/sso-login-page.component';
import { AuthModule } from '@oam/auth';
import { AccountConfigModule } from '@oam/account-shared/lib/accounts-management/modules/account-config.module';
import { CellsInfoConfigModule } from '@oam/pm-counters-modal/lib/cells-info-config.module';
import { cuCellsInfoConfig } from '../config/pm-modal/cells-info-endpoint';
import { DiagnosticContentMenuConfigModule } from '@oam/diagnostic-shared/lib/components/diagnostic-content-menu/diagnostic-content-menu-config.module';
import { ErrorsTabConfigModule } from '@oam/errors/lib/core/services/errors-tab-config.module';
import { CU_ERRORS_TAB_MENU_ITEMS } from '@oam/webem/app/core/constants/cu-errors-tabs-routs.constants';
import { FeatureFlagGuard } from '../../../../../../libs/ng-feature-flag/src/lib/guards/feature-flag.guard';
import { RoleAccessGuard } from '@oam/role-access-ng/lib/guards/role-access.guard';
import { CustomPreloadingStrategyService } from '@oam/core/lib/services/custom-preloading-strategy.service';
import { XnLinksStatusModule } from '@oam/xn-links-status';


const mainPage = WebemRoutesPaths.dashboard;

const CAN_ACTIVATE_GUARD = {
    canActivate: [AuthGuardService, FeatureFlagGuard, RoleAccessGuard]
};

const COMMON_GUARD_SERVICE = {
    ...CAN_ACTIVATE_GUARD,
    canLoad: [AuthGuardService, FeatureFlagGuard, RoleAccessGuard]
};

export const routes: Routes = [
    {
        path: WebemRoutesPaths.dashboard,
        component: SingleColumnLayoutComponent,
        data: { preload: true },
        ...CAN_ACTIVATE_GUARD,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CuDashboardModule
        )
    },
    {
        path: WebemRoutesPaths.cuStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CuStatusModule
        )
    },
    {
        path: WebemRoutesPaths.dusStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DusStatusModule
        )
    },
    {
        path: WebemRoutesPaths.vnfcStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.VmStatusModule
        )
    },
    {
        path: WebemRoutesPaths.x2Status,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.X2StatusModule
        )
    },
    {
        path: WebemRoutesPaths.xnLinksStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.XnLinksStatusModule
        )
    },
    {
        path: WebemRoutesPaths.networkSlicing,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.NetworkSlicingModule
        )
    },
    {
        path: WebemRoutesPaths.ngLinkStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.NgLinkModule
        )
    },
    {
        path: WebemRoutesPaths.cellsStatus,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CellsStatusModule
        )
    },
    {
        path: WebemRoutesPaths.cellsMapping,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CellsMappingModule
        )
    },
    {
        path: WebemRoutesPaths.parameterEditor,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.ParameterEditorModule
        )
    },
    {
        path: WebemRoutesPaths.ipsecPskConfiguration,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.IpsecPskConfigurationModule
        )
    },
    {
        path: WebemRoutesPaths.parameterSearch,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.ConfigurationParameterSearchModule
        )
    },
    {
        path: WebemRoutesPaths.errors,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.ConfigurationErrorsModule
        )
    },
    {
        path: WebemRoutesPaths.splitView,
        component: SplitViewLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.LazySplitViewLibModule
        )
    },
    {
        path: WebemRoutesPaths.compareObjects,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.ConfigurationCompareObjectsModule
        )
    },
    {
        path: WebemRoutesPaths.alarmActive,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.AlarmActiveModule
        )
    },
    {
        path: WebemRoutesPaths.alarmHistorical,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.AlarmHistoryModule
        )
    },
    {
        path: WebemRoutesPaths.faultToggling,
        component: SingleColumnLayoutComponent,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.FaultTogglingModule
        ),
        ...COMMON_GUARD_SERVICE
    },
    {
        path: WebemRoutesPaths.performance,
        component: SingleColumnLayoutWithActionsBarComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.PmV2Module
        )
    },
    {
        path: WebemRoutesPaths.certificateOperator,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CertificateOperatorModule
        )
    },
    {
        path: WebemRoutesPaths.certificateAutomatic,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CertificateAutomaticModule
        )
    },
    {
        path: WebemRoutesPaths.certificateCrl,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.CertificateCrlModule
        )
    },
    {
        path: WebemRoutesPaths.uamLocalUserAccount,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.OperatorAccountManagementModule
        )
    },
    {
        path: WebemRoutesPaths.uamServiceAccountManagement,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.ServiceAccountModule
        )
    },
    {
        path: WebemRoutesPaths.uamSessions,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.SessionsModule
        )
    },
    {
        path: WebemRoutesPaths.diagnosticSnapshot,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DiagnosticSnapshotModule
        )
    },
    {
        path: WebemRoutesPaths.diagnosticUplane,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.TracingModule
        )
    },
    {
        path: WebemRoutesPaths.diagnosticCplane,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DiagnosticCplaneModule
        )
    },
    {
        path: WebemRoutesPaths.ipConnectivity,
        component: SingleColumnLayoutComponent,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DiagnosticIpConnectivityModule
        ),
        ...COMMON_GUARD_SERVICE
    },
    {
        path: WebemRoutesPaths.pmtuDiscovery,
        component: SingleColumnLayoutComponent,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DiagnosticPmtuDiscoveryModule
        ),
        ...COMMON_GUARD_SERVICE
    },
    {
        path: WebemRoutesPaths.ipsecAssociations,
        component: SingleColumnLayoutComponent,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.DiagnosticIpSecurityAssociationModule
        ),
        ...COMMON_GUARD_SERVICE
    },
    {
        path: WebemRoutesPaths.uiSettings,
        component: SingleColumnLayoutComponent,
        ...COMMON_GUARD_SERVICE,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.UiSettingsModule
        )
    },
    {
        path: WebemRoutesPaths.login,
        component: LoginPageComponent,
        data: { title: 'CU WebEM' }
    },
    {
        path: WebemRoutesPaths.ssologin,
        component: SsoLoginPageComponent
    },
    {
        path: WebemRoutesPaths.passwordExpired,
        loadChildren: () => import('./lazy-loaded-bundle').then(
            m => m.PasswordExpiredModule
        )
    },
    {
        path: WebemRoutesPaths.pageNotFound,
        component: PageNotFoundComponent
    },
    {
        path: '',
        redirectTo: mainPage,
        pathMatch: 'full'
    },
    {
        path: '**',
        redirectTo: WebemRoutesPaths.pageNotFound
    }
];

const layoutConfig: LayoutsConfig = {
    statusBarModel,
    quickMenuAlarmsModel,
    quickMenuItems,
    navigations
};

@NgModule({
    imports: [
        AuthModule,
        CommonModule,
        RouterModule.forRoot(routes, {
            initialNavigation: 'enabledNonBlocking',
            relativeLinkResolution: 'legacy',
            preloadingStrategy: CustomPreloadingStrategyService
        }),
        DiagnosticContentMenuConfigModule.forRoot({ splitTracingTabs : true }),
        AccountConfigModule.forRoot({ isConfigCu: true }),
        ErrorsTabConfigModule.forRoot({ routes: CU_ERRORS_TAB_MENU_ITEMS }),
        CellsInfoConfigModule.forRoot(cuCellsInfoConfig),
        TracingConfigModule.forRoot({ isConfigVdu: false, targetComponents: CU_TARGET_COMPONENTS, withBitmaskFilters: false }),
        PageNotFoundModule,
        LayoutsModule,
        LayoutsConfigModule.forRoot(layoutConfig)
    ],
    exports: [RouterModule],
    providers: [],
    declarations: []
})
export class WebemRouterModule {
}
