import { Action, createReducer, on } from '@ngrx/store';
import { loadingLegalNoticeModalMessage } from '../legal-notice.constants';
import {
    setLegalNoticeMessage,
    showLegalNoticeModal,
    submitLegalNoticeModal,
    denyLegalNoticeModal
} from '../actions/legal-notice-modal.actions';

export const legalNoticeModalFeatureKey = 'legalNoticeModal';

export interface LegalNoticeModalState {
    isVisible: boolean;
    securityNotice: string;
    confirmButtonVisible?: boolean;
    okButtonVisible?: boolean;
}

export const initialState: LegalNoticeModalState = {
    isVisible: false,
    securityNotice: loadingLegalNoticeModalMessage,
    confirmButtonVisible: false,
    okButtonVisible: false
};

const legalNoticeModalReducer = createReducer(
    initialState,
    on(setLegalNoticeMessage, (state, { securityNotice, confirmButtonVisible, okButtonVisible }) => ({
        ...state,
        securityNotice: securityNotice.replace(/\\n/g, '\n'),
        confirmButtonVisible,
        okButtonVisible
    })),
    on(showLegalNoticeModal, (state) => ({
        ...state,
        isVisible: true
    })),
    on(submitLegalNoticeModal, denyLegalNoticeModal, (state) => ({
        ...state,
        isVisible: false
    }))
);

export function reducer(state: LegalNoticeModalState, action: Action) {
    return legalNoticeModalReducer(state, action);
}
