import { InjectionToken } from '@angular/core';
import { SplitViewRestrictionsService } from './split-view-restrictions.service';
import { SplitViewConfig } from '@oam/split-view/lib/core/models';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';

export const SPLIT_VIEW_CONFIG = new InjectionToken<SplitViewConfig>(
    'SplitViewConfig'
);


export function provideSplitViewConfigService(splitViewConfig: any, navigationService: NavigationService) {

    return new SplitViewRestrictionsService(splitViewConfig, navigationService);
}
