import { Pipe, PipeTransform } from '@angular/core';
import { Section } from '@oam/cu-ui';
import { CloudInfrastructureElementState, IpInterface } from '@oam/cu-asm-api/models';
import { uniqBy } from 'lodash';

@Pipe({
    name: 'toIpInterfacesDetailsProperties',
    pure: true
})
export class ToIpInterfacesDetailsPropertiesPipe implements PipeTransform {
    transform(cloudElement: CloudInfrastructureElementState | undefined): Section {
        if (!cloudElement) return [];

        return uniqBy(cloudElement.ipInterfaces, 'interfaceName').map((ipInterface: IpInterface)  => {
            return {
                key: ipInterface.interfaceName,
                value: ipInterface.ipAddress
            };
        });
    }
}
