import { Injectable } from '@angular/core';

export class AccountConfig {
    isConfigCu: boolean;
}

export enum NetElementType {
    CU = 'CU',
    VDU = 'vDU'
}

export enum ACTIVATION_FEEDBACK {
    ACTIVATION_SUCCEDED = 'Plan activation has finished successfully',
    ACTIVATION_WITH_RESET = ' reset will be performed after plan activation',
    CELLS_LOCK = 'The following will be locked\nCells:'
}

@Injectable({
    providedIn: 'root'
})
export class AccountConfigService {
    private readonly isCu: boolean;
    private readonly netElementType: NetElementType;

    constructor(config: AccountConfig) {
        if (config) {
            this.isCu = config.isConfigCu;
            this.netElementType = this.isCu ? NetElementType.CU : NetElementType.VDU;
        }
    }

    get isConfigCu(): boolean {
        return this.isCu;
    }

    getNetElementType(): NetElementType {
        return this.netElementType;
    }
}
