import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FeatureFlagDirective } from './directives/feature-flag.directive';
import { FeatureFlag, FeatureFlagProperty, FlagsType } from '@oam/feature-flag';
import { Route } from '@angular/router';

export type FeatureFlagGuardRule = FeatureFlagProperty & Pick<Route, 'path'>;

export class NgFeatureFlagModuleConfig {
    featureFlagGuardRules: Required<FeatureFlagGuardRule>[];
    featureFlagConf: FlagsType;

    constructor(featureFlagGuardRules: Required<FeatureFlagGuardRule>[], featureFlagConf: FlagsType) {
        this.featureFlagGuardRules = featureFlagGuardRules;
        this.featureFlagConf = featureFlagConf;
    }
}

@NgModule({
    imports: [CommonModule],
    declarations: [FeatureFlagDirective],
    exports: [FeatureFlagDirective]
})
export class NgFeatureFlagModule {
    static forRoot(moduleConfig: NgFeatureFlagModuleConfig): ModuleWithProviders<NgFeatureFlagModule> {
        const featureFlagInst = new FeatureFlag(moduleConfig.featureFlagConf);

        return {
            ngModule: NgFeatureFlagModule,
            providers: [
                {
                    provide: NgFeatureFlagModuleConfig,
                    useValue: moduleConfig
                },
                {
                    provide: FeatureFlag,
                    useValue: featureFlagInst
                }
            ]
        };
    }

}
