import { Injectable } from '@angular/core';
import { DetailsPanelConfig } from '@oam/details-panel/lib/core/model/details-panel-config';

@Injectable({
    providedIn: 'root'
})
export class DetailsPanelConfigService {
    config: DetailsPanelConfig;

    constructor() {
    }

    getUrlsWithDetailsPanel() {
        return this.config.urlsWithDetailPanel;
    }


}
