import { Pipe, PipeTransform } from '@angular/core';
import {
    MAP_TO_UI_COLLECTING_STATUS_DISPLAY,
    MAP_TO_UI_TRANSFER_STATUS_DISPLAY
} from '@oam/diagnostic-snapshot/core/models';

const MAP_TO_UI_MSG = {
    ...MAP_TO_UI_COLLECTING_STATUS_DISPLAY,
    ...MAP_TO_UI_TRANSFER_STATUS_DISPLAY
};

@Pipe({
    name: 'translateOperationStatus'
})
export class TranslateOperationStatusPipe implements PipeTransform {

    transform(status: string): string {
        return MAP_TO_UI_MSG[status];
    }
}
