import { validateSync } from 'class-validator';
import { cleanUiSettingsCache } from '@oam/core/lib/actions/app-settings.actions';
import { Store } from '@ngrx/store';
import { LayoutBase } from '@oam/core/lib/functions/layout-base';

const WNLS_DASHBOARD = 'wnls_dashboard';

export async function validateAndClearLayout(store: Store, LayoutClass: new (layoutItem: any) => LayoutBase) {
    const item = localStorage.getItem(WNLS_DASHBOARD);
    if (!item) return;
    const dashboardLayout = JSON.parse(item);

    const errorItem = dashboardLayout.find((layoutItem: any) => {
        const layoutInst = new LayoutClass(layoutItem);
        const errors = validateSync(layoutInst, { whitelist: true, forbidNonWhitelisted: true });

        return errors.length > 0;
    });

    if (errorItem) {
        localStorage.removeItem(WNLS_DASHBOARD);
        store.dispatch(cleanUiSettingsCache({ payload: true }));
    }
}
