import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { HelpService } from '@oam/core/lib/services/help.service';
import { catchError, map, mergeMap, tap } from 'rxjs/operators';
import { NGXLogger } from 'ngx-logger';
import { DownloadHelp, DownloadHelpError, DownloadHelpSuccess, HelpSaveError, HelpActionTypes } from '@oam/core/lib/actions/help.actions';
import { defer, Observable, of } from 'rxjs';
import { FileSaverService } from 'ngx-filesaver';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';

@Injectable()
export class HelpEffects {

    constructor(
        private readonly actions$: Actions,
        private readonly helpService: HelpService,
        private readonly logger: NGXLogger,
        private readonly fileSaver: FileSaverService
    ) {}

    getHelp$ = createEffect(() =>
        this.actions$.pipe(
            ofType(HelpActionTypes.GetHelp),
            tap(() => this.logger.trace('[HelpEffect] Action getHelp recognized')),
            map(({ payload: { filePath, fileName } }) => new DownloadHelp({ filePath, fileName })),
        ),
    );

    downloadHelp$ = createEffect(() =>
        this.actions$.pipe(
            ofType(HelpActionTypes.DownloadHelp),
            mergeMap(({ payload: { filePath, fileName }}) => this.helpService.downloadHelp(filePath).pipe(
                map((blob: Blob) => new DownloadHelpSuccess({ blob, fileName })),
                catchError((error: any) => of(new DownloadHelpError({ error })))
            ))
        )
    );

    downloadHelpSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(HelpActionTypes.DownloadHelpSuccess),
            tap(() => this.logger.trace('[HelpEffect] Download help successful. Saving file...')),
            mergeMap(({ payload: { blob, fileName }}: DownloadHelpSuccess) =>
                defer<void>(() => this.saveFile$(blob, fileName)).pipe(
                    tap(() => this.logger.trace('[HelpEffect] Save help successful')),
                    catchError((error: Error) => of(new HelpSaveError({ error })))
                )
            )
        ),
    { dispatch: false }
    );

    saveHelpErrors$ = createEffect(() =>
        this.actions$.pipe(
            ofType(HelpActionTypes.HelpSaveError),
            tap(({ payload }: HelpSaveError) =>
                this.logger.trace(`[HelpEffect] Help saving error: ${payload.error.message}`)),
            map(({ payload }: HelpSaveError) =>
                addNotification({payload: {
                    detail: `Error in saving help file: ${payload.error.message}`,
                    severity: 'error',
                    summary: 'Help file save error'
                }})
            )
        )
    );

    downloadHelpErrors$ = createEffect(() =>
        this.actions$.pipe(
            ofType(HelpActionTypes.DownloadHelpError),
            tap(({ payload }: DownloadHelpError) =>
                this.logger.trace(`[HelpEffect] Help downloading error: ${payload.error.message}`)),
            map(({ payload }: DownloadHelpError) =>
                addNotification({payload: {
                    detail: `Error in downloading help file: ${payload.error.message}`,
                    severity: 'error',
                    summary: 'Help file download error'
                }})
            )
        )
    );

    saveFile$(blob: Blob, fileName: string): Observable<void> {
        this.fileSaver.save(blob, fileName);

        return new Observable<void>(
            subscriber => subscriber.next()
        );
    }
}
