import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges
} from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { openDialog } from '../../../webem-overlay/core/actions/overlay.actions';
import { RenewSessionDialogComponent } from '../renew-session-dialog/renew-session-dialog.component';
import { Store } from '@ngrx/store';
import { ROLE_NAME_MAPPING } from '@oam/shared/lib/constants/role-access-constants';
import { Userrole } from '@oam/cu-asm-api/models';
import { UserInformationService } from '@oam/core/lib/services/user-information.service';

@Component({
    selector: 'ui-webem-user-menu',
    templateUrl: './user-menu.component.html',
    styleUrls: ['./user-menu.component.scss']
})

export class UserMenuComponent implements OnInit, OnChanges {
    @Input() userName: string;
    @Input() lastLoginTime: string;
    @Input() passwordExpirationDate: string;
    @Input() invalidLoginAttempts: string;
    @Input() tokenExpirationTime: string;

    @Output() userAccountManagementClick = new EventEmitter<void>();
    @Output() settingsClick = new EventEmitter<void>();
    @Output() helpClick = new EventEmitter<void>();
    @Output() logOutClick = new EventEmitter<void>();

    model: ButtonModel;
    refreshButtonModel: ButtonModel;
    currentRole: string;
    readOnlyMode: boolean | undefined;

    constructor(private readonly store: Store,
                private readonly userInformationService: UserInformationService) {
    }

    ngOnInit() {
        this.createButtonModel();
        this.setRoleAndReadOnlyMode();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.userName) {
            this.createButtonModel();
        }
    }

    onUserAccountManagementClick() {
        this.userAccountManagementClick.emit();
    }

    onSettingsClick() {
        this.settingsClick.emit();
    }

    onHelpClick() {
        this.helpClick.emit();
    }

    onLogOutClick() {
        this.logOutClick.emit();
    }

    openRenewPasswordDialog() {
        this.store.dispatch(openDialog({
            componentType: RenewSessionDialogComponent,
            config: {
                header: 'Session token renewal',
                closable: false,
            }
        }));
    }

    private createButtonModel() {
        this.model = {
            label: this.userName,
            icon: 'mdi mdi-account-outline mdi-21px-bold',
            theme: ButtonTheme.Secondary,
            length: ButtonLength.ShortBigIcon
        };

        this.refreshButtonModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-autorenew',
            title: 'Renew session',
            length: ButtonLength.Long
        };
    }

    private setRoleAndReadOnlyMode(): void {
        const { role, readOnlyMode } = this.userInformationService.getRoleAndReadOnlyMode();
        this.currentRole = ROLE_NAME_MAPPING[role];
        this.readOnlyMode = role === Userrole.ReadOnlyUserAccessMode ? false : readOnlyMode;
    };
}
