import { Component, OnDestroy, OnInit } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { Subject } from 'rxjs';
import { filter, map, pairwise, startWith, takeUntil } from 'rxjs/operators';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { SPLIT_VIEW_LOCATION, RoutesPathsWithParams } from '../../core/models';
import { selectSplitViewsDetails } from '../../core/selectors/split-view.selectors';
import {
    changeSelectionInBottomView,
    changeSelectionInTopView,
    changeSelectionRestriction,
    removeSelectedViews,
    setAvailableSplitViewOptions,
    setLastSelectedViews,
    setViewsFromUrlPath,
    updateViewsWithoutRedirect
} from '../../core/actions/split-view.actions';
import { SplitViewRestrictionsService } from '@oam/split-view/lib/core/services/split-view-restrictions.service';
import { GroupOfViews } from '@oam/split-view/lib/core/models/group-of-views.interface';
import { SingleView } from '@oam/split-view/lib/core/models/single-view.interface';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { statusGroup, alarmGroup } from '@oam/navigation/lib/navigation/core/models/navigation-group.model';
import { PageName } from '@oam/navigation/lib/navigation/core/models/page-name';

@Component({
    selector: 'webem-split-view-dropdowns',
    templateUrl: './split-view-dropdowns.component.html',
    styleUrls: ['./split-view-dropdowns.component.scss']
})
export class SplitViewDropdownsComponent implements OnInit, OnDestroy {
    views: {
        topViews: GroupOfViews[],
        bottomViews: GroupOfViews[]
    };
    selectedTopView?: string | RoutesPathsWithParams;
    selectedBottomView?: string | RoutesPathsWithParams;
    restrictionsForGroupViews?: boolean;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    private readonly splitViewPath: string = '/split-view';

    readonly scrollHeight: string = '320px';
    readonly topViewPlaceholder = '<Select first view>';
    readonly bottomViewPlaceholder = '<Select second view>';

    constructor(private readonly store: Store, private readonly route: ActivatedRoute,
                private readonly vnfcSharedService: VnfcSharedService,
                private readonly router: Router, private readonly splitViewRestrictionsService: SplitViewRestrictionsService) {
    }

    ngOnInit() {
        this.router.events.pipe(
            filter(event => event instanceof NavigationEnd),
            map((navEnd: NavigationEnd) => navEnd.urlAfterRedirects),
            startWith(''),
            pairwise(),
            takeUntil(this.ngUnsubscribe))
            .subscribe(urls => {
                if (this.isNavigateToSplitViewAgain(urls)) {
                    this.store.dispatch(removeSelectedViews());
                    const availableViews = this.splitViewRestrictionsService.getAvailableViews() as GroupOfViews[];
                    this.store.dispatch(setAvailableSplitViewOptions({
                        availableViews: availableViews
                    }));
                } else {
                    this.changeSelectionRestrictionInDropdownList(SPLIT_VIEW_LOCATION.TOP);
                    this.changeSelectionRestrictionInDropdownList(SPLIT_VIEW_LOCATION.BOTTOM);
                    this.store.dispatch(updateViewsWithoutRedirect());
                }
            });

        if (this.router.url === this.splitViewPath) {
            this.store.dispatch(setLastSelectedViews());
        } else {
            this.store.dispatch(setViewsFromUrlPath());
        }

        this.store.pipe(
            select(selectSplitViewsDetails),
            takeUntil(this.ngUnsubscribe)
        ).subscribe(state => {
            const views = this.flatteningValueInAvailableViews(state.availableViews);
            this.views = {
                topViews: views.filter(view => view.label === PageName.dashboard || view.label === statusGroup.label),
                bottomViews: views.filter(view => view.label === alarmGroup.label)
            };
            this.selectedBottomView = this.selectValidView(state.bottomView as string, state.availableViews);
            this.selectedTopView = this.selectValidView(state.topView as string, state.availableViews);
            this.restrictionsForGroupViews = state.restrictionsForGroupViews;
        });
    }

    private selectValidView(view: string, availableViews: GroupOfViews[]): string | RoutesPathsWithParams | undefined {

        const itemView = availableViews
            .map((availableView) => availableView.items)
            .flat()
            .find((item: SingleView) => {
                if (item.value.hasOwnProperty('url') && item.value.hasOwnProperty('param')) {
                    return view && view.includes((item.value as RoutesPathsWithParams).url);
                }

                return view && view.includes(item.value as string);
            });

        return itemView && itemView.value.hasOwnProperty('url') && itemView.value.hasOwnProperty('param') ?
            `${(itemView.value as RoutesPathsWithParams).url}/${(itemView.value as RoutesPathsWithParams).param}` :
            itemView && itemView.value;
    }

    onTopViewChange() {
        this.store.dispatch(changeSelectionInTopView({
            location: SPLIT_VIEW_LOCATION.TOP, selectedView: this.selectedTopView
        }));
    }

    onBottomViewChange() {
        this.store.dispatch(changeSelectionInBottomView({
            location: SPLIT_VIEW_LOCATION.BOTTOM, selectedView: this.selectedBottomView
        }));
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    changeSelectionRestrictionInDropdownList(viewLocation: string) {
        const selectedView: string | RoutesPathsWithParams | undefined =
            viewLocation === SPLIT_VIEW_LOCATION.TOP ? this.selectedBottomView : this.selectedTopView;
        if (selectedView && this.splitViewRestrictionsService.isUrlInRestrictionGroup(selectedView as string) &&
            !this.restrictionsForGroupViews) {
                this.store.dispatch(changeSelectionRestriction({ selectionRestriction: true }));
        } else if (
            this.restrictionsForGroupViews && (!selectedView ||
            !this.splitViewRestrictionsService.isUrlInRestrictionGroup(selectedView as string))) {
                this.store.dispatch(changeSelectionRestriction({ selectionRestriction: false }));
        }
    }

    private isNavigateToSplitViewAgain(urls: string[]): boolean {
        const previousUrl = urls[0];
        const actualUrl = urls[1];

        return actualUrl === this.splitViewPath && previousUrl.includes(actualUrl);
    }

    flatteningValueInAvailableViews(availableViews: GroupOfViews[]): GroupOfViews[] {
        return availableViews.map((view: GroupOfViews) => {
            const items = view.items.map(item => {
                return {
                    ...item,
                    value: item.value.hasOwnProperty('url') && item.value.hasOwnProperty('param') ?
                        `${(item.value as RoutesPathsWithParams).url}/${(item.value as RoutesPathsWithParams).param}` : item.value
                } as SingleView;
            });

            return {
                ...view,
                items
            };
        });
    }

}
