import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { NavigationSearchKeyboardService } from '../../shared/navigation-search-keyboard/navigation-search-keyboard.service';


@Component({
    selector: 'webem-navigation-search-input',
    templateUrl: './navigation-search-input.component.html',
    styleUrls: ['./navigation-search-input.component.scss']
})
export class NavigationSearchInputComponent implements OnInit, OnDestroy {
    @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
    @Input() value = '';
    @ViewChild('input') searchInputField: any;
    changeValueSubject: Subject<void> = new Subject<void>();

    constructor(private readonly navSearchKeyboardService: NavigationSearchKeyboardService) {
    }

    ngOnInit(): void {
        this.changeValueSubject
            .pipe(debounceTime(200))
            .subscribe(() => this.valueChange.emit(this.value));
    }

    ngAfterContentChecked(): void {
        if (this.searchInputField) this.searchInputField.nativeElement.focus();
    }

    onValueChange(event: KeyboardEvent): void {
        this.navSearchKeyboardService.handleKeyUp(event);
        this.changeValueSubject.next();
    }

    resetValue(): void {
        this.valueChange.emit('');
    }

    ngOnDestroy(): void {
        this.changeValueSubject.complete();
    }
}
