import { Directive, Input, TemplateRef } from '@angular/core';

export interface PanelToggleConfiguration {
    position: 'start' | 'center' | 'end';
    align: 'start' | 'center' | 'end';
    order: number;
}

const DEFAULT_CONFIGURATION: PanelToggleConfiguration = {
    position: 'start',
    align: 'center',
    order: 1,
};

@Directive({
    selector: '[uiWebemPanelToggle]'
})
export class PanelToggleDirective {
    configuration: PanelToggleConfiguration = DEFAULT_CONFIGURATION;
    @Input() set uiWebemPanelToggle(configuration: PanelToggleConfiguration) {
        this.configuration = configuration;
    }

    constructor(
        public template: TemplateRef<any>,
    ) {}
}
