import { Observable } from 'rxjs';

import { StatusService } from '@oam/cu-asm-api/services';
import { CuCellsDiff } from '@oam/cu-asm-api';

export const cuCellsInfoConfig = {
    getCellsDiff: getCuCellsDif
};

function getCuCellsDif(statusService: StatusService): Observable<CuCellsDiff> {
    return statusService.getCuCellsDiff();
}
