import { createFeatureSelector, createSelector } from '@ngrx/store';
import { collectingSnapshotFeatureKey } from '../reducers/collecting-snapshot.reducer';
import {
    COLLECT_SNAPSHOT_BTN_TITLE,
    FilesDataResponse,
    ICollectingSnapshotState, SERVER_CONNECTION_FAILURE
} from '../models';
import {
    selectSnapshotServerConnectionFailure,
    selectCollectingSnapshotAvailability
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot-server-status.selector';
import {
    CANCEL_IS_ONGOING,
    COLLECTING_SNAPSHOT_IS_ONGOING,
    SNAPSHOT_COLLECTING_STATUS, START_CANCELING_STATUS
} from '@oam/diagnostic-snapshot/core/models/snapshot-progress-status';
import { SNAPSHOT_OPERATION_IS_NOT_AVAILABILITY } from '@oam/diagnostic-snapshot/core/models/snapshot-operation-btn-title.constants';
import { selectTransferSnapshotIsOngoing } from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';
import { isEmpty } from 'lodash';


export const selectCollectingSnapshotState =
    createFeatureSelector<ICollectingSnapshotState>(collectingSnapshotFeatureKey);

export const selectCollectingSnapshotOperationId = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => collectingSnapshotState.operationId
);

export const selectCollectingSnapshotStartedAt = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => collectingSnapshotState.startedAt
);

export const selectCollectingSnapshotDuration = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => (collectingSnapshotState.finishedAt as number) - (collectingSnapshotState.startedAt as number)
);
export const selectstartHeartBeatStatus = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => collectingSnapshotState.startHeartBeatStatus
);
export const selectCollectingSnapshotSavingIsOngoing = createSelector(
    selectCollectingSnapshotState,
    ({collectStatus}) => collectStatus === SNAPSHOT_COLLECTING_STATUS.PREPARING_FILES_ONGOING
);

export const selectCollectingSnapshotIsCanceled = createSelector(
    selectCollectingSnapshotState,
    ({ collectStatus }) => collectStatus === SNAPSHOT_COLLECTING_STATUS.CANCELING_SUCCEEDED
);

export const selectCollectingSnapshotErrorMessage = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => collectingSnapshotState.errorMessage
);

export const selectCollectingSnapshotIsFinished = createSelector(
    selectCollectingSnapshotState,
    ({ collectStatus }) => collectStatus === SNAPSHOT_COLLECTING_STATUS.COLLECTING_SNAPSHOT_COMPLETE
);

export const selectCollectingSnapshotFiles = createSelector(
    selectCollectingSnapshotState,
    collectingSnapshotState => collectingSnapshotState.files
);

export const selectCollectingSnapshotFilesChanged = createSelector(
    selectCollectingSnapshotFiles,
    collectingSnapshotFiles => {
        if (!collectingSnapshotFiles || collectingSnapshotFiles.length === 0) return [];

        return collectingSnapshotFiles.map((file: FilesDataResponse) => {
            return {
                fileSize: file.sizeInByte < 1024 ? 1 : Math.round((file.sizeInByte / 1024)),
                fileId: file.fileId
            };
        });
    }
);

export const selectCollectingSnapshotFileStates = createSelector(
    selectCollectingSnapshotFiles,
    collectingSnapshotFiles => {
        return collectingSnapshotFiles.filter((file: FilesDataResponse) => {
            return file.fileStates === 'initial';
        });
    }
);

export const downloadFilesCollectingSuccessSelector = createSelector(
    selectCollectingSnapshotFiles,
    collectingSnapshotFiles => {

        if (isEmpty(collectingSnapshotFiles)) return false;

        return collectingSnapshotFiles.every((file: FilesDataResponse) => {
            return file.fileStates === 'completed';
        });
    }
);

export const selectCollectingSnapshotIsCancelling = createSelector(
    selectCollectingSnapshotState,
    ({ startCancelingStatus, collectStatus }) =>
        (!!startCancelingStatus && CANCEL_IS_ONGOING.includes(startCancelingStatus)) ||
        (!!collectStatus && CANCEL_IS_ONGOING.includes(collectStatus))
);

export const selectCollectingSnapshotIsOngoing = createSelector(
    selectCollectingSnapshotState,
    ({ collectStatus }) => !!collectStatus && COLLECTING_SNAPSHOT_IS_ONGOING.includes(collectStatus)
);

export const selectCollectingSnapshotIsStarting = createSelector(
    selectCollectingSnapshotState,
    ({ collectStatus }) => collectStatus === SNAPSHOT_COLLECTING_STATUS.COLLECTING_FILES_START
);

export const selectCollectingSnapshotIsOngoingOrCancelling = createSelector(
    selectCollectingSnapshotIsOngoing,
    selectCollectingSnapshotIsCancelling,
    (collectingSnapshotIsOngoing, cancelCollectSnapshotIsOngoing) => collectingSnapshotIsOngoing || cancelCollectSnapshotIsOngoing
);

export const selectCollectingStatus = createSelector(
    selectCollectingSnapshotState,
    ({collectStatus}) => collectStatus
);

export const selectCollectingSnapshotIsFailed = createSelector(
    selectCollectingSnapshotState,
    ({ collectStatus }) => collectStatus === SNAPSHOT_COLLECTING_STATUS.COLLECTING_SNAPSHOT_FAILED
);

export const selectSnapshotCancelingIsFailed = createSelector(
    selectCollectingSnapshotState,
    ({ startCancelingStatus }) => startCancelingStatus === START_CANCELING_STATUS.CANCELING_FAILED
);

export const selectCollectSnapshotBtnTitle = createSelector(
    selectCollectingSnapshotIsOngoing,
    selectCollectingSnapshotIsCancelling,
    selectCollectingSnapshotAvailability,
    selectSnapshotServerConnectionFailure,
    selectTransferSnapshotIsOngoing,
    (
        collectingSnapshotIsOngoing,
        collectingSnapshotIsCancelling,
        isSnapshotOperationAvailability,
        isServerConnectionFailure,
        isTransferSnapshotOngoing
    ) => {
        if (collectingSnapshotIsCancelling) {
            return COLLECT_SNAPSHOT_BTN_TITLE.CANCELLING;
        } else if (collectingSnapshotIsOngoing) {
            return COLLECT_SNAPSHOT_BTN_TITLE.ONGOING;
        } else if (isServerConnectionFailure) {
            return SERVER_CONNECTION_FAILURE;
        } else if (!isSnapshotOperationAvailability || isTransferSnapshotOngoing) {
            return SNAPSHOT_OPERATION_IS_NOT_AVAILABILITY;
        } else {
            return COLLECT_SNAPSHOT_BTN_TITLE.DEFAULT;
        }
    }
);

export const selectSnapshotOperationIsOngoing = createSelector(
    selectCollectingSnapshotIsStarting,
    selectCollectingSnapshotIsOngoingOrCancelling,
    (isCollectingSnapshotStarting, isCollectingSnapshotIsOngoingOrCancelling) => isCollectingSnapshotStarting || isCollectingSnapshotIsOngoingOrCancelling
);

export const selectSnapshotOperationStatus = createSelector(
    selectCollectingSnapshotIsStarting,
    selectCollectingSnapshotSavingIsOngoing,
    selectCollectingSnapshotIsOngoing,
    selectCollectingSnapshotIsFinished,
    selectCollectingSnapshotIsFailed,
    selectCollectingSnapshotIsCancelling,
    selectCollectingSnapshotIsCanceled,
    selectSnapshotOperationIsOngoing,
    (isCollectingSnapshotStarting, isCollectingSnapshotSavingOngoing, isCollectingSnapshotOngoing,
     isCollectingSnapshotFinished, isCollectingSnapshotFailed,
     isCancellingCollection, isCollectionCancelled, isSnapshotOperationOngoing) => {
        return {
            isCollectingSnapshotStarting,
            isCollectingSnapshotSavingOngoing,
            isCollectingSnapshotOngoing,
            isCollectingSnapshotFinished,
            isCollectingSnapshotFailed,
            isCancellingCollection,
            isCollectionCancelled,
            isSnapshotOperationOngoing
        };
    }
);
