import { Pipe, PipeTransform } from '@angular/core';
import { RaisedAlarm } from '@oam/cu-ui/lib/raised-alarms/model/raised-alarm';
import { Alarm } from '@oam/cu-asm-api/models/alarm';
import { toRaisedAlarm } from '@oam/core/lib/functions';

@Pipe({
    name: 'toActiveAlarms',
    pure: true
})
export class ToActiveAlarmsPipe implements PipeTransform {
    transform(alarms: Alarm[] | null): RaisedAlarm[] {
        if (!alarms) return [];

        return alarms.map(toRaisedAlarm);
    }
}
