import { Action } from '@ngrx/store';
import { HttpErrorResponse } from '@angular/common/http';

export enum HelpActionTypes {
  GetHelp = '[User] [Help] Get help',
  DownloadHelp = '[Help] Download help',
  DownloadHelpError = '[Help] Download help error',
  DownloadHelpSuccess = '[Help] Download help success',
  HelpSaveError = '[Help] Save help error'
}

export class GetHelp implements Action {
    constructor(public payload: { filePath: string, fileName: string }) {}

    readonly type = HelpActionTypes.GetHelp;
}

export class DownloadHelp implements Action {
    constructor(public payload: { filePath: string, fileName: string }) {}

    readonly type = HelpActionTypes.DownloadHelp;
}

export class DownloadHelpError implements Action {
    constructor(public payload: { error: HttpErrorResponse }) {}

    readonly type = HelpActionTypes.DownloadHelpError;
}

export class DownloadHelpSuccess implements Action {
    constructor(public payload: { blob: Blob, fileName: string }) {}

    readonly type = HelpActionTypes.DownloadHelpSuccess;
}

export class HelpSaveError implements Action {
    constructor(public payload: { error: Error }) {}

    readonly type = HelpActionTypes.HelpSaveError;
}

