import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FileUploadModule } from 'primeng/fileupload';
import { MessagesModule } from 'primeng/messages';

import { AlarmMessageComponent } from './alarm-message.component';
import { NgFeatureFlagModule } from '@oam/ng-feature-flag';

@NgModule({
    declarations: [
        AlarmMessageComponent
    ],
    imports: [
        CommonModule,
        FileUploadModule,
        MessagesModule,
        NgFeatureFlagModule
    ],
    exports: [AlarmMessageComponent]
})
export class AlarmMessageModule { }
