  /**
   * Allows user to revoke/delete all logged-in session.
   *
   * User specific all sessions deletion : user password required <br> Authentication is actually performed by rcpum, for Authentication failure case, rcpum may send an additional status code in "additionalStatus" field of the error response structure, the original http status code from rcpum is stored in "status" filed of the error response structure. apigw will not change the error response structure.<br> As WebEM is not able to handle other new error case status codes than {401 403 405 460 461 462}, apigw will convert all other rcpum authentication failure status code to HTTP status code "401".<br> <br> The additional status codes are the following:
   *  - 7  Authentication failed
   *  - 13 User account is expired
   *  - 101 Authentication failed and the account was locked because of that
   *  - 102 Account is locked because of too many authentication failures
   *  - 103 User account is invalid due to expired password
   *  - 104 Too many simultaneous sessions in the system
   *  - 105 Too many simultaneous sessions for the user
   *  - 106 Account is locked by the administrator
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `revokeSessionsPost()` instead.
   *
   * This method doesn't expect any request body.
   */
  revokeSessionsPost$Response(params: {

    /**
     * Authorization containing the word Basic followed by a space and a base64-encoded string username:password
     */
    Authorization: string;

  }): Observable<StrictHttpResponse<void>> {

    const rb = new RequestBuilder(this.rootUrl, SessionMgmtService.RevokeSessionsPostPath, 'post');
    if (params) {

      rb.header('Authorization', params.Authorization, {});

    }
    return this.http.request(rb.build({
      responseType: 'text',
      accept: '*/*'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return (r as HttpResponse<any>).clone({ body: undefined }) as StrictHttpResponse<void>;
      })
    );
  }

