import { ContentChild, Directive, Input, OnInit, TemplateRef } from '@angular/core';
import { CurledUpAreaDirective } from '../curled-up-area/curled-up-area.directive';

export class SplitAreaDirectiveContext {
    minSize?: any;
    maxSize?: any;
    size: any;
    defaultSize?: any;
    id: number;
    hideGutter?: boolean;
}

@Directive({
    selector: '[uiWebemSplitArea]'
})
export class SplitAreaDirective implements OnInit {
    public context = new SplitAreaDirectiveContext();
    @ContentChild(CurledUpAreaDirective, { static: true }) curledArea: CurledUpAreaDirective;

    @Input()
    set uiWebemSplitArea(value: SplitAreaDirectiveContext | null) {
        Object.assign(this.context, value);
    }

    constructor(public template: TemplateRef<any>) {
    }

    ngOnInit() {
    }


}
