import { Pipe, PipeTransform } from '@angular/core';
import { Alarm } from '@oam/cu-asm-api/models';

interface AlarmsContainer {
    alarms?: Alarm[] | null
}

@Pipe({ name: 'hasAlarms', pure: true })
export class HasAlarmsPipe implements PipeTransform {
    transform({ alarms }: AlarmsContainer): boolean {
        if (!alarms) return false;

        return alarms.length > 0;
    }
}
