import { DateTime, DatetimeService } from '@oam/core';
import { select, Store } from '@ngrx/store';
import { selectUserTokenExpirationTime } from '@oam/core/lib/user/core/reducers/user-reducer';
import { Injectable } from '@angular/core';
import { differenceInSeconds } from 'date-fns';

const ONE_MINUTE = 60;

@Injectable({
    providedIn: 'root'
})
export class TokenExpirationTimeService {
    constructor(private readonly store: Store,
                private readonly datetimeService: DatetimeService) {
        this.calculateRemainingTokenExpirationTime();
    }

    private tokenExpirationTime: number;
    public _expirationTime?: string;

    calculateRemainingTokenExpirationTime(): void {
        this.store.pipe(select(selectUserTokenExpirationTime))
            .subscribe((expirationTime) => {
                this._expirationTime = expirationTime;
                this.calculateRemainingTime();
            });
    }

    isRemainingTimeLessThan40Minutes(): boolean {
        return this.tokenExpirationTime < 40;
    }

    calculateRemainingTime(): void {
        const currentTime = DateTime.currentDateTime;
        let expires = '';
        if(this._expirationTime?.includes('UTC')) {
            expires = this._expirationTime;
        } else {
            expires = this.formatLoginInfoDate(this._expirationTime);
        }
        this.tokenExpirationTime = Math.round(differenceInSeconds(expires, currentTime) / ONE_MINUTE);
    }

    getTokenExpirationTime(): number {
        return this.tokenExpirationTime;
    }

    private formatLoginInfoDate(lastLogin: string | undefined): string {
        if (!lastLogin) return '';

        lastLogin = lastLogin.replace(/\s[A-Z]+(?=\s[0-9]+$)/, '');

        return this.datetimeService.getFormattedDateTime(lastLogin, false);
    }

}
