import { Pipe, PipeTransform } from '@angular/core';
import { StatusIconType } from '@oam/shared/lib/core/models/status-icon-color.definition';
import { get } from 'lodash';

type CircleType = { [key: string]: string };

const mapping: CircleType = {
    [StatusIconType.Light1]: 'pi-circle-off',
    [StatusIconType.Light2]: 'pi-circle-off'
};

@Pipe({
    name: 'getCircleType',
    pure: true
})
export class GetCircleTypePipe implements PipeTransform {

    transform(statusIcon: StatusIconType): string {

        return get(mapping, statusIcon, 'pi-circle-on');
    }

}
