import { Injectable, NgZone } from '@angular/core';

import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';
import { SnapshotService } from '@oam/cu-asm-api/services';
import { ConsoleLogsDatabaseService } from '@oam/core/lib/services/console-logs-database.service';
import { saveAs } from 'file-saver';
import JSZip from 'jszip';
import { FilesDataResponse } from '@oam/diagnostic-snapshot/core/models';
import {
    downloadingFilesCompleted,
    setSnapshotStatusToCanceled,
    startCollectingLocalSnapshotHearbeat,
    startSavingFiles,
    updateDownloadFileStatus
} from '../actions/snapshot.action';
import { DateTime } from '@oam/core';
import { isEmpty } from 'lodash';
import { WebemStateService } from '@oam/core/lib/services/webem.state.service';
import { SnapshotFetchFilesConfig } from '../services/snapshot-fetch-files-config';
import {
    selectCollectingSnapshotIsCanceled
} from '../selectors/collecting-snapshot.selector';
import { take } from 'rxjs/operators';
import { NGXLogger } from 'ngx-logger';

@Injectable({
    providedIn: 'root'
})
export class SnapshotFetchFilesService {
    public readonly mainZip: JSZip;
    public filesListTXT: string;
    private canceled: boolean;
    constructor(
        private readonly logger: NGXLogger,
        private readonly httpClient: HttpClient,
        private readonly store: Store,
        private readonly snapshotService: SnapshotService,
        private readonly consoleLogsDatabaseService: ConsoleLogsDatabaseService,
        private readonly zone: NgZone,
        private readonly webemState: WebemStateService,
        private readonly config: SnapshotFetchFilesConfig,
    ) {
        this.mainZip = new JSZip();
        this.filesListTXT = '';
    }

    async startDownloadingFiles(filesList: FilesDataResponse[], operationId: string | undefined) {
        for (const file of filesList) {
            this.store.dispatch(updateDownloadFileStatus({fileId: file.fileId,  fileStates: 'ongoing' }));
        }
        let listOfFiles = '';
        this.canceled = false;
        for (const file of filesList) {
            if (this.canceled) {
               this.logger.log('snapshot is canceled');
                     break;
                  }
            const snapshotFile = await this.snapshotService.getLocalSnapshotFile(
                { operationId: operationId as string, fileId: file.fileId }
            ).toPromise().catch( () =>{
                this.store.dispatch(updateDownloadFileStatus({fileId: file.fileId,  fileStates: 'completed' }));

                return '';
            });
            if (snapshotFile) {
                this.mainZip.file(file.fileId, snapshotFile);
                listOfFiles += file.fileId;
                try {
                    const zippedFiles = await JSZip().loadAsync(snapshotFile).catch(() => '\n\n');
                    listOfFiles += (zippedFiles instanceof JSZip) ? this.getNamesOfZippedFiles(zippedFiles) : zippedFiles;
                    this.store.dispatch(updateDownloadFileStatus({fileId: file.fileId,  fileStates: 'completed' }));
                } catch (e) {
                    this.logger.log(`Snapshot collection error on ${file.fileId}`, e);
                }
            }
        }
        this.filesListTXT += listOfFiles;
    }
    async saveFilesLocal(operationId: string | undefined) {
        const logs = await this.consoleLogsDatabaseService.getBlobLogs();
        const browserLogs = `${logs.name}\n\n`;
        this.mainZip.file(logs.name, logs.blob);
        this.mainZip.file('snapshot_file_list.txt', browserLogs + this.filesListTXT);
        this.store.select(selectCollectingSnapshotIsCanceled).pipe(take(1)).subscribe(async cancelled => {
            if (!cancelled) {
                await this.zone.runOutsideAngular(async () => {
                    this.store.dispatch(startSavingFiles());
                    const content = await this.mainZip.generateAsync({ type: 'blob' });
                    saveAs(content, this.createSnapshotName());
                });
                this.store.dispatch(startCollectingLocalSnapshotHearbeat({operationId: operationId as string, startHeartBeatStatus: false }));
                this.store.dispatch(downloadingFilesCompleted({ operationId: operationId as string }));
            }
        });
        this.mainZip.files = {};
        this.filesListTXT = '';
    }

    createSnapshotName(): string {
        const date = new DateTime().toFormattedFileDateTime();
        const dnName = 'MRBTS';
        let webemStateText = '';
        const webemState = this.webemState.getWebemState();
        if (webemState) {
            webemStateText = [...this.config.SNAPSHOT_NAME_PARAMETERS, 'softwareVersion']
                .filter(property => webemState[property])
                .reduce((stateText, property) => stateText + `${webemState[property]}_`, '');
        }

        return `Snapshot_${dnName}-${webemStateText}${date}.zip`;
    }

    getNamesOfZippedFiles(zippedFiles: JSZip): string {
        let listOfZippedFiles = '';
        if (isEmpty(zippedFiles.files)) return listOfZippedFiles += '\n\n';
        listOfZippedFiles += ':';
        zippedFiles.forEach(fileName => {
            listOfZippedFiles += `\n\t${fileName}`;
        });

        return listOfZippedFiles += '\n\n';
    }

    cancelCollectingFiles() {
        this.canceled = true;
        this.store.dispatch(setSnapshotStatusToCanceled());
        this.mainZip.files = {};
        this.filesListTXT = '';
    }
}
