import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ButtonModel, ButtonTheme, ButtonThickness } from '@oam/cu-ui';
import {
    selectSnapshotTransferStatus, selectTransferOperationStatus, selectTransferSnapshotBtnTitle,
    selectTransferSnapshotDuration,
    selectTransferSnapshotErrorMessage,
    selectTransferSnapshotStartedAt
} from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';
import { select, Store } from '@ngrx/store';
import { takeUntil } from 'rxjs/operators';
import {
    cleanTransferStateIfTransferIsNotOngoing,
    startTransferSnapshotToNMS
} from '@oam/diagnostic-snapshot/core/actions/snapshot.action';
import { TimerServiceService } from '@oam/diagnostic-snapshot/core/services/timer-service.service';
import {
    ErrorMessage, ITransferSnapshot
} from '@oam/diagnostic-snapshot/core/models';
import { selectSnapshotOperationIsOngoing } from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot.selector';

@Component({
    selector: 'diagnostic-snapshot-transfer',
    templateUrl: './diagnostic-snapshot-transfer.component.html',
    styleUrls: ['./diagnostic-snapshot-transfer.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DiagnosticSnapshotTransferComponent implements OnInit {
    @Input() isSnapshotIdle: boolean;
    @Input() remoteSnapshotFileName: string | undefined;

    selectTransferOperationStatus$: Observable<ITransferSnapshot>;
    transferSnapshotDurationTimer$: Observable<number>;
    transferSnapshotDuration$: Observable<number>;
    selectTransferSnapshotErrorMessage$: Observable<ErrorMessage | undefined>;
    transferSnapshotBtnTitle$: Observable<string>;
    selectSnapshotTransferStatus$: Observable<string | undefined>;
    selectSnapshotOperationIsOngoing$: Observable<boolean>;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();


    readonly transferSnapshotToNMSButtonModel: ButtonModel = {
        label: 'Transfer Snapshot',
        theme: ButtonTheme.Accent,
        thickness: ButtonThickness.Default
    };

    constructor(private readonly store: Store, private readonly timerService: TimerServiceService) {};

    ngOnInit(): void {
        this.transferSnapshotDurationTimer$ = this.timerService.setSnapshotDurationTimer(selectTransferSnapshotStartedAt).pipe(
            takeUntil(this.ngUnsubscribe)
        );
        this.transferSnapshotDuration$ = this.timerService.setSnapshotDuration(selectTransferSnapshotDuration).pipe(
            takeUntil(this.ngUnsubscribe)
        );

        this.selectTransferOperationStatus$ = this.store.pipe(
            select(selectTransferOperationStatus),
            takeUntil(this.ngUnsubscribe)
        );

        this.selectTransferSnapshotErrorMessage$ = this.store.pipe(
            select(selectTransferSnapshotErrorMessage),
            takeUntil(this.ngUnsubscribe)
        );

        this.selectSnapshotTransferStatus$ = this.store.pipe(
            select(selectSnapshotTransferStatus),
            takeUntil(this.ngUnsubscribe)
        );

        this.transferSnapshotBtnTitle$ = this.store.pipe(
            select(selectTransferSnapshotBtnTitle),
            takeUntil(this.ngUnsubscribe)
        );

        this.selectSnapshotOperationIsOngoing$ = this.store.pipe(
            select(selectSnapshotOperationIsOngoing),
            takeUntil(this.ngUnsubscribe)
        );
    }

    ngOnDestroy(): void {
        this.store.dispatch(cleanTransferStateIfTransferIsNotOngoing());
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    startTransferSnapshot() {
        this.store.dispatch(startTransferSnapshotToNMS());
    }
}
