import { createFeatureSelector, createSelector, select } from '@ngrx/store';
import { getMergedRoute, MergedRoute } from '@oam/core/lib/selectors/routing.selectors';
import { filter, mapTo, scan } from 'rxjs/operators';
import { pipe } from 'rxjs';
import { DETAILS_PANEL, DetailsPanelState } from '@oam/details-panel/lib/core/reducers/detail-panel.reducer';
import { convertUrlIntoKey } from '@oam/details-panel/lib/core/utils/detail-panel.utils';
import { DetailPanelSplitViewData } from '@oam/details-panel/lib/core/model/detail-panel-split-view-data.interface';

const detailPanelState = createFeatureSelector<DetailsPanelState>(DETAILS_PANEL);
const splitViewOutlets = ['topView', 'bottomView'];

export const isDetailPanelVisible = createSelector(
    detailPanelState,
    getMergedRoute,
    (state: DetailsPanelState, mergeRoute: MergedRoute, props?: DetailPanelSplitViewData) => {
        if (mergeRoute && mergeRoute.url && splitViewOutlets.some(route => mergeRoute.url.includes(route))) {
            const outlet = props && props.outlet ? props.outlet : undefined;
            const topView = props && props.topView ? props.topView : undefined;
            const bottomView = props && props.bottomView ? props.bottomView : undefined;

            return state[convertUrlIntoKey(outlet === 'topView' ? topView : bottomView)];
        }

        if (mergeRoute && mergeRoute.url) return !!state[convertUrlIntoKey(mergeRoute.url)];

        return false;
    }
);
export const isDetailsPanelBeingResized = createSelector(detailPanelState, state => state.isBeingResized);
export const detailsPanelFinishedResizing = pipe(
    select(isDetailsPanelBeingResized),
    scan<boolean, [boolean, boolean]>((acc, isBeingResized) => [acc[1], isBeingResized], [false, false]),
    filter(([wasResizing, isResizing]) => wasResizing && !isResizing),
    mapTo(true)
);
