import { Component, OnInit } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { OperationStatus } from '@oam/core/lib/models/operation.interface';
import { ActivatedRoute } from '@angular/router';
import { loginInProgress, ssoLoginStarted } from '../../core/actions/login.actions';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { getLoginStatus } from '../../core/reducers/login.reducer';
import { isEmpty } from 'lodash';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { setToken } from '@oam/core/lib/actions/user.actions';
import { TokenType } from '@oam/core/lib/store/user-state';

@Component({
    selector: 'webem-sso-login-page',
    templateUrl: './sso-login-page.component.html',
    styleUrls: ['./sso-login-page.component.scss']
})

export class SsoLoginPageComponent implements OnInit {
    loginStatus$: Observable<OperationStatus>;

    constructor(
        private readonly store: Store,
        private readonly activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.loginStatus$ = this.store.pipe(select(getLoginStatus));
        const token = this.activatedRoute.snapshot.queryParams && this.activatedRoute.snapshot.queryParams.token;
        if (!isEmpty(token)) {
            this.store.dispatch(loginInProgress());
            this.store.dispatch(setToken({ payload: { type: TokenType.BEARER, value: token } }));
            this.store.dispatch(ssoLoginStarted({ payload: token }));
        } else {
            this.store.dispatch(redirect({ routerLink: RouterLink.login, activatedRoute: this.activatedRoute }));
        }
    }
}
