/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { AuthenticationService } from './services/authentication.service';
import { SessionMgmtService } from './services/session-mgmt.service';
import { UpstreamApiMgmtService } from './services/upstream-api-mgmt.service';
import { StatisticsService } from './services/statistics.service';
import { ConfigurationService } from './services/configuration.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    AuthenticationService,
    SessionMgmtService,
    UpstreamApiMgmtService,
    StatisticsService,
    ConfigurationService,
    ApiConfiguration
  ],
})
export class CuApiGwClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuApiGwClientModule> {
    return {
      ngModule: CuApiGwClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuApiGwClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuApiGwClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
