import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Observable, of } from 'rxjs';
import { catchError, concatMap, filter, map, mergeMap, switchMap, timeout, withLatestFrom } from 'rxjs/operators';
import {
    denyLegalNoticeModal,
    getLegalNoticeMessage,
    setLegalNoticeMessage,
    showLegalNoticeModal
} from '../actions/legal-notice-modal.actions';
import { SecService } from '@oam/cu-asm-api/services';
import {
    defaultLegalNoticeModalMessage,
    errorLegalNoticeModalMessage, legalNoticeModalConfig, loadingLegalNoticeModalMessage
} from '../legal-notice.constants';
import { logout, LogoutActionsTypes } from '@oam/auth/lib/auth/core/actions/logout.actions';
import { TypedAction } from '@ngrx/store/src/models';
import { openDialog } from '@oam/cu-ui/lib/webem-overlay/core/actions/overlay.actions';
import { LegalNoticeModalComponent } from '@oam/cu-ui/lib/legal-notice-modal/components/legal-notice-modal/legal-notice-modal.component';
import { Store } from '@ngrx/store';
import {
    isLegalNoticeVisibleSelector
} from '@oam/cu-ui/lib/legal-notice-modal/core/selectors/legal-notice-modal.selectors';

@Injectable()
export class LegalNoticeModalEffects {
    getLegalNoticeMessage$ = createEffect(() =>
        this.actions$.pipe(
            ofType(getLegalNoticeMessage),
            switchMap(() => {
                return this.secService.getSystemInfo().pipe(
                    timeout(30000),
                    withLatestFrom(
                        this.store.select(isLegalNoticeVisibleSelector)
                    ),
                    filter(([, isVisible]) => {
                        return isVisible;
                    }),
                    concatMap(([systemInfo]) => of(
                        setLegalNoticeMessage({
                            securityNotice: systemInfo.securityNotice || defaultLegalNoticeModalMessage,
                            confirmButtonVisible: true,
                            okButtonVisible: false
                        })
                    )),
                    catchError(() => of(
                        setLegalNoticeMessage({
                            securityNotice: errorLegalNoticeModalMessage,
                            confirmButtonVisible: false,
                            okButtonVisible: true
                        })
                    ))
                );
            })
        )
    );

    denyLegalNoticeModal$: Observable<TypedAction<LogoutActionsTypes.LOGOUT>> = createEffect(() =>
        this.actions$.pipe(
            ofType(denyLegalNoticeModal),
            concatMap(() => of(logout()))
        )
    );

    showLegalNoticeModal$ = createEffect(() =>
        this.actions$.pipe(
            ofType(showLegalNoticeModal),
            mergeMap(() => {
                return [
                    openDialog({
                        componentType: LegalNoticeModalComponent,
                        config: legalNoticeModalConfig
                    }),
                    setLegalNoticeMessage({
                        securityNotice: loadingLegalNoticeModalMessage,
                        confirmButtonVisible: false,
                        okButtonVisible: false
                    })];
            })
        )
    );

    constructor(
        private readonly actions$: Actions,
        private readonly secService: SecService,
        private readonly store: Store
    ) {
    }
}
