import { omit } from 'lodash';
import { on, createReducer, Action } from '@ngrx/store';
import {
    addNotificationId,
    removeNotificationFromStore,
    addNotificationToHistory
} from '../actions/toast-message.actions';
import { ToastMessageState } from '../models/toast-message-state.type';

export const featureKey = 'toastMessage';

export const initialState: ToastMessageState = {
    toastIds: {},
    notifications: []
};

const onRemoveNotificationFromStore = on(removeNotificationFromStore, (state: ToastMessageState, { payload }) => {
    const newToastIds = omit(state.toastIds, payload);

    return {
        ...state,
        toastIds: {
            ...newToastIds
        }
    };
});

const onAddNotificationId = on(addNotificationId, (state: ToastMessageState, { payload }) => {
    return {
        ...state,
        toastIds: {
            ...state.toastIds,
            [payload.toastType]: payload.toastId
        }
    };
});

const onAddNotificationToHistory = on(addNotificationToHistory, (state: ToastMessageState, { notification }) => {
    return {
        ...state,
        notifications: [...state.notifications, notification]
    };
});

const toastMessageReducer = createReducer(
    initialState,
    onRemoveNotificationFromStore,
    onAddNotificationId,
    onAddNotificationToHistory
);

export function reducer(state: ToastMessageState | undefined, action: Action) {
    return toastMessageReducer(state, action);
}

