import { Component, Input } from '@angular/core';
import { Section } from '../../model';

@Component({
    selector: 'ui-webem-details-section-list',
    templateUrl: './details-section-list.component.html',
    styleUrls: ['./details-section-list.component.scss']
})
export class DetailsSectionListComponent {

    @Input()
    title: string;

    @Input()
    subTitle: string;

    @Input()
    id: string;

    @Input()
    listData: Section;

    constructor() { }

    trackByFn(index: number) {
        return index;
    }

}
