import { createFeatureSelector, createSelector, select } from '@ngrx/store';
import {
    KEYBOARD_SHORTCUTS,
    KeyboardShortcutsState
} from '../reducers/keyboard-shortcuts.reducer';

const keyboardShortcutsAvailabilityState = createFeatureSelector<KeyboardShortcutsState>(KEYBOARD_SHORTCUTS);

export const areKeyboardShortcutsEnabled = createSelector(keyboardShortcutsAvailabilityState, state => state.enabled);

