import { Injectable } from '@angular/core';
import { TargetComponentOptions } from '@oam/tracing/lib/core/models';


export class TracingConfig {
    isConfigVdu: boolean;
    targetComponents: TargetComponentOptions[];
    withBitmaskFilters: boolean;
}


@Injectable({
    providedIn: 'root'
})
export class TracingConfigService {
    private readonly isVdu: boolean;
    private readonly areBitmapsVisible: boolean;
    private readonly targetComponents: TargetComponentOptions[];

    constructor(config: TracingConfig) {
        if (config) {
            this.isVdu = config.isConfigVdu;
            this.targetComponents = config.targetComponents;
            this.areBitmapsVisible = config.withBitmaskFilters;
        }
    }

    get isConfigVdu(): boolean {
        return this.isVdu;
    }

    get targetComponentOptions(): TargetComponentOptions[] {
        return this.targetComponents;
    }

}
