import { ChartPluginsOptions } from 'chart.js';
const TextFontColor = '#1B1B1B';
const fontColor = 'rgba(27, 27, 27, 1)';
const fontFamily = 'Nokia Pure';
const whiteColor = 'rgba(255, 255, 255, 1)';

const colors = [
    '#124191', '#DA1914', '#FFCC00', '#28C94C', '#00A1CC', '#898683', '#9340F4', '#EF7A11', '#13D8E2', '#E83ADC',
    '#2D2D2C', '#60EF03', '#0D562A', '#B7B6B6', '#FF8080', '#F9E746', '#7C4503', '#E882E5', '#87BBF9', '#AE7DF4'
];

