import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { take, map, mergeMap, mapTo, withLatestFrom, tap, filter } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { isEmpty } from 'lodash';

import {
    addNotification,
    removeNotification,
    removeNotificationFromStore,
    addSimpleNotification,
    removeAllNotifications,
    addNotificationToHistory
} from '../actions/toast-message.actions';

import { ToastrService } from 'ngx-toastr';
import { select, Store } from '@ngrx/store';
import { selectToastIds } from '../selectors/toast-message.selectors';
import { ToastIdTypes } from '@oam/core/lib/models/toast';
import { DateTime } from '../classes/date-time';
import { isUserAuthenticated } from '@oam/cu-ui/lib/page-not-found/core/functions/page-not-found.utils';

@Injectable()
export class ToastMessageEffects {
    constructor(private readonly actions$: Actions, private readonly toastr: ToastrService, private readonly store: Store) {
    }

    addNotification$ = createEffect(() => this.actions$.pipe(
        ofType(addNotification),
        filter(() => isUserAuthenticated()),
        map(({ payload: { severity, detail, summary } }) => {
            return addSimpleNotification({ payload: { severity, detail, summary } });
        })
    ));

    addNotificationToStore$ = createEffect(() => this.actions$.pipe(
        ofType(addNotification),
        map(({ payload: { severity, detail, summary } }) => {
            return addNotificationToHistory({ notification: {severity, detail, summary, date: new DateTime()} });
        })
    ));

    addSimpleNotification$ = createEffect(() => this.actions$.pipe(
        ofType(addSimpleNotification),
        tap(({ payload: { severity, detail, summary } }) => {
            this.toastr[severity](detail, summary, {enableHtml: true});
        })
    ), { dispatch: false });

    removeNotification$ = createEffect(() => this.actions$.pipe(
        ofType(removeNotification),
        withLatestFrom(
            this.store.select(selectToastIds)
        ),
        map(([{ payload }, toastIds]) => {
            this.toastr.clear(toastIds[payload]);

            return removeNotificationFromStore({ payload });
        })
    ));

    removeAllNotifications$ = createEffect(() => this.actions$.pipe(
        ofType(removeAllNotifications),
        withLatestFrom(
            this.store.select(selectToastIds)
        ),
        mergeMap(([, toastIdsFromStore]) => {
            const toastIds: number[] = this.toastr.toasts.map((toastInstance) => toastInstance.toastId);
            toastIds.forEach(toastId => {
                this.toastr.clear(toastId);
            });

            const toastIdsKeysFromStore = Object.keys(toastIdsFromStore) as [ToastIdTypes];

            if (!isEmpty(toastIdsKeysFromStore)) {
                const toBeDispatched: ReturnType<typeof removeNotificationFromStore>[] = [];

                toastIdsKeysFromStore.forEach((toastKey) => {

                    toBeDispatched.push(removeNotificationFromStore({
                        payload: toastKey
                    }));
                });

                return toBeDispatched;
            }

            return EMPTY;
        })
    ));
}
