import { ModuleWithProviders, NgModule } from '@angular/core';
import { LayoutsConfig } from '@oam/layouts/lib/layouts-config.service';


@NgModule({})
export class LayoutsConfigModule {
    static forRoot(layoutsConfig: LayoutsConfig): ModuleWithProviders<LayoutsConfigModule> {
        return {
            ngModule: LayoutsConfigModule,
            providers: [
                {
                    provide: LayoutsConfig,
                    useValue: layoutsConfig
                }
            ]
        };
    }
}
