import { Component, EventEmitter, HostBinding, Input, Output } from '@angular/core';

@Component({
    selector: 'ui-webem-input-clear',
    templateUrl: './input-clear.component.html',
    styleUrls: ['./input-clear.component.scss']
})
export class InputClearComponent {
    @Input() isDisabled: boolean;

    @HostBinding('class') class = 'ui-inputgroup-addon';

    @Output()
    buttonClicked: EventEmitter<MouseEvent> = new EventEmitter<MouseEvent>();

    constructor() { }

    onClickEvent(event: MouseEvent): void {
        this.buttonClicked.emit(event);
    }

}
