import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { OverlayPanel } from 'primeng/overlaypanel';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonOptions } from '../../../button/model/button-options.definition';

@Component({
    selector: 'ui-webem-overlay-menu-bar',
    templateUrl: './overlay-menu-bar.component.html',
    styleUrls: ['./overlay-menu-bar.component.scss']
})
export class OverlayMenuBarComponent implements OnInit {
    @Input() model: ButtonModel;
    @Input() options?: ButtonOptions;
    @Input() appendTo: string | HTMLDivElement;
    @Input() disabled: boolean;
    @Input() buttonId: string;
    @Input() styleClass?: string;
    @Input() title = '';
    @Input() roleAccessFlag?: string;
    @Input() componentsToHideBeforeOverlayPanel: object[] = [];
    @Output() visibilityChange = new EventEmitter<boolean>();
    @ViewChild(OverlayPanel, { static: true }) private readonly overlayPanel: OverlayPanel;

    constructor() {
    }

    ngOnInit() {
    }

    toggle(event: Event) {
        this.overlayPanel.toggle(event);
        this.visibilityChange.emit(this.isOverlayVisible());
    }

    hide() {
        if (this.isOverlayVisible()) {
            this.overlayPanel.hide();
            this.visibilityChange.emit(false);
        }
    }

    isOverlayVisible() {
        return this.overlayPanel.overlayVisible;
    }

    onHide() {
        this.componentsToHideBeforeOverlayPanel.forEach((component: any) => {
            if (!!component && !!component.overlayVisible) {
                component.overlayVisible = false;
            }
        });
    }
}
