import { createAction, props } from '@ngrx/store';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { ActivatedRoute } from '@angular/router';

export enum RouterActionTypes {
    REDIRECT = '[User] [Router] REDIRECT',
    FORCE_REDIRECT = '[User] [Router] FORCE REDIRECT WITHOUT CHECKING SPLIT VIEW MODE',
    REDIRECT_WITH_DATA = '[User] [Router] REDIRECT WITH DATA'
}

export const forceRedirect = createAction(
    RouterActionTypes.FORCE_REDIRECT,
    props<{ routerLink: RouterLink, redirectUrl?: string }>()
);

export const redirect = createAction(
    RouterActionTypes.REDIRECT,
    props<{ routerLink: RouterLink | string, activatedRoute: ActivatedRoute, data?: {}, redirectUrl?: string }>()
);

