import _ from 'lodash';
import { Injectable } from '@angular/core';
import { PageName } from '@oam/navigation/lib/navigation/core/models/page-name';

export const vnfcTypeSessionKey = 'vnfcType';
export enum VnfcType {
    vmCu = 'vmCu',
    vDu = 'vDu',
    vCu = 'vCu'
}
let vnfcType: string | null = '';
const filterMenuForVmcu = [
    { label: PageName.ipsecPskConfiguration } as any,
    { label: PageName.networkSlicingStatus } as any,
    { label: PageName.ngLinksStatus } as any,
    { label: PageName.xnLinksStatus } as any,
    { label: PageName.ipsecAssociations } as any
];

@Injectable({
    providedIn: 'root'
})
export class VnfcSharedService {

    setVnfcType(vnfcTypeValue: string) {
        vnfcType = vnfcTypeValue;
    }

    getVnfcType() {
        return vnfcType;
    }

    isVcu(): boolean {
        return VnfcType.vCu === this.getVnfcType();
    }

    isVdu(): boolean {
        return VnfcType.vDu === this.getVnfcType();
    }

    isVmcu(): boolean {
        return VnfcType.vmCu === this.getVnfcType();
    }

    filterMenuForVcuVmcu<T>(routes: T[]): T[] {
        if (this.isVcu()) {
            removeFromVcuArray(routes);
            for (const r of routes) {
                removeFromVcuArray(r['items']);
            }
        }

        if (this.isVmcu()) {
            removeFromVmcuArray(routes);
            for (const r of routes) {
                removeFromVmcuArray(r['items']);
            }
        }

        return routes as unknown as T[];

        function removeFromVcuArray(array: any[]) {
            _.remove(array, { label: PageName.cplane } as any);
        }

        function removeFromVmcuArray(array: any[]) {
            filterMenuForVmcu.forEach((label) => {
                _.remove(array, label);
            });
        }
    }

    constructor() {}
}
