import { Inject, Injectable } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { WebemRoutesPaths } from '@oam/cu-shared';
import { Observable, Subscription } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { areKeyboardShortcutsEnabled } from '../selectors/keyboard-shortcuts.selectors';
import { DateTime } from '@oam/core';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { size } from 'lodash';
import { KeyboardShortcutsConfig } from '../services/keyboard-shortcuts-config';
import { KeyboardShortcutsModule } from '../../keyboard-shortcuts.module';
import { ShortcutPath } from '../models/shortcut-path';


@Injectable({
    providedIn: KeyboardShortcutsModule,
})
export class KeyboardShortcutsService {
    private static readonly DELAY: number = 1000;
    private buffer: string;
    private lastKeyPressTime: number;
    keyboardShortcutsState$: Observable<any>;
    keyboardShortcutsSubscription: Subscription;
    shortcutsEnabled: boolean;
    fullPathsList: ShortcutPath;

    constructor(@Inject(DOCUMENT) private readonly document: Document,
                private readonly router: Router,
                private readonly store: Store,
                private readonly activatedRoute: ActivatedRoute,
                private readonly config: KeyboardShortcutsConfig) {
        this.fullPathsList = this.config.SHORTCUTS_PATHS;
        this.buffer = '';
        this.lastKeyPressTime = new DateTime().getTime();
    }

    turnOn(): void {
        this.document.body.addEventListener('keyup', this.onKeyUp.bind(this));

        this.keyboardShortcutsState$ = this.store.pipe(
            select(areKeyboardShortcutsEnabled)
        );

        this.keyboardShortcutsSubscription = this.keyboardShortcutsState$
            .subscribe((keyboardShortcutsState) => {
                this.shortcutsEnabled = keyboardShortcutsState;
            });
    }

    turnOff(): void {
        this.keyboardShortcutsSubscription.unsubscribe();
        this.document.body.removeEventListener('keyup', this.onKeyUp.bind(this), true);
    }

    onKeyUp(event: KeyboardEvent): void {
        if (!this.shortcutsEnabled
            || this.isFocusedInTextField(event)
            || this.isModalOverlayPanelOrDropdownOpen(this.document)
            || this.isOnLoginPage()) return;

        const currentTime: number = event.timeStamp;
        if (currentTime - this.lastKeyPressTime > KeyboardShortcutsService.DELAY) this.buffer = '';
        this.buffer += event.key.toLocaleUpperCase();
        this.lastKeyPressTime = currentTime;
        if (this.buffer.length > 2) this.buffer = this.buffer.substring(1);

        const path: string = this.fullPathsList[this.buffer];

        if (path) {
            this.buffer = '';
            this.changePath(path);
        }
    }

    private changePath(path: string): void {
        this.store.dispatch(redirect({ routerLink: path, activatedRoute: this.activatedRoute}));
    }

    private isFocusedInTextField(event: any): boolean | undefined {
        if (!event) return;

        return this.isTextInput(event) ||
            event.target.nodeName === 'TEXTAREA' ||
            event.target.isContentEditable;
    }

    private isTextInput(event: any): boolean {
        return event.target.nodeName === 'INPUT' &&
            event.target.getAttribute('type') !== 'button' &&
            event.target.getAttribute('type') !== 'radio' &&
            event.target.getAttribute('type') !== 'checkbox';
    }

    private isModalOverlayPanelOrDropdownOpen(document: Document): boolean {
        return size(document.querySelectorAll('.p-dialog, .p-overlaypanel, .p-dropdown-panel')) > 0;
    }

    private isOnLoginPage(): boolean {
       return this.router.url === '/' + WebemRoutesPaths.login;
    }
}
