/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { CuOamAgentClientService } from './services/cu-oam-agent-client.service';
import { CuInternalService } from './services/cu-internal.service';
import { StatusService } from './services/status.service';
import { VduInternalService } from './services/vdu-internal.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    CuOamAgentClientService,
    CuInternalService,
    StatusService,
    VduInternalService,
    ApiConfiguration
  ],
})
export class CuOamAgentClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuOamAgentClientModule> {
    return {
      ngModule: CuOamAgentClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuOamAgentClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuOamAgentClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
