import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges, OnDestroy,
    OnInit,
    Output, SimpleChanges
} from '@angular/core';
import { AlarmSeverityType } from '../../../alarm-severity-icon/model';
import { Du } from '@oam/cu-asm-api/models/du';
import { AdministrativeState } from '@oam/cu-asm-api/models/administrative-state';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { VisibleDuWidgetParameters } from '../../model/du-widget-parameters.definition';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { GetHighestAlarmSeverityPipe } from '../../../alarm-severity-icon/pipes/get-highest-alarm-severity.pipe';
import { TileTooltipContent } from '../../../tile-tooltip/models';
import { capitalize } from 'lodash';
import { NullableValuePipe } from '@oam/shared/lib/modules/webem-common';
import { Subject, Subscription } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { selectCommissioningFlagStatus } from '@oam/commissioning-flag-store/lib/core/selectors/select-commissioning-status';
import { takeUntil } from 'rxjs/operators';
import { MenuAction } from '../../../button-with-menu/model/menu-action';
import { RoleAccessForbiddenFlagEnum } from '@oam/core/lib/models/role-access/role-access-forbidden-flag.enmu';

@Component({
    selector: 'ui-webem-du',
    templateUrl: './du.component.html',
    styleUrls: ['./du.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DuComponent implements OnInit, OnChanges, OnDestroy {
    private static readonly getHighestAlarmSeverityPipe = new GetHighestAlarmSeverityPipe();
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    @Input() du: Du;
    @Input() isRedirectionToCellViewAvailable: boolean;
    @Input() visibleDuParameters: VisibleDuWidgetParameters;
    @Input() isDuSelected: boolean;
    @Input() container?: ElementRef;
    @Input() isCellSummaryInOneRow = false;

    @Output() lockDu = new EventEmitter<void>();
    @Output() unlockDu = new EventEmitter<void>();
    @Output() showCells = new EventEmitter<void>();
    @Output() goToPe = new EventEmitter<void>();
    @Output() toggleMenu = new EventEmitter<void>();

    duMenuItems: MenuAction[];
    highestSeverityAlarm?: AlarmSeverityType;
    totalNumberOfCellsInGivenDu: number;
    isLocked: boolean;
    tooltipPanelContent: TileTooltipContent[];
    tooltipPanelContentStatus: TileTooltipContent[];
    private commissioningSubscription: Subscription;
    isCommissioningInProgress: boolean;

    readonly optionModel: ButtonModel = {
        title: 'Actions',
        icon: 'mdi mdi-dots-vertical',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long
    };

    constructor(
      private readonly nullableValuePipe: NullableValuePipe,
      private readonly store: Store
    ) {}

    ngOnInit(): void {
        this.recalculateStateForDu();
        this.recalculateTooltipContent();
        this.commissioningSubscription = this.store.pipe(
            takeUntil(this.ngUnsubscribe),
            select(selectCommissioningFlagStatus)
        )
        .subscribe(data => {
            this.isCommissioningInProgress = data;
            this.setMenu();
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        this.recalculateStateForDu();
        this.recalculateTooltipContent();
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    private checkAdministrativeState() {
        this.isLocked = this.du.administrativeState === AdministrativeState.Locked;
    }

    private setMenu() {
        const lockUnlockOption = this.getLockUnlockOption();
        this.duMenuItems = [
            lockUnlockOption,
            ...this.isRedirectionToCellViewAvailable ? [
                {
                    label: 'Show cells',
                    icon: 'mdi mdi-hexagon-multiple-outline',
                    command: () => this.showCells.emit()
                }
            ] : [],
            {
                label: 'Go to PE',
                icon: 'mdi mdi-import',
                command: () => this.goToPe.emit()
            }
        ];
    }

    protected recalculateStateForDu() {
        this.checkAdministrativeState();
        this.setMenu();
        this.highestSeverityAlarm = DuComponent.getHighestAlarmSeverityPipe.transform(this.du.alarms || []);
        this.totalNumberOfCellsInGivenDu = this.getTotalNumberOfCellsInGivenDu();
    }

    private getLockUnlockOption(): MenuAction {
        if (this.isLocked) {
            return {
                label: 'Unlock DU',
                icon: 'mdi mdi-lock-open-variant-outline mdi-16px',
                command: () => this.unlockDu.emit(),
                disabled: this.isCommissioningInProgress,
                roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
            };
        }

        return {
            label: 'Lock DU',
            icon: 'mdi mdi-lock mdi-16px',
            command: () => this.lockDu.emit(),
            disabled: this.isCommissioningInProgress,
            roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
        };
    }

    private getTotalNumberOfCellsInGivenDu() {
        return this.du.cellsSummary.basicCellsSummary.administrativeState.locked
            + this.du.cellsSummary.basicCellsSummary.administrativeState.unlocked;
    }

    private recalculateTooltipContent() {
        const { enabled, disabled } = this.du.cellsSummary.basicCellsSummary.operationalState;
        const { locked, unlocked } = this.du.cellsSummary.basicCellsSummary.administrativeState;

        this.tooltipPanelContent = [
            { title: 'gNB DU ID', value: this.du.gnbDuId },
            { title: 'DU Name', value: this.nullableValuePipe.transform(this.du.gnbDuName) },
            { title: 'Total Cells', value: this.totalNumberOfCellsInGivenDu },
            { title: 'Cells enabled/disabled', value: `${enabled}/${disabled}` },
            { title: 'Cells unlocked/locked', value: `${unlocked}/${locked}` }
        ];

        this.tooltipPanelContentStatus = [
            { title: 'F1 Link status', value: capitalize(this.nullableValuePipe.transform(this.du.f1LinkStatus)) },
            { title: 'Administrative state', value: capitalize(this.du.administrativeState) }
        ];
    }

    emitToggleAction() {
        this.toggleMenu.emit();
    }
}
