import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DiagnosticContentMenuComponent } from './components/diagnostic-content-menu/diagnostic-content-menu.component';
import { DetailsSectionModule, SubmenuModule, NoSelectedMessageModule, WebemButtonModule } from '@oam/cu-ui';
import { TestmodelContentMenuComponent } from './components/testmodel-content-menu/testmodel-content-menu.component';
import { RunningTestModelsEffects } from '@oam/diagnostic-shared/lib/core/effects/running-test-models.effects';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';
import * as fromRunningTestModelsReducer from '@oam/diagnostic-shared/lib/core/reducers/running-test-models.reducer';
import { TestmodelPanelDisableMessageComponent } from './components/testmodel-panel-disable-message/testmodel-panel-disable-message.component';
import { TestModelDetailsComponent } from './components/test-model-details/test-model-details.component';
import { DiagnosticTestTableComponent } from '@oam/diagnostic-shared/lib/components/diagnostic-test-table/diagnostic-test-table.component';
import { DetailsPanelModule } from '@oam/details-panel';
import { TableModule } from 'primeng/table';
import { WebemCommonModule } from '@oam/shared/lib/modules/webem-common';

@NgModule({
    imports: [
        CommonModule,
        SubmenuModule,
        StoreModule.forFeature(fromRunningTestModelsReducer.featureKey, fromRunningTestModelsReducer.reducer),
        EffectsModule.forFeature([
            RunningTestModelsEffects
        ]),
        DetailsPanelModule,
        DetailsSectionModule,
        NoSelectedMessageModule,
        TableModule,
        WebemButtonModule,
        WebemCommonModule
    ],
    declarations: [
        DiagnosticContentMenuComponent,
        TestmodelContentMenuComponent,
        TestmodelPanelDisableMessageComponent,
        TestModelDetailsComponent,
        DiagnosticTestTableComponent
    ],
    exports: [
        DiagnosticContentMenuComponent,
        TestmodelContentMenuComponent,
        TestmodelPanelDisableMessageComponent,
        DiagnosticTestTableComponent
    ]
})
export class DiagnosticSharedModule {
}
