import { createAction, props } from '@ngrx/store';
import { ToastMessage } from './toast-message';
import { ToastIdTypes } from '../models/toast';
import { Notification } from '../models/notification.type';

export enum ToastMessageActionTypes {
    addNotification = '[Effect] addNotification',
    addNotificationId = '[Effect] addNotificationId',
    addSimpleNotification = '[Effect] addSimpleNotification',
    RemoveNotification = '[Effect] RemoveNotification',
    RemoveAllNotifications = '[Effect] RemoveAllNotifications',
    RemoveNotificationFromStore = '[Effect] RemoveNotificationFromStore',
    addNotificationToHistory= '[Reducer] addNotificationToHistory'
}

export const addNotification =
    createAction(ToastMessageActionTypes.addNotification, props<{payload: ToastMessage}>());

export const addNotificationId =
    createAction(ToastMessageActionTypes.addNotificationId, props<{payload: {toastType: ToastIdTypes, toastId: number}}>());

export const addSimpleNotification =
    createAction(ToastMessageActionTypes.addSimpleNotification, props<{payload: ToastMessage}>());

export const removeNotification =
    createAction(ToastMessageActionTypes.RemoveNotification, props<{payload: ToastIdTypes}>());

export const removeAllNotifications =
    createAction(ToastMessageActionTypes.RemoveAllNotifications);

export const removeNotificationFromStore =
    createAction(ToastMessageActionTypes.RemoveNotificationFromStore, props<{payload: ToastIdTypes}>());

export const addNotificationToHistory =
    createAction(ToastMessageActionTypes.addNotificationToHistory, props<{notification: Notification}>());
