import { createAction, props } from '@ngrx/store';
import { DynamicMessageDialogConfig } from '../../core/models';
import { Type } from '@angular/core';
import { Confirmation } from 'primeng/api';
import { DynamicDialogConfig } from 'primeng/dynamicdialog';

export enum OverlayActionTypes {
    OpenDialog = '[Dialog] [Effect] Open dialog',
    CloseDialog = '[Dialog] [Effect] Close dialog',
    CloseDialogsByName = '[Dialog] [Effect] Close specified dialogs',
    ShowMessage = '[MessageDialog] [Effect] Show message',
    ConfirmDialog = '[ConfirmDialog] [Effect] Show confirmation dialog',
    closeAllDialogs = '[Dialog] [Effect] Close All dialog',
}

export const openDialog = createAction(
    OverlayActionTypes.OpenDialog,
    props<{componentType: Type<any>, config: DynamicDialogConfig}>()
);

export const closeOutdatedDeltaPlanDialog = createAction(
    OverlayActionTypes.CloseDialog
);

export const closeDialogsByName = createAction(
    OverlayActionTypes.CloseDialogsByName,
    props<{closedComponent: any}>()
);

export const closeAllDialogs= createAction(
    OverlayActionTypes.closeAllDialogs,
);

export const showMessage = createAction(
    OverlayActionTypes.ShowMessage,
    props<DynamicMessageDialogConfig>()
);

export const confirmDialog = createAction(
    OverlayActionTypes.ConfirmDialog,
    props<Confirmation>()
);
