import { Pipe, PipeTransform } from '@angular/core';
import {
    ChannelMapping,
    ExternalSystemModule,
    Lna,
    MhaAld, RaeAld,
    Raeu, RetAld,
    Retu,
    RmodConnector,
    VduCell
} from '@oam/cu-asm-api/models';
import { get } from 'lodash';

@Pipe({ name: 'getClassExtension', pure: true })
export class GetClassExtensionPipe implements PipeTransform {
    transform(
        isSelected = false,
        selectedVduCell?: VduCell | null,
        rmodRId?: string,
        selectedSmod?: ExternalSystemModule | null,
        rmodSmodExtRId?: string,
        selectedRetu?: { retu: Retu, retId: string } | null,
        selectedRetAld?: RetAld | null,
        selectedRaeu?: { raeu: Raeu, raeId: string } | null,
        selectedRaeAld?: RaeAld | null,
        selectedLna?: { lna: Lna, mhaId: string } | null,
        selectedMhaAld?: MhaAld | null
    ): string {
        let extentionClass = '';
        if (isSelected) extentionClass = ' selected';
        const cellCondition = selectedVduCell && !!selectedVduCell.channelsMapping.find(
            (channel: ChannelMapping) => channel.rmodRId === rmodRId
        );

        const smodCondition = selectedSmod && (selectedSmod.smodExtRId === rmodSmodExtRId);
        const retuCondition = selectedRetu && selectedRetu.retu && selectedRetu.retu.antennaList
            && !!selectedRetu.retu.antennaList.find(
                (connector: RmodConnector) => connector.rmodRId === rmodRId
            );
        const raeuCondition = selectedRaeu && selectedRaeu.raeu && selectedRaeu.raeu.antennaList
            && !!selectedRaeu.raeu.antennaList.find(
                (connector: RmodConnector) => connector.rmodRId === rmodRId
            );

        const retAldCondition = isAldPortsIncludesRmodId(rmodRId, selectedRetAld);
        const raeAldCondition = isAldPortsIncludesRmodId(rmodRId, selectedRaeAld);

        const lnaCondition = selectedLna && selectedLna.lna &&
            selectedLna.lna.amplifiedAntennaLine.rmodRId === rmodRId;

        const mhaAldCondition = selectedMhaAld && selectedMhaAld.controlPorts
            && !!selectedMhaAld.controlPorts.find(
                (connector: RmodConnector) => connector.rmodRId === rmodRId
            );

        if (cellCondition || smodCondition || retuCondition || raeuCondition ||
            lnaCondition || mhaAldCondition || retAldCondition || raeAldCondition) extentionClass += ' RMOD-highlighted';

        return extentionClass;
    }

}

function isAldPortsIncludesRmodId (rmodRId: string | undefined, ald?: RetAld | RaeAld | null): RmodConnector | undefined {
    const aldPorts: RmodConnector[] = get(ald, 'controlPorts', []);

    return aldPorts.find((connector: RmodConnector) => connector.rmodRId === rmodRId);
}
