import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { NGXLogger } from 'ngx-logger';
import { UiConfig, uiKeyPrefix, UiModelSettingsType } from '@oam/core/lib/services/model/';
import { schema } from './model/appSettingsSchema';
import Ajv from 'ajv';
import { APP_SETTINGS_FILE_EXTENSION } from '@oam/core/lib/models/app-settings.model';

export const REGISTRY_KEY = 'wnls-settings-registry';

export type Registry = Set<string>;

export type ParseResult = {
    content: object[];
    isValid: boolean;
};

@Injectable({
  providedIn: 'root'
})

export class AppSettingsService {
    constructor(private readonly logger: NGXLogger) {
    }

    private static addPrefix(keyName: string): string {
        return `${uiKeyPrefix}${keyName}`;
    }

    generateUiSettingsFile(): string {
        const registry = Array.from(this.loadRegistry());

        const entries = registry.map((elem: string): UiModelSettingsType => ({
            name: elem,
            layoutConfig: JSON.parse(localStorage.getItem(elem) as string)
        }));

        const content = JSON.stringify(entries);

        return btoa(encodeURIComponent(content));
    }

    downloadUiSettingsFile(): Observable<Blob> {
        const blob = new Blob(
            [this.generateUiSettingsFile()],
            { type: 'text/plain' });

        return of(blob);
    }

    restoreDefaultUiSettings() {
        const registry = this.loadRegistry();
        registry.forEach(item => {
            localStorage.removeItem(item);
        });
        this.saveRegistry(new Set());
    }

    importUiSettings(content: object[]): void {
        if (!content.length) {
            this.restoreDefaultUiSettings();
        }

        content.forEach((elem: { name: string; layoutConfig: UiConfig; }) => {
            const key = elem.name.substring(uiKeyPrefix.length);
            this.saveUiSettings(key, elem.layoutConfig);
        });
    }

    validateJsonSchema(data: object): any {
        const ajv = new Ajv();
        const validation =  ajv.addSchema(schema, 'appSettingsSchema').validate('appSettingsSchema', data);

        if (!validation) this.logger.log(ajv.errorsText());

        return validation;
    }

    readFile(file: File): Observable<object> {
        return new Observable((obs: any) => {
            const isExtensionValid = file.name.endsWith(APP_SETTINGS_FILE_EXTENSION);
            if (!(file instanceof File && isExtensionValid)) {
                obs.error(`Invalid file extension: ${file.name} (expected ${APP_SETTINGS_FILE_EXTENSION})`);
            }

            const reader = new FileReader();
            reader.readAsText(file);
            reader.onerror = err => obs.error(err);
            reader.onabort = err => obs.error(err);
            reader.onload = () => {
                try {
                    const parsedContent = JSON.parse(decodeURIComponent(atob(reader.result as string)));
                    obs.next(parsedContent);
                } catch (e) {
                    obs.error(e);
                }
            };
            reader.onloadend = () => obs.complete();
        });
    }

    validate(fileContent: Observable<object>): Observable<ParseResult> {
        return fileContent.pipe(map((content: object[]) => {
            const isValid = (): boolean => {
                return this.validateJsonSchema(content);
            };

            return {
                content,
                isValid: isValid()
            };
        }));
    }

    readAndValidate(file: File): Observable<ParseResult> {
        return this.validate(this.readFile(file));
    }

    loadRegistry(): Registry {
        let registry: string[] = [];
        const registryEntry = localStorage.getItem(REGISTRY_KEY);

        if (registryEntry) {
            registry = JSON.parse(registryEntry);
        }

        if (!Array.isArray(registry)) {
            registry = [];
        }

        return new Set(registry);
    }

    saveRegistry(registry: Registry) {
        localStorage.setItem(REGISTRY_KEY, JSON.stringify(Array.from(registry)));
    }

    addKeyToRegistry(key: string) {
        const currentRegistry = this.loadRegistry();
        currentRegistry.add(key);
        this.saveRegistry(currentRegistry);
    }

    removeKeyFromRegistry(key: string) {
        const currentRegistry = this.loadRegistry();
        currentRegistry.delete(key);
        this.saveRegistry(currentRegistry);
    }

    saveUiSettings(keyName: string, uiSettings: UiConfig): void {
        if (keyName) {
            localStorage.setItem(AppSettingsService.addPrefix(keyName), JSON.stringify(uiSettings));
            this.addKeyToRegistry(AppSettingsService.addPrefix(keyName));
        }
    }

    deleteItemFromLocalStorageByKey(keyName: string): void {
        const storageKeyName = AppSettingsService.addPrefix(keyName);

        localStorage.removeItem(storageKeyName);
        this.removeKeyFromRegistry(storageKeyName);
    }

    getUiSettings(keyName: string, defaultUi: UiConfig): UiConfig {
        const item = localStorage.getItem(AppSettingsService.addPrefix(keyName)) as string;

        if (!item) {
            return defaultUi;
        }

        return JSON.parse(item);
    }
}
