import { Injectable } from '@angular/core';
import { Role, RoleAccessService } from '@oam/role-access-core';
import { ChildOperationRoles } from '@oam/configuration-shared/lib/models/child-operation-roles';
import {
    DEFAULT_CHANGE_TO_PLAN_CONFIGURATION_MESSAGE,
    FORBIDDEN_CHANGE_TO_PLAN_CONFIGURATION_MESSAGE,
    NO_CHILD_DEFINED
} from '@oam/configuration-shared/lib/core/constants/configuration-shared.constants';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { find, includes, some } from 'lodash';
import { RoleAccessForbiddenFlagEnum } from '@oam/core/lib/models/role-access/role-access-forbidden-flag.enmu';

@Injectable({
    providedIn: 'root'
})
export class RoleAccessPeService {

    constructor(private roleAccessService: RoleAccessService, private vnfcSharedService: VnfcSharedService) {
    }

    public isParameterEditableForCurrentRole(secAdmOnly: boolean | undefined): boolean {
        if (this.vnfcSharedService.isVmcu()) return true;
        if (this.roleAccessService.readOnlyMode || this.roleAccessService.currentRole === Role.READ_ONLY_USER) return false;
        if (this.roleAccessService.currentRole === Role.SYSTEM_USER) return true;

        return secAdmOnly ? this.roleAccessService.currentRole === Role.SEC_USER : this.roleAccessService.currentRole === Role.APP_USER;
    }

    public isMoEditableForCurrentRole(operationRoles: string[]) {
        if (this.vnfcSharedService.isVmcu()) return true;
        if (this.roleAccessService.readOnlyMode || this.roleAccessService.currentRole === Role.READ_ONLY_USER) return false;
        if (this.roleAccessService.currentRole === Role.SYSTEM_USER) return true;

        return includes(operationRoles, this.roleAccessService.currentRole);
    }

    public isAnyChildMoEditableForCurrentRole(childMoOperationRoles: ChildOperationRoles[]) {
        if (this.vnfcSharedService.isVmcu()) return true;
        if (this.roleAccessService.readOnlyMode || this.roleAccessService.currentRole === Role.READ_ONLY_USER) return false;
        if (this.roleAccessService.currentRole === Role.SYSTEM_USER) return true;
        // we should keep origin button status if one have no child definition in NIDD
        if (this.isNoChildDefined(childMoOperationRoles)) return true;

        return some(childMoOperationRoles, (mo) => {
            return includes(mo.operationRoles, this.roleAccessService.currentRole);
        });
    }

    private isNoChildDefined(childMoOperationRoles: ChildOperationRoles[]): boolean {
        return !!find(childMoOperationRoles, (operationRoles) => {
            return operationRoles.moName === NO_CHILD_DEFINED;
        });
    }

    public isChangeToPlanedConfigurationAllowedForCurrentRole(): boolean {
        return !this.roleAccessService.isForbiddenForCurrentRole(RoleAccessForbiddenFlagEnum.CHANGE_TO_PLANNED_CONFIGURATION);
    }

    public getChangeToPlanedConfigurationMessageForCurrentRole(): string {
        return this.isChangeToPlanedConfigurationAllowedForCurrentRole()? DEFAULT_CHANGE_TO_PLAN_CONFIGURATION_MESSAGE : FORBIDDEN_CHANGE_TO_PLAN_CONFIGURATION_MESSAGE;
    }
}
