import { ChangeDetectorRef, Directive, ElementRef, Input, OnInit } from '@angular/core';
import {
    DISABLED_DEFAULT_PATH,
    NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE,
    RoleAccessService,
    TITLE_DEFAULT_PATH
} from '@oam/role-access-core';
import { get, set } from 'lodash';
import { RoleAccessConfig } from '@oam/role-access-ng/lib/role-access.config';

/**
 * @param {string} roleAccessFlag - Function name
 * @param {string} [disabledPath="disabled"] - The path of the disabled attribute of the host instance
 * @param {string} [titlePath="title"] - The path of the title attribute of the host instance
 */
export class RoleAccessInputModel {
    roleAccessFlag: string;
    disabledPath?: string;
    titlePath?: string;
}

interface InputModel {
    roleAccessFlag: string;
    disabledPath: string;
    titlePath: string;
}

/**
 * @description
 * oamRoleAccessControl control function operation permission based on user role
 *
 * @usageNotes
 * Used when the host instance is encapsulated by components
 *
 * ```html
 * <ui-webem-button
 *      [oamRoleAccessControl]="'snapshot'"
 * </ui-webem-button>
 * ```
 */
@Directive({
    selector: '[oamRoleAccessControl]'
})
export class RoleAccessControlDirective implements OnInit {
    private isForbiddenForCurrentRole = false;
    private inputModel: InputModel = {
        roleAccessFlag: '',
        disabledPath: DISABLED_DEFAULT_PATH,
        titlePath: TITLE_DEFAULT_PATH
    };

    @Input() titleOnHost = false;
    @Input() hostInstance: any;

    @Input() set oamRoleAccessControl(inputModel: string | InputModel) {
        let roleAccessFlag = this.inputModel.roleAccessFlag;

        if (typeof inputModel === 'object') {
            this.inputModel = { ...this.inputModel, ...inputModel };
        } else {
            roleAccessFlag = inputModel;
        }

        if (this.roleAccessService.isForbiddenForCurrentRole(roleAccessFlag)) {
            this.isForbiddenForCurrentRole = true;
        }
    }

    constructor(
        private readonly roleAccessConfig: RoleAccessConfig,
        private readonly roleAccessService: RoleAccessService,
        private readonly cd: ChangeDetectorRef,
        public readonly el: ElementRef
    ) {
    }

    ngOnInit(): void {
        if (!this.hostInstance) {
            throw new Error('The host component must be one of [' + 'ButtonComponent,' + 'Checkbox]');
        }
    }

    ngAfterViewChecked() {
        if (!this.isForbiddenForCurrentRole || !this.isToUpdateValuesDifferentWithCurrent()) return;

        set(this.hostInstance, this.inputModel.disabledPath, true);
        if (this.titleOnHost) {
            this.el.nativeElement.title = NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE;
        } else {
            set(this.hostInstance, this.inputModel.titlePath, NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE);
        }

        this.cd.detectChanges();
    }

    isToUpdateValuesDifferentWithCurrent() {
        return get(this.hostInstance, this.inputModel.disabledPath) !== NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE
            || (this.titleOnHost && this.el.nativeElement.title !== NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE)
            || get(this.hostInstance, this.inputModel.titlePath) !== true;
    }
}
