import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ElementRef,
    OnDestroy
} from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { MenuAction } from '../../../button-with-menu/model/menu-action';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { selectCommissioningFlagStatus } from '@oam/commissioning-flag-store/lib/core/selectors/select-commissioning-status';
import { RoleAccessForbiddenFlagEnum } from '@oam/core/lib/models/role-access/role-access-forbidden-flag.enmu';
import { CuCell } from '@oam/cu-asm-api';

@Component({
    selector: 'ui-webem-group-tile',
    templateUrl: './group-tile.component.html',
    styleUrls: ['./group-tile.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class GroupTileComponent implements OnInit, OnDestroy {
    @Input() title: string;
    @Input() container?: ElementRef;
    @Input() cells: CuCell[];
    @Input() isVdu: boolean;

    @Output() lockAction = new EventEmitter<void>();
    @Output() unlockAction = new EventEmitter<void>();
    @Output() goToPe = new EventEmitter<void>();

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    private commissioningSubscription: Subscription;
    isCommissioningInProgress: boolean;
    readonly optionModel: ButtonModel = {
        title: 'Actions',
        icon: 'mdi mdi-dots-vertical',
        theme: ButtonTheme.Secondary
    };

    menuOptions: MenuAction[];

    constructor(private readonly store: Store) {
    }

    ngOnInit(): void {
        this.commissioningSubscription = this.store.pipe(
          takeUntil(this.ngUnsubscribe),
          select(selectCommissioningFlagStatus)
        )
          .subscribe(data => {
              this.isCommissioningInProgress = data;
              this.setMenu();
          });
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    shouldButtonDisabled(administrativeState: 'Locked'|'Unlocked'): boolean {
        return !this.isVdu && this.cells.every(cell => cell.basicCell.administrativeState === administrativeState);
    }

    setMenu(): void {
        this.menuOptions = [
            {
                label: 'Lock cell group',
                icon: 'mdi mdi-lock',
                command: () => this.lockAction.emit(),
                disabled: this.isCommissioningInProgress || this.shouldButtonDisabled('Locked'),
                roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
            },
            {
                label: 'Unlock cell group',
                icon: 'mdi mdi-lock-open-variant-outline mdi-16px',
                command: () => this.unlockAction.emit(),
                disabled: this.isCommissioningInProgress || this.shouldButtonDisabled('Unlocked'),
                roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
            },
            {
                label: 'Go to PE',
                icon: 'mdi mdi-import',
                command: () => this.goToPe.emit()
            }
        ];
    }
}
