/* tslint:disable */
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BaseService } from '../base-service';
import { ApiConfiguration } from '../api-configuration';
import { StrictHttpResponse } from '../strict-http-response';
import { RequestBuilder } from '../request-builder';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

import { LoginRequest } from '../models/login-request';
import { LoginResponse } from '../models/login-response';
import { SsoLoginResponse } from '../models/sso-login-response';

@Injectable({
  providedIn: 'root',
})
export class AuthenticationService extends BaseService {
  constructor(
    config: ApiConfiguration,
    http: HttpClient
  ) {
    super(config, http);
  }

  /**
   * Path part for operation loginPost
   */
  static readonly LoginPostPath = '/login';

  /**
   * Allows users to authenticate and receive an authentication token (JWT) for further API access.
   *
   * Authentication and token generation<br> <br> Authentication is actually performed by rcpum, for Authentication failure case, rcpum may send an additional status code in "additionalStatus" field of the error response structure, the original http status code from rcpum is stored in "status" filed of the error response structure. apigw will not change the error response structure.<br> As WebEM is not able to handle other new error case status codes than {401 403 405 460 461 462}, apigw will convert all other rcpum authentication failure status code to HTTP status code "401".<br> <br> The additional status codes are the following:
   *  - 7  Authentication failed
   *  - 13 User account is expired
   *  - 101 Authentication failed and the account was locked because of that
   *  - 102 Account is locked because of too many authentication failures
   *  - 103 User account is invalid due to expired password
   *  - 104 Too many simultaneous sessions in the system
   *  - 105 Too many simultaneous sessions for the user
   *  - 106 Account is locked by the administrator
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `loginPost()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  loginPost$Response(params: {

    /**
     * Authorization containing the word Basic followed by a space and a base64-encoded string username:password
     */
    Authorization: string;
  
    /**
     * Indicate Optional Parameters
     */
    body?: LoginRequest
  }): Observable<StrictHttpResponse<LoginResponse>> {

    const rb = new RequestBuilder(this.rootUrl, AuthenticationService.LoginPostPath, 'post');
    if (params) {

      rb.header('Authorization', params.Authorization, {});

      rb.body(params.body, 'application/json');
    }
    return this.http.request(rb.build({
      responseType: 'json',
      accept: 'application/json'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return r as StrictHttpResponse<LoginResponse>;
      })
    );
  }

  /**
   * Allows users to authenticate and receive an authentication token (JWT) for further API access.
   *
   * Authentication and token generation<br> <br> Authentication is actually performed by rcpum, for Authentication failure case, rcpum may send an additional status code in "additionalStatus" field of the error response structure, the original http status code from rcpum is stored in "status" filed of the error response structure. apigw will not change the error response structure.<br> As WebEM is not able to handle other new error case status codes than {401 403 405 460 461 462}, apigw will convert all other rcpum authentication failure status code to HTTP status code "401".<br> <br> The additional status codes are the following:
   *  - 7  Authentication failed
   *  - 13 User account is expired
   *  - 101 Authentication failed and the account was locked because of that
   *  - 102 Account is locked because of too many authentication failures
   *  - 103 User account is invalid due to expired password
   *  - 104 Too many simultaneous sessions in the system
   *  - 105 Too many simultaneous sessions for the user
   *  - 106 Account is locked by the administrator
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `loginPost$Response()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  loginPost(params: {

    /**
     * Authorization containing the word Basic followed by a space and a base64-encoded string username:password
     */
    Authorization: string;
  
    /**
     * Indicate Optional Parameters
     */
    body?: LoginRequest
  }): Observable<LoginResponse> {

    return this.loginPost$Response(params).pipe(
      map((r: StrictHttpResponse<LoginResponse>) => r.body as LoginResponse)
    );
  }

  /**
   * Path part for operation ssologinPost
   */
  static readonly SsologinPostPath = '/ssologin';

  /**
   * SingleSignOn interface of APIGW to be used by northbound services for token generation.
   *
   * Authentication and token generation for NMS SSO with local username and password <br> Authentication is actually performed by rcpum, for Authentication failure case, rcpum may send an additional status code in "additionalStatus" field of the error response structure, the original http status code from rcpum is stored in "status" filed of the error response structure. apigw will not change the error response structure.<br> As WebEM is not able to handle other new error case status codes than {401 403 405 460 461 462}, apigw will convert all other rcpum authentication failure status code to HTTP status code "401".<br> <br> The additional status codes are the following:
   *  - 7  Authentication failed
   *  - 13 User account is expired
   *  - 101 Authentication failed and the account was locked because of that
   *  - 102 Account is locked because of too many authentication failures
   *  - 103 User account is invalid due to expired password
   *  - 104 Too many simultaneous sessions in the system
   *  - 105 Too many simultaneous sessions for the user
   *  - 106 Account is locked by the administrator
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `ssologinPost()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  ssologinPost$Response(params: {

    /**
     * Authorization containing the word Basic followed by a space and a base64-encoded string username:password
     */
    Authorization: string;
  
    /**
     * Indicate Optional Parameters
     */
    body?: LoginRequest
  }): Observable<StrictHttpResponse<SsoLoginResponse>> {

    const rb = new RequestBuilder(this.rootUrl, AuthenticationService.SsologinPostPath, 'post');
    if (params) {

      rb.header('Authorization', params.Authorization, {});

      rb.body(params.body, 'application/json');
    }
    return this.http.request(rb.build({
      responseType: 'json',
      accept: 'application/json'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return r as StrictHttpResponse<SsoLoginResponse>;
      })
    );
  }

  /**
   * SingleSignOn interface of APIGW to be used by northbound services for token generation.
   *
   * Authentication and token generation for NMS SSO with local username and password <br> Authentication is actually performed by rcpum, for Authentication failure case, rcpum may send an additional status code in "additionalStatus" field of the error response structure, the original http status code from rcpum is stored in "status" filed of the error response structure. apigw will not change the error response structure.<br> As WebEM is not able to handle other new error case status codes than {401 403 405 460 461 462}, apigw will convert all other rcpum authentication failure status code to HTTP status code "401".<br> <br> The additional status codes are the following:
   *  - 7  Authentication failed
   *  - 13 User account is expired
   *  - 101 Authentication failed and the account was locked because of that
   *  - 102 Account is locked because of too many authentication failures
   *  - 103 User account is invalid due to expired password
   *  - 104 Too many simultaneous sessions in the system
   *  - 105 Too many simultaneous sessions for the user
   *  - 106 Account is locked by the administrator
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `ssologinPost$Response()` instead.
   *
   * This method sends `application/json` and handles request body of type `application/json`.
   */
  ssologinPost(params: {

    /**
     * Authorization containing the word Basic followed by a space and a base64-encoded string username:password
     */
    Authorization: string;
  
    /**
     * Indicate Optional Parameters
     */
    body?: LoginRequest
  }): Observable<SsoLoginResponse> {

    return this.ssologinPost$Response(params).pipe(
      map((r: StrictHttpResponse<SsoLoginResponse>) => r.body as SsoLoginResponse)
    );
  }

  /**
   * Path part for operation logoutPost
   */
  static readonly LogoutPostPath = '/logout';

  /**
   * Allows user to logout from a specific session.
   *
   * User specific session deletion : session jwt required.
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `logoutPost()` instead.
   *
   * This method doesn't expect any request body.
   */
  logoutPost$Response(params: {

    /**
     * Authorization containing the word Bearer followed by a space and a token
     */
    Authorization: string;

  }): Observable<StrictHttpResponse<void>> {

    const rb = new RequestBuilder(this.rootUrl, AuthenticationService.LogoutPostPath, 'post');
    if (params) {

      rb.header('Authorization', params.Authorization, {});

    }
    return this.http.request(rb.build({
      responseType: 'text',
      accept: '*/*'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return (r as HttpResponse<any>).clone({ body: undefined }) as StrictHttpResponse<void>;
      })
    );
  }

  /**
   * Allows user to logout from a specific session.
   *
   * User specific session deletion : session jwt required.
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `logoutPost$Response()` instead.
   *
   * This method doesn't expect any request body.
   */
  logoutPost(params: {

    /**
     * Authorization containing the word Bearer followed by a space and a token
     */
    Authorization: string;

  }): Observable<void> {

    return this.logoutPost$Response(params).pipe(
      map((r: StrictHttpResponse<void>) => r.body as void)
    );
  }

}
