import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import {
    FormParameters,
    ValidationRules
} from '../interface/user-information-change.interface';
import {
    hasBiggerLengthThanMin, hasDisallowedUserNameAsciiChar,
    hasDisallowedPasswordAsciiChar,
    hasDisallowedSpecialAsciiChar,
    hasMinimumDigitNumbers,
    hasMinNonAlphanumericChar,
    hasSmallerLengthThanMax, hasTwoSameConsecutiveChar, hasUpperAndLowerCase,
    isPasswordNotSameAsUserName, isUserNameNotIncludedInPassword, hasUpperCase, hasLowerCase
} from '../utils/validation.utils';

export class UserInformationValidator {
    static passwordsMatch(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const password = (control.get(FormParameters.NEW_PASSWORD) as AbstractControl).value;
            const confirmPassword = (control.get(FormParameters.CONFIRM_PASSWORD) as AbstractControl).value;

            return password && confirmPassword && password !== confirmPassword ?
                { [ValidationRules.PASSWORDS_MATCH]: true } :
                null;
        };
    }

    static passwordNotSameAsUsername(currentUsername: string): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const password = (control.get(FormParameters.NEW_PASSWORD) as AbstractControl).value;
            const username = (control.get(FormParameters.USERNAME) as AbstractControl).value;
            const usernameToCheck = username || currentUsername;

            return password && usernameToCheck && !isPasswordNotSameAsUserName(password, usernameToCheck) || !password ?
                { [ValidationRules.PASSWORD_NOT_SAME_AS_USERNAME]: true } :
                null;
        };
    }

    static passwordNotContainUsername(currentUsername: string): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const password = (control.get(FormParameters.NEW_PASSWORD) as AbstractControl).value;
            const username = (control.get(FormParameters.USERNAME) as AbstractControl).value;
            const usernameToCheck = username || currentUsername;

            return password && usernameToCheck && !isUserNameNotIncludedInPassword(password, usernameToCheck) || !password ?
                { [ValidationRules.PASSWORD_NOT_CONTAIN_USERNAME]: true } :
                null;
        };
    }

    static minLength(minLength: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasBiggerLengthThanMin(value, minLength) || !value ?
                { [ValidationRules.MIN_LENGTH]: true} :
                null;
        };
    }

    static maxLength(maxLength: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasSmallerLengthThanMax(value, maxLength) || !value ?
                { [ValidationRules.MAX_LENGTH]: true } :
                null;
        };
    }

    static noConsecutiveCharacters(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return hasTwoSameConsecutiveChar(value) || !value ?
                { [ValidationRules.CONSECUTIVE_CHAR]: true } :
                null;
        };
    }

    static minimumDigitNumbers(minNumberOfDigit: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasMinimumDigitNumbers(value, minNumberOfDigit) || !value ?
                { [ValidationRules.DIGIT_NUMBER]: true } :
                null;
        };
    }

    static upperCaseAndLowerCase(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasUpperAndLowerCase(value) || !value ?
                { [ValidationRules.LOWER_UPPER_CASE]: true } :
                null;
        };
    }

    static upperCase(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasUpperCase(value) || !value ?
                { [ValidationRules.UPPER_CASE]: true } :
                null;
        };
    }

    static lowerCase(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasLowerCase(value) || !value ?
                { [ValidationRules.LOWER_CASE]: true } :
                null;
        };
    }

    static disallowedUserNameAsciiCharacters(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return hasDisallowedUserNameAsciiChar(value) || !value ?
                { [ValidationRules.DISALLOWED_ASCII]: true } :
                null;
        };
    }

    static disallowedPasswordAsciiCharacters(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return hasDisallowedPasswordAsciiChar(value) || !value ?
                { [ValidationRules.DISALLOWED_ASCII]: true } :
                null;
        };
    }

    static minNonAlphaNumerical(minNonAlphaNumerical: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return !hasMinNonAlphanumericChar(value, minNonAlphaNumerical) || !value ?
                { [ValidationRules.NON_ALPHA_NUMBER]: true } :
                null;
        };
    }

    static disallowedSpecialAsciiCharacters(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const value = control.value;

            return hasDisallowedSpecialAsciiChar(value) || !value ?
                { [ValidationRules.DISALLOWED_ASCII]: true } :
                null;
        };
    }
}
