import { ChangeDetectorRef, Component, OnInit, OnDestroy, Input, ChangeDetectionStrategy, OnChanges, SimpleChanges } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { select, Store } from '@ngrx/store';
import {
    ErrorMessage,
    IOption, ISnapshotOperationState,
    SNAPSHOT_LOCATION
} from '@oam/diagnostic-snapshot/core/models';
import { Observable, Subject } from 'rxjs';
import { distinctUntilChanged, filter, shareReplay, takeUntil, take } from 'rxjs/operators';
import { DEFAULT_LOCATION, DEFAULT_RANGE, LOCATION_VALUES, RANGE_VALUES } from '@oam/diagnostic-snapshot/core/config';
import { TimerServiceService } from '@oam/diagnostic-snapshot/core/services/timer-service.service';
import { TokenExpirationTimeService } from '@oam/core/lib/services/token-expiration-time.service';
import {
    selectCollectingSnapshotDuration,
    selectCollectingSnapshotErrorMessage,
    selectCollectingSnapshotStartedAt,
    selectCollectingSnapshotState,
    selectCollectingStatus,
    selectCollectSnapshotBtnTitle,
    selectSnapshotOperationStatus
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot.selector';
import { ButtonModel, ButtonTheme, ButtonThickness, confirmDialog, openDialog } from '@oam/cu-ui';
import { SessionTokenRenewalDialogComponent } from '@oam/cu-ui/lib/session-token-renewal/components/session-token-renewal-dialog/session-token-renewal-dialog.component';
import {
    cleanCollectSnapshotResultState,
    cleanSnapshotStateIfCollectingIsNotOngoing,
    setSnapshotFormValues,
    startCancellingCollectionLocalSnapshot,
    startCancellingCollectionRemoteSnapshot,
    startCollectingLocalSnapshot,
    startCollectingRemoteSnapshot,
    updateSnapshotCancelState
} from '@oam/diagnostic-snapshot/core/actions/snapshot.action';
import { confirmDialogExclamationIcon } from '@oam/shared';
import { selectTokenRenewalSuccessful } from '@oam/auth/lib/auth/core/reducers/login.reducer';
import {
    selectSnapshotServerConnectionFailure
} from '@oam/diagnostic-snapshot/core/selectors/collecting-snapshot-server-status.selector';
import { selectTransferSnapshotIsOngoing } from '@oam/diagnostic-snapshot/core/selectors/transfer-snapshot-to-nms.selector';

@Component({
    selector: 'diagnostic-snapshot-collection',
    templateUrl: './diagnostic-snapshot-collection.component.html',
    styleUrls: ['./diagnostic-snapshot-collection.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DiagnosticSnapshotCollectionComponent implements OnInit, OnDestroy {
    @Input() isSnapshotIdle: boolean;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    readonly startCollectingSnapshotButtonModel: ButtonModel = {
        label: 'Collect Snapshot',
        theme: ButtonTheme.Accent,
        thickness: ButtonThickness.Default
    };

    readonly stopCollectingSnapshotButtonModel: ButtonModel = {
        label: 'Cancel',
        theme: ButtonTheme.Default,
        icon: 'mdi mdi-15px mdi-window-close',
        thickness: ButtonThickness.Default
    };
    collectSnapshotForm: FormGroup;
    coverageValues: IOption[];
    locationValues: IOption[];

    collectSnapshotBtnTitle$: Observable<string>;
    collectingSnapshotDurationTimer$: Observable<number>;
    collectingSnapshotDuration$: Observable<number>;
    selectCollectingSnapshotErrorMessage$: Observable<ErrorMessage | undefined>;
    snapshotServerConnectionFailure$: Observable<boolean>;
    selectCollectingStatus$: Observable<string | undefined>;
    selectSnapshotOperationStatus$: Observable<ISnapshotOperationState>;
    isTransferSnapshotOngoing$: Observable<boolean>;

    constructor(
        private readonly store: Store,
        private readonly timerService: TimerServiceService,
        private readonly cd: ChangeDetectorRef,
        private readonly tokenExpirationService: TokenExpirationTimeService
    ) {
        this.coverageValues = RANGE_VALUES;
        this.locationValues = LOCATION_VALUES;

        this.collectSnapshotForm = new FormGroup({
            coverage: new FormControl(DEFAULT_RANGE.value, [Validators.required]),
            location: new FormControl(DEFAULT_LOCATION.value, [Validators.required]),
            filterRegx: new FormControl(''),
            reason: new FormControl('')
        });
    }

    ngOnInit(): void {
        this.collectingSnapshotDurationTimer$ = this.timerService.setSnapshotDurationTimer(selectCollectingSnapshotStartedAt).pipe(
            takeUntil(this.ngUnsubscribe)
        );
        this.collectingSnapshotDuration$ = this.timerService.setSnapshotDuration(selectCollectingSnapshotDuration).pipe(
            takeUntil(this.ngUnsubscribe)
        );

        this.snapshotServerConnectionFailure$ = this.store.pipe(
            select(selectSnapshotServerConnectionFailure),
            takeUntil(this.ngUnsubscribe)
        );

        this.store.pipe(
            select(selectCollectingSnapshotState),
            take(1))
            .subscribe(({collectStatus, location, reason, filterRegx, coverage}) => {
                    if (collectStatus) {
                        this.collectSnapshotForm.patchValue({
                            location, reason, filterRegx, coverage
                        });
                    }
                }
            );

        this.selectSnapshotOperationStatus$ = this.store.pipe(
            select(selectSnapshotOperationStatus),
            takeUntil(this.ngUnsubscribe)
        );

        this.selectSnapshotOperationStatus$.subscribe(({isSnapshotOperationOngoing}) => {
            isSnapshotOperationOngoing ?
                this.collectSnapshotForm.disable({ emitEvent: false }) :
                this.collectSnapshotForm.enable({ emitEvent: false });
        });

        this.collectSnapshotForm.valueChanges.pipe(
            takeUntil(this.ngUnsubscribe),
            distinctUntilChanged()
        ).subscribe(() => {
            this.store.dispatch(cleanCollectSnapshotResultState());
        });

        this.selectCollectingStatus$ = this.store.pipe(
            select(selectCollectingStatus),
            takeUntil(this.ngUnsubscribe)
        );

        this.collectSnapshotBtnTitle$ = this.store.pipe(
            select(selectCollectSnapshotBtnTitle),
            takeUntil(this.ngUnsubscribe)
        );

        this.selectCollectingSnapshotErrorMessage$ = this.store.pipe(
            select(selectCollectingSnapshotErrorMessage),
            takeUntil(this.ngUnsubscribe)
        );

        this.store.select(selectTokenRenewalSuccessful)
            .pipe(
                takeUntil(this.ngUnsubscribe),
                filter(isRenewalSuccessful =>
                    !!isRenewalSuccessful?.status
                    && !!isRenewalSuccessful?.shouldStartOperationAfterTokenRenewal))
            .subscribe(() => {
                this.startCollectingSnapshot();
            });

        this.isTransferSnapshotOngoing$ = this.store.pipe(
            select(selectTransferSnapshotIsOngoing),
            takeUntil(this.ngUnsubscribe)
        );
    }

    ngOnDestroy(): void {
        this.store.dispatch(cleanSnapshotStateIfCollectingIsNotOngoing());
        this.store.dispatch(updateSnapshotCancelState());
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    showConfirmationModal() {
        this.store.dispatch(confirmDialog({
            header: 'Cancel Snapshot',
            key: 'snapshotConfirmationDialog',
            message: 'You are about to cancel Snapshot collection\n\n Are you sure you want to proceed?',
            icon: confirmDialogExclamationIcon,
            rejectLabel: 'Cancel',
            accept: this.onYesButtonClick
        }));
    }

    onYesButtonClick = (): void => {
        this.snapshotLocationIsLocal() ?
            this.store.dispatch(startCancellingCollectionLocalSnapshot()) :
            this.store.dispatch(startCancellingCollectionRemoteSnapshot());
    };

    startCollectingSnapshot() {
        this.store.dispatch(setSnapshotFormValues({
            reason: this.collectSnapshotForm.value.reason,
            coverage: this.collectSnapshotForm.value.coverage,
            filterRegx: this.collectSnapshotForm.value.filterRegx,
            location: this.collectSnapshotForm.value.location
        }));

        this.snapshotLocationIsLocal() ?
            this.store.dispatch(startCollectingLocalSnapshot()) :
            this.store.dispatch(startCollectingRemoteSnapshot());
    }

    checkSessionTimeAndStartCollectingSnapshot() {
        if (this.snapshotLocationIsLocal() &&
            this.tokenExpirationService.isRemainingTimeLessThan40Minutes()) {
            this.openRenewTokenDialog();
        } else {
            this.startCollectingSnapshot();
        }
    }

    openRenewTokenDialog(): void {
        this.store.dispatch(openDialog({
            componentType: SessionTokenRenewalDialogComponent,
            config: {
                header: 'Session token renewal',
                closable: false,
                data: {
                    startSnapshot: this.startCollectingSnapshot.bind(this)
                }
            }
        }));
    }

    snapshotLocationIsLocal = (): boolean => {
        return this.collectSnapshotForm.value.location === SNAPSHOT_LOCATION.LOCAL;
    };
}
