import { ChangeDetectorRef, Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { delay, filter, map, mergeMap } from 'rxjs/operators';
import { OutletUrlPathService } from '@oam/core/lib/services/outlet-url-path.service';
import { ButtonEvent } from '@oam/cu-ui';
import { isDetailPanelVisible } from '@oam/details-panel/lib/core/selectors/detail-panel.selectors';
import {
    setDetailsPanelResizingFlag,
    toggleDetailsPanelVisibility
} from '@oam/details-panel/lib/core/actions/detail-panel.actions';
import { SPLIT_VIEW_LOCATION } from '@oam/details-panel/lib/core/model/split-view-location.type';

export interface DetailsPanelButton {
    icon: string;
    label: string;
    title: string;
}

@Component({
    selector: 'webem-details-panel-toggle-button',
    templateUrl: './details-panel-toggle-button.component.html',
    styleUrls: ['./details-panel-toggle-button.component.scss']
})
export class DetailsPanelToggleButtonComponent implements OnInit, OnDestroy {
    currentRoute: string;
    topView?: string;
    bottomView?: string;
    detailPanelButton: DetailsPanelButton;
    private readonly panelAnimationDelay = 140;
    private buttonModelSub: Subscription;
    private routerEventsSub: Subscription;
    detailsPanelVisible: boolean;

    @Input()
    isOnSplitView = false;

    @Output()
    buttonClicked = new EventEmitter<ButtonEvent>();

    constructor(private readonly store: Store,
                private readonly router: Router,
                private readonly actRoute: ActivatedRoute,
                private readonly cd: ChangeDetectorRef,
                private readonly outletService: OutletUrlPathService) {
    }

    ngOnInit() {
        if (this.isOnSplitView) {
            this.subscribeToSplitViewDetailsPanelVisibilityChanges();
            this.subscribeToNavigationEndRouterEvent();
        } else {
            this.subscribeToDetailsPanelVisibilityChanges();
        }
    }

    ngOnDestroy() {
        this.buttonModelSub.unsubscribe();
        if (this.routerEventsSub) this.routerEventsSub.unsubscribe();
        this.cd.detach();
    }

    toggleDetailPanelVisibility(): void {
        this.buttonClicked.emit();

        if (this.isOnSplitView) {
            const splitViewRouterOutlet = this.actRoute.snapshot.outlet;

            this.currentRoute = this.router.url;
            this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP);
            this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM);

            this.store.dispatch(toggleDetailsPanelVisibility(
                { url: splitViewRouterOutlet === 'topView' ? this.topView : this.bottomView })
            );
        } else {
            this.store.dispatch(toggleDetailsPanelVisibility({ url: this.router.url }));
            this.store.dispatch(setDetailsPanelResizingFlag({ payload: true }));
        }
    }

    private subscribeToDetailsPanelVisibilityChanges(): void {
        this.buttonModelSub = this.store.pipe(
            select(isDetailPanelVisible, { url: this.router.url }),
            map(this.getButtonModel)
        ).subscribe((buttonModel) => {
            this.detailPanelButton = buttonModel;
            this.cd.detectChanges();
        });
    }

    private subscribeToSplitViewDetailsPanelVisibilityChanges(): void {
        const splitViewRouterOutlet = this.actRoute.snapshot.outlet;

        this.currentRoute = this.router.url;
        this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP);
        this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM);

        this.buttonModelSub = this.store.pipe(
            select(isDetailPanelVisible, {
                outlet: splitViewRouterOutlet,
                topView: this.topView,
                bottomView: this.bottomView
            }),
            delay(this.panelAnimationDelay),
            map(this.getButtonModel)
        ).subscribe((buttonModel) => {
            this.detailPanelButton = buttonModel;
            this.cd.detectChanges();
        });
    }

    private subscribeToNavigationEndRouterEvent(): void {
        this.routerEventsSub = this.router.events.pipe(
            filter(event => event instanceof NavigationEnd),
            mergeMap(() => {
                const splitViewRouterOutlet = this.actRoute.snapshot.outlet;

                this.currentRoute = this.router.url;
                this.topView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.TOP);
                this.bottomView = this.outletService.getOutletWebemRoutePath(this.currentRoute, SPLIT_VIEW_LOCATION.BOTTOM);

                return this.store.pipe(
                    select(isDetailPanelVisible, {
                        outlet: splitViewRouterOutlet,
                        topView: this.topView,
                        bottomView: this.bottomView
                    }),
                    delay(this.panelAnimationDelay),
                    map(this.getButtonModel)
                );
            })
        ).subscribe((buttonModel) => {
            this.detailPanelButton = buttonModel;
            this.cd.detectChanges();
        });
    }

    private getButtonModel = (visible: boolean): DetailsPanelButton => {
        this.detailsPanelVisible = visible;

        return visible ? {
            title: 'Close details panel',
            label: ' ',
            icon: 'mdi mdi-chevron-right'
        } : {
            title: 'Open details panel',
            label: ' ',
            icon: 'mdi mdi-format-list-bulleted',
        };
    };
}
