import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { catchError, mapTo, mergeMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { HttpErrorResponse } from '@angular/common/http';
import { setLoginInitialValues } from '../actions/login.actions';
import { removeAllNotifications } from '@oam/core/lib/actions/toast-message.actions';
import { ActivatedRoute } from '@angular/router';
import { clearTokens, removeUser } from '@oam/core/lib/actions/user.actions';
import {
    logout,
    logoutFailed,
    logoutInProgress,
    logoutSuccessful
} from '../actions/logout.actions';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { AuthenticationService } from '@oam/cu-api-gw-api/services/authentication.service';
import { TokenType } from '@oam/core/lib/store/user-state';
import { closeAllDialogs } from '@oam/cu-ui/lib/webem-overlay/core/actions/overlay.actions';
import { pmServerAvailable } from '@oam/pm-v2/lib/core/actions/counters-pm-service.actions';

@Injectable()
export class LogoutEffects {

    constructor(private readonly actions$: Actions,
                private readonly activatedRoute: ActivatedRoute,
                private readonly secService: AuthenticationService,
    ) {
    }

    logout$ = createEffect(() =>
        this.actions$.pipe(
            ofType(logout),
            mergeMap(() => {
                const authToken = sessionStorage.getItem(TokenType.BEARER);
                const authorization = `${TokenType.BEARER} ${authToken}`;

                return this.secService.logoutPost({ Authorization: authorization }).pipe(
                        mergeMap(() => [
                            clearTokens(),
                            removeUser(),
                            setLoginInitialValues(),
                            removeAllNotifications(),
                            closeAllDialogs(),
                            redirect({ routerLink: RouterLink.login, activatedRoute: this.activatedRoute }),
                            logoutSuccessful(),
                            pmServerAvailable()
                        ]),
                        catchError((error: HttpErrorResponse) => of(logoutFailed({ payload: error.error })))
                    );
                }
            )
        )
    );

    logoutInProgress$ = createEffect(() =>
        this.actions$.pipe(
            ofType(logout),
            mapTo(logoutInProgress())
        )
    );

}
