import { createAction, props } from '@ngrx/store';
import { SPLIT_VIEW_LOCATION, RoutesPathsWithParams } from '@oam/split-view';

import { SplitViewState } from '../reducers/split-view.reducer';
import { GroupOfViews } from '@oam/split-view/lib/core/models/group-of-views.interface';

export enum SplitViewActionTypes {
    ChangeSelectionInTopView = '[User] [Split View] Change selected item in top view',
    ChangeSelectionInBottomView = '[User] [Split View] Change selected item in bottom view',
    RemoveSelectedViews = '[Split View] Remove selected items in both views',
    SetViewsFromUrlPath = '[Split View] Set views by using url outlets',
    UpdateViewsWithoutRedirect = '[Split View] Update views without redirect',
    UpdateViewsAccordingToUrlPath = '[Split View] Update views according to url outlets',
    UpdateStoreWithoutRedirect = '[Split View] Update store without redirect',
    ChangeSelectionRestriction = '[Split View] Change selection restriction in list of views',
    UpdateSelectedViews = '[Split View] Update Selected views',
    UpdateWholeState = '[Split View] Update whole state',
    SetLastSelectedViews = '[Split View] Navigate to last selected views',
    SetAvailableSplitViewOptions = '[Split View] Set available views',
}

export const changeSelectionInTopView = createAction(
    SplitViewActionTypes.ChangeSelectionInTopView,
    props<{ location: SPLIT_VIEW_LOCATION, selectedView?: string | RoutesPathsWithParams }>()
);

export const setAvailableSplitViewOptions = createAction(
    SplitViewActionTypes.SetAvailableSplitViewOptions,
    props<{ availableViews: GroupOfViews[] }>()
);

export const changeSelectionInBottomView = createAction(
    SplitViewActionTypes.ChangeSelectionInBottomView,
    props<{ location: SPLIT_VIEW_LOCATION, selectedView?: string | RoutesPathsWithParams }>()
);

export const changeSelectionRestriction = createAction(
    SplitViewActionTypes.ChangeSelectionRestriction,
    props<{ selectionRestriction: boolean }>()
);

export const updateSelectedViews = createAction(
    SplitViewActionTypes.UpdateSelectedViews,
    props<{ views: GroupOfViews[] }>()
);

export const updateWholeState = createAction(
    SplitViewActionTypes.UpdateWholeState,
    props<{ newState: SplitViewState }>()
);

export const setViewsFromUrlPath = createAction(
    SplitViewActionTypes.SetViewsFromUrlPath
);

export const updateViewsWithoutRedirect = createAction(
    SplitViewActionTypes.UpdateViewsWithoutRedirect
);

export const updateViewsAccordingToUrlPath = createAction(
    SplitViewActionTypes.UpdateViewsAccordingToUrlPath,
    props<{ topOutlet?: string, bottomOutlet?: string }>()
);

export const updateStoreWithoutRedirect = createAction(
    SplitViewActionTypes.UpdateStoreWithoutRedirect,
    props<{ topOutlet?: string, bottomOutlet?: string }>()
);


export const setLastSelectedViews = createAction(SplitViewActionTypes.SetLastSelectedViews);

export const removeSelectedViews = createAction(SplitViewActionTypes.RemoveSelectedViews);
