import { Component, OnInit, Input, OnChanges, AfterViewInit, SimpleChanges, ElementRef } from '@angular/core';
import { MenuItem } from 'primeng/api';
import _ from 'lodash';

const P_STEPS_ITEM = '.p-steps-item';
const UI_STATE_COMPLETE = 'ui-state-complete';

@Component({
    selector: 'ui-webem-stepper',
    templateUrl: './stepper.component.html',
    styleUrls: ['./stepper.component.scss']
})
export class StepperComponent implements OnChanges, AfterViewInit {

    @Input() areStepsFailed: boolean;
    @Input() activeIndex: number;
    @Input() steps: MenuItem[];
    @Input() freeze: boolean;

    constructor(private host: ElementRef) {
    }

    ngOnChanges(changes: any) {
        if (changes.activeIndex) {
            const previousValue = changes.activeIndex.previousValue;
            const currentValue = changes.activeIndex.currentValue;
            if (currentValue < previousValue) {
                this.resetCompletedSteps();
                this.addClassesToCompletedSteps(this.activeIndex);
            } else {
                this.addClassesToCompletedSteps(this.activeIndex);
            }
        }
    }

    ngAfterViewInit() {
        this.addClassesToCompletedSteps(this.activeIndex);
    }

    resetCompletedSteps() {
        const steps = this.host.nativeElement.querySelectorAll(P_STEPS_ITEM);
        _.forEach(steps, (step) => {
            step.classList.remove(UI_STATE_COMPLETE);
        });
    }

    addClassesToCompletedSteps(completedSteps: number) {
        const steps = this.host.nativeElement.querySelectorAll(P_STEPS_ITEM);
        _.times(completedSteps, index => {
            if (steps[index] && !steps[index].classList.contains(UI_STATE_COMPLETE)) {
                steps[index].classList.add(UI_STATE_COMPLETE);
            }
        });
    }
}
