import { createFeatureSelector, createSelector } from '@ngrx/store';
import {
    collectingSnapshotServerStatusFeatureKey,
    ICollectingSnapshotServerStatus
} from '../reducers/collecting-snapshot-server-status.reducer';
import { SnapshotServerStatus } from '@oam/diagnostic-snapshot/core/models/snapshot-server-status';

export const selectCollectingSnapshotServerStatusFeature =
    createFeatureSelector<ICollectingSnapshotServerStatus>(collectingSnapshotServerStatusFeatureKey);

export const selectCollectingSnapshotServerStatus = createSelector(
    selectCollectingSnapshotServerStatusFeature,
    collectingSnapshotServerStatus => collectingSnapshotServerStatus.snapshotServerStatus
);

export const selectCollectingSnapshotServerStatusIsCancelling = createSelector(
    selectCollectingSnapshotServerStatusFeature,
    collectingSnapshotServerStatus =>
        collectingSnapshotServerStatus.snapshotServerStatus === SnapshotServerStatus.Cancelling
);

export const selectCollectingSnapshotServerStatusIsOngoing = createSelector(
    selectCollectingSnapshotServerStatusFeature,
    collectingSnapshotServerStatus =>
        collectingSnapshotServerStatus.snapshotServerStatus === SnapshotServerStatus.Local_Ongoing
        || collectingSnapshotServerStatus.snapshotServerStatus === SnapshotServerStatus.Remote_Ongoing
);

export const selectCollectingSnapshotAvailability = createSelector(
    selectCollectingSnapshotServerStatusFeature,
    collectingSnapshotServerStatus => collectingSnapshotServerStatus.snapshotServerStatus === SnapshotServerStatus.Idle
);

export const selectSnapshotServerConnectionFailure = createSelector(
    selectCollectingSnapshotServerStatusFeature,
    collectingSnapshotServerStatus => collectingSnapshotServerStatus.snapshotServerStatus === 'server error'
);
