import moment from 'moment';
import { Injectable } from '@angular/core';
import { DateTime } from '../classes/date-time';
import { DateTimeFormats } from '@oam/core/lib/models';

@Injectable({
    providedIn: 'root'
})
export class DatetimeService {
    constructor() {
    }

    public static getCurrentUnixTimestamp(): number {
        return new DateTime().getTime();
    }

    public getTimestampFromISOTime(isoTime = ''): number {
        const formatISOTime = this.formatFromISO2GMTTime(isoTime);

        return (formatISOTime !== 'Invalid date') ? Date.parse(isoTime) : NaN;
    }

    public getFormattedDateTime(Timestamp = '', timezone = false): string {
        if (/^[0-9]*$/.test(Timestamp)) {
            return new DateTime(parseInt(Timestamp, 10)).toFormattedDateTime(timezone);
        }

        return new DateTime(Timestamp).toFormattedDateTime(timezone);
    }

    public formatFromISO8601(timestamp = '', shouldRemoveTimezone = false): string {
        if (shouldRemoveTimezone) {
            return new DateTime(timestamp.substring(0, timestamp.length - 6)).toFormattedDateTime(false);
        }

        return new DateTime(timestamp).toFormattedDateTime(false);
    }

    public formatFromISO2GMTTime(timestamp = ''): string {
        const m = moment(timestamp).utcOffset(timestamp);

        return m.format(DateTimeFormats.DateTimeWithTimezone);
    }
}
