import { ModuleWithProviders, NgModule } from '@angular/core';
import { DetailsPanelConfig } from '@oam/details-panel/lib/core/model/details-panel-config';
import {
    DETAILS_PANEL_CONFIG,
    provideDetailsPanelConfigService
} from '@oam/details-panel/lib/core/services/details-panel-config-token';
import { DetailsPanelConfigService } from '@oam/details-panel/lib/core/services/details-panel-config.service';


@NgModule({})
export class DetailsPanelConfigModule {
    static forRoot(detailsPanelConfig: DetailsPanelConfig): ModuleWithProviders<DetailsPanelConfigModule> {

        return {
            ngModule: DetailsPanelConfigModule,
            providers: [
                {
                    provide: DETAILS_PANEL_CONFIG,
                    useValue: detailsPanelConfig
                },
                {
                    provide: DetailsPanelConfigService,
                    useFactory: provideDetailsPanelConfigService,
                    deps: [ DETAILS_PANEL_CONFIG ]
                }
            ]
        };
    }
}
