import { createFeatureSelector, createSelector } from '@ngrx/store';
import { selectAllPlansInfo } from '../selectors/plans-info-selector';
import { SelectedPlanState } from '../reducers/selected-plan.reducer';
import { PlanOrigin, PlansInfo } from '../model';
import { AllPlansInfo, isDeltaPlan } from '@oam/configuration-shared/lib/core/model/plans-info';
import { DeltaPlanConflictState } from '@oam/configuration-shared/lib/core/model/delta-plan-conflict-state';

export const selectSelectedPlanState = createFeatureSelector<SelectedPlanState>('selectedPlan');

export const selectSelectedPlanId = createSelector(
    selectSelectedPlanState,
    (state: SelectedPlanState) => {
        return state.planId;
    }
);

export const selectSelectedPlanOrigin = createSelector(
    selectSelectedPlanState,
    (state: SelectedPlanState) => state.planOrigin
);

export const selectSelectedPlanRequestInfo = createSelector(
    selectSelectedPlanId,
    selectSelectedPlanOrigin,
    (planId: string, planOrigin: PlanOrigin) => {
        return { planId, planOrigin };
    }
);

export const selectSelectedPlan = createSelector(
    selectSelectedPlanId,
    selectAllPlansInfo,
    (planId, plans) => {
        return plans.find(plan => plan.planId === planId);
    }
);

export const selectSelectedDeltaPlanIsOutdated = createSelector(
    selectSelectedPlan,
    (selectedPlan: AllPlansInfo | undefined) => {
        if (selectedPlan && isDeltaPlan(selectedPlan)) {
            return selectedPlan.deltaState === DeltaPlanConflictState.Conflict;
        }

        return false;
    }
);

export const selectIsSelectedPlanActive = createSelector(
    selectSelectedPlan,
    (plan: PlansInfo | undefined) => {
        return plan ? plan.from === PlanOrigin.ACTIVE : false;
    }
);

export const selectIsSelectedPlanStaged = createSelector(
    selectSelectedPlan,
    (plan: PlansInfo | undefined) => {
        return plan ? plan.from === PlanOrigin.STAGE : false;
    }
);
