import { Action } from '@ngrx/store';

export enum CompatibilityBrowserActionTypes {
    BrowserInformationClosed = '[User] [IncompatibilityBrowser] Browser incompatibility info closed',
    YourBrowserIsSupport = '[IncompatibilityBrowser] Your browser is support with app',
    YourBrowserIsNotSupport = '[IncompatibilityBrowser] Your browser is not support with app'
}

export class BrowserIncompatibilityInfoClosed implements Action {
    readonly type = CompatibilityBrowserActionTypes.BrowserInformationClosed;
}

export class BrowserIsSupport implements Action {
    readonly type = CompatibilityBrowserActionTypes.YourBrowserIsSupport;
}

export class BrowserIsNotSupport implements Action {
    readonly type = CompatibilityBrowserActionTypes.YourBrowserIsNotSupport;
}

export type CompatibilityBrowserActions = BrowserIsSupport |
    BrowserIsNotSupport | BrowserIncompatibilityInfoClosed;
