import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { SideBarMenuItem } from '@oam/cu-ui';
import { select, Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { selectIsExpandedMainMenu } from '../../core/reducers/main-menu.reducer';
import { changeMainMenuExpandState } from '../../core/actions/main-menu.actions';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { selectUrlWithoutParams } from '@oam/core/lib/selectors/router.selectors';
import { ActivatedRoute } from '@angular/router';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';

@Component({
    selector: 'webem-page-navigation-menu',
    templateUrl: './page-navigation-menu.component.html',
    styleUrls: ['./page-navigation-menu.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PageNavigationMenuComponent implements OnInit {
    @Input() navigationMenuModel: Navigation[];
    currentRoute: Observable<string>;
    isExpanded: Observable<boolean>;

    menuAction = (item: SideBarMenuItem) => {
        this.store.dispatch(redirect({routerLink: item.data, activatedRoute: this.activatedRoute}));
    };

    constructor(private readonly store: Store, private readonly activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.currentRoute = this.store.pipe(select(selectUrlWithoutParams));
        this.isExpanded = this.store.pipe(select(selectIsExpandedMainMenu));
    }

    changeExpandState(isExpanded: boolean) {
        this.store.dispatch(changeMainMenuExpandState({ payload: { isExpanded } }));
    }
}
