import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable } from 'rxjs';
import { TokenType } from '@oam/core/lib/store/user-state';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {
    constructor() {}

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        const authToken = sessionStorage.getItem(`${TokenType.BEARER}`) || sessionStorage.getItem(`${TokenType.PASSWORD_CHANGE}`);

        if (authToken && !request.headers.get('Authorization')) {
            const cloned = request.clone({
                headers: request.headers.set('Authorization', `Bearer ${authToken}`)
            });

            return next.handle(cloned);
        } else {
            return next.handle(request);
        }
    }
}
