import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Notification } from '@oam/core/lib/models/notification.type';
import { OverlayPanel } from 'primeng/overlaypanel';
import { select, Store } from '@ngrx/store';
import { takeUntil } from 'rxjs/operators';
import { selectNotifications } from '@oam/core/lib/selectors/toast-message.selectors';
import { Subject } from 'rxjs';
import { DateTime } from '@oam/core/lib/classes/date-time';

@Component({
    selector: 'ui-webem-user-menu-overlay',
    templateUrl: './user-menu-overlay.component.html',
    styleUrls: ['./user-menu-overlay.component.scss']
})
export class UserMenuOverlayComponent implements OnInit, OnDestroy {
    @ViewChild(OverlayPanel, { static: true }) private readonly overlayPanel: OverlayPanel;

    notifications: Notification[];
    openDate: Date;
    buttonClasses = 'user-menu-overlay-button ui-button-secondary custom-button ui-button-long';
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    constructor(private readonly store: Store) {
    }

    ngOnInit() {
        this.store.pipe(
            takeUntil(this.ngUnsubscribe),
            select(selectNotifications)
        ).subscribe(notifications => {
            if (!notifications) return;

            this.notifications = [...notifications].reverse();
        });
    }

    toggle(event: MouseEvent) {
        this.overlayPanel.toggle(event);
        if (this.isOverlayVisible()) {
            this.openDate = new DateTime();
        }
    }

    hide() {
        if (this.isOverlayVisible()) {
            this.overlayPanel.hide();
        }
    }

    isOverlayVisible() {
        return this.overlayPanel.overlayVisible;
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
