import { union } from 'lodash';
import { PmCounter, PmCounterValue } from '../models/counters.interface';

export function getAllTimestamps(countersModel: PmCounter[]): string[] {
    return union(...
        countersModel.map((counterModel: PmCounter) =>
            counterModel.counterValues.map((value: PmCounterValue) => value.startTime)
        )
    );
}
