import { get } from 'lodash';
import { Injectable, NgZone } from '@angular/core';
import { asyncScheduler, concat, Observable, of, SchedulerLike, Subject, throwError, timer } from 'rxjs';
import { catchError, concatMap, distinctUntilChanged, observeOn, switchMap, takeUntil, filter, timeout } from 'rxjs/operators';
import { leaveZone } from '../functions/leave-zone-scheduler';
import { enterZone } from '../functions/enter-zone-scheduler';
import { ActionResponse, PollingConfig, ResponseStatus, SerializedActionResponse } from './model';
import { ActionCreator } from '@ngrx/store';
import {
    clearActionProgress,
    setActionProgress
} from '@oam/cu-ui/lib/action-progress/core/actions/action-progress.actions';
import { TypedAction } from '@ngrx/store/src/models';
import { HttpErrorResponse } from '@angular/common/http';

const defaultIsActionDistinctPredicate = (previous: ActionResponse, current: ActionResponse) => {
    return !!previous && !!current && previous.status === current.status;
};

const defaultActionResponseSerializer = (response: ActionResponse): SerializedActionResponse => {
    return {
        actionId: response.actionId,
        status: response.status as ResponseStatus,
        errorMessage: response.errorMessage
    };
};

@Injectable({
    providedIn: 'root'
})
export class PollingService {
    public POLLING_PERIOD_MS = 5000;

    // set to 10s because seems the connection in E2E test is not able to response in 5s which causes E2E test failure
    public REQUEST_TIMEOUT_MS = 10000;

    constructor(private readonly ngZone: NgZone) {
    }

    public getTimer(scheduler: SchedulerLike, dueTime = 0, pollingPeriod= this.POLLING_PERIOD_MS): Observable<number> {
        return timer(dueTime, pollingPeriod, leaveZone(this.ngZone, scheduler))
            .pipe(
                observeOn(enterZone(this.ngZone, scheduler))
            );
    }

    public executeAction<D, B, R>(
        pollingConfig: PollingConfig<D, B, R>
    ): Observable<ActionCreator<any, any>> {
        const serializer = pollingConfig.endpointResponseSerializer || defaultActionResponseSerializer;
        const spinAfterSuccess: boolean = get(pollingConfig, 'actionProgress.spinAfterSuccess', false);
        const clearActionProgressArray = this.getActionArray(
            pollingConfig.actionProgress && clearActionProgress(pollingConfig.actionProgress)
        );

        const response$ = pollingConfig.requestEndpoint(pollingConfig.requestParams).pipe(
            switchMap((response) => {
                const serializedResponse = serializer(response as any);

                switch (serializedResponse.status) {
                    case ResponseStatus.Ongoing:
                        return this.pollForActionSuccess(
                            serializedResponse.actionId,
                            pollingConfig
                        );
                    case ResponseStatus.Success: {
                        const successActionObservable = pollingConfig.onActionSuccess && pollingConfig.onActionSuccess({
                            response,
                            additionalData: pollingConfig.additionalData
                        });

                        return [
                            ...this.getActionArray(successActionObservable),
                            ...(spinAfterSuccess ? [] : clearActionProgressArray)
                        ];
                    }
                    case ResponseStatus.Failure: {
                        const failureActionObservable = pollingConfig.onActionFailure({
                            response,
                            additionalData: pollingConfig.additionalData
                        });

                        return [
                            ...this.getActionArray(failureActionObservable),
                            ...clearActionProgressArray
                        ];
                    }
                    default:
                        return throwError(
                            {
                                message: `The response object: ${JSON.stringify(response)} does not have status property ` +
                                    'or the status property does not match one of the following: [ongoing, success, failure]'
                            });
                }
            }),
            catchError((err) => {
                const errorActionObservable = pollingConfig.onActionError({
                    error: err,
                    additionalData: pollingConfig.additionalData
                });

                return [
                    ...this.getActionArray(errorActionObservable),
                    ...clearActionProgressArray
                ];
            })
        );

        return pollingConfig.actionProgress
            ? concat(of(setActionProgress(pollingConfig.actionProgress)), response$)
            : response$;
    }

    private pollForActionSuccess<D, B, R>(
        actionId: string,
        config: PollingConfig<D, B, R>
    ): Observable<any> {
        const stopPolling$: Subject<void> = new Subject();
        const serializer = config.endpointResponseSerializer || defaultActionResponseSerializer;
        const actionDistinctPredicate = config.isActionDistinctPredicate || defaultIsActionDistinctPredicate;
        const spinAfterSuccess: boolean = get(config, 'actionProgress.spinAfterSuccess', false);
        const clearActionProgressArray = this.getActionArray(
            config.actionProgress && clearActionProgress(config.actionProgress)
        );

        return this.getTimer(asyncScheduler).pipe(
            takeUntil(stopPolling$),
            concatMap(() => config.pollingEndpoint({ actionId }).pipe(
                timeout(this.REQUEST_TIMEOUT_MS),
                catchError(err => {
                    if (this.shouldExitPolling(config, err)) {
                        return throwError(err);
                    }

                    const ongoingStatus = {
                        status: ResponseStatus.Ongoing
                    };

                    return of(ongoingStatus);
                })
            )),
            distinctUntilChanged(actionDistinctPredicate),
            filter(() => !stopPolling$.isStopped),
            concatMap((response) => {
                const serializedResponse = serializer(response);

                if (serializedResponse.status === ResponseStatus.Ongoing) {
                    const onGoingAction = config.onActionOngoing
                        && config.onActionOngoing({ response, additionalData: config.additionalData });

                    return [
                        ...this.getActionArray(onGoingAction)
                    ];
                }
                stopPolling$.next();
                stopPolling$.complete();

                const successOrFailureAction = serializedResponse.status === ResponseStatus.Success
                    ? config.onActionSuccess && config.onActionSuccess({
                        response,
                        additionalData: config.additionalData
                      })
                    : config.onActionFailure({ response, additionalData: config.additionalData });

                return [
                    ...this.getActionArray(successOrFailureAction),
                    ...(spinAfterSuccess ? [] : clearActionProgressArray)
                ];
            }),
            catchError(err => {
                if (!stopPolling$.isStopped) {
                    return of(config.onActionError({ error: err, additionalData: config.additionalData }));
                }

                return of();
            })
        );
    }

    private getActionArray(action: TypedAction<any> | undefined): TypedAction<any>[] {
        return action ? [action] : [];
    }

    private shouldExitPolling<D, B, R>(config: PollingConfig<D, B, R>, error: HttpErrorResponse): boolean {
        return !config.pollUnlessHttpErrorCode
            || config.pollUnlessHttpErrorCode
            && config.pollUnlessHttpErrorCode.indexOf(error.status) >= 0;
    }
}
