import { Action, createFeatureSelector, createReducer, on } from '@ngrx/store';
import {
    logoutFailed,
    logoutInProgress,
    logoutSuccessful
} from '../actions/logout.actions';
import { OperationStatus } from '@oam/core/lib/models/operation.interface';

export const featureKey = 'logout';

export const initialState: OperationStatus = {
    inProgress: false,
    successful: false,
    failed: false,
    failedReason: ''
};

const logoutReducer = createReducer(
    initialState,
    on(logoutInProgress, state => ({ ...state, inProgress: true })),
    on(logoutFailed, (state, action) => ({ ...state, failed: true, inProgress: false, failedReason: action.payload })),
    on(logoutSuccessful, state => ({ ...state, successful: true, inProgress: false }))
);

export function reducer(state: OperationStatus, action: Action) {
    return logoutReducer(state, action);
}

export const getLogoutStatus = createFeatureSelector<OperationStatus>(featureKey);

