import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { isEmpty } from 'lodash';
import { SideBarMenuGroup, SideBarMenuItem, SideBarMenuModel, ThirdMenuItem } from '../../model/side-bar-menu-model';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { Observable, Subject } from 'rxjs';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { select, Store } from '@ngrx/store';
import { areKeyboardShortcutsEnabled } from '@oam/keyboard-shortcuts/lib/core/selectors/keyboard-shortcuts.selectors';
import { forceRedirect } from '@oam/core/lib/actions/router.actions';

interface SelectionModel {
    selectedGroupId: number;
    selectedItemId: number;
    selectedChildId: number;
}

interface FloatMenuState {
    isFloatMenuVisible: boolean;
    groupId: number;
}

@Component({
    selector: 'ui-webem-side-bar-menu',
    templateUrl: './side-bar-menu.component.html',
    styleUrls: ['./side-bar-menu.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SideBarMenuComponent implements OnInit, OnDestroy {
    @Input() model: SideBarMenuModel;
    @Input() isExpanded = false;
    @Output() menuAction = new EventEmitter<SideBarMenuItem>();
    @Output() expandAction = new EventEmitter<boolean>();

    clickAction = new Subject<SideBarMenuItem>();
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    keyboardShortcutsState$: Observable<any>;
    shortcutsEnabled: boolean;

    menuSelectionModel: SelectionModel = { selectedGroupId: -1, selectedItemId: -1, selectedChildId: -1 };

    floatMenuState: FloatMenuState = { groupId: -1, isFloatMenuVisible: false };
    thirdfloatMenuState: FloatMenuState = { groupId: -1, isFloatMenuVisible: false };

    expandMenuButtonModel: ButtonModel = {
        title: 'Expand',
        icon: 'mdi mdi-chevron-right-circle-outline',
        theme: ButtonTheme.Secondary
    };

    collapseMenuButtonModel: ButtonModel = {
        title: 'Collapse',
        icon: 'mdi mdi-chevron-left-circle-outline',
        theme: ButtonTheme.Secondary
    };

    expandCollapseMenuButton: ButtonModel;
    keyboardShortcutsEnabled$: Observable<boolean>;

    constructor(private readonly store: Store) {
    }

    ngOnInit() {
        this.keyboardShortcutsEnabled$ = this.store.pipe(select(areKeyboardShortcutsEnabled));
        this.menuSelectionModel = this.model.items.reduce((selectionModel: SelectionModel, groupItem: SideBarMenuGroup, itemId: number) => {
            if (groupItem.isActive) {
                selectionModel.selectedGroupId = itemId;
                selectionModel.selectedItemId = groupItem.items.findIndex(({ isActive }) => isActive);
                const selectedChildrenGroup = groupItem.items[selectionModel.selectedItemId].children as ThirdMenuItem[];
                if (!isEmpty(selectedChildrenGroup)) {
                    selectionModel.selectedChildId = selectedChildrenGroup.findIndex(({ isActive }) => isActive);
                }
            }

            return selectionModel;
        }, this.menuSelectionModel);

        this.expandCollapseMenuButton = this.isExpanded ? this.collapseMenuButtonModel : this.expandMenuButtonModel;

        this.clickAction.pipe(
            takeUntil(this.ngUnsubscribe),
            debounceTime(120)
        ).subscribe((item) => this.menuAction.emit(item));

        this.keyboardShortcutsState$ = this.store.pipe(
            select(areKeyboardShortcutsEnabled)
        );

        this.keyboardShortcutsState$
            .pipe(takeUntil(this.ngUnsubscribe))
            .subscribe((keyboardShortcutsState) => {
                this.shortcutsEnabled = keyboardShortcutsState;
            });
    }

    isThirdMenuItemActived(itemId: number, groupId: number, childId: number) {
        return this.menuSelectionModel.selectedChildId === childId
            && groupId === this.menuSelectionModel.selectedGroupId
            && itemId === this.menuSelectionModel.selectedItemId;
    }

    hasChildren(item: SideBarMenuItem) {
        return !isEmpty(item.children);
    }

    onSideBarIconMouseLeave(event: MouseEvent) {
        if (event.currentTarget) (event.currentTarget as HTMLElement).blur();

        this.hideFloatMenu();
    }

    showFloatMenu(itemId: number) {
        this.floatMenuState.groupId = itemId;
        this.floatMenuState.isFloatMenuVisible = true;
    }

    showThirdFloatMenu(thirdItemId: number) {
        this.thirdfloatMenuState.groupId = thirdItemId;
        this.thirdfloatMenuState.isFloatMenuVisible = true;
    }

    hideFloatMenu() {
        this.floatMenuState.isFloatMenuVisible = false;
        this.thirdfloatMenuState.isFloatMenuVisible = false;
    }

    selectGroupAndNavigateToFirstTab(item: SideBarMenuGroup, groupId: number) {
        if (item.items.length === 0) {
            this.selectMenuItem(-1, -1, groupId, item);
        }
        this.store.dispatch(forceRedirect({routerLink: item.data}));
    }

    selectMenuItem(childId: number, itemId: number, groupId: number, item: SideBarMenuItem, event?: MouseEvent) {
        this.menuSelectionModel.selectedItemId = itemId;
        this.menuSelectionModel.selectedGroupId = groupId;
        if (childId === -1 && !isEmpty(item?.children)) {
            this.menuSelectionModel.selectedChildId = item.children ? item.children.findIndex(({ isActive }) => isActive) : -1;
        } else {
            this.menuSelectionModel.selectedChildId = childId;
        }

        this.clickAction.next(item);
        event?.stopPropagation();
    }

    selectMenuItemAndCollapse(childId: number, itemId: number, groupId: number, item: SideBarMenuItem) {
        if (item.children?.length) return;

        this.selectMenuItem(childId, itemId, groupId, item);

        if (this.isExpanded) {
            this.expandMenuStateAction();
        }
    }

    expandMenuStateAction() {
        this.isExpanded = !this.isExpanded;
        this.expandCollapseMenuButton = this.isExpanded ? this.collapseMenuButtonModel : this.expandMenuButtonModel;

        this.expandAction.emit(this.isExpanded);
    }

    isSelectedItem(itemId: number, groupId: number) {
        return this.menuSelectionModel.selectedItemId === itemId && this.menuSelectionModel.selectedGroupId === groupId ;
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
