import { AbstractControl } from '@angular/forms';
import { BTS_SECURITY_ADMINISTRATOR } from '@oam/shared/lib/constants/role-access-constants';

export const SPECIAL_USERNAME_ALLOWCHARACTERS = '._-'.split('');
export const SPECIAL_PASSWORD_ALLOWCHARACTERS = '!#$%&\'()*+,-.;=?@[]_~/'.split('');

export function hasSmallerLengthThanMax(value: string, maxLength: number): boolean {
    return value.length <= maxLength;
}

export function hasBiggerLengthThanMin(value: string, minLength: number): boolean {
    return value.length >= minLength;
}

export function hasTwoSameConsecutiveChar(value: string): boolean {
    const sameConsecutiveCharacterExpression = new RegExp(/(.)\1/, 'g');

    return sameConsecutiveCharacterExpression.test(value);
}

export function hasMinimumDigitNumbers(value: string, minNumberOfDigits: number): boolean {
    const digitNumberExpression = new RegExp(/\d/, 'g');
    const arrayOfDigits = value.match(digitNumberExpression) || [];

    return arrayOfDigits.length >= minNumberOfDigits;
}

export function hasUpperAndLowerCase(value: string): boolean {
    return value !== value.toLowerCase() && value !== value.toUpperCase();
}

export function hasUpperCase(value: string): boolean {
    return value !== value.toLowerCase();
}

export function hasLowerCase(value: string): boolean {
    return value !== value.toUpperCase();
}

export function isInWhiteList(specialAllowCharacters: string[], value: string): boolean {
    const pwdArray = value.split('');

    const isInWhiteListResult = pwdArray.every((char) => {
        if (isFromAtoZ(char) || isFromatoz(char) || isFrom0to9(char) ||
            isSpecialAllowCharacters(char)) {
            return true;
        }

        return false;
    });

    function isFromAtoZ(char: string): boolean {
        return char.charCodeAt(0) >= 65 && char.charCodeAt(0) <= 90;
    }

    function isFromatoz(char: string): boolean {
        return char.charCodeAt(0) >= 97 && char.charCodeAt(0) <= 122;
    }

    function isFrom0to9(char: string): boolean {
        return char.charCodeAt(0) >= 48 && char.charCodeAt(0) <= 57;
    }

    function isSpecialAllowCharacters(char: string): boolean {
        return specialAllowCharacters.includes(char);
    }

    return isInWhiteListResult;
}

export function hasDisallowedSpecialAsciiChar(value: string): boolean {
    const charsArray = value.split('');

    const isSpecialAllowCharacters = charsArray.some((char) => {
        return !(/^[\x21\x23-\x7E]*$/.test(char));
    });

    return isSpecialAllowCharacters;
}

export function hasDisallowedUserNameAsciiChar(value: string): boolean {
    return !isInWhiteList(SPECIAL_USERNAME_ALLOWCHARACTERS, value);
}

export function hasDisallowedPasswordAsciiChar(value: string): boolean {
    return !isInWhiteList(SPECIAL_PASSWORD_ALLOWCHARACTERS, value);
}

export function hasMinNonAlphanumericChar(value: string, minNonAlphaNumericChar: number): boolean {
    if (minNonAlphaNumericChar === 0) return true;

    const minNonAlphaNumericalExpression = new RegExp(`(?=(.*(_|[^\\w])){${minNonAlphaNumericChar}}).+`, 'g');

    return minNonAlphaNumericalExpression.test(value);
}

export function isPasswordNotSameAsUserName(password: string, username: string): boolean {
    const reversedUsername = username.split('').reverse().join('');

    return password.toLowerCase() !== username.toLowerCase() &&
        password.toLowerCase() !== reversedUsername.toLowerCase();
}

export function isUserNameNotIncludedInPassword(password: string, username: string): boolean {
    if (!isPasswordNotSameAsUserName(password, username)) {
        return true;
    }

    const reversedUsername = username.split('').reverse().join('');

    return !password.toLowerCase().includes(username.toLowerCase()) &&
        !password.toLowerCase().includes(reversedUsername.toLowerCase());
}

export function judgeForActionStatusChangeOrNot(newPassword: AbstractControl, confirmPassword: AbstractControl,
                                                isPasswordValid: boolean,
                                                isActivationStatusChange: boolean) {

    return (newPassword.value || confirmPassword.value) ? isPasswordValid : isActivationStatusChange;
}

export function judgeForCurrentPassword(selectRoleName: string,
                                        newPassword: AbstractControl,
                                        confirmPassword: AbstractControl,
                                        isPasswordValid: boolean,
                                        currentPassword: AbstractControl,
                                        isActivationStatusShow: boolean,
                                        isActivationStatusChange: boolean) {

    const isSecurityAccount = selectRoleName === BTS_SECURITY_ADMINISTRATOR;

    if (isSecurityAccount && isActivationStatusShow &&
        isActivationStatusChange
        && !newPassword.value && !confirmPassword.value
        && !currentPassword.value) {
        return true;
    }

    if (!currentPassword.value || !currentPassword.valid) {
        return false;
    }

    if (isActivationStatusShow && isActivationStatusChange) {
        return (newPassword.value || confirmPassword.value) ? isPasswordValid
            : !isSecurityAccount;
    } else {
        return isPasswordValid;
    }

}

export function judgeForUsernameAndCurrentPassword(username: AbstractControl, newPassword: AbstractControl,
                                                   confirmPassword: AbstractControl,
                                                   currentPassword: AbstractControl, isUsernameValid: boolean,
                                                   isPasswordValid: boolean): boolean {
    if (!currentPassword.value || !currentPassword.valid) {
        return false;
    }

    if (!newPassword.value && !confirmPassword.value) {
        return isUsernameValid;
    } else {
        return username.value ? isUsernameValid && isPasswordValid : isPasswordValid;
    }

}
