import { Injector, ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RoleAccessControlDirective } from '@oam/role-access-ng/lib/directives/role-access-control.directive';
import { RoleAccessControlNativeDirective } from '@oam/role-access-ng/lib/directives/role-access-control-native.directive';
import { RoleAccessService } from '@oam/role-access-core';
import { Route } from '@angular/router';
import { RoleAccessConfig } from '@oam/role-access-ng/lib/role-access.config';

export type RoleAccessProperty = {
    roleAccessFlag?: string
};

export type RoleAccessGuardRule = RoleAccessProperty & Pick<Route, 'path'>;

@NgModule({
    imports: [
        CommonModule
    ],
    declarations: [
        RoleAccessControlNativeDirective,
        RoleAccessControlDirective
    ],
    exports: [
        RoleAccessControlNativeDirective,
        RoleAccessControlDirective
    ]
})
export class RoleAccessModule {
    static forRoot(moduleConfig: RoleAccessConfig): ModuleWithProviders<RoleAccessModule> {
        return {
            ngModule: RoleAccessModule,
            providers: [
                {
                    provide: RoleAccessConfig,
                    useValue: moduleConfig
                },
                {
                    provide: RoleAccessService,
                    useFactory: (
                        roleAccessConfig: RoleAccessConfig,
                        injector: Injector
                    ) => {
                        const dep = injector.get(roleAccessConfig.ignoreWhen.token);

                        return new RoleAccessService(
                            roleAccessConfig.userProfiles,
                            roleAccessConfig.ignoreWhen.fn(dep)
                        );
                    },
                    deps: [RoleAccessConfig, Injector]
                }
            ]
        };
    }
}
