import { select, Store } from '@ngrx/store';
import { selectInformation } from '@oam/core/lib/user/core/reducers/user-reducer';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class UserInformationService {
    information: any;

    constructor(private readonly store: Store) {
        this.store.pipe(select(selectInformation))
            .subscribe((information) => {
                this.information = information;
            });
    }

    getRoleAndReadOnlyMode() {
        const { role, readOnlyMode } = this.information;

        return { role, readOnlyMode };
    };

    getRole() {
        return this.information.role;
    };
}
