import { ModuleWithProviders, NgModule } from '@angular/core';
import { TracingConfig } from '@oam/tracing/lib/tracing-config.service';


@NgModule({})
export class TracingConfigModule {
    static forRoot(additionalData: TracingConfig): ModuleWithProviders<TracingConfigModule> {
        return {
            ngModule: TracingConfigModule,
            providers: [
                {
                    provide: TracingConfig,
                    useValue: additionalData
                }
            ]
        };
    }
}
