import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    Input,
    OnChanges,
    OnDestroy,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { MenuAction } from '../../model/menu-action';
import { Menu } from 'primeng/menu';
import { isEqual } from 'lodash';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { ButtonOptions } from '../../../button/model/button-options.definition';
import { NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE, RoleAccessService } from '@oam/role-access-core';

@Component({
    selector: 'ui-webem-button-with-menu',
    templateUrl: './button-with-menu.component.html',
    styleUrls: ['./button-with-menu.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ButtonWithMenuComponent implements OnDestroy, OnChanges {
    @Input() menuOptions: MenuAction[];
    @Input() buttonConfig?: ButtonModel;
    @Input() options: ButtonOptions;
    @Input() scrollableContainer?: ElementRef | HTMLElement;
    @Input() id?: string;

    @ViewChild('menu') menu: Menu;

    constructor(private roleAccessService: RoleAccessService) {}

    readonly ACTIONS_BUTTON_MODEL: ButtonModel = {
        title: 'Actions',
        icon: 'mdi mdi-dots-vertical',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long
    };
    private boundScrollEventTargets: HTMLElement[] = [];
    btnConfig: ButtonModel;
    hideMenu = () => this.menu.hide();

    ngOnInit() {
        if (!this.buttonConfig) {
            this.btnConfig = this.ACTIONS_BUTTON_MODEL;
        } else {
            this.btnConfig = this.buttonConfig;
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (this.buttonConfig) this.btnConfig = this.buttonConfig;

        if (!isEqual(changes.menuOptions?.previousValue, changes.menuOptions?.currentValue)) {
            this.menuOptions.forEach((option) => {
                if (this.roleAccessService.isForbiddenForCurrentRole(option.roleAccessFlag)) {
                    option.disabled = true;
                    option.title = NOT_ACCESSIBLE_FOR_CURRENT_ROLE_TITLE;
                }
            });
        }
    }

    ngOnDestroy() {
        this.unbindScrollListeners();
    }

    onMenuShow() {
        if (!this.scrollableContainer) return;

        const scrollableContainerElement: HTMLElement = (this.scrollableContainer as ElementRef).nativeElement ?
            (this.scrollableContainer as ElementRef).nativeElement :
            this.scrollableContainer;
        this.bindScrollListeners(scrollableContainerElement);
    }

    onMenuHide() {
        this.unbindScrollListeners();
    }

    bindScrollListeners(scrollableContainer: HTMLElement) {
        scrollableContainer.addEventListener('scroll', this.hideMenu);
        this.boundScrollEventTargets.push(scrollableContainer);
    }

    unbindScrollListeners() {
        if (this.boundScrollEventTargets.length === 0) return;

        this.boundScrollEventTargets.forEach(target =>
            target.removeEventListener('scroll', this.hideMenu)
        );
        this.boundScrollEventTargets = [];
    }
}
