import { createReducer, on, Action, createFeatureSelector, createSelector } from '@ngrx/store';
import { Cloud } from '@oam/shared/lib/core/models/cloud.type';
import { setCloudAction } from '@oam/shared/lib/core/actions/cloud.actions';

export const featureKey = 'cloud';

export interface CloudState {
    cloud: Cloud;
}

export const initialState: CloudState = {
    cloud: {
        cloudName: '',
        cloudId: ''
    }
};

const cloudReducer = createReducer(initialState,
    on(setCloudAction, (state, { payload: cloud }) => ({ ...state, cloud })),
);


export function reducer(state: CloudState, action: Action) {
    return cloudReducer(state, action);
}

export const selectCloudState = createFeatureSelector<CloudState>(featureKey);
export const selectCloud = createSelector(selectCloudState, (state: CloudState) => state.cloud);
