import { Injectable } from '@angular/core';
import { Router, UrlTree } from '@angular/router';
import { SPLIT_VIEW_LOCATION } from '@oam/split-view';

@Injectable({
    providedIn: 'root'
})
export class OutletUrlPathService {
    private curUrlTree: UrlTree;

    constructor(private readonly router: Router) {}

    getOutletWebemRoutePath(url: string, viewLocation: SPLIT_VIEW_LOCATION): string | undefined {
        this.curUrlTree  = this.router.parseUrl(url);
        if (this.curUrlTree.root.children.primary.children[viewLocation]) {
            return this.getSegmentsOutletPath(viewLocation);
        }
    }

    private getSegmentsOutletPath(view: SPLIT_VIEW_LOCATION): string {
        return this.curUrlTree.root.children.primary.children[view].segments
            .map((outlet) => outlet.path).join('/');
    }
}
