import { createAction, props } from '@ngrx/store';

export const getLegalNoticeMessage = createAction(
    '[LegalNoticeModal] Get Legal Notice Modal'
);

export const setLegalNoticeMessage = createAction(
    '[LegalNoticeModal] Set Legal Notice Modal',
    props<{ securityNotice: string, confirmButtonVisible?: boolean, okButtonVisible?: boolean, logoutButtonVisible?: boolean, icon?: string }>()
);

export const showLegalNoticeModal = createAction(
    '[LegalNoticeModal] Show Legal Notice Modal'
);

export const openLegalNoticeModal = createAction(
    '[LegalNoticeModal] Open Legal Notice Modal before redirect'
);

export const submitLegalNoticeModal = createAction(
    '[User][LegalNoticeModal] Submit Legal Notice Modal'
);

export const denyLegalNoticeModal = createAction(
    '[User][LegalNoticeModal] Deny Legal Notice Modal'
);
