import { Component, ElementRef, EventEmitter, Input, Output } from '@angular/core';
import { Section } from '../../model';
import { DetailsButton } from '@oam/core/lib/models/buttons';

@Component({
    selector: 'ui-webem-details-section',
    templateUrl: './details-section.component.html',
    styleUrls: ['./details-section.component.scss'],
})
export class DetailsSectionComponent {
    // TODO: this component will be switched with details-section-custom
    //  component when all panels in app will be replaced with new detail panel

    @Input()
    title: string;

    @Input()
    subTitle: string;

    @Input()
    id: string;

    @Input()
    data: Section;

    @Input()
    withCssClass = '';

    @Input()
    buttons: DetailsButton[];

    @Input()
    list: Array<string>;

    @Input()
    dataWithActionsMenu: Section;

    @Input()
    container?: ElementRef;

    @Output()
    menuAction = new EventEmitter<string>();

    emptyList = '–';

    constructor() {
    }

    trackByFn(index: number) {
        return index;
    }
}
