import { Injectable } from '@angular/core';
import { Cu, VDu } from '@oam/cu-asm-api';

@Injectable({
    providedIn: 'root'
})
export class WebemStateService {
    private _webemState: Cu | VDu;

    getWebemState() {
        return this._webemState;
    }

    setWebemState(_webemState: Cu | VDu) {
        this._webemState = _webemState;
    }
}
