import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { ButtonThickness } from '../../../button/model/button-thickness.enum';
import { SelectItem } from 'primeng/api';

@Component({
    selector: 'ui-webem-check-box-table-row-filter',
    templateUrl: './check-box-table-row-filter.component.html',
    styleUrls: ['./check-box-table-row-filter.component.scss']
})

export class CheckBoxTableRowFilterComponent {
    @Input() column: any;
    @Input() options:  SelectItem[];
    @Input() selection: string[];
    @Input() appendTo: string;
    @Output() filterChanged = new EventEmitter<{ value: Array<string>; column: string; }>();
    isFilterApplied: boolean;

    readonly model: ButtonModel = {
        label: 'Filter',
        icon: 'mdi mdi-filter',
        length: ButtonLength.Long,
        thickness: ButtonThickness.Thin
    };

    onFilterChange(event: Array<string>): void {
        setTimeout(() => {
            this.filterChanged.emit({value: event, column: this.column});
        });
    }
}
