import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { DiagnosticSnapshotHomeComponent } from '@oam/diagnostic-snapshot/pages/diagnostic-snapshot-home/diagnostic-snapshot-home.component';
import { DiagnosticContentMenuComponent } from '../../../diagnostic/diagnostic-shared/src/lib/components/diagnostic-content-menu/diagnostic-content-menu.component';


const routes: Routes = [
    {
        path: '',
        component: DiagnosticSnapshotHomeComponent,
        outlet: 'content_center'
    },
    {
        path: '',
        component: DiagnosticContentMenuComponent,
        outlet: 'content_menu'
    }
];

@NgModule({
    declarations: [],
    imports: [CommonModule, RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DiagnosticSnapshotRoutingModule {
}
