import {
    AfterContentInit,
    Component,
    ContentChildren,
    OnInit,
    QueryList,
    Input, AfterViewInit, Output, EventEmitter
} from '@angular/core';
import { SplitAreaDirective } from '../../directives/split-area/split-area.directive';
import { IOutputData } from 'angular-split/lib/interface';
import { CurledUpAreaDirective } from '../../directives/curled-up-area/curled-up-area.directive';
import { CurledUpAreaMap } from '../../model/curled-up-area-map-type';

@Component({
    selector: 'ui-webem-panel-split-area',
    templateUrl: './panel-split-area.component.html',
    styleUrls: ['./panel-split-area.component.scss']
})
export class PanelSplitAreaComponent implements OnInit, AfterContentInit {

    @Output() transitionEnd = new EventEmitter<void>();

    @ContentChildren(SplitAreaDirective, { descendants: true }) markers: QueryList<SplitAreaDirective>;
    @ContentChildren(CurledUpAreaDirective) curledUpArea: QueryList<CurledUpAreaDirective>;

    curledUpAreaMap: CurledUpAreaMap = {};
    @Input()
    splitDirection: ('horizontal' | 'vertical') = 'horizontal';

    constructor() {
    }

    gutterClick(data: IOutputData) {
        const markers = this.markers.toArray();
        const curledUpAraForGutter = this.curledUpArea.toArray()[data.gutterNum - 1];
        const forId = curledUpAraForGutter && curledUpAraForGutter.context && curledUpAraForGutter.context.forId;
        const rightMarker = markers.find((marker) => marker.context.id === forId);

        if (rightMarker) {
            if (rightMarker.context.size === rightMarker.context.maxSize || rightMarker.context.size >= rightMarker.context.defaultSize) {
                rightMarker.context.size = rightMarker.context.minSize;
            } else {
                rightMarker.context.size = rightMarker.context.defaultSize || rightMarker.context.maxSize;
            }
        }
    }


    dragEnd(data: IOutputData) {
        this.markers.toArray().forEach((marker: SplitAreaDirective, index: number) => {
            marker.context.size = data.sizes[index];
        });
    }

    trackMarker(index: number) {
        return index;
    }

    ngOnInit() {
    }

    ngAfterContentInit(): void {
        this.curledUpArea.toArray().forEach((curledUpAreaDirective) => {
            if (curledUpAreaDirective.context) {
                this.curledUpAreaMap[curledUpAreaDirective.context.forId] = curledUpAreaDirective;
            }
        });
    }
}
