import {
    Component,
    ContentChildren,
    OnInit,
    QueryList,
    Input,
    EventEmitter,
    Output
} from '@angular/core';
import { SplitPanelDirective } from '../../directives/split-panel/split-panel.directive';
import { PanelToggleDirective } from '../../directives/panel-toggle/panel-toggle.directive';

@Component({
    selector: 'ui-webem-panel-split',
    templateUrl: './panel-split.component.html',
    styleUrls: ['./panel-split.component.scss']
})
export class PanelSplitComponent implements OnInit {
    @Input() direction: 'horizontal' | 'vertical' = 'horizontal';
    @Input() useTransition = true;
    @Input() unit: 'pixel' | 'percent' = 'pixel';
    @Output() transitionEnd = new EventEmitter<void>();
    @Output() dragStart = new EventEmitter<void>();
    @Output() dragEnd = new EventEmitter<void>();

    @ContentChildren(SplitPanelDirective) splitPanels: QueryList<SplitPanelDirective>;
    @ContentChildren(PanelToggleDirective) panelToggles: QueryList<PanelToggleDirective>;

    constructor() {}

    ngOnInit() {}

    trackIndex(index: number) {
        return index;
    }
}
