import { Component, Input, OnDestroy, OnInit, QueryList, SimpleChanges, ViewChildren, ViewChild, ElementRef } from '@angular/core';
import { ListKeyManager } from '@angular/cdk/a11y';
import { SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { RoutesList } from '../../models/routes-list';
import { ARROW_DOWN, ARROW_UP, ENTER } from '../../constants/keyboard-keys.constants';
import { NavigationSearchKeyboardService } from '../../shared/navigation-search-keyboard/navigation-search-keyboard.service';
import { NavigationSearchRoutesListItemComponent } from '../navigation-search-routes-list-item/navigation-search-routes-list-item.component';


@Component({
    selector: 'webem-navigation-search-routes-list',
    templateUrl: './navigation-search-routes-list.component.html',
    styleUrls: ['./navigation-search-routes-list.component.scss']
})
export class NavigationSearchRoutesListComponent implements OnInit, OnDestroy {
    @Input() routesList: RoutesList;
    @Input() isOverlayClosed: boolean;
    routesListChanged = false;
    NOT_FOUND = 'Not found';
    keyboardEventsManager: ListKeyManager<any>;
    @ViewChildren(NavigationSearchRoutesListItemComponent) listItems: QueryList<any>;
    @ViewChild('listref', { read: ElementRef }) listref: ElementRef;

    keyboardEventSubscription: Subscription;

    constructor(private readonly navSearchKeyboardService: NavigationSearchKeyboardService) {
    }

    ngOnInit(): void {
        this.keyboardEventSubscription = this.navSearchKeyboardService.handleKeyboardEvent
            .subscribe((event: KeyboardEvent) => {
                this.handleKeyUp(event);
            });
    }

    ngAfterViewInit(): void {
        this.keyboardEventsManager = new ListKeyManager(this.listItems)
            .withWrap()
            .skipPredicate((item: NavigationSearchRoutesListItemComponent) => item.isDisabled());
        this.initKeyManagerHandlers();
    }

    ngAfterViewChecked(): void {
        if (this.routesListChanged) this.updateStylesInRoutesList();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.routesList && !changes.routesList.firstChange) this.routesListChanged = true;
        if (changes.isOverlayClosed && changes.isOverlayClosed.currentValue && this.keyboardEventsManager) {
            this.keyboardEventsManager.setActiveItem(-1);
        }
    }

    ngOnDestroy(): void {
        this.keyboardEventSubscription.unsubscribe();
    }

    private initKeyManagerHandlers() {
        this.keyboardEventsManager.change
            .subscribe((activeIndex: number) => {
                this.listItems.map((item, index: number) => {
                    item.setActive(activeIndex === index);

                    return item;
                });
                this.scrollToRouteIndex(activeIndex);
            });
    }

    private handleKeyUp(event: KeyboardEvent): void {
        event.stopImmediatePropagation();
        if (this.keyboardEventsManager) {
            if ([ARROW_UP, ARROW_DOWN].includes(event.key)) {
                this.keyboardEventsManager.onKeydown(event);
            } else if (event.key === ENTER && this.keyboardEventsManager.activeItem) {
                this.keyboardEventsManager.activeItem.redirectToSelectedRoute();
            }
        }
    }

    scrollToRouteIndex(index: number) {
        if (!this.listref || !this.listref.nativeElement || !this.listref.nativeElement.children) return;

        const container = this.listref.nativeElement;
        const routeElements = container.children;
        const element = routeElements[index];
        if (!element) return;

        const childElement = element.children[0];
        if (!childElement) return;

        const firstRouteElement = routeElements[0];
        if (!firstRouteElement) return;

        if (this.isElementCompletelyVisible(container, element, childElement)) return;

        const firstElementOffset = firstRouteElement.offsetTop;
        const elementTop = element.offsetTop;
        container.scrollTop = elementTop - firstElementOffset;
    }

    private isElementCompletelyVisible(container: HTMLElement, element: HTMLElement, childElement: HTMLElement) {
        const containerTop = container.scrollTop + container.offsetTop;
        const containerBottom = containerTop + container.clientHeight;

        const elementHeight = childElement.clientHeight;
        const elementTop = element.offsetTop;
        const elementBottom = elementHeight + elementTop;

        return elementTop >= containerTop && elementBottom <= containerBottom;
    }

    private updateStylesInRoutesList(): void {
        let styledLabel;
        this.routesList.filteredRoutes.forEach((route: SelectItem) => {
            if (!route.label) return;
            styledLabel = route.label.replace(new RegExp(this.routesList.searchText, 'gi'), (match: string) => {
                return `<span class="bold-and-underlined-text">${match}</span>`;
            });

            const elementId = `navigation-search-${route.value}`;
            // @ts-ignore
            if (document.getElementById(elementId)) document.getElementById(elementId).innerHTML = styledLabel;
        });
        this.routesListChanged = false;
    }
}
