import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { SaveConfigurationState } from '../../model/save-button-state';

@Component({
    selector: 'ui-webem-save-button',
    templateUrl: './save-button.component.html',
    styleUrls: ['./save-button.component.scss']
})
export class SaveButtonComponent implements OnInit {
    @Input() enabledButtonTitle: string;
    @Input() ongoingButtonTitle: string;
    @Input() disabledButtonTitle: string;

    @Input() saveConfigurationState: SaveConfigurationState;
    @Output() saveClick = new EventEmitter<void>();

    saveConfigurationStateEnum = SaveConfigurationState;

    saveButtonEnabledModel: ButtonModel;
    saveButtonDisabledModel: ButtonModel;
    saveButtonOngoingModel: ButtonModel;

    constructor() {
    }

    ngOnInit() {
        this.saveButtonEnabledModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-content-save',
            title: this.enabledButtonTitle,
            length: ButtonLength.Long
        };

        this.saveButtonDisabledModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-content-save',
            title: this.disabledButtonTitle,
            length: ButtonLength.Long
        };

        this.saveButtonOngoingModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-loading mdi-spin',
            title: this.ongoingButtonTitle,
            length: ButtonLength.Long
        };
    }

    onSaveClick() {
        this.saveClick.emit();
    }
}
