import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';

import { LegalNoticeModalComponent } from './components/legal-notice-modal/legal-notice-modal.component';
import { LegalNoticeModalEffects } from './core/effects/legal-notice-modal.effects';
import * as legalNoticeReducer from './core/reducers/legal-notice-modal.reducer';
import { ConfirmationService } from 'primeng/api';
import { ConfirmDialogModule } from 'primeng/confirmdialog';
import { WebemButtonModule } from '@oam/cu-ui/lib/button/button.module';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';

@NgModule({
    declarations: [LegalNoticeModalComponent],
    imports: [
        CommonModule,
        ConfirmDialogModule,
        StoreModule.forFeature(legalNoticeReducer.legalNoticeModalFeatureKey, legalNoticeReducer.reducer),
        EffectsModule.forFeature([LegalNoticeModalEffects]),
        WebemButtonModule
    ],
    providers: [
        ConfirmationService, DynamicDialogRef, DynamicDialogConfig
    ],
    exports: [
        LegalNoticeModalComponent
    ]
})
export class LegalNoticeModalModule {
}
