import { ErrorHandler, NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { metaReducers, reducers } from './core/reducers/webem-reducer';
import { environment } from '../environments/environment';
import * as fromCu from '@oam/cu-shared/lib/core/reducers/cu.reducer';
import * as fromCloud from '@oam/core/lib/reducers/cloud.reducer';
import * as cellsStatusReducer from '@oam/cells-status/lib/core/reducers/cells-status.reducer';
import * as fromToast from '@oam/core/lib/reducers/toast-message.reducer';
import * as fromUser from '@oam/core/lib/reducers/user.reducer';
import * as fromAppSettings from '@oam/core/lib/reducers/app-settings.reducer';
import {
    DefaultRouterStateSerializer,
    routerReducer,
    RouterStateSerializer,
    StoreRouterConnectingModule
} from '@ngrx/router-store';
import { ROUTER_STATE } from '@oam/core/lib/selectors/router.selectors';
import { LoggerModule, NgxLoggerLevel } from 'ngx-logger';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { RouterEffects } from '@oam/core/lib/effects/router.effects';
import { ToastMessageEffects } from '@oam/core/lib/effects/toast-message.effects';
import { HelpEffects } from '@oam/core/lib/effects/help.effects';
import { LogForActionsEffects } from '@oam/core/lib/effects/log-for-actions.effects';
import { getRuntimeChecksConfiguration } from '@oam/webem/app/core/store/store-runtime-checks-configuration';
import { ToastrModule } from 'ngx-toastr';
import { NoopAnimationsModule } from '@angular/platform-browser/animations';
import { WebemRouterModule } from './core/router/webem-router.module';
import { NxModule } from '@nrwl/angular';
import { StoreModule } from '@ngrx/store';
import { BrowserModule } from '@angular/platform-browser';
import { EffectsModule } from '@ngrx/effects';
import { FlexLayoutModule } from '@angular/flex-layout';
import { CuTrsApiModule } from '@oam/cu-trs-api/cu-trs-api.module';
import { CuApiGwClientModule } from '@oam/cu-api-gw-api/cu-api-gw-client.module';
import { CuCmClientModule } from '@oam/cu-cm-api/cu-cm-client.module';
import { CuPmv2ClientModule } from '@oam/cu-pmv2-api/cu-pmv-2-client.module';
import { CuAsmClientModule } from '@oam/cu-asm-api/cu-asm-client.module';
import { CuFmClientModule } from '@oam/cu-fm-api/cu-fm-client.module';
import { CuCpConfigClientModule } from '@oam/cu-cp-config-api/cu-cp-config-client.module';
import { CuEffects } from '@oam/cu-shared/lib/core/effects/cu.effects';
import { ConfigurationSharedServicesModule } from '@oam/configuration-shared/lib/configuration-shared-services.module';
import {
    fromBrowserCompatibilityReducer,
    fromOverlay,
    LegalNoticeModalModule,
    SpinnerModule,
    WebemOverlayModule
} from '@oam/cu-ui';
import { AppSettingsEffects } from '@oam/core/lib/effects/app-settings.effects';
import { BlockUIModule } from 'primeng/blockui';
import { ConfirmationService } from 'primeng/api';
import { ErrorHandlerService } from '@oam/webem/app/error-handler.service';
import { CellsStatusSetFilterEffects } from '@oam/cells-status/lib/core/effects/cells-status-set-filter.effects';
import { KeyboardShortcutsModule } from '@oam/keyboard-shortcuts/lib/keyboard-shortcuts.module';
import { SplitViewRestrictionsModule } from '@oam/split-view/lib/split-view-restrictions.module';
import { RESTRICTED_PATTERNS } from './core/config/split-view/restricted-selection-group';
import { CU_AVAILABLE_VIEWS } from '@oam/webem/app/core/config/split-view/available-list-of-views';
import { DetailsPanelConfigModule } from '@oam/details-panel/lib/details-panel-config.module';
import { CU_DETAILS_PANEL_CONFIG } from '@oam/webem/app/core/config/details-panel/details-panel-config';
import { CU_REFRESHING_VIEWS } from './core/config/split-view/refreshing-views';
import { WebemRoutesPaths } from '@oam/cu-shared';
import { SHORTCUT_PATHS_CU } from '@oam/webem/app/core/config/keyboard-shortcuts/keyboard-shortcuts-config';
import { EnvironmentVariablesModule } from '@oam/core/lib/environment-variables/environment-variables.module';
import { ErrorInterceptor } from '@oam/core/lib/interceptors/error.interceptor';
import { JwtInterceptor } from '@oam/core/lib/interceptors/jwt.interceptor';
import { UserModule } from '@oam/core/lib/user/user.module';
import * as fromNavigationSearch
    from '@oam/layouts/lib/modules/navigation-search/core/reducers/navigation-search.reducer';
import { CustomSerializer } from '@oam/core/lib/services/custom-serializer.service';
import { CuOamAgentClientModule } from '@oam/cu-oam-agent-api';
import { DiagnosticSnapshotModule } from '@oam/diagnostic-snapshot/diagnostic-snapshot.module';
import { SNAPSHOT_NAME_PARAMETERS_CU } from '@oam/webem/app/core/config/snapshot-name-parameters/snapshot-name-parameters-config';
import { NgFeatureFlagModule } from '@oam/ng-feature-flag';
import { featureFlagConf } from '@oam/webem/environments/feature-flag-conf.prod';
import { navigations } from '@oam/webem/app/core/models/navigations.model';
import { mapToFeatureFlagGuardRules } from '@oam/core/lib/functions/feature-flag-util';
import { NavigationModule } from '@oam/navigation';
import { VCU_LOCAL_USER_PROFILES } from '@oam/webem/app/core/models/vcu-local-user-profiles';
import { RoleAccessModule } from '@oam/role-access-ng';
import { mapToRoleAccessGuardRules } from '@oam/role-access-ng/lib/guards/role-access.util';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';

@NgModule({
    declarations: [AppComponent],
    imports: [
        NavigationModule.forRoot(navigations),
        NgFeatureFlagModule.forRoot({
            featureFlagConf,
            featureFlagGuardRules: mapToFeatureFlagGuardRules(navigations)
        }),
        RoleAccessModule.forRoot({
            userProfiles: VCU_LOCAL_USER_PROFILES,
            roleAccessGuardRules: mapToRoleAccessGuardRules(navigations),
            index: RouterLink.dashboard,
            ignoreWhen: {
                token: VnfcSharedService,
                fn: vnfcSharedService => () => vnfcSharedService.isVmcu()
            }
        }),
        HttpClientModule,
        EnvironmentVariablesModule.forRoot(environment),
        LoggerModule.forRoot({
            level: environment.production ? NgxLoggerLevel.LOG : NgxLoggerLevel.TRACE
        }),
        BrowserModule,
        NoopAnimationsModule,
        LegalNoticeModalModule,
        WebemRouterModule,
        NxModule.forRoot(),
        ToastrModule.forRoot({
            positionClass: 'toast-bottom-right'
        }),
        StoreModule.forRoot(reducers, {
            ...getRuntimeChecksConfiguration(environment),
            metaReducers
        }),
        StoreModule.forFeature(cellsStatusReducer.featureKey, cellsStatusReducer.reducer),
        StoreModule.forFeature(fromBrowserCompatibilityReducer.featureKey, fromBrowserCompatibilityReducer.reducer),
        StoreModule.forFeature(fromOverlay.overlayStateFeatureKey, fromOverlay.reducer),
        StoreModule.forFeature(fromNavigationSearch.navigationSearchStateFeatureKey, fromNavigationSearch.reducer),
        StoreModule.forFeature(fromCu.featureKey, fromCu.reducer),
        StoreModule.forFeature(ROUTER_STATE, routerReducer),
        StoreModule.forFeature(fromToast.featureKey, fromToast.reducer),
        StoreModule.forFeature(fromUser.featureKey, fromUser.reducer),
        StoreModule.forFeature(fromAppSettings.appSettingsFeatureKey, fromAppSettings.reducer),
        StoreModule.forFeature(fromCloud.featureKey, fromCloud.reducer),

        EffectsModule.forRoot([ToastMessageEffects, LogForActionsEffects]),
        EffectsModule.forFeature([RouterEffects, CuEffects, HelpEffects, AppSettingsEffects, CellsStatusSetFilterEffects]),
        ConfigurationSharedServicesModule,
        WebemOverlayModule.forRoot(),
        WebemOverlayModule.forFeature(),
        FlexLayoutModule,
        UserModule,
        CuApiGwClientModule.forRoot({ rootUrl: '/api/sec/v1' }),
        CuTrsApiModule.forRoot({ rootUrl: '/api/trs/v1' }),
        CuPmv2ClientModule.forRoot({ rootUrl: '/api/pm/v2' }),
        CuFmClientModule.forRoot({ rootUrl: '/api/fm/v2' }),
        StoreRouterConnectingModule.forRoot({ serializer: DefaultRouterStateSerializer, stateKey: ROUTER_STATE }),
        CuAsmClientModule.forRoot({ rootUrl: '/api/asm/v1' }),
        CuCpConfigClientModule.forRoot({ rootUrl: '/api/cp-config/v1' }),
        CuOamAgentClientModule.forRoot({ rootUrl: '/api/agent/v1' }),
        CuCmClientModule.forRoot({ rootUrl: '/api/cm' }),
        environment.instrumentation,
        SpinnerModule,
        BlockUIModule,
        KeyboardShortcutsModule.forRoot({
            SHORTCUTS_PATHS: SHORTCUT_PATHS_CU
        }),
        DetailsPanelConfigModule.forRoot(CU_DETAILS_PANEL_CONFIG),
        SplitViewRestrictionsModule.forRoot({
            RESTRICTED_PATTERNS,
            AVAILABLE_VIEWS: CU_AVAILABLE_VIEWS,
            REFRESHING_VIEWS: CU_REFRESHING_VIEWS,
            DEFAULT_REDIRECTION_AFTER_FAILED_RESTRICTION_CHECKED: WebemRoutesPaths.splitView
        }),
        DiagnosticSnapshotModule.forRoot({
                SNAPSHOT_NAME_PARAMETERS: SNAPSHOT_NAME_PARAMETERS_CU
        })
    ],
    entryComponents: [],
    providers: [
        { provide: RouterStateSerializer, useClass: CustomSerializer },
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
        { provide: ErrorHandler, useClass: ErrorHandlerService },
        ConfirmationService
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}

