import { createAction, props } from '@ngrx/store';

enum ActionTypes {
    ToggleDetailsPanelVisibility = '[Details Panel] Toggle details panel visibility',
    Resizing = '[Details Panel] Resizing panel'
}

export const toggleDetailsPanelVisibility = createAction(
    ActionTypes.ToggleDetailsPanelVisibility, props<{ url?: string }>()
);
export const setDetailsPanelResizingFlag = createAction(
    ActionTypes.Resizing, props<{ payload: boolean}>()
);
