import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { NGXLogger } from 'ngx-logger';
import { updateUserInformation } from '@oam/core/lib/actions/user.actions';
import { select, Store } from '@ngrx/store';
import { ConsoleLogsDatabaseService } from '@oam/core/lib/services/console-logs-database.service';
import { UserInformation } from '@oam/core/lib/store/user-state';
import { RouteConfigLoadEnd, RouteConfigLoadStart, Router } from '@angular/router';
import { get } from 'lodash';
import { WebemRoutesPaths } from '@oam/cu-shared';
import { KeyboardShortcutsService } from '@oam/keyboard-shortcuts/lib/core/services/keyboard-shortcuts.service';
import { gitVersion } from '../environments/git-version';
import { ConsoleLoggerMonitor } from '@oam/core/lib/utils/console-logger-monitor';
import { logout } from '@oam/auth/lib/auth/core/actions/logout.actions';
import { VnfcSharedService, vnfcTypeSessionKey } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { FeatureFlag } from '@oam/feature-flag';
import { selectRoleAndReadOnlyMode } from '@oam/core/lib/user/core/reducers/user-reducer';
import { RoleAccessService } from '@oam/role-access-core';

@Component({
    selector: 'webem-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy, OnInit {
    loadingRouteConfig = false;

    constructor(private readonly logger: NGXLogger,
                private readonly store: Store,
                private readonly logsDatabase: ConsoleLogsDatabaseService,
                private readonly router: Router,
                private readonly vnfcSharedService: VnfcSharedService,
                private featureFlag: FeatureFlag,
                private readonly roleAccessService: RoleAccessService,
                private readonly keyboardShortcuts: KeyboardShortcutsService) {
        this.logger.registerMonitor(new ConsoleLoggerMonitor(logsDatabase));
    }

    @HostListener('window:beforeunload', ['$event'])
    async onBeforeUnloadHandler(event: BeforeUnloadEvent) {
        if (window.process) return;

        event.preventDefault();

        await this.store.dispatch(logout());
        (event || window.event).returnValue = '';

        return '';
    }

    ngOnDestroy(): void {
        this.keyboardShortcuts.turnOff();
    }

    ngOnInit(): void {
        this.logger.info('feature flags', this.featureFlag['flags']);
        this.keyboardShortcuts.turnOn();
        this.logger.trace('Application start');
        this.logger.trace(`Application info: CU version: ${gitVersion.branch}@${gitVersion.commitLong}`);
        const userInfoFomSession = sessionStorage.getItem('userInfo');
        if (userInfoFomSession) {
            const userInfo: UserInformation = JSON.parse(userInfoFomSession);
            this.store.dispatch(updateUserInformation({ payload: userInfo }));
        }
        const vnfcType = sessionStorage.getItem(vnfcTypeSessionKey);
        if (vnfcType) this.vnfcSharedService.setVnfcType(vnfcType);

        this.router.events.subscribe(event => {
            const path = get(event, 'route.path');
            if (!path || path === WebemRoutesPaths.dashboard) return;
            if (event instanceof RouteConfigLoadStart) {
                this.loadingRouteConfig = true;
            } else if (event instanceof RouteConfigLoadEnd) {
                this.loadingRouteConfig = false;
            }
        });

        this.store.pipe(select(selectRoleAndReadOnlyMode))
            .subscribe(({role, readOnlyMode}) => {
                this.roleAccessService.setRoleAndReadOnlyMode({
                    currentRole: role,
                    readOnlyMode: readOnlyMode
                });
            });
    }
}
