import { Action, createReducer, on } from '@ngrx/store';

import {
    changeSelectionInBottomView,
    changeSelectionInTopView,
    changeSelectionRestriction,
    removeSelectedViews,
    setAvailableSplitViewOptions,
    updateSelectedViews,
    updateWholeState
} from '../actions/split-view.actions';
import { RoutesPathsWithParams } from '@oam/split-view';
import { GroupOfViews } from '@oam/split-view/lib/core/models/group-of-views.interface';

export interface SplitViewState {
    topView?: string | RoutesPathsWithParams;
    bottomView?: string | RoutesPathsWithParams,
    restrictionsForGroupViews?: boolean,
    availableViews: GroupOfViews[],
}
export const splitViewsStateFeatureKey = 'splitViews';

export const initialState: SplitViewState = {
    restrictionsForGroupViews: undefined,
    availableViews: [{
        label: '',
        icon: '',
        items: [],
    }]
};

const splitViewReducer = createReducer(
    initialState,

    on(setAvailableSplitViewOptions, (state, {availableViews}) => ({
        ...state,
        availableViews
    })),

    on(changeSelectionInTopView, (state, action) => ({
        ...state,
        topView: action.selectedView
    })),
    on(changeSelectionInBottomView, (state, action) => ({
        ...state,
        bottomView: action.selectedView
    })),
    on(changeSelectionRestriction, (state, action) => ({
        ...state,
        restrictionsForGroupViews: action.selectionRestriction
    })),
    on(updateSelectedViews, (state, action) => ({
        ...state,
        availableViews: action.views
    })),
    on(removeSelectedViews, (state) => ({
        ...state,
        topView: undefined,
        bottomView: undefined
    })),
    on(updateWholeState, (state, action) => action.newState)
);

export function reducer(state: SplitViewState, action: Action) {
    return splitViewReducer(state, action);
}
