import { Component, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { combineLatest, Observable, Subject, Subscription } from 'rxjs';
import {
    securityNoticeConfirmButtonVisibleSelector, securityNoticeErrorSelector,
    securityNoticeSelector
} from '../../core/selectors/legal-notice-modal.selectors';
import { logout } from '@oam/auth/lib/auth/core/actions/logout.actions';
import { denyLegalNoticeModal, submitLegalNoticeModal } from '../../core/actions/legal-notice-modal.actions';
import { BaseDialogComponent } from '@oam/shared/lib/components/base-dialog/base-dialog-component';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { ButtonModel } from '@oam/cu-ui/lib/button/model/button-model.definition';
import { ButtonTheme } from '@oam/cu-ui/lib/button/model/button-theme.enum';
import { takeUntil } from 'rxjs/operators';
import { BUTTONLABEL, CLASSES } from '@oam/cu-ui/lib/legal-notice-modal/core/legal-notice.constants';
@Component({
    selector: 'ui-webem-legal-notice-modal',
    templateUrl: './legal-notice-modal.component.html',
    styleUrls: ['./legal-notice-modal.component.scss']
})
export class LegalNoticeModalComponent extends BaseDialogComponent  implements OnInit, OnDestroy {
    securityNotice$: Observable<string>;
    securityNoticeButton$: Subscription;
    securityNoticeConfirmVisible$: Observable<boolean>;
    securityNoticeError$: Observable<boolean>;
    ngUnsubscribe$: Subject<void> = new Subject<void>();
    iconType: string;

    readonly confirmButtonModel = {
        label: 'Confirm',
        iconPosition: 'right',
        theme: ButtonTheme.Accent
    } as ButtonModel;

    commonRejectButtonModel = {
        label: 'Reject',
        iconPosition: 'right',
        theme: ButtonTheme.Default
    } as ButtonModel;

    constructor(
        protected store: Store,
        public config: DynamicDialogConfig,
        ref: DynamicDialogRef
    ) {
        super(store, ref);
    }

    ngOnInit(): void {
        this.securityNotice$ = this.store.select(securityNoticeSelector);
        this.securityNoticeConfirmVisible$ = this.store.select(securityNoticeConfirmButtonVisibleSelector);
        this.securityNoticeButton$ = combineLatest(
            this.securityNoticeConfirmVisible$,
            this.store.select(securityNoticeErrorSelector))
            .pipe(takeUntil(this.ngUnsubscribe$))
            .subscribe(([confirmButtonVisible, errorButtonVisible]) => {
                this.commonRejectButtonModel ={
                    ...this.commonRejectButtonModel,
                    theme: confirmButtonVisible ? ButtonTheme.Default : ButtonTheme.Accent,
                    label: confirmButtonVisible ? BUTTONLABEL.REJECT : (errorButtonVisible ? BUTTONLABEL.OK : BUTTONLABEL.LOGOUT)
                };
                this.iconType = confirmButtonVisible ? CLASSES.INFO : (errorButtonVisible ? CLASSES.ERROR : CLASSES.LOADING);
            });
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe$.next();
        this.ngUnsubscribe$.complete();
    }

    accept(): void {
        this.store.dispatch(submitLegalNoticeModal());
        this.closeDialog();
    }

    deny(): void {
        this.store.dispatch(logout());
        this.store.dispatch(denyLegalNoticeModal());
        this.closeDialog();
    }
}
