import { Table } from 'primeng/table';

export { applyPrimeTablePatch };

function applyPrimeTablePatch(table: Table, containerPadding = 55) {

    table.onColumnResizeBegin = (event: MouseEvent) => {
        table.lastResizerHelperX = (event.pageX - containerPadding + table.containerViewChild.nativeElement.scrollLeft);
        event.preventDefault();
        event.stopPropagation();
    };

    table.onColumnResize = (event: MouseEvent) => {
        table.resizeHelperViewChild.nativeElement.style.height = table.containerViewChild.nativeElement.offsetHeight + 'px';
        table.resizeHelperViewChild.nativeElement.style.top = 0 + 'px';
        table.resizeHelperViewChild.nativeElement.style.left =
            (event.pageX - containerPadding + table.containerViewChild.nativeElement.scrollLeft) + 'px';
        table.resizeHelperViewChild.nativeElement.style.display = 'block';

        event.stopPropagation();
    };
}
