import {
    Component,
   HostListener, OnInit,
    ChangeDetectorRef,
    TemplateRef,
    ViewContainerRef,
    ViewChild,
    OnDestroy,
    AfterViewInit
} from '@angular/core';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { RoutedMenuItem, SplitAreaDirectiveContext } from '@oam/cu-ui';
import { StatusModelBar } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { Store } from '@ngrx/store';
import { QuickMenuAlarms } from '@oam/navigation/lib/navigation/core/models/quick-menu-alarms.interface';
import { SplitViewRestrictionsService } from '@oam/split-view/lib/core/services/split-view-restrictions.service';
import { setAvailableSplitViewOptions } from '@oam/split-view/lib/core/actions/split-view.actions';
import { calculateMiddleOfView } from '@oam/split-view/lib/core/functions/split-view-functions';

import { combineLatest, Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { selectAppSettingsRefresh } from '@oam/core/lib/reducers/app-settings.reducer';

import { selectSplitViewsDetails } from '@oam/split-view/lib/core/selectors/split-view.selectors';
import { SPLIT_VIEW_LOCATION } from '@oam/details-panel/lib/core/model/split-view-location.type';
import { LayoutsConfigService } from '@oam/layouts/lib/layouts-config.service';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';

type RefreshMetadataItem = {
    container: ViewContainerRef,
    template: TemplateRef<any>
};

type RefreshMetadata = {
    [TKey in SPLIT_VIEW_LOCATION]: RefreshMetadataItem | undefined
};

@Component({
    selector: 'webem-split-view-layout',
    templateUrl: './split-view-layout.component.html',
    styleUrls: ['./split-view-layout.component.scss']
})
export class SplitViewLayoutComponent implements OnInit, OnDestroy, AfterViewInit {
    navigationMenu: Navigation[];
    quickMenuItems: RoutedMenuItem[];
    contentSize: SplitAreaDirectiveContext | null;
    statusBarModel: StatusModelBar;
    quickMenuAlarmsModel: QuickMenuAlarms;
    gutterLocation: number;

    ngUnsubscribe$: Subject<void> = new Subject<void>();

    @ViewChild('topViewPanel', { static: true }) topViewPanelTemplateRef: TemplateRef<any>;
    @ViewChild('bottomViewPanel', { static: true }) bottomViewPanelTemplateRef: TemplateRef<any>;

    @ViewChild('topViewContainer', { read: ViewContainerRef }) topViewContainerRef: ViewContainerRef;
    @ViewChild('bottomViewContainer', { read: ViewContainerRef }) bottomViewContainerRef: ViewContainerRef;

    @HostListener('window:resize')
    onResize() {
        this.gutterLocation = calculateMiddleOfView();
    }

    constructor(
        public changeDetectorRef: ChangeDetectorRef,
        private readonly store: Store,
        private readonly splitViewRestrictionsService: SplitViewRestrictionsService,
        private readonly vnfcSharedService: VnfcSharedService,
        private readonly layoutsConfigService: LayoutsConfigService) {
    }

    ngOnInit(): void {
        this.navigationMenu = this.layoutsConfigService.getNavigations;
        this.quickMenuItems = this.layoutsConfigService.getQuickMenuItems;
        this.statusBarModel = this.layoutsConfigService.getStatusBarModel;
        this.quickMenuAlarmsModel = this.layoutsConfigService.getQuickMenuAlarmsModel;
        this.gutterLocation = calculateMiddleOfView();
        this.store.dispatch(setAvailableSplitViewOptions({
            availableViews: this.splitViewRestrictionsService.getAvailableViews()
        }));
    }

    ngAfterViewInit(): void {
        combineLatest([
            this.store.select(selectAppSettingsRefresh),
            this.store.select(selectSplitViewsDetails)
        ]).pipe(
            filter(([counter]) => counter > 0),
            takeUntil(this.ngUnsubscribe$)
        ).subscribe(([, { topView, bottomView }]) => {
            const refreshMetadata: RefreshMetadata = this.getViewRefreshMetadata();
            const refreshLocation = this.splitViewRestrictionsService.getViewRefreshLocation(topView, bottomView);
            this.refreshView(refreshMetadata[refreshLocation]);
        });

        this.changeDetectorRef.detectChanges();
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe$.next();
        this.ngUnsubscribe$.complete();
    }

    getViewRefreshMetadata(): RefreshMetadata {
        return {
            [SPLIT_VIEW_LOCATION.TOP]: {
                container: this.topViewContainerRef,
                template: this.topViewPanelTemplateRef
            },
            [SPLIT_VIEW_LOCATION.BOTTOM]: {
                container: this.bottomViewContainerRef,
                template: this.bottomViewPanelTemplateRef
            },
            [SPLIT_VIEW_LOCATION.NONE]: undefined
        };
    }

    refreshView(refreshMetadataItem: RefreshMetadataItem | undefined): void {
        if (refreshMetadataItem) {
            refreshMetadataItem.container.clear();
            refreshMetadataItem.container.createEmbeddedView(refreshMetadataItem.template);
        }
    }
}
