import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, CanLoad, Route, Router } from '@angular/router';
import { RoleAccessConfig } from '@oam/role-access-ng';
import { RoleAccessService } from '@oam/role-access-core';

@Injectable({
    providedIn: 'root'
})
export class RoleAccessGuard implements CanActivate, CanLoad {
    private mapping: Map<string, string>;

    constructor(private readonly router: Router, private roleAccessConfig: RoleAccessConfig, public roleAccessService: RoleAccessService) {
        this.mapping = this.roleAccessConfig.roleAccessGuardRules.reduce((result, rule) => {
            result.set(rule.path, rule.roleAccessFlag);

            return result;
        }, new Map<string, string>());
    }

    canLoad(route: Route): boolean {
        this.redirectToDefault(route.path);

        return this.isPathEnabled(route.path);
    }

    canActivate(route: ActivatedRouteSnapshot): boolean {
        this.redirectToDefault(route.routeConfig?.path);

        return this.isPathEnabled(route.routeConfig?.path);
    }

    isPathEnabled(path = ''): boolean {
        const roleAccessFlag = this.mapping.get(path);

        return roleAccessFlag ? !this.roleAccessService.isForbiddenForCurrentRole(roleAccessFlag) : true;
    }

    redirectToDefault (path = '') {
        //  dashboard as default page after user login.
        //  Otherwise when user with role(e.g. BTSApplication)  want to access restricted page would be stuck
        if (!this.isPathEnabled(path)) {
            this.router.navigate([this.roleAccessConfig.index]);
        }
    }
}
