import { createAction } from '@ngrx/store';


export enum NavigationSearchActions {
    OpenOverlay = '[Navigation Search] Open overlay',
    CloseOverlay = '[Navigation Search] Close overlay'
}

export const setNavigationSearchOverlayOpened = createAction(
    NavigationSearchActions.OpenOverlay
);

export const setNavigationSearchOverlayClosed = createAction(
    NavigationSearchActions.CloseOverlay
);
