import { Injectable } from '@angular/core';
import { LayoutsConfigService } from '@oam/layouts/lib/layouts-config.service';
import { select, Store } from '@ngrx/store';
import { VDuOperationalState } from '@oam/cu-asm-api';
import { ModelState } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { clearTestModels } from '@oam/diagnostic-shared/lib/core/actions/running-test-models.actions';
import { distinctUntilChanged } from 'rxjs/operators';
import { EMPTY_STRING } from '@oam/shared/lib/constants/common-charactors';
import { isEmpty, isEqual, isNil } from 'lodash';
import { UlTestModelResult } from '@oam/cu-cp-config-api';

export interface CumulativeResult {
    receivedBits: number,
    unreceivedBits: number,
    defectiveBits: number,
    receivedBlocks: number,
    unreceivedBlocks: number,
    defectiveBlocks: number
}

@Injectable({
    providedIn: 'root'
})

export class DiagnosticTestmodelService {

    public modelState: ModelState;
    private testModelOperations = {
        dlOperationID: 0
    };
    private dlTestModelOperationResultMsgs = {};

    constructor(private readonly layoutsConfigService: LayoutsConfigService,
                private readonly store: Store) {
        this.store.pipe(
            select(this.layoutsConfigService.getStatusBarModel.selectModelState),
            distinctUntilChanged(isEqual)
        ).subscribe((modelState) => {
            this.modelState = modelState;
            if (!this.isTDSState()) this.store.dispatch(clearTestModels());
        });
    }

    public isTDSState(): boolean {
        return this.modelState.operationalState === VDuOperationalState.Test;
    }

    public getOperationId(testType: string): number {
        return this.testModelOperations[testType];
    }

    public setOperationId(testType: string, value: number): void {
        this.testModelOperations[testType] = value;
    }

    public getOperationResultMsgByOperationID(operationID: number): string {
        return this.dlTestModelOperationResultMsgs[operationID] || EMPTY_STRING;
    }

    public setOperationResultMsgByOperationID(operationID: number, value: string): void {
        this.dlTestModelOperationResultMsgs[operationID] = value;
    }

    public getCumulativeResult(ulTestModelResult: Array<UlTestModelResult> | undefined) {
        if (!isNil(ulTestModelResult) && !isEmpty(ulTestModelResult)) {
            return ulTestModelResult.reduce((cumulativeResult: CumulativeResult, currentValue: any) => {
                cumulativeResult.receivedBits += currentValue.receivedBits;
                cumulativeResult.unreceivedBits += currentValue.unreceivedBits;
                cumulativeResult.receivedBlocks += currentValue.receivedBlocks;
                cumulativeResult.unreceivedBlocks += currentValue.unreceivedBlocks;
                cumulativeResult.defectiveBits += currentValue.defectiveBits;
                cumulativeResult.defectiveBlocks += currentValue.defectiveBlocks;

                return cumulativeResult;
            }, {
                receivedBits: 0,
                unreceivedBits: 0,
                defectiveBits: 0,
                receivedBlocks: 0,
                unreceivedBlocks: 0,
                defectiveBlocks: 0
            });
        }

        return {};
    }
}
