import { createFeatureSelector, createSelector } from '@ngrx/store';
import { BrowserCompatibilityState } from '../reducers/compatibility-browser.reducer';

export const selectBrowserCompatibilityState = createFeatureSelector<BrowserCompatibilityState>('browserCompatibility');

export const selectBrowserCompatibility = createSelector(
    selectBrowserCompatibilityState,
    browserCompatibility => browserCompatibility.browserIsSupport || browserCompatibility.browserInformationClosed
);
