import { createAction, props } from '@ngrx/store';

export enum StatusFilterActionTypes {
    setVisibleDuById = '[User] [Status Filter] Set DU id filter',
    resetFilters = '[User] [Cell Status Filter] Reset Cell status filters',
}

export const setVisibleDuByIdAction =
    createAction(StatusFilterActionTypes.setVisibleDuById, props<{ payload: string }>());

export const resetCellsViewFiltersAction =
    createAction(StatusFilterActionTypes.resetFilters);
