import { Pipe, PipeTransform } from '@angular/core';
import { lowerCase } from 'lodash';

const MRBTS_PREFIX = 'mrbts-';

@Pipe({
    name: 'formatMrbtsId'
})
export class FormatMrbtsIdPipe implements PipeTransform {

    transform(mrbtsId: string): string {
        const hasMrbtsPrefix = mrbtsId && mrbtsId.toLowerCase().startsWith(MRBTS_PREFIX);
        if (hasMrbtsPrefix) return mrbtsId;

        return `MRBTS-${mrbtsId}`;
    }

}
