import { createEntityAdapter, EntityAdapter } from '@ngrx/entity';
import { CountersValuesState } from '../store/counters-values.state';
import {
    appendCountersValues,
    changeCountersView,
    clearCountersValues,
    clearEndTime,
    clearStartTime,
    setCountersFilter,
    setCountersValues, retrievingCounterValuesFailed,
    setEndTime,
    setLatestEtag,
    setStartTime, retrievingCounterValuesSucceed
} from '../actions/counters-values.actions';
import { CountersViews } from '../models/counters-values.interface';
import { Action, createReducer, on } from '@ngrx/store';
import { CounterValues } from '../store/counter-values';
import { filterCounterKey } from '../functions/counter-id-utils';

export const countersValuesAdapter: EntityAdapter<CounterValues> = createEntityAdapter<CounterValues>({
    selectId: (counterValues: CounterValues) => counterValues.uniqueId
});

function appendCounters(state: CountersValuesState, values: CounterValues[], isOldRequest: boolean): CountersValuesState {
    const newValues: CounterValues[] = values.map((counterValues) => {
        const current: CounterValues | undefined = state.values.entities[counterValues.uniqueId];

        if (!current || !isOldRequest) {
            return {
                uniqueId: counterValues.uniqueId,
                values: counterValues.values
            };
        }

        return {
            uniqueId: counterValues.uniqueId,
            values: filterCounterKey(current.values, counterValues.values)
        };
    });

    return ({ ...state, values: countersValuesAdapter.setAll(newValues, state.values) });
}

const countersValuesInitialState: CountersValuesState = {
    values: countersValuesAdapter.getInitialState(),
    viewType: CountersViews.TABLE,
    filter: '',
    startTime: '',
    endTime: '',
    latestEtag: '',
    failedToRetrieveCountersValues: false
};

const reducer = createReducer(
    countersValuesInitialState,
    on(appendCountersValues, (state, { values, isOldRequest }) => appendCounters(state, values, isOldRequest)),
    on(setCountersValues, (state, { values }) => {
        return ({ ...state, values: countersValuesAdapter.setAll(values, state.values) });
    }),
    on(clearCountersValues, (state) => {
        return { ...state, values: countersValuesAdapter.removeAll(state.values) };
    }),
    on(setCountersFilter, (state, { filter }) => ({ ...state, filter })),
    on(setStartTime, (state, { timestamp }) => ({ ...state, startTime: timestamp })),
    on(clearStartTime, (state) => ({ ...state, startTime: '' })),
    on(setEndTime, (state, { timestamp }) => ({ ...state, endTime: timestamp })),
    on(clearEndTime, (state) => ({ ...state, endTime: '' })),
    on(changeCountersView, (state, { viewType }) => ({ ...state, viewType })),
    on(setLatestEtag, (state, { etag }: any) => ({ ...state, latestEtag: etag })),
    on(retrievingCounterValuesFailed, (state) => ({ ...state, failedToRetrieveCountersValues: true })),
    on(retrievingCounterValuesSucceed, (state) => ({ ...state, failedToRetrieveCountersValues: false }))
);

export function countersValuesReducer(state: CountersValuesState | undefined, action: Action) {
    return reducer(state, action);
}
