import { Action, createReducer, on } from '@ngrx/store';
import { convertUrlIntoKey } from '../utils/detail-panel.utils';
import { toggleDetailsPanelVisibility, setDetailsPanelResizingFlag } from '../actions/detail-panel.actions';

export const DETAILS_PANEL = 'DetailsPanel';

export interface DetailsPanelState {
    [key: string]: boolean;
    isBeingResized: boolean;
}

export const initialState: DetailsPanelState = {
    isBeingResized: false
};

const detailPanelReducer = createReducer(
    initialState,
    on(toggleDetailsPanelVisibility, (state, { url }) => {
        const stateKey = convertUrlIntoKey(url);

        return { ...state, [stateKey]: !state[stateKey] };
    }),
    on(setDetailsPanelResizingFlag, (state, { payload: isBeingResized }) => {
        return { ...state, isBeingResized };
    }),
);

export function reducer(state: DetailsPanelState | undefined, action: Action) {
    return detailPanelReducer(state, action);
}
