import { createAction, props } from '@ngrx/store';
import { RunningTestModels } from '@oam/cu-cp-config-api';
import { HttpErrorResponse } from '@angular/common/http';

enum RunningTestModelsActionTypes {
    StartRunningTestModelsPolling = '[Effect] Start Running Test Models Polling',
    RunningTestModelsRequest = '[Effect] Running Test Models Request',
    StopRunningTestModelsPolling = '[Effect] Stop Running Test Models Polling',
    UpdateRunningTestModels = '[Effect] Update Running Test Models',
    RunningTestModelsRequestError = '[Effect] Running Test Models Request Error',
    ClearTestModels = '[Test Model] Clear Test Models'
}

export const startRunningTestModelsPolling = createAction(RunningTestModelsActionTypes.StartRunningTestModelsPolling);
export const runningTestModelsRequest = createAction(RunningTestModelsActionTypes.RunningTestModelsRequest);
export const stopRunningTestModelsPolling = createAction(RunningTestModelsActionTypes.StopRunningTestModelsPolling);
export const updateRunningTestModels = createAction(RunningTestModelsActionTypes.UpdateRunningTestModels, props<{ payload: RunningTestModels }>());
export const runningTestModelsRequestError = createAction(RunningTestModelsActionTypes.RunningTestModelsRequestError, props<{ error: HttpErrorResponse }>());
export const clearTestModels = createAction(RunningTestModelsActionTypes.ClearTestModels);
