import { Component, OnDestroy, OnInit } from '@angular/core';
import { DateTime } from '@oam/core';
import { select, Store } from '@ngrx/store';
import { selectIsDisconnected } from '../../core/selectors/server-connection.selectors';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { Environment } from '@oam/core/lib/environment-variables/core/models';

@Component({
    selector: 'webem-server-connection-notification',
    templateUrl: './server-connection-notification.component.html',
    styleUrls: ['./server-connection-notification.component.scss']
})
export class ServerConnectionNotificationComponent implements OnInit, OnDestroy {
    appName: string;
    timestamp: string;
    isDisconnected: boolean;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    constructor(private readonly store: Store, private readonly environment: Environment) {
    }

    ngOnInit(): void {
        this.store.pipe(
            select(selectIsDisconnected),
            takeUntil(this.ngUnsubscribe)
        ).subscribe((isDisconnected: boolean) => {
            this.isDisconnected = isDisconnected;
            this.timestamp = this.getTimestamp();
        });

        this.appName = this.environment.appMetadata.type;
    }

    getTimestamp(): string {
        return new DateTime().toFormattedDateTime();
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
