import { createFeatureSelector, createSelector } from '@ngrx/store';
import { Dictionary } from '@ngrx/entity';
import { CounterInfo } from '@oam/cu-pmv2-api/models/counter-info';
import {
    countersValuesAdapter,
} from '../reducers/counters-values.reducer';
import {
    checkedNodesAdapter
} from '../reducers/counters-selection.reducer';
import { countersInfoAdapter } from '../reducers/counters-info.reducer';
import { PmState } from '../store/pm.state';
import { CountersValuesState } from '../store/counters-values.state';
import { CountersSelectionState } from '../store/counters-selection.state';
import { TreePanelState } from '../reducers/counters-tree-panel.reducer';
import { countersDetailsPanelAdapter } from '../reducers/counters-details-panel.reducer';
import { CountersExpansionState } from '../store/counters-expansion.state';
import { CountersFilterState } from '../store/counters-filter.state';
import { CountersInfoState } from '../store/counters-info.state';
import { CountersPmServiceState } from '../store/counters-pm-service.state';

const countersValueSelectors = countersValuesAdapter.getSelectors();
const checkedNodesSelectors = checkedNodesAdapter.getSelectors();
const countersInfoSelectors = countersInfoAdapter.getSelectors();
const countersDetailsSelectors = countersDetailsPanelAdapter.getSelectors();

const selectPmV2State = createFeatureSelector<PmState>('PerformanceManagementV2');

const selectCountersInfoState = createSelector(selectPmV2State, (state: PmState) => state.countersInfo);
const selectCountersValuesState = createSelector(selectPmV2State, (state: PmState) => state.countersValues);
const selectCountersSelectionState = createSelector(selectPmV2State, (state: PmState) => state.countersSelection);
const selectCountersToggleTreePanelState = createSelector(selectPmV2State, (state: PmState) => state.countersToggleTree);
const selectCountersDetailsState = createSelector(selectPmV2State, (state: PmState) => state.countersDetails);
const selectCountersExpansionState = createSelector(selectPmV2State, (state: PmState) => state.countersExpansion);
const selectCountersFilterState = createSelector(selectPmV2State, (state: PmState) => state.countersFilter);
const selectCountersPmServiceState = createSelector(selectPmV2State, (state: PmState) => state.countersPmService);

const selectValues = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.values);
const selectCheckedNodes = createSelector(selectCountersSelectionState, (state: CountersSelectionState) => state.checkedNodes);
const selectInfo = createSelector(selectCountersInfoState, (state: CountersInfoState) => state.info);

const selectCountersInfoEntries = createSelector(selectInfo, countersInfoSelectors.selectEntities);

export const selectCountersView = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.viewType);
export const selectAllCheckedCountersValues = createSelector(selectValues, countersValueSelectors.selectAll);
export const selectStartTime = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.startTime);
export const selectEndTime = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.endTime);
export const selectAllCheckedCounters = createSelector(selectCheckedNodes, checkedNodesSelectors.selectAll);
export const selectTimeFrame = createSelector(selectCountersSelectionState, (state: CountersSelectionState) => state.timeFrame);
export const selectCounterStartTime = createSelector(
    selectCountersSelectionState,
    (state: CountersSelectionState) => state.counterStartTime
);
export const selectAllCheckedCountersInfo = createSelector(selectInfo, countersInfoSelectors.selectAll);
export const selectOneCheckedCountersInfo = createSelector(selectCountersInfoEntries,
    (additionalInfos: Dictionary<CounterInfo>, props: { id: string }) => additionalInfos[props.id]);
export const selectLatestEtag = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.latestEtag);
export const selectCheckedCountersValuesFilter = createSelector(selectCountersValuesState, (state: CountersValuesState) => state.filter);
export const performanceManagementV2PanelToggle = createSelector(selectCountersToggleTreePanelState,
    (state: TreePanelState) => state.hideTogglePanelTree);
export const selectAllDetailsCounters = createSelector(selectCountersDetailsState, countersDetailsSelectors.selectAll);
export const selectExpandedNodes = createSelector(selectCountersExpansionState, (state: CountersExpansionState) => state.expandedNodes);
export const selectCountersFilter = createSelector(selectCountersFilterState, (state: CountersFilterState) => state.countersFilter);
export const selectCounterInfoRetrieveFailed = createSelector(selectCountersInfoState,
    (state: CountersInfoState) => state.failedToRetrieveCountersInfo);
export const selectCountersValuesRetrieveFailed = createSelector(selectCountersValuesState,
    (state: CountersValuesState) => state.failedToRetrieveCountersValues);
export const selectPmServerDown = createSelector(selectCountersPmServiceState,
    (state: CountersPmServiceState) => state.isPmServerDown);
