import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output
} from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { MenuAction } from '../../../button-with-menu/model/menu-action';
import { CellAdministrativeState, CellTechnologyType, CuCell, NullableOperationalState } from '@oam/cu-asm-api/models';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';
import { VisibleCuCellWidgetParameters } from '../../core/models/cell-tile-widget-parameters.definition';
import { TileTooltipContent } from '../../../tile-tooltip/models';
import { capitalize } from 'lodash';
import { NullableValuePipe } from '@oam/shared/lib/modules/webem-common';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { selectCommissioningFlagStatus } from '@oam/commissioning-flag-store/lib/core/selectors/select-commissioning-status';
import { RoleAccessForbiddenFlagEnum } from '@oam/core/lib/models/role-access/role-access-forbidden-flag.enmu';
import { VnfcSharedService } from '../../../../../../vnfc-shared/src/lib/vnfc-store/services/vnfc-shared.service';

@Component({
    selector: 'ui-webem-cell-tile',
    templateUrl: './cell-tile.component.html',
    styleUrls: ['./cell-tile.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class CellTileComponent implements OnInit, OnChanges {
    @Input() model: CuCell;
    @Input() visibleParameters: VisibleCuCellWidgetParameters;
    @Input() selected: boolean;
    @Input() container?: ElementRef | HTMLDivElement;

    @Output() lock = new EventEmitter<void>();
    @Output() unlock = new EventEmitter<void>();
    @Output() menuToggle = new EventEmitter<void>();
    @Output() goToPe = new EventEmitter<void>();

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    readonly OPERATIONAL_STATE = NullableOperationalState;
    readonly ADMINISTRATIVE_STATE = CellAdministrativeState;
    readonly TECHNOLOGY_TYPE = CellTechnologyType;
    private commissioningSubscription: Subscription;
    isCommissioningInProgress: boolean;
    readonly ACTIONS_BUTTON_MODEL: ButtonModel = {
        title: 'Actions',
        icon: 'mdi mdi-dots-vertical',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long
    };

    readonly GO_TO_PARAMETER_EDITOR_ACTION: MenuAction = {
        label: 'Go to PE',
        icon: 'mdi mdi-import',
        command: () => this.goToPe.emit()
    };

    readonly capitalize = capitalize;

    unlockedCellActions: MenuAction[];
    lockedCellActions: MenuAction[];
    tooltipPanelContent: TileTooltipContent[];
    tooltipPanelContentStatus: TileTooltipContent[];
    isConfigVcu: boolean;

    constructor(
      private readonly nullableValuePipe: NullableValuePipe,
      private readonly store: Store,
      private readonly vnfcSharedService: VnfcSharedService
    ) {}

    ngOnInit() {
        this.isConfigVcu = this.vnfcSharedService.isVcu();
        this.recalculateTooltipContent();
        this.commissioningSubscription = this.store.pipe(
          takeUntil(this.ngUnsubscribe),
          select(selectCommissioningFlagStatus)
        )
          .subscribe(data => {
              this.isCommissioningInProgress = data;
              this.setMenu();
          });
    }

    ngOnChanges() {
        this.recalculateTooltipContent();
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    private recalculateTooltipContent() {
        const fddNrarfcn: TileTooltipContent[] = [
            { title: 'Downlink NRARFCN', value: this.nullableValuePipe.transform(this.model.basicCell.nrarfcnDl) },
            { title: 'Uplink NRARFCN', value: this.nullableValuePipe.transform(this.model.basicCell.nrarfcnUl) }
        ];
        const tddNrarfcn: TileTooltipContent[] = [{
            title: 'NRARFCN',
            value: this.nullableValuePipe.transform(this.model.basicCell.nrarfcn)
        }];
        const nrarfcn = this.model.basicCell.technology === CellTechnologyType.Fdd ? fddNrarfcn : tddNrarfcn;

        this.tooltipPanelContent = [
            { title: 'NR cell identity', value: this.nullableValuePipe.transform(this.model.basicCell.nrCellIdentity) },
            { title: 'Cell name', value: this.nullableValuePipe.transform(this.model.basicCell.cellName) },
            { title: 'Cell technology', value: this.model.basicCell.technology },
            { title: 'PCI', value: this.model.basicCell.physicalLayerCellID },
            { title: 'Cell type', value: this.nullableValuePipe.transform(this.model.type) },
            ...nrarfcn,
            { title: 'Serving DU ID', value: this.model.duName },
            { title: 'Cell group ID', value: this.model.basicCell.cellGroupId }

        ];
        this.tooltipPanelContentStatus = [
            { title: 'Operational state', value: capitalize(this.nullableValuePipe.transform(this.model.basicCell.operationalState)) },
            { title: 'Administrative state', value: capitalize(this.model.basicCell.administrativeState) }
        ];

        if (this.vnfcSharedService.isVcu()) {
            this.tooltipPanelContentStatus.push({ title: 'Barring status', value: capitalize(this.nullableValuePipe.transform(this.model.barringStatus)) });
        }
    }

    setMenu(): void {
        this.unlockedCellActions = [
            {
                label: 'Lock cell',
                icon: 'mdi mdi-lock mdi-16px',
                command: () => this.lock.emit(),
                disabled: this.isCommissioningInProgress,
                roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
            },
            this.GO_TO_PARAMETER_EDITOR_ACTION
        ];
        this.lockedCellActions = [
            {
                label: 'Unlock cell',
                icon: 'mdi mdi-lock-open-variant-outline mdi-16px',
                command: () => this.unlock.emit(),
                disabled: this.isCommissioningInProgress,
                roleAccessFlag: RoleAccessForbiddenFlagEnum.LOCK_UNLOCK
            },
            this.GO_TO_PARAMETER_EDITOR_ACTION
        ];
    }
}
