import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DiagnosticSnapshotHomeComponent } from './pages/diagnostic-snapshot-home/diagnostic-snapshot-home.component';
import { ReactiveFormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';
import { StoreModule } from '@ngrx/store';
import * as fromCollectingSnapshotServerStatus from './core/reducers/collecting-snapshot-server-status.reducer';
import * as fromCollectingSnapshot from './core/reducers/collecting-snapshot.reducer';
import * as fromTransferSnapshotToNMS from './core/reducers/transfer-snapshot-to-nms.reducer';
import { EffectsModule } from '@ngrx/effects';
import { CollectingSnapshotServerStatusEffects } from './core/effects/collecting-snapshot-server-status.effects';

import {
    AlarmMessageModule,
    PanelModule,
    SpinnerModule,
    StepperModule,
    ToggleIconButtonModule,
    WebemButtonModule,
    WebemOverlayModule
} from '@oam/cu-ui';
import { DiagnosticSnapsthotDetailsComponent } from './components/diagnostic-snapsthot-details/diagnostic-snapsthot-details.component';
import { CollectingSnapshotEffects } from './core/effects/collecting-snapshot.effects';
import { DiagnosticSnapshotRoutingModule } from './diagnostic-snapshot-routing.module';
import { DiagnosticSharedModule } from '@oam/diagnostic-shared/lib/diagnostic-shared.module';
import { SessionTokenRenewalModule } from '@oam/cu-ui/lib/session-token-renewal/session-token-renewal.module';
import { SnapshotFetchFilesConfig } from '@oam/diagnostic-snapshot/core/services/snapshot-fetch-files-config';
import { ButtonModule } from 'primeng/button';
import { ProgressBarModule } from 'primeng/progressbar';
import { TableModule } from 'primeng/table';
import { RadioButtonModule } from 'primeng/radiobutton';
import { InputTextareaModule } from 'primeng/inputtextarea';
import { DialogModule } from 'primeng/dialog';
import { DiagnosticSnapshotCollectionComponent } from './components/diagnostic-snapshot-collection/diagnostic-snapshot-collection.component';
import { TransferSnapshotToNmsEffects } from '@oam/diagnostic-snapshot/core/effects/transfer-snapshot-to-nms.effects';
import { DiagnosticSnapshotTransferComponent } from './components/diagnostic-snapshot-transfer/diagnostic-snapshot-transfer.component';
import { TranslateOperationStatusPipe } from '@oam/diagnostic-snapshot/core/pipe/translate-operation-status.pipe';
import { NgFeatureFlagModule } from '@oam/ng-feature-flag';
import { FormatTimePipe } from '@oam/diagnostic-snapshot/core/pipe/format-time.pipe';

@NgModule({
    declarations: [
        DiagnosticSnapshotHomeComponent,
        DiagnosticSnapsthotDetailsComponent,
        DiagnosticSnapshotCollectionComponent,
        DiagnosticSnapshotTransferComponent,
        TranslateOperationStatusPipe,
        FormatTimePipe
    ],
    imports: [
        DiagnosticSharedModule,
        CommonModule,
        PanelModule,
        RadioButtonModule,
        FlexLayoutModule,
        ReactiveFormsModule,
        InputTextareaModule,
        AlarmMessageModule,
        StoreModule.forFeature(
            fromCollectingSnapshot.collectingSnapshotFeatureKey,
            fromCollectingSnapshot.reducer
        ),
        StoreModule.forFeature(
            fromTransferSnapshotToNMS.transferSnapshotFeatureKey,
            fromTransferSnapshotToNMS.reducer
        ),
        StoreModule.forFeature(
            fromCollectingSnapshotServerStatus.collectingSnapshotServerStatusFeatureKey,
            fromCollectingSnapshotServerStatus.reducer
        ),
        EffectsModule.forFeature([CollectingSnapshotServerStatusEffects, CollectingSnapshotEffects, TransferSnapshotToNmsEffects]),
        InputTextareaModule,
        TableModule,
        ProgressBarModule,
        DialogModule,
        ButtonModule,
        ToggleIconButtonModule,
        WebemOverlayModule,
        SpinnerModule,
        WebemButtonModule,
        DiagnosticSnapshotRoutingModule,
        StepperModule,
        SessionTokenRenewalModule,
        NgFeatureFlagModule
    ]
})
export class DiagnosticSnapshotModule {
    static forRoot(config: SnapshotFetchFilesConfig): ModuleWithProviders<DiagnosticSnapshotModule> {
        return {
            ngModule: DiagnosticSnapshotModule,
            providers: [
                {
                    provide: SnapshotFetchFilesConfig,
                    useValue: config,
                }
            ]
        };
    }
}
