import { Action, createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import {
    setRemainingSeconds,
    setRemainingTime
} from '../actions/session-token-time.actions';


export const sessionTokenTimeFeatureKey = 'sessionTokenTime';

export interface RemainingTimeState {
    remainingTime?: number
    remainingSeconds?: string | null;
    startCountingSeconds: boolean;
}

export const initialState: RemainingTimeState = {
    startCountingSeconds: false,
};


export const reducer = createReducer(
  initialState,
    on(setRemainingTime, (state, { remainingTime }) => ({ ...state, remainingTime })),
    on(setRemainingSeconds, (state, { remainingSeconds }) => ({ ...state, remainingSeconds }))
);

const selectSessionTime = createFeatureSelector<RemainingTimeState>(sessionTokenTimeFeatureKey);

export const selectRemainingTime = createSelector(selectSessionTime, state => state.remainingTime);
export const selectRemainingSeconds = createSelector(selectSessionTime, state => state.remainingSeconds);

