import { ActionReducer } from '@ngrx/store';
import { MAIN_MENU_KEY } from '@oam/navigation/lib/navigation/core/reducers/main-menu.reducer';
import { storageSync } from '@larscom/ngrx-store-storagesync';
import { KEYBOARD_SHORTCUTS } from '@oam/keyboard-shortcuts/lib/core/reducers/keyboard-shortcuts.reducer';
import { DETAILS_PANEL } from '@oam/details-panel/lib/core/reducers/detail-panel.reducer';


export function localStorageSyncReducer(reducer: ActionReducer<any>): ActionReducer<any> {
    return storageSync<any>({
        features: [
            { stateKey: MAIN_MENU_KEY },
            { stateKey: DETAILS_PANEL },
            { stateKey: KEYBOARD_SHORTCUTS }
        ],
        storage: window.localStorage
    })(reducer);
}
