import { createAction, props } from '@ngrx/store';
import { ActionProgress } from '../model';

export enum ActionProgressTypes {
    SetActionProgress = '[ActionProgress] Set Action Progress',
    ClearActionProgress = '[ActionProgress] Clear Action Progress',
    ClearActionProgresses = '[ActionProgress] Clear Action Progresses'
}

export const setActionProgress = createAction(
    ActionProgressTypes.SetActionProgress,
    props<ActionProgress>()
);

export const clearActionProgress = createAction(
    ActionProgressTypes.ClearActionProgress,
    props<ActionProgress>()
);

export const clearActionProgresses = createAction(
    ActionProgressTypes.ClearActionProgresses,
    props<{ actionProgresses: ActionProgress[] }>()
);
