import { UserProfileInterface } from '../model/user-profile.interface';
import { Role } from '../model/role.enmu';

export class RoleAccessService {
    private readonly userProfiles: UserProfileInterface[];
    currentRole: string;
    readOnlyMode: boolean | undefined;

    constructor(
        userProfiles: UserProfileInterface[],
        private skipPredictor: (roleAccessFlag: string) => boolean
    ) {
        this.userProfiles = userProfiles;
    }

    public isForbiddenForCurrentRole(roleAccessFlag: string | undefined) {

        if (!roleAccessFlag || this.skipPredictor(roleAccessFlag)) return false;

        return !!this.getProfileByName()?.forbidden.includes(roleAccessFlag);
    };

    getProfileByName(): UserProfileInterface | undefined {
        const profileKey = this.readOnlyMode ? Role.READ_ONLY_USER : this.currentRole;

        return this.userProfiles.find(({ roleId }: UserProfileInterface) => roleId === profileKey);
    };

    setRoleAndReadOnlyMode({
                               currentRole,
                               readOnlyMode
                           }: Pick<RoleAccessService, 'currentRole' | 'readOnlyMode'>): void {
        this.currentRole = currentRole;
        this.readOnlyMode = readOnlyMode;
    };
}
