import { createAction, props } from '@ngrx/store';

export enum LogoutActionsTypes {
    LOGOUT = '[User] [Auth] Logout user',
    LOGOUT_FAILED = '[Auth] Logout failed',
    LOGOUT_SUCCESSFUL = '[Auth] Logout successful',
    LOGOUT_IN_PROGRESS = '[Auth] Logout in progress'
}

export const logout = createAction(LogoutActionsTypes.LOGOUT);
export const logoutSuccessful = createAction(LogoutActionsTypes.LOGOUT_SUCCESSFUL);
export const logoutInProgress = createAction(LogoutActionsTypes.LOGOUT_IN_PROGRESS);
export const logoutFailed = createAction(
    LogoutActionsTypes.LOGOUT_FAILED,
    props<{ payload: string }>()
);
