import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { map, mergeMap, catchError } from 'rxjs/operators';
import {
    importUiSettings,
    exportUiSettingsFile,
    exportUiSettingsFileError,
    exportUiSettingsFileSuccess,
    exportUiSettings,
    importUiSettingsFile,
    importUiSettingsFileError,
    importUiSettingsFileSuccess,
    restoreDefaultUiSettings,
    restoreDefaultUiSettingsSuccess,
    restoreDefaultUiSettingsValues,
    restoreDefaultUiSettingsError,
    uiRefresh
} from '@oam/core/lib/actions/app-settings.actions';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import { AppSettingsService, ParseResult } from '@oam/core/lib/services/app-settings.service';
import { FileSaverService } from 'ngx-filesaver';
import { ToastMessage } from '@oam/core/lib/actions/toast-message';
import {
    AppSettingsLabels,
    ErrorMessageDetails,
    APP_SETTINGS_FILE_EXTENSION
} from '@oam/core/lib/models/app-settings.model';

@Injectable()
export class AppSettingsEffects {

    constructor(
        private readonly actions$: Actions,
        private readonly appSettingsService: AppSettingsService,
        private readonly fileSaver: FileSaverService
    ) {
    }

    importUiSettings$ = createEffect(() =>
        this.actions$.pipe(
            ofType(importUiSettings),
            mergeMap((action) => {
                return [
                    this.notify$('Import settings started', 'info', 'Import settings'),
                    importUiSettingsFile({ file: action.file })
                ];
            })
        )
    );

    importUiSettingsFile$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(importUiSettingsFile),
            mergeMap(({ file }) => this.appSettingsService.readAndValidate(file).pipe(
                catchError(() => of(importUiSettingsFileError({ errorMessageDetails: ErrorMessageDetails.WRONG_FILE }))))),
            map(({ isValid, content }: ParseResult) => {
                if (!isValid) {
                    return importUiSettingsFileError({ errorMessageDetails: ErrorMessageDetails.WRONG_FILE });
                }

                this.appSettingsService.importUiSettings(content);

                return importUiSettingsFileSuccess();
            })
        );
    });

    importUiSettingsFileSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(importUiSettingsFileSuccess),
            mergeMap(() => {
                return [
                    uiRefresh(),
                    this.notify$(
                        AppSettingsLabels.MODAL_IMPORT_SUCCESS_DETAIL,
                        AppSettingsLabels.MODAL_TYPE_SUCCESS,
                        AppSettingsLabels.MODAL_IMPORT_SUMMARY
                    )
                ];
            })
        )
    );

    importUiSettingsFileError$ = createEffect(() =>
        this.actions$.pipe(
            ofType(importUiSettingsFileError),
            map(({ errorMessageDetails }) => {
                return this.notify$(
                    errorMessageDetails,
                    AppSettingsLabels.MODAL_TYPE_ERROR,
                    AppSettingsLabels.MODAL_IMPORT_SUMMARY);
            })
        )
    );

    exportUiSettings$ = createEffect(() => {
        return this.actions$.pipe(
            ofType(exportUiSettings),
            mergeMap(() => {
                return [
                    this.notify$('Export settings to file', 'info', 'Export settings'),
                    exportUiSettingsFile()
                ];
            })
        );
    });

    exportUiSettingsFile$ = createEffect(() =>
        this.actions$.pipe(
            ofType(exportUiSettingsFile),
            mergeMap(() => this.appSettingsService.downloadUiSettingsFile().pipe(
                map((file: Blob) => {
                    this.saveFile$(file, 'ui_settings' + APP_SETTINGS_FILE_EXTENSION);

                    return exportUiSettingsFileSuccess();
                }),
                catchError(error => of(exportUiSettingsFileError({ errorMessageDetails: error.message })))
                )
            )
        )
    );

    exportUiSettingsFileSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(exportUiSettingsFileSuccess),
            map(() => this.notify$('', 'success', 'Export file successful'))
        )
    );

    exportUiSettingsFileError$ = createEffect(() =>
        this.actions$.pipe(
            ofType(exportUiSettingsFileError),
            map(() => this.notify$('Error', 'error', 'Error while export settings file'))
        )
    );

    restoreDefaultUiSettings$ = createEffect(() =>
        this.actions$.pipe(
            ofType(restoreDefaultUiSettings),
            mergeMap(() => {
                return [
                    this.notify$('Restore default settings', 'info', 'Restore settings'),
                    restoreDefaultUiSettingsValues()
                ];
            })
        )
    );

    restoreDefaultUiSettingsValues$ = createEffect(() =>
        this.actions$.pipe(
            ofType(restoreDefaultUiSettingsValues),
            map(() => {
                this.appSettingsService.restoreDefaultUiSettings();

                return restoreDefaultUiSettingsSuccess();
            })
        )
    );

    restoreDefaultUiSettingsSuccess$ = createEffect(() =>
        this.actions$.pipe(
            ofType(restoreDefaultUiSettingsSuccess),
            map(() => this.notify$('', 'success', 'Restore default settings successful'))
        )
    );

    restoreDefaultUiSettingsError$ = createEffect(() =>
        this.actions$.pipe(
            ofType(restoreDefaultUiSettingsError),
            map(() => this.notify$('Error', 'error', 'Error while restoring default settings'))
        )
    );

    saveFile$(blob: Blob, fileName: string): Observable<void> {
        this.fileSaver.save(blob, fileName);

        return new Observable<void>(
            subscriber => subscriber.next()
        );
    }

    notify$(detail: string, severity: ToastMessage['severity'], summary: string) {
        return addNotification({
            payload: { detail, severity, summary }
        });
    }
}
