import { Alarm } from '@oam/cu-asm-api/models/alarm';

export function mapAlarms(alarms: Alarm[] | null): string {
    if (!alarms) return '';

    return alarms.reduce((acc, cur, index) => {
        const row = `[${cur.severity}] #${cur.alarmId} ${cur.alarmName} (#${cur.faultId} ${cur.faultName})`;

        return index !== alarms.length - 1 ?
            `${acc}${row}\n` :
            `${acc}${row}`;
    }, '') || '';
}
