import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { filter, map, withLatestFrom } from 'rxjs/operators';
import { PollingService } from '@oam/core/lib/services/polling.service';
import { Store } from '@ngrx/store';
import {
    changeSelectionInBottomView,
    changeSelectionInTopView,
    changeSelectionRestriction,
    setLastSelectedViews,
    setViewsFromUrlPath,
    updateSelectedViews,
    updateStoreWithoutRedirect,
    updateViewsAccordingToUrlPath,
    updateViewsWithoutRedirect,
    updateWholeState
} from '../../core/actions/split-view.actions';
import { Router } from '@angular/router';
import { SPLIT_VIEW_LOCATION, RoutesPathsWithParams } from '../models';
import { selectAvailableViews, selectSplitViewsDetails } from '../selectors/split-view.selectors';
import { VduRoutesPaths } from '@oam/vdu-shared';
import { selectUrl } from '@oam/core/lib/selectors/router.selectors';
import { SplitViewRestrictionsService } from '../services/split-view-restrictions.service';
import { GroupOfViews } from '../models/group-of-views.interface';
import { SingleView } from '../models/single-view.interface';
import { OutletUrlPathService } from '@oam/core/lib/services/outlet-url-path.service';
import { get } from 'lodash';
const restrictedGroup = 'Configuration';

@Injectable()
export class SplitViewEffects {
    changeSplitViews$ = createEffect(() =>
        this.actions$.pipe(
            ofType(changeSelectionInTopView, changeSelectionInBottomView),
            withLatestFrom(this.store.select(selectAvailableViews)),
            map(([action, availableViews]) =>
                updateSelectedViews({ views: this.getUpdatedAvailableViews(action, availableViews) })
            )
        )
    );

    navigateToChangedView$ = createEffect(() =>
        this.actions$.pipe(
          ofType(changeSelectionInTopView, changeSelectionInBottomView),
          map(action => {
            const url = action.selectedView && action.selectedView.hasOwnProperty('url') ?
              (action.selectedView as RoutesPathsWithParams).url :
              action.selectedView as string;
            const navigate: string[] = url.split('/');
            if (action.selectedView && action.selectedView.hasOwnProperty('param')) {
              navigate.push((action.selectedView as RoutesPathsWithParams).param);
            }
            const param = get(this.router, 'currentUrlTree.root.children.primary.segments[0].parameters', {});

            return this.router.navigate([
                'split-view', param, { outlets: { [action.location]: [...navigate] } }
              ]
            );

          })
        ),
      { dispatch: false }
    );

    navigateToLastSelectedViews$ = createEffect(() =>
            this.actions$.pipe(
                ofType(setLastSelectedViews),
                withLatestFrom(this.store.select(selectSplitViewsDetails)),
                map(
                    ([, state]) => this.router.navigate(['split-view', {
                        outlets: {
                            [SPLIT_VIEW_LOCATION.TOP]: state.topView || null,
                            [SPLIT_VIEW_LOCATION.BOTTOM]: state.bottomView || null
                        }
                    }])
                )
            ),
        { dispatch: false }
    );

    changeSelectionRestriction$ = createEffect(() =>
        this.actions$.pipe(
            ofType(changeSelectionRestriction),
            withLatestFrom(this.store.select(selectAvailableViews)),
            map(([action, availableViews]) =>
                updateSelectedViews({ views: this.getAvailableViewsWithRestriction(action, availableViews) })
            )
        )
    );

    updateViewsByUrlOutlet$ = createEffect(() =>
        this.actions$.pipe(
            ofType(setViewsFromUrlPath),
            withLatestFrom(this.store.select(selectUrl)),
            filter(([, url]) => !!url),
            map(() => [{
                    'topView': this.outletUrlService.getOutletWebemRoutePath(this.router.url, SPLIT_VIEW_LOCATION.TOP),
                    'bottomView': this.outletUrlService.getOutletWebemRoutePath(this.router.url, SPLIT_VIEW_LOCATION.BOTTOM)
                }]
            ),
            map(
                ([outlets]) =>
                    updateViewsAccordingToUrlPath({ topOutlet: outlets.topView, bottomOutlet: outlets.bottomView })
            )
        )
    );

    updateViewsWithoutRedirect$ = createEffect(() =>
        this.actions$.pipe(
            ofType(updateViewsWithoutRedirect),
            withLatestFrom(this.store.select(selectUrl)),
            map(([, url]) => {
                    return [{
                        'topView': this.outletUrlService.getOutletWebemRoutePath(url as string, SPLIT_VIEW_LOCATION.TOP),
                        'bottomView': this.outletUrlService.getOutletWebemRoutePath(url as string, SPLIT_VIEW_LOCATION.BOTTOM)
                    }];
                }
            ),
            map(
                ([outlets]) => {
                    return updateStoreWithoutRedirect({ topOutlet: outlets.topView, bottomOutlet: outlets.bottomView });
                }
            )
        )
    );

    navigateToViewsSelectedByUrlOutlets$ = createEffect(() =>
            this.actions$.pipe(
                ofType(updateViewsAccordingToUrlPath),
                map(
                    (action) => this.router.navigate(['split-view', {
                        outlets: {
                            [SPLIT_VIEW_LOCATION.TOP]: action.topOutlet || null,
                            [SPLIT_VIEW_LOCATION.BOTTOM]: action.bottomOutlet || null
                        }
                    }])
                )
            ),
        { dispatch: false }
    );

    setViewsAccordingToUrlOutlet$ = createEffect(() =>
        this.actions$.pipe(
            ofType(updateViewsAccordingToUrlPath, updateStoreWithoutRedirect),
            withLatestFrom(this.store.select(selectSplitViewsDetails)),
            map(([action, state]) => {
                let topChanged = false;
                let bottomChanged = false;
                const switchedViews =
                    state.availableViews.map(group => {
                        if (topChanged && bottomChanged) return group;
                        const updateItems = group.items.map((item: SingleView) => {
                            if (topChanged && bottomChanged) return item;
                            if (!topChanged && item.value === action.topOutlet) {
                                topChanged = true;

                                return { ...item, disabledBy: SPLIT_VIEW_LOCATION.TOP, disabled: true };
                            } else if (!bottomChanged && item.value === action.bottomOutlet) {
                                bottomChanged = true;

                                return { ...item, disabledBy: SPLIT_VIEW_LOCATION.BOTTOM, disabled: true };
                            } else if (state.bottomView === item.value || state.topView === item.value) {
                                return { ...item, disabled: false };
                            } else {
                                return item;
                            }
                        });

                        return { ...group, items: updateItems };
                    });

                return updateWholeState({
                    newState:
                        {
                            topView: action.topOutlet,
                            bottomView: action.bottomOutlet,
                            availableViews: switchedViews,
                            restrictionsForGroupViews: state.restrictionsForGroupViews
                        }
                });
            })
        )
    );

    getUpdatedAvailableViews(action: any, availableViews: GroupOfViews[]) {
        return availableViews.map(group => {
                const updateItems = group.items.map((item: SingleView) => {
                    if (item.disabled && item.disabledBy === action.location) {
                        return { ...item, disabled: false, disabledBy: SPLIT_VIEW_LOCATION.NONE };
                    }
                    if (item.value === action.selectedView) {
                        return { ...item, disabled: true, disabledBy: action.location };
                    }
                    const value = item.value && (item.value as RoutesPathsWithParams).url ?
                        (item.value as RoutesPathsWithParams).url :
                        item.value as VduRoutesPaths;
                    if (this.splitViewRestrictionsService.isUrlInRestrictionGroup(value) &&
                        action.selectedView &&
                        this.splitViewRestrictionsService.isUrlInRestrictionGroup(action.selectedView)
                    ) {
                        return { ...item, disabledBy: action.location };
                    }

                    return item;
                });

                return { ...group, items: updateItems };
            }
        );
    }

    getAvailableViewsWithRestriction(action: any, availableViews: GroupOfViews[]) {
        return availableViews.map(group => {
            if (group.label === restrictedGroup) {
                return {
                    ...group, items: group.items.map(
                        (item: SingleView) => ({ ...item, disabled: action.selectionRestriction })
                    )
                };
            }

            return group;
        });
    }


    constructor(
        private readonly actions$: Actions,
        private readonly pollingService: PollingService,
        private readonly store: Store,
        private readonly router: Router,
        private readonly outletUrlService: OutletUrlPathService,
        private readonly splitViewRestrictionsService: SplitViewRestrictionsService
    ) {
    }
}
