import { Pipe, PipeTransform } from '@angular/core';
import { AvailabilityStatus, NullableAvailabilityStatus } from '@oam/cu-asm-api/models';
import { availabilityStatusLabels } from '../../../models/availability-status-labels';
import { AvailabilityStatusClasses } from '../../../models/availability-status-classes';
import { StatusIconType } from '../../../models/status-icon-color.definition';

type StatusType = {
    title: string,
    type: StatusIconType,
    class: AvailabilityStatusClasses
};

const availabilityStatusMap:
    Partial<{ [K in (NullableAvailabilityStatus & AvailabilityStatus)]:
        [string, StatusIconType, AvailabilityStatusClasses] }> = {
    [NullableAvailabilityStatus.OnLine]: [
        availabilityStatusLabels[NullableAvailabilityStatus.OnLine],
        StatusIconType.Success,
        AvailabilityStatusClasses.OnLine
    ],
    [AvailabilityStatus.OnLine]: [
        availabilityStatusLabels[NullableAvailabilityStatus.OnLine],
        StatusIconType.Success,
        AvailabilityStatusClasses.OnLine
    ],
    [NullableAvailabilityStatus.NotInstalled]: [
        availabilityStatusLabels[NullableAvailabilityStatus.NotInstalled],
        StatusIconType.Disabled,
        AvailabilityStatusClasses.NotInstalled
    ],
    [NullableAvailabilityStatus.OffLine]: [
        availabilityStatusLabels[NullableAvailabilityStatus.OffLine],
        StatusIconType.Dark,
        AvailabilityStatusClasses.OffLine
    ],
    [NullableAvailabilityStatus.PowerOff]: [
        availabilityStatusLabels[NullableAvailabilityStatus.PowerOff],
        StatusIconType.Off,
        AvailabilityStatusClasses.PowerOff
    ],
    [NullableAvailabilityStatus.Failed]: [
        availabilityStatusLabels[NullableAvailabilityStatus.Failed],
        StatusIconType.Danger,
        AvailabilityStatusClasses.Failed
    ],
    [NullableAvailabilityStatus.Degraded]: [
        availabilityStatusLabels[NullableAvailabilityStatus.Degraded],
        StatusIconType.Warning,
        AvailabilityStatusClasses.Degraded
    ],
    [NullableAvailabilityStatus.Dependency]: [
        availabilityStatusLabels[NullableAvailabilityStatus.Dependency],
        StatusIconType.Light2,
        AvailabilityStatusClasses.Dependency
    ],
    [NullableAvailabilityStatus.OffDuty]: [
        availabilityStatusLabels[NullableAvailabilityStatus.OffDuty],
        StatusIconType.Black,
        AvailabilityStatusClasses.OffDuty
    ],
    [NullableAvailabilityStatus.InTest]: [
        availabilityStatusLabels[NullableAvailabilityStatus.InTest],
        StatusIconType.Black,
        AvailabilityStatusClasses.InTest
    ],
    [NullableAvailabilityStatus.LogFull]: [
        availabilityStatusLabels[NullableAvailabilityStatus.LogFull],
        StatusIconType.Black,
        AvailabilityStatusClasses.LogFull
    ],
};

@Pipe({ name: 'mapAvailabilityStatus', pure: true })
export class MapAvailabilityStatusPipe implements PipeTransform {
    transform( availabilityStatus?: NullableAvailabilityStatus | AvailabilityStatus | null ): StatusType {
        if (!availabilityStatus) return { title: '', type: StatusIconType.Unknown, class: AvailabilityStatusClasses.OffLine };
        const statusIconData = availabilityStatusMap[availabilityStatus];

        return statusIconData ?
            { title: statusIconData[0], type: statusIconData[1], class: statusIconData[2] } :
            { title: '', type: StatusIconType.Unknown, class: AvailabilityStatusClasses.OffLine };
    }
}
