import { CompatibilityBrowserActions, CompatibilityBrowserActionTypes } from '../actions/compatibility-browser.actions';

export const featureKey = 'browserCompatibility';

export interface BrowserCompatibilityState {
    browserIsSupport: boolean;
    browserInformationClosed: boolean;
}

export const initialState: BrowserCompatibilityState = {
    browserIsSupport: true,
    browserInformationClosed: false
};

export function reducer(state = initialState, action: CompatibilityBrowserActions): BrowserCompatibilityState {
    switch (action.type) {
        case CompatibilityBrowserActionTypes.YourBrowserIsSupport:
            return { ...state, browserIsSupport: true };
        case CompatibilityBrowserActionTypes.YourBrowserIsNotSupport:
            return { ...state, browserIsSupport: false };
        case CompatibilityBrowserActionTypes.BrowserInformationClosed:
            return { ... state, browserInformationClosed: true };
        default:
            return state;
    }
}
