import { Injectable } from '@angular/core';
import { CanActivate, CanLoad } from '@angular/router';
import { Store } from '@ngrx/store';
import { forceRedirect } from '@oam/core/lib/actions/router.actions';
import { clearTokens, removeUser } from '@oam/core/lib/actions/user.actions';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { setLoginInitialValues } from '@oam/auth/lib/auth/core/actions/login.actions';
import { TokenType } from '@oam/core/lib/store/user-state';

@Injectable({
    providedIn: 'root'
})
export class AuthGuardService implements CanActivate, CanLoad {
    constructor(private readonly store: Store) {}

    private isAuthenticated(): boolean {
        const bearerToken = sessionStorage.getItem(`${TokenType.BEARER}`);

        return !!bearerToken;
    }

    private handlePageLoad(): boolean {
        const authenticated = this.isAuthenticated();

        if (!authenticated) {
            this.store.dispatch(clearTokens());
            this.store.dispatch(removeUser());
            this.store.dispatch(setLoginInitialValues());
            this.store.dispatch(forceRedirect({routerLink: RouterLink.login, redirectUrl: location.pathname}));
        }

        return authenticated;
    }

    canActivate(): boolean {
        return this.handlePageLoad();
    }

    canLoad(): boolean {
        return this.handlePageLoad();
    }
}
