import { Injectable } from '@angular/core';
import { RoutedMenuItem } from '@oam/cu-ui';
import { StatusModelBar } from '@oam/navigation/lib/navigation/core/models/status-bar-info.interface';
import { QuickMenuAlarms } from '@oam/navigation/lib/navigation/core/models/quick-menu-alarms.interface';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { RoleAccessService } from '@oam/role-access-core';
import { OFFLINE_USER_MODE } from '@oam/shared/lib/constants/role-access-constants';
import { PageName } from '@oam/navigation/lib/navigation/core/models/page-name';

export class LayoutsConfig {
    quickMenuItems: RoutedMenuItem[];
    statusBarModel: StatusModelBar;
    quickMenuAlarmsModel: QuickMenuAlarms;
    navigations: Navigation[];
}


@Injectable({
    providedIn: 'root'
})
export class LayoutsConfigService {
    private readonly layoutConfig: LayoutsConfig;

    constructor(config: LayoutsConfig, private readonly roleAccessService: RoleAccessService) {
        if (config) this.layoutConfig = config;
    }

    get getQuickMenuItems() {
        if (this.roleAccessService.currentRole === OFFLINE_USER_MODE) {
            return this.layoutConfig.quickMenuItems.filter((item) => {
                return item.title !== PageName.snapshot;
            });
        }
        return this.layoutConfig.quickMenuItems;
    }

    get getStatusBarModel() {
        return this.layoutConfig.statusBarModel;
    }

    get getQuickMenuAlarmsModel() {
        return this.layoutConfig.quickMenuAlarmsModel;
    }

    get getNavigations() {
        return this.layoutConfig.navigations;
    }
}
