import { createFeatureSelector, createSelector } from '@ngrx/store';
import { PlansInfoState, planBasicInfoFeatureKey } from '../reducers/plans-info.reducer';
import { PlanOrigin, PlansInfo, LOADED_PLANS_LIMITS } from '../model';
import { DeltaPlansInfo } from '@oam/configuration-shared/lib/core/model/plans-info';
import { DeltaPlanConflictState } from '@oam/configuration-shared/lib/core/model/delta-plan-conflict-state';

export const selectPlansInfoState = createFeatureSelector<PlansInfoState>(planBasicInfoFeatureKey);

export const selectAllPlansInfo = createSelector(
    selectPlansInfoState,
    state => {
        return [
            ... state.activePlans,
            ... state.deltaPlans,
            ... state.inactivePlans,
            ... state.stagedPlans,
        ];
    }
);

export const selectPlansInfoByOrigin = (originList: PlanOrigin[]) => createSelector(
    selectAllPlansInfo,
    (plans: PlansInfo[]) => plans ? plans.filter((plan) => originList.includes(plan.from)) : []
);

export const selectStagedPlans = createSelector(
    selectAllPlansInfo,
    (plans: PlansInfo[]) => plans.filter((plan) => plan.from === PlanOrigin.STAGE)
);

export const selectPlanById = (planId: string) => createSelector(
    selectAllPlansInfo,
    (plans: PlansInfo[]) => {
        return plans.find(plan => plan.planId === planId);
    }
);


export const selectStagedPlansCount = createSelector(
    selectStagedPlans,
    (plans: PlansInfo[]): number => plans.length
);

export const selectStagedPlansLimitExceeded = createSelector(
    selectStagedPlans,
    (plans: PlansInfo[]): boolean => plans.length >= LOADED_PLANS_LIMITS.STAGED
);


export const selectDeltaPlans = createSelector(
    selectAllPlansInfo,
    (plans: PlansInfo[]) => plans.filter((plan) => plan.from === PlanOrigin.DELTA)
);

export const selectCurrentPlan = createSelector(
    selectAllPlansInfo,
    (plans: PlansInfo[]) => plans.find((plan) => plan.from === PlanOrigin.ACTIVE)
);

export const selectedOutdatedDeltaPlans = createSelector(
    selectDeltaPlans,
    (plans: DeltaPlansInfo[]): DeltaPlansInfo[] => plans.filter(plan =>
        plan.deltaState === DeltaPlanConflictState.Conflict)
);

export const selectDeltaPlansCount = createSelector(
    selectDeltaPlans,
    (plans: PlansInfo[]): number => plans.length
);

export const selectDeltaPlansLimitExceeded = createSelector(
    selectDeltaPlans,
    (plans: PlansInfo[]): boolean => plans.length >= LOADED_PLANS_LIMITS.DELTA
);

