/* tslint:disable */
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BaseService } from '../base-service';
import { ApiConfiguration } from '../api-configuration';
import { StrictHttpResponse } from '../strict-http-response';
import { RequestBuilder } from '../request-builder';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';

import { Interface } from '../models/interface';

@Injectable({
  providedIn: 'root',
})
export class VduInterfacesService extends BaseService {
  constructor(
    config: ApiConfiguration,
    http: HttpClient
  ) {
    super(config, http);
  }

  /**
   * Path part for operation interfaceGet
   */
  static readonly InterfaceGetPath = '/interface';

  /**
   * Get interface data.
   *
   *
   *
   * This method provides access to the full `HttpResponse`, allowing access to response headers.
   * To access only the response body, use `interfaceGet()` instead.
   *
   * This method doesn't expect any request body.
   */
  interfaceGet$Response(params: {

    /**
     * interface name.
     */
    type: 'mplane' | 'cuplane';

  }): Observable<StrictHttpResponse<Interface>> {

    const rb = new RequestBuilder(this.rootUrl, VduInterfacesService.InterfaceGetPath, 'get');
    if (params) {

      rb.query('type', params.type, {});

    }
    return this.http.request(rb.build({
      responseType: 'json',
      accept: 'application/json'
    })).pipe(
      filter((r: any) => r instanceof HttpResponse),
      map((r: HttpResponse<any>) => {
        return r as StrictHttpResponse<Interface>;
      })
    );
  }

  /**
   * Get interface data.
   *
   *
   *
   * This method provides access to only to the response body.
   * To access the full response (for headers, for example), `interfaceGet$Response()` instead.
   *
   * This method doesn't expect any request body.
   */
  interfaceGet(params: {

    /**
     * interface name.
     */
    type: 'mplane' | 'cuplane';

  }): Observable<Interface> {

    return this.interfaceGet$Response(params).pipe(
      map((r: StrictHttpResponse<Interface>) => r.body as Interface)
    );
  }

}
