/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { PeService } from './services/pe.service';
import { SnapshotService } from './services/snapshot.service';
import { StatusService } from './services/status.service';
import { SecService } from './services/sec.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    PeService,
    SnapshotService,
    StatusService,
    SecService,
    ApiConfiguration
  ],
})
export class CuAsmClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuAsmClientModule> {
    return {
      ngModule: CuAsmClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuAsmClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuAsmClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
