import { Injectable } from '@angular/core';

export class DiagnosticContentMenuConfig {
    splitTracingTabs: boolean;
}


@Injectable({
    providedIn: 'root'
})
export class DiagnosticContentMenuConfigService {
    splitTracingTabs: boolean;

    constructor(config: DiagnosticContentMenuConfig) {
        if (config) this.splitTracingTabs = config.splitTracingTabs;
    }

    get getMenuConfig() {
        return this.splitTracingTabs;
    }
}
