import { NIDD_DEFINITION } from '@oam/configuration-shared/lib/core/model/definition-error-enum';
import { ValidationMessages } from '@oam/configuration-shared/lib/core/model/definition-validation-messages.enum';


export class DefinitionsError {
    constructor(private readonly errorType?: string | null, private readonly additionalValue?: string | null) {
    }

    getErrorType(): string | null | undefined {
        return this.errorType;
    }

    getError(): string {
        switch (this.errorType) {
            case NIDD_DEFINITION.VALUE_IS_OUT_OF_RANGE:
                return `${ValidationMessages.VALUE_IS_OUT_OF_RANGE}${this.additionalValue}`;
            case NIDD_DEFINITION.PARAMETER_IS_REQUIRED:
                return ValidationMessages.PARAMETER_IS_REQUIRED;
            case NIDD_DEFINITION.INCORRECT_STEP:
                return `${ValidationMessages.INCORRECT_STEP}${this.additionalValue}`;
            case NIDD_DEFINITION.INCORRECT_ENUM:
                return ValidationMessages.INCORRECT_ENUM;
            case NIDD_DEFINITION.VALUE_IS_TO_TOO_LONG:
                return `${ValidationMessages.VALUE_IS_TO_TOO_LONG}${this.additionalValue}`;
            case NIDD_DEFINITION.VALUE_IS_TOO_SHORT:
                return `${ValidationMessages.VALUE_IS_TOO_SHORT}${this.additionalValue}`;
            case NIDD_DEFINITION.INCORRECT_PATTERN:
                return ValidationMessages.INCORRECT_PATTERN;
            case NIDD_DEFINITION.INCORRECT_REGEX:
                return ValidationMessages.INCORRECT_REGEX;
            case NIDD_DEFINITION.SHOULD_BE_NUMBER:
                return ValidationMessages.SHOULD_BE_NUMBER;
            case NIDD_DEFINITION.PARAMETER_DEFINITION_ERROR_DESCRIPTION:
                return `${this.additionalValue}`;
            default:
                return  '';
        }
    }

    getErrorAdditional(): string | null | undefined {
        return this.additionalValue;
    }
}

