import { ModuleWithProviders, NgModule } from '@angular/core';
import { DiagnosticContentMenuConfig } from './diagnostic-content-menu-config.service';


@NgModule({})
export class DiagnosticContentMenuConfigModule {
    static forRoot(additionalData: DiagnosticContentMenuConfig): ModuleWithProviders<DiagnosticContentMenuConfigModule> {
        return {
            ngModule: DiagnosticContentMenuConfigModule,
            providers: [
                {
                    provide: DiagnosticContentMenuConfig,
                    useValue: additionalData
                }
            ]
        };
    }
}
