/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { CmService } from './services/cm.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    CmService,
    ApiConfiguration
  ],
})
export class CuCmClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuCmClientModule> {
    return {
      ngModule: CuCmClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuCmClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuCmClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
