import { Store } from '@ngrx/store';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { BaseDialogComponent } from '@oam/shared/lib/components/base-dialog/base-dialog-component';
import { ButtonModel, ButtonTheme } from '@oam/cu-ui';

@Component({
    selector: 'webem-token-info-dialog',
    templateUrl: './token-info-dialog.component.html',
    styleUrls: ['./token-info-dialog.component.scss']
})
export class TokenInfoDialogComponent extends BaseDialogComponent implements OnInit, OnDestroy {
    readonly okButtonModel: ButtonModel = {
        label: 'OK',
        theme: ButtonTheme.Accent
    };

    constructor(protected store: Store,
                public config: DynamicDialogConfig,
                ref: DynamicDialogRef) {
        super(store, ref);
    }

    ngOnInit(): void {
        super.ngOnInit();
    }

    closeDialog(): void {
        super.closeDialog();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
    }
}
