import { Actions, createEffect } from '@ngrx/effects';
import { NGXLogger } from 'ngx-logger';
import { filter, tap } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Environment } from '@oam/core/lib/environment-variables/core/models';

@Injectable()
export class LogForActionsEffects {
    constructor(
        private readonly actions$: Actions,
        private readonly logger: NGXLogger,
        private readonly environment: Environment
    ) { }

    logForActions$ = createEffect(() =>
        this.actions$.pipe(
            filter((action) => {
                return action.type.indexOf('[User]') !== -1;
            }),
            tap((action) => {
                try {
                    this.logger.log(`[ActionLogger] ${JSON.stringify(this.environment.enablePayloadInLogs ?
                        action : { type: action.type })}`);
                } catch (err) {
                    this.logger.log(`[ActionLogger] ${JSON.stringify({ type: action.type })}`);
                }
            })
        ), { dispatch: false }
    );
}
