import { Directive, Input, TemplateRef, ViewContainerRef } from '@angular/core';
import { FeatureFlag } from '@oam/feature-flag';

@Directive({
    selector: '[oamFeatureFlag]'
})
export class FeatureFlagDirective {

    @Input() set oamFeatureFlag(flag: string) {
        if (this.featureFlag.isFeatureEnabled(flag)) {
            this.viewContainer.createEmbeddedView(this.templateRef);
        } else {
            this.viewContainer.clear();

        }
    }

    constructor(private featureFlag: FeatureFlag,
                private viewContainer: ViewContainerRef, private templateRef: TemplateRef<any>) {
    }
}
