import { Token, UserState } from '@oam/core/lib/store/user-state';
import { Action, createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import { createEntityAdapter, EntityAdapter, EntityState } from '@ngrx/entity';
import {
    clearTokens,
    removeFromUserInformation, removeToken, setLastSessionToken,
    setToken, setUsernameForPasswordExpired,
    updateUserInformation,
    updateDowngradedPrivilegesDialogPopupFlag, updateLoginResponse,
    setCurrentAccountActivatedStatus
} from '@oam/core/lib/actions/user.actions';

export const featureKey = 'user';

export const tokenAdapter: EntityAdapter<Token> = createEntityAdapter<Token>({
    selectId: (token: Token) => token.type
});

export const initialPlansState: EntityState<Token> = tokenAdapter.getInitialState();

export const initialState: UserState = {
    information: {
        username: '',
        expires: '',
        failedLoginAttempts: '',
        lastFailedLogin: '',
        lastLogin: '',
        passwordExpirationDate: '',
        readOnlyMode: false,
        role: ''
    },
    shouldDowngradedPrivilegesDialogPopup: false,
    tokens: initialPlansState,
    response: {
        Expires: '',
        FailedLoginAttempts: '',
        LastFailedLogin: '',
        LastLogin: '',
        PasswordExpirationDate: '',
        ReadOnlyMode: false,
        Role: '',
        Token: '',
        Type: ''
    },
    username: '',
    accountActiveStatus: []
};

const userReducer = createReducer(
    initialState,
    on(updateUserInformation, (state, action) => ({
        ...state,
        information: {
            ...state.information,
            ...action.payload
        }
    })),
    on(updateLoginResponse, (state, action) => ({
        ...state,
        ...action.payload
    })),
    on(updateDowngradedPrivilegesDialogPopupFlag, (state, action) => ({
        ...state,
        shouldDowngradedPrivilegesDialogPopup: action.payload
    })),
    on(removeFromUserInformation, (state, action) => {
        const updatedUserInfo = { ...state.information };
        delete updatedUserInfo[action.payload];

        return {
            ...state,
            information: updatedUserInfo
        };
    }),
    on(setToken, (state, action) => ({
        ...state,
        tokens: tokenAdapter.addOne(action.payload, state.tokens)
    })),
    on(setLastSessionToken, (state, action) => ({
        ...state,
        lastSessionToken: action.authToken
    })),
    on(clearTokens, state => ({
        ...state,
        tokens: tokenAdapter.removeAll(state.tokens)
    })),
    on(removeToken, (state, action) => ({
        ...state,
        tokens: tokenAdapter.removeOne(action.payload, state.tokens)
    })),
    on(setUsernameForPasswordExpired, (state, action) => ({
        ...state,
        information: {
            ...state.information,
            username: action.payload
        }
    })),
    on(setCurrentAccountActivatedStatus, (state, action) => ({
        ...state,
        accountActiveStatus: action.payload
    }))
);

export function reducer(state: UserState, action: Action) {
    return userReducer(state, action);
}

export const getUser = createFeatureSelector<UserState>(featureKey);
const { selectAll } = tokenAdapter.getSelectors();
export const selectTokens = createSelector(getUser, (state) => state.tokens);
export const selectDowngradedPrivilegesDialogPopupFlag = createSelector(getUser, (state) => state.shouldDowngradedPrivilegesDialogPopup);
export const selectInformation = createSelector(getUser, (state) => state.information);
export const selectUsername = createSelector(selectInformation, (state) => state.username);
export const selectUserTokenExpirationTime = createSelector(selectInformation, (state) => state.expires);
export const selectLastSessionToken = createSelector(getUser, (state) => state.lastSessionToken);
export const selectRoleAndReadOnlyMode = createSelector(
    selectInformation,
    ({ role, readOnlyMode }) => {
        return {role, readOnlyMode};
});
export const selectCurrentAccountActivatedStatus = createSelector(getUser, (state) => state.accountActiveStatus);
export const selectAllTokens = createSelector(selectTokens, selectAll);
export const selectUserRole = createSelector(selectInformation, (state) => state.role);
export const selectLonginResponse = createSelector(getUser, ({ response, username }) => {
    return { response, username };
});
