import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EffectsModule } from '@ngrx/effects';
import { WebemButtonModule } from '../button/button.module';
import { WebemOverlayService } from './core/services/webem-overlay.service';
import { OverlayEffects } from './core/effects/overlay.effects';
import { DynamicMessageDialogComponent } from './components/dynamic-message-dialog/dynamic-message-dialog.component';
import { ConfirmDialogComponent } from './components/confirm-dialog/confirm-dialog.component';
import { OverlayPanelComponent } from './components/overlay-panel/overlay-panel.component';
import { ConfirmationService } from 'primeng/api';
import { DynamicDialogModule, DialogService } from 'primeng/dynamicdialog';
import { OverlayPanelModule } from 'primeng/overlaypanel';
import { DialogModule } from 'primeng/dialog';
import { ConfirmDialogModule } from 'primeng/confirmdialog';

@NgModule({
    declarations: [
        DynamicMessageDialogComponent,
        ConfirmDialogComponent,
        OverlayPanelComponent
    ],
    imports: [
        CommonModule,
        WebemButtonModule,
        DialogModule,
        DynamicDialogModule,
        ConfirmDialogModule,
        OverlayPanelModule
    ],
    exports: [
        ConfirmDialogComponent,
        OverlayPanelComponent
    ],
    entryComponents: [
        DynamicMessageDialogComponent
    ]
})
export class WebemOverlayModule {
    static forRoot(): ModuleWithProviders<WebemOverlayModule> {
        return {
            ngModule: WebemOverlayModule,
            providers: [
                DialogService,
                ConfirmationService,
                WebemOverlayService
            ]
        };
    }

    static forFeature() {
        return EffectsModule.forFeature([
            OverlayEffects
        ]);
    }
}
