import { createFeatureSelector, createSelector } from '@ngrx/store';
import { NavigationSearchState } from '../models/navigation-search-state';
import { navigationSearchStateFeatureKey } from '../reducers/navigation-search.reducer';

export const selectNavigationSearch = createFeatureSelector<NavigationSearchState>(navigationSearchStateFeatureKey);

export const selectIsNavigationSearchOverlayOpened = createSelector(
    selectNavigationSearch,
    (state: NavigationSearchState) => state.isNavigationSearchOverlayOpened
);
