import { ConfigurationSaveVariant } from '../models';
import { DateTime } from '../classes/date-time';
import { sanitizeWithUnderScore } from '@oam/core/lib/utils/shared-utils';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';

type PlanOrigin = 'active' | 'inactive' | 'delta' | 'stage' | 'dashboard';
const EMPTY_PLACEHOLDER_ASCII = '-';
const exp = new RegExp(EMPTY_PLACEHOLDER, 'g');

export function generatePlanFileName(saveVariant: ConfigurationSaveVariant, planOrigin: PlanOrigin, planName = '', modelState: any) {
    let outputFileName = '';
    const mrbtsId = modelState.mrbtsId;
    const btsName = modelState.btsName;
    const sanitizeBtsName = sanitizeWithUnderScore(btsName);
    const dateTime = new DateTime().toFormattedFileDateTime();

    switch (planOrigin) {
        case 'dashboard':
            outputFileName = `Configuration_scf_MRBTS-${mrbtsId}_${sanitizeBtsName}_${dateTime}`;
            break;
        case 'stage':
            outputFileName =  `Plan_configuration_scf_MRBTS-${mrbtsId}_${sanitizeBtsName}_${dateTime}`;
            break;
        case 'active':
            outputFileName = saveVariant === ConfigurationSaveVariant.ConfigurationOnly
                ? `Actual_configuration_scf_MRBTS-${mrbtsId}_${sanitizeBtsName}_${dateTime}`
                : `Full_actual_configuration_scf_MRBTS-${mrbtsId}_${sanitizeBtsName}_${dateTime}`;
            break;
        case 'delta':
            outputFileName = `Delta_configuration_scf_MRBTS-${mrbtsId}_${sanitizeBtsName}_${dateTime}`;
            break;
        default:
            outputFileName = `SCF_${planName}_${dateTime}`;
    }

    // Replacing non-ascii empty placeholder since backend is incompatible with SCF file contains non-ascii character
    return outputFileName.replace(exp, EMPTY_PLACEHOLDER_ASCII);
}
