import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { getParser } from 'bowser';
import { BrowserCompatibilityConfig } from './config';
import {
    BrowserIncompatibilityInfoClosed,
    BrowserIsSupport,
    BrowserIsNotSupport
} from '../../actions/compatibility-browser.actions';
import { ButtonThickness } from '../../../button/model/button-thickness.enum';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { selectBrowserCompatibility } from '../../selectors/browser-compatibility.selectors';
import { AccountConfigService } from '@oam/account-shared/lib/accounts-management/services/account-config.service';
import { OFFLINE_USER_MODE } from '@oam/shared/lib/constants/role-access-constants';
import { RoleAccessService } from '@oam/role-access-core';

const CHROME = 'chrome';
const FIREFOX = 'firefox';
const BRAVE = 'brave';
@Component({
    selector: 'ui-webem-browser-incompatibility-bar',
    templateUrl: './browser-incompatibility-bar.component.html',
    styleUrls: ['./browser-incompatibility-bar.component.scss']
})
export class BrowserIncompatibilityBarComponent implements OnInit {
    browserCompatibility$: Observable<boolean>;
    browserName: string;
    isSupported: boolean;
    closeButton = {
        icon: 'mdi mdi-close',
        thickness: ButtonThickness.Thin,
        theme: ButtonTheme.Secondary
    } as ButtonModel;

    constructor(private readonly store: Store,
                private readonly accountConfigService: AccountConfigService,
                private readonly roleAccessService: RoleAccessService) {}

    isNotOffline() {
        return this.roleAccessService.currentRole !== OFFLINE_USER_MODE;
    }

    ngOnInit() {
        this.browserCompatibility$ = this.store.select(selectBrowserCompatibility);
        this.checkBrowserCompatibility();
    }

    checkBrowserCompatibility() {
        const browser = getParser(window.navigator.userAgent);
        this.createInfoMessage(browser);
    }

    closeInformation() {
        this.store.dispatch(new BrowserIncompatibilityInfoClosed());
    }

    createInfoMessage(browser: any): void {
        this.browserName = this.getBrowserName(browser);
        this.isSupported = BrowserCompatibilityConfig.browserSupportedNames.names.includes(this.browserName);

        this.store.dispatch(this.isSupported ? new BrowserIsSupport() :
            new BrowserIsNotSupport());
    }

    getBrowserName(browser: any): string {
        const name = browser.getBrowserName().toLocaleLowerCase();
        const isBrave = name === CHROME && this.isBrave();

        return isBrave ? BRAVE : name;
    }

    isBrave(): boolean {
        const navigator = window.navigator as any;

        return navigator.brave !== undefined;
    }
}
