import { AfterViewInit, Directive, ElementRef, ViewChild } from '@angular/core';
import { Table } from 'primeng/table';
import { TreeTable } from 'primeng/treetable';

// workaround for https://github.com/primefaces/primeng/issues/9202
@Directive({
    selector: '[sharedFixTableColumnsResize]'
})
export class FixTableColumnsResizeDirective implements AfterViewInit {

    @ViewChild(TreeTable) treetable: ElementRef;
    @ViewChild(Table) table: ElementRef;

    constructor(private host: ElementRef) {
    }

    ngAfterViewInit() {
        if (!this.host || !this.host.nativeElement) return;

        const treeTableBody = this.host.nativeElement.querySelector('.p-treetable-virtual-scrollable-body');
        const dataTableBody = this.host.nativeElement.querySelector('.p-datatable-virtual-scrollable-body');
        if (treeTableBody) {
            treeTableBody.classList.add('p-treetable-scrollable-body');
        } else if (dataTableBody) {
            dataTableBody.classList.add('p-datatable-scrollable-body');
        }
    }
}
