import { Injectable } from '@angular/core';
import { SecService } from '@oam/cu-asm-api/services';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { catchError, filter, mapTo, mergeMap, tap, map } from 'rxjs/operators';
import { AccountInformation } from '@oam/cu-asm-api/models/account-information';
import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import { HttpErrorResponse } from '@angular/common/http';
import { of } from 'rxjs';
import {
    clearTokens,
    displayPasswordExpirationWarning, removeFromUserInformation, removeToken,
    removeUser,
    setToken,
    updateUser, updateUserInformation
} from '@oam/core/lib/actions/user.actions';
import { TokenType } from '@oam/core/lib/store/user-state';
import { setTokenInfoInitialValues } from '@oam/auth/lib/auth/core/actions/token-info.actions';

@Injectable()
export class UserEffects {
    updateUser$ = createEffect(() =>
        this.actions$.pipe(
            ofType(updateUser),
            mergeMap((action) => {
                const encodedUsername = window.btoa(action.payload.username);

                return this.secService.accountUsersSystemGet({
                    Username: encodedUsername
                }).pipe(
                    mergeMap((accountInfo: AccountInformation) => [
                        updateUserInformation({
                            payload: {
                                ...action.payload,
                                passwordExpirationDate: accountInfo.passwordExpirationDate || ''
                            }
                        })]),
                    catchError((httpResponse: HttpErrorResponse) => {
                        if (httpResponse.status === 500) {
                            return of(addNotification({
                                payload: {
                                    severity: 'error',
                                    summary: httpResponse.error.summary,
                                    detail: httpResponse.error.content
                                }
                            }));
                        }

                        return of(addNotification({
                            payload: {
                                severity: 'error',
                                summary: 'Retrieve user account information for local system administrator',
                                detail: 'Failed to retrieve additional user information'
                            }
                        }));
                    })
                );
            })
        )
    );

    checkIfPasswordExpired$ = createEffect(() =>
        this.actions$.pipe(
            ofType(updateUser),
            filter((action) => !!action.payload.passwordAboutToExpireMessage),
            mapTo(displayPasswordExpirationWarning())
        )
    );

    removeUser$ = createEffect(() =>
        this.actions$.pipe(
            ofType(removeUser),
            tap(() => {
                sessionStorage.removeItem('userInfo');
            })
        ), { dispatch: false }
    );

    setToken$ = createEffect(() =>
        this.actions$.pipe(
            ofType(setToken),
            tap((action) => {
                sessionStorage.setItem(`${action.payload.type}`, action.payload.value);
            })
        ), { dispatch: false }
    );

    clearToken$ = createEffect(() =>
        this.actions$.pipe(
            ofType(clearTokens),
            tap(() => {
                sessionStorage.removeItem(`${TokenType.BEARER}`);
                sessionStorage.removeItem(`${TokenType.PASSWORD_CHANGE}`);
            }),
            map(() => {
                return setTokenInfoInitialValues();
            })
        )
    );

    removeToken$ = createEffect(() =>
        this.actions$.pipe(
            ofType(removeToken),
            tap((action) => {
                sessionStorage.removeItem(`${action.payload}`);
            })
        ), { dispatch: false }
    );

    updateUserInformation$ = createEffect(() =>
        this.actions$.pipe(
            ofType(updateUserInformation),
            tap((action) => {
                const existingUserInfo = sessionStorage.getItem('userInfo');
                const updatedUserInfo = existingUserInfo ? { ...JSON.parse(existingUserInfo), ...action.payload } : action.payload;
                sessionStorage.setItem('userInfo', JSON.stringify(updatedUserInfo));
            })
        ), { dispatch: false }
    );

    RemoveFromUserInformation$ = createEffect(() =>
        this.actions$.pipe(
            ofType(removeFromUserInformation),
            tap((action) => {
                const userInfo = JSON.parse(sessionStorage.getItem('userInfo') || '');
                delete userInfo[action.payload];
                sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
            })
        ), { dispatch: false }
    );

    constructor(private readonly actions$: Actions,
                private readonly secService: SecService
    ) {
    }
}
