import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ButtonThickness } from '../../../button/model/button-thickness.enum';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';

@Component({
    selector: 'ui-webem-show-password-button',
    templateUrl: './show-password-button.component.html',
    styleUrls: ['./show-password-button.component.scss']
})
export class ShowPasswordButtonComponent {

    @Input() disabled: boolean;
    @Output() isPasswordVisible = new EventEmitter<boolean>();
    isVisible = false;

    eyeButton = {
        icon: 'mdi mdi-eye',
        thickness: ButtonThickness.Thin,
        theme: ButtonTheme.Secondary
    } as ButtonModel;

    crossedEyeButton = {
        icon: 'mdi mdi-eye-off',
        thickness: ButtonThickness.Thin,
        theme: ButtonTheme.Secondary
    } as ButtonModel;

    constructor() {}

    showPassword() {
        this.isVisible = true;
        this.isPasswordVisible.emit(this.isVisible);
    }

    hidePassword() {
        this.isVisible = false;
        this.isPasswordVisible.emit(this.isVisible);
    }

}
