import { IsDefined } from 'class-validator';

export class LayoutBase {
    @IsDefined()
    y: number;
    @IsDefined()
    x: number;
    @IsDefined()
    wXl: number;
    @IsDefined()
    hXl: number;
    @IsDefined()
    yXl: number;
    @IsDefined()
    xXl: number;

    constructor(layout: any) {
        const propertyNames = Object.getOwnPropertyNames(layout);
        propertyNames.forEach((key) => {
            this[key] = layout[key];
        });
    }
}
