import {
    AfterViewChecked,
    ChangeDetectionStrategy, ChangeDetectorRef,
    Component,
    Input, NgZone,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { Table } from 'primeng/table';
import { PmTableColumn, PmTableRow } from './models/pm-table.interface';
import { getFrozenRows, getScrollableColumns, getScrollableRows } from './utils/pm-table.utils';
import { sortPmCounterValuesByStartTime } from '../utils/pm-data.utils';
import { ngxCsv } from 'ngx-csv/ngx-csv';
import { PmCounter } from '../models/counters.interface';

@Component({
    selector: 'ui-webem-pm-table',
    templateUrl: './pm-table.component.html',
    styleUrls: ['./pm-table.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PmTableComponent implements OnInit, OnChanges, AfterViewChecked {
    @Input() pmCounters: PmCounter[];
    @Input() loading: boolean;
    @Input() selectCounterDialogStatus: boolean;
    @ViewChild('pTable') pmTable: Table;

    readonly frozenColumns: PmTableColumn[];
    scrollableColumns: PmTableColumn[];
    frozenRows: PmTableRow[];
    scrollableRows: PmTableRow[];
    isTableEmpty: boolean;
    animation: number;

    constructor(private readonly zone: NgZone,public readonly _changeDetectorRef: ChangeDetectorRef ) {
        this.scrollableColumns = [];
        this.isTableEmpty = true;
        this.frozenColumns = [{ field: 'counterId', header: 'Counter' }];
    }

    ngOnInit(): void {
        this.updateValues();
    }

    ngOnChanges(changes: SimpleChanges) {
        if(changes.selectCounterDialogStatus && !changes.selectCounterDialogStatus.firstChange){
            changes.selectCounterDialogStatus?.currentValue? this._changeDetectorRef.detach(): this._changeDetectorRef.reattach();
        }
        if (changes.pmCounters || changes.filterValue || changes.filterCaseSensitive || changes.filterKey) {
            this.updateValues();
        }
    }

    ngAfterViewChecked() {
        if (this.pmTable) {
            this.zone.runOutsideAngular(() => {
                if (this.animation) window.cancelAnimationFrame(this.animation);
                this.animation = window.requestAnimationFrame(() => this.modifyTableHeight());
            });
        }
    }

    public exportToCsv(fileName: string): ngxCsv {
        const tableHeader = this.frozenColumns.concat(this.scrollableColumns).reduce((obj, item) => {
            obj[item.field] = item.header;

            return obj;
        }, {});
        const tableData = this.frozenRows.concat(this.scrollableRows);

        return new ngxCsv([tableHeader].concat(tableData), fileName);
    }

    private updateValues() {
        const sortedPmData = sortPmCounterValuesByStartTime('desc', this.pmCounters);

        this.isTableEmpty = !sortedPmData.length;
        this.frozenRows = getFrozenRows(sortedPmData);
        this.scrollableColumns = getScrollableColumns(sortedPmData);
        this.scrollableRows = getScrollableRows(sortedPmData, 'desc');
    }

    private modifyTableHeight() {
        const pmTableNativeElem = this.pmTable.containerViewChild.nativeElement;
        const elementToBeModified = pmTableNativeElem.getElementsByClassName('p-datatable-scrollable-view p-datatable-unfrozen-view')[0];
        const elementToBeModifiedHeight =
            pmTableNativeElem.getElementsByClassName('p-datatable-scrollable-view p-datatable-frozen-view')[0];
        const scrollableBody = pmTableNativeElem.getElementsByClassName('p-datatable-scrollable-body')[1];
        const scrollWidth = scrollableBody.offsetWidth - scrollableBody.clientWidth;
        const scrollHeight = scrollableBody.offsetHeight - scrollableBody.clientHeight;

        if ((elementToBeModified && elementToBeModified.classList) ||
            (elementToBeModifiedHeight && elementToBeModifiedHeight.classHeight)) {
            const scrollWidthClass = Array.from(elementToBeModified.classList)
                .find((className: string) => className.includes('scroll-width'));
            const scrollHeightClass = Array.from(elementToBeModifiedHeight.classList)
                .find((className: string) => className.includes('scroll-height'));


            elementToBeModified.classList.remove(scrollWidthClass);
            elementToBeModifiedHeight.classList.remove(scrollHeightClass);

            if (scrollWidth) {
                elementToBeModified.classList.add(`scroll-width-${scrollWidth.toString()}`);
            }

            if (scrollHeight) {
                elementToBeModifiedHeight.classList.add(`scroll-height-${scrollHeight.toString()}`);
            }
        }

        if (scrollableBody.offsetHeight <= scrollableBody.clientHeight) {
            elementToBeModified.classList.add('horizontal-scroll-not-in-table');
        } else {
            elementToBeModified.classList.remove('horizontal-scroll-not-in-table');
        }

        if (scrollableBody.offsetWidth > scrollableBody.clientWidth) {
            elementToBeModified.classList.add('vertical-scroll-in-table');
        } else {
            elementToBeModified.classList.remove('vertical-scroll-in-table');
        }
    }
}
