import { UserProfileInterface } from '@oam/role-access-core';
import { RoleAccessGuardRule } from '@oam/role-access-ng/lib/role-access.module';

type IgnoreWhenType<T = any> = {
    fn: (dep: T) => (dep: T) => boolean;
    token: (T)
};
export class RoleAccessConfig {
    roleAccessGuardRules: Required<RoleAccessGuardRule>[];
    userProfiles: UserProfileInterface[];
    readonly index: string;
    ignoreWhen: IgnoreWhenType;

    constructor(roleAccessGuardRules: Required<RoleAccessGuardRule>[], userProfiles: UserProfileInterface[], index: string, ignoreWhen: IgnoreWhenType) {
        this.roleAccessGuardRules = roleAccessGuardRules;
        this.userProfiles = userProfiles;
        this.index = index;
        this.ignoreWhen = ignoreWhen;
    }
}
