import { createAction, props } from '@ngrx/store';
import { CounterNode } from '@oam/pm-shared/lib/measurement-store/models/counters-selection.interface';

export enum CountersSelectionActionTypes {
    SubscribeToCounterChanges = '[PMV2] [User] [PM Counters Selection] Subscribe counters change',
    UnsubscribeFromCounterChanges = '[PMV2] [PM Counters Selection] Unsubscribe counters change',
    SelectCounters = '[PMV2] [User] [PM Counters Selection] Select counters',
    ClearCountersSelection = '[PMV2] [User] [PM Counters Selection] Clear selection',
}

export const subscribeToCountersChanges = createAction(
    CountersSelectionActionTypes.SubscribeToCounterChanges
);

export const unsubscribeFromCountersChanges = createAction(
    CountersSelectionActionTypes.UnsubscribeFromCounterChanges
);

export const selectCounters = createAction(
    CountersSelectionActionTypes.SelectCounters,
    props<{nodes: CounterNode[], counterStartTime: string, timeFrame: number}>()
);

export const clearCountersSelection = createAction(
    CountersSelectionActionTypes.ClearCountersSelection
);
