import { createFeatureSelector, createSelector } from '@ngrx/store';
import { tokenInfoFeatureKey } from '../reducers/token-info.reducer';
import { TokenInfo } from '@oam/cu-api-gw-api';

const tokenInfoState = createFeatureSelector<TokenInfo>(tokenInfoFeatureKey);
export const getTokenInfo = createSelector(tokenInfoState, (state) => state);
