import { ModuleWithProviders, NgModule } from '@angular/core';
import { AccountConfig } from '@oam/account-shared/lib/accounts-management/services/account-config.service';


@NgModule({})
export class AccountConfigModule {
    static forRoot(additionalData: AccountConfig): ModuleWithProviders<AccountConfigModule> {
        return {
            ngModule: AccountConfigModule,
            providers: [
                {
                    provide: AccountConfig,
                    useValue: additionalData
                }
            ]
        };
    }
}
