import { createFeatureSelector, createSelector } from '@ngrx/store';
import { serverConnectionFeatureKey, ServerConnectionState } from '../reducers/server-connection.reducer';

export const selectServerConnectionState = createFeatureSelector<ServerConnectionState>(serverConnectionFeatureKey);

export const selectIsConnected = createSelector(
    selectServerConnectionState,
    (state: ServerConnectionState) => state && state.isConnected
);

export const selectIsDisconnected = createSelector(
    selectIsConnected,
    (isConnected: boolean) => !isConnected
);
