import { Action, createReducer, on } from '@ngrx/store';
import * as _ from 'lodash';
import {
    addCollectedFilesToStore,
    cancellingSnapshotIsFailed,
    updateSnapshotCancelState,
    cleanSnapshotState,
    collectingLocalSnapshotIsFailed,
    collectingLocalSnapshotIsSucceeded,
    collectingLocalSnapshotOngoing,
    collectingRemoteSnapshotIsFailed,
    collectingRemoteSnapshotIsSucceeded,
    collectingRemoteSnapshotOngoing,
    setSnapshotFormValues,
    setSnapshotStatusToCanceled,
    cancellingCollectionIsOngoing,
    startCollectingLocalSnapshot,
    startCollectingRemoteSnapshot,
    startDownloadingFiles,
    startSavingFiles,
    startCancellingCollectionLocalSnapshot,
    startCancellingCollectionRemoteSnapshot,
    downloadingFilesCompleted,
    cleanCollectSnapshotResultState,
    startTransferSnapshotToNMS,
    updateDownloadFileStatus,
    startCollectingLocalSnapshotHearbeat
} from '../actions/snapshot.action';
import { ICollectingSnapshotState, SNAPSHOT_LOCATION } from '../models';
import { SNAPSHOT_COVERAGE } from '@oam/diagnostic-snapshot/core/models/snapshot-coverage-type';
import {
    START_CANCELING_STATUS,
    SNAPSHOT_COLLECTING_STATUS
} from '@oam/diagnostic-snapshot/core/models/snapshot-progress-status';

export const collectingSnapshotFeatureKey = 'collectingSnapshot';

export const initialState: ICollectingSnapshotState = {
    location: SNAPSHOT_LOCATION.LOCAL,
    filterRegx: '',
    reason: '',
    coverage: SNAPSHOT_COVERAGE.FULL,
    operationId: '',
    files: [],
    collectStatus: undefined,
    startCancelingStatus: undefined,
    startHeartBeatStatus: false
};

export const collectingSnapshotReducer = createReducer(
    initialState,
    on(cleanSnapshotState, () => {
        return { ...initialState };
    }),
    on(setSnapshotStatusToCanceled, (state) => {
        return {
            ...state,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.CANCELING_SUCCEEDED,
            startHeartBeatStatus: false
        };
    }),
    on(startCancellingCollectionLocalSnapshot, startCancellingCollectionRemoteSnapshot, (state) => {
        return {
            ...state,
            startCancelingStatus: START_CANCELING_STATUS.START_CANCELING,
        };
    }),
    on(cancellingCollectionIsOngoing, (state) => {
        return {
            ...state,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.CANCELLING,
            startCancelingStatus: undefined,
            files: []
        };
    }),
    on(collectingLocalSnapshotIsSucceeded, (state) => {
        return { ...state, collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_FILES_SUCCEEDED };
    }),
    on(cancellingSnapshotIsFailed, (state) => {
        return {
            ...state,
            startCancelingStatus: START_CANCELING_STATUS.CANCELING_FAILED
        };
    }),
    on(collectingLocalSnapshotOngoing, (state, { operationId, files, startedAt }) => {
        return {
            ...state,
            operationId,
            files,
            startedAt,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_FILES_ONGOING,
        };
    }),
    on(collectingRemoteSnapshotOngoing, (state, { operationId, startedAt }) => {
        return {
            ...state,
            operationId,
            startedAt,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_FILES_ONGOING,
        };
    }),
    on(startCollectingRemoteSnapshot, startCollectingLocalSnapshot, (state) => {
        return {
            ...state,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_FILES_START,
            startCancelingStatus: undefined,
            errorMessage: undefined,
            startedAt: undefined
        };
    }),
    on(downloadingFilesCompleted, collectingRemoteSnapshotIsSucceeded, (state) => {
        return {
            ...state,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_SNAPSHOT_COMPLETE,
            finishedAt: Date.now(),
            startCancelingStatus: undefined
        };
    }),
    on(collectingLocalSnapshotIsFailed, collectingRemoteSnapshotIsFailed, (state, { error }) => {
        return {
            ...state,
            collectStatus: SNAPSHOT_COLLECTING_STATUS.COLLECTING_SNAPSHOT_FAILED,
            startCancelingStatus: undefined,
            errorMessage: error,
            finishedAt: state.finishedAt || Date.now(),
            startHeartBeatStatus: false
        };
    }),
    on(startDownloadingFiles, (state) => {
        return { ...state, collectStatus: SNAPSHOT_COLLECTING_STATUS.DOWNLOADING_FILES_ONGOING };
    }),
    on(startCollectingLocalSnapshotHearbeat, (state, { startHeartBeatStatus}) => {
        return { ...state,
            startHeartBeatStatus: startHeartBeatStatus };
    }),
    on(addCollectedFilesToStore, (state, { files }) => {
        const fileNameInt = _.keyBy(state.files, 'fileId');
        const updateFiles = files.map((file) => {
            return {
                fileId: file.fileId,
                fileStates: !fileNameInt[file.fileId] ? 'initial' : fileNameInt[file.fileId].fileStates,
                sizeInByte: file.sizeInByte
            };
        });

        return { ...state,
            files: updateFiles };
    }),
    on(updateDownloadFileStatus, (state, { fileId, fileStates } ) => {
        return { ...state,
            files: state.files.map((file) => fileId === file.fileId ? {...file, fileStates: fileStates }
                : file
            )
        };
    }),
    on(startSavingFiles, (state) => {
        return { ...state, collectStatus: SNAPSHOT_COLLECTING_STATUS.PREPARING_FILES_ONGOING };
    }),
    on(setSnapshotFormValues, (state, { reason, coverage, filterRegx, location }) => {
        return { ...state, reason, coverage, filterRegx, location };
    }),
    on(updateSnapshotCancelState, (state) => {
        return {
            ...state,
            startCancelingStatus: state.startCancelingStatus === START_CANCELING_STATUS.START_CANCELING ?
                state.startCancelingStatus : undefined
        };
    }),
    on(cleanCollectSnapshotResultState, startTransferSnapshotToNMS, (state) => {
        return {
            ...state,
            operationId: '',
            files: [],
            collectStatus: undefined,
            startCancelingStatus: undefined
        };
    }),
);

export function reducer(state: ICollectingSnapshotState | undefined, action: Action) {
    return collectingSnapshotReducer(state, action);
}
