import {
    AfterViewInit,
    Component,
    Input,
    OnInit,
    ContentChildren,
    QueryList,
    ViewChild,
    OnChanges
} from '@angular/core';
import { TileTooltipAllowedValue, TileTooltipContent } from '../../models';
import { ToggleParentTooltipDirective } from '../../directives/toggle-parent-tooltip/toggle-parent-tooltip.directive';
import { Tooltip } from 'primeng/tooltip';
import { NullableValuePipe } from '@oam/shared/lib/modules/webem-common';
import { ToggleTooltipByChildTooltipDirective } from '@oam/cu-ui/lib/tile-tooltip/directives/toggle-tooltip/toggle-tooltip-by-child-tooltip.directive';

@Component({
    selector: 'ui-webem-tile-tooltip',
    templateUrl: './tile-tooltip.component.html',
    styleUrls: ['./tile-tooltip.component.scss']
})
export class TileTooltipComponent implements OnInit, OnChanges, AfterViewInit {
    @Input() tooltipTitleHeader: string;
    @Input() tooltipTitleValue: string;
    @Input() tooltipContent?: TileTooltipContent[];
    @Input() tooltipTileState?: TileTooltipContent[];

    @ViewChild(Tooltip, { static: true }) private readonly tooltipRef: Tooltip;

    @ContentChildren(ToggleParentTooltipDirective, { descendants: true })
    toggleParentTooltipDirectives: QueryList<ToggleParentTooltipDirective>;

    @ContentChildren(ToggleTooltipByChildTooltipDirective, { descendants: true })
    toggleTooltipByChildTooltipDirectives: QueryList<ToggleTooltipByChildTooltipDirective>;

    tooltipText: string;

    constructor(private readonly nullableValuePipe: NullableValuePipe) {}

    ngOnInit(): void {
        this.generateTooltip();
    }

    ngOnChanges() {
        if (!this.tooltipRef.active) {
            this.tooltipRef.hide();
        }
    }

    ngAfterViewInit(): void {
        [...this.toggleParentTooltipDirectives, ...this.toggleTooltipByChildTooltipDirectives].forEach(element => {
            element.hideTooltip.subscribe(() => this.tooltipRef.hide());
            element.showTooltip.subscribe(() => this.tooltipRef.show());
        });
    }

    onLeaveHideTooltip() {
        this.tooltipRef.hide();
    }

    onEnterGenerateTooltip() {
        this.generateTooltip();
    }

    generateTooltip() {
        if (!this.tooltipContent && !this.tooltipTileState) return;
        this.tooltipText = this.getTooltipRowHtml(this.tooltipTitleHeader, this.tooltipTitleValue);
        if (this.tooltipContent) {
            this.tooltipText += '<br/>';
            this.mapContentArrayToString(this.tooltipContent);
        }
        if (this.tooltipTileState) {
            this.tooltipText += '<br/>';
            this.mapContentArrayToString(this.tooltipTileState);
        }
    }

    private mapContentArrayToString(contentArray: TileTooltipContent[]) {
        contentArray.forEach((content, index) => {
            this.tooltipText += this.getTooltipRowHtml(content.title, content.value);
        });
    }

    private getTooltipRowHtml(title: string, value: TileTooltipAllowedValue): string {
        return `<div data-title="${this.formatTooltipRowData(title)}" class="tooltip-row">
                <div class="name-column">${title}:</div>
                <div class="value-column">${this.nullableValuePipe.transform(value)}</div>
            </div>`;
    }

    private formatTooltipRowData(str: string): string {
        return str.replace(/\W+/g, '-').toLowerCase();
    }
}
