import { Observable } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { BaseDialogComponent } from '@oam/shared/lib/components/base-dialog/base-dialog-component';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { selectUsername } from '@oam/core/lib/user/core/reducers/user-reducer';
import { takeUntil } from 'rxjs/operators';
import { OperationStatus } from '@oam/core/lib/models/operation.interface';
import {
    getLoginStatus,
    selectLoginInProgress,
    selectTokenRenewalSuccessful
} from '@oam/auth/lib/auth/core/reducers/login.reducer';
import { renewSessionToken } from '@oam/core/lib/actions/user.actions';
import { resetOperationStatus, setLoginInitialValues } from '@oam/auth/lib/auth/core/actions/login.actions';

@Component({
    selector: 'ui-webem-renew-session-dialog',
    templateUrl: './renew-session-dialog.component.html',
    styleUrls: ['./renew-session-dialog.component.scss']
})
export class RenewSessionDialogComponent extends BaseDialogComponent implements OnInit, OnDestroy {
    readonly okButtonModel: ButtonModel = {
        label: 'OK',
        theme: ButtonTheme.Accent
    };
    readonly cancelButtonModel: ButtonModel = {
        label: 'Cancel'
    };
    showPassword: boolean;
    form: FormGroup;
    username: string;
    loginStatus$: Observable<OperationStatus>;

    constructor(protected store: Store,
                private readonly formBuilder: FormBuilder,
                public config: DynamicDialogConfig,
                ref: DynamicDialogRef) {
        super(store, ref);
    }

    ngOnInit(): void {
        this.showPassword = false;
        super.ngOnInit();

        this.form = this.formBuilder.group({
            password: ['', Validators.required]
        });
        this.store.select(selectUsername)
            .pipe(takeUntil(this.ngUnsubscribe))
            .subscribe(username => this.username = username);
        this.loginStatus$ = this.store.pipe(select(getLoginStatus));
        this.store.select(selectLoginInProgress)
            .pipe(takeUntil(this.ngUnsubscribe))
            .subscribe(inProgress => {
                inProgress ? this.form.disable() : this.form.enable();
            });

    }

    closeDialog(): void {
        this.store.dispatch(setLoginInitialValues());
        super.closeDialog();
    }

    renewSessionToken(): void {
        this.store.dispatch(renewSessionToken(
            {
                username: this.username,
                password: this.form.value.password,
                shouldStartOperation: false
            }
        ));
        this.store.select(selectTokenRenewalSuccessful)
            .pipe(takeUntil(this.ngUnsubscribe))
            .subscribe(isRenewalSuccessful => {
                if (isRenewalSuccessful?.status) {
                    this.closeDialog();
                    this.store.dispatch(resetOperationStatus());
                }
            });
    }

    setPasswordVisible(isVisible: boolean): void {
        this.showPassword = isVisible;
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }
}
