import { EntityState } from '@ngrx/entity';
import { LoginResponse, TokenInfo } from '@oam/cu-api-gw-api';
import { LocalUserPropertie } from '@oam/cu-asm-api';

export interface UserState {
    information: UserInformation;
    shouldDowngradedPrivilegesDialogPopup?: boolean;
    tokens: EntityState<Token>;
    lastSessionToken?: string | null;
    response: LoginResponse | TokenInfo,
    username: string,
    accountActiveStatus?: LocalUserPropertie[]
}

export interface UserInformation {
    passwordExpirationDate?: string;
    username: string;
    expires?: string;
    failedLoginAttempts?: string;
    lastFailedLogin?: string;
    lastLogin?: string;
    passwordAboutToExpireMessage?: string;
    readOnlyMode: boolean,
    role: string
}

export interface Token {
    type: string;
    value: string;
    username?: string;
}

export enum TokenType  {
    PASSWORD_CHANGE = 'temporaryPasswordChange',
    BEARER = 'Bearer'
}
