import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import { PageNavigationMenuComponent } from './components/page-navigation-menu/page-navigation-menu.component';
import { MAIN_MENU_KEY, reducer } from './core/reducers/main-menu.reducer';
import { SideBarMenuGroupsPipe } from './core/pipes/side-bar-menu-groups.pipe';
import { SideBarMenuModule } from '@oam/cu-ui';
import { Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';
import { FeatureFlag } from '@oam/feature-flag';
import { VnfcSharedService } from '@oam/vnfc-shared/lib/vnfc-store/services/vnfc-shared.service';
import { RoleAccessService } from '@oam/role-access-core';

@NgModule({
    declarations: [PageNavigationMenuComponent, SideBarMenuGroupsPipe],
    imports: [
        CommonModule,
        SideBarMenuModule,
        StoreModule.forFeature(MAIN_MENU_KEY, reducer)
    ],
    exports: [PageNavigationMenuComponent, SideBarMenuGroupsPipe]
})
export class NavigationModule {
    static forRoot(navigations: Navigation[]): ModuleWithProviders<NavigationModule> {

        return {
            ngModule: NavigationModule,
            providers: [
                {
                    provide: NavigationService,
                    useFactory: (featureFlag: FeatureFlag, vnfcSharedService: VnfcSharedService, roleAccessService: RoleAccessService) => {
                        return new NavigationService(navigations, featureFlag, vnfcSharedService, roleAccessService);
                    },
                    deps: [FeatureFlag, VnfcSharedService, RoleAccessService]
                }
            ]
        };
    }

}
