import { PmCounter, PmCounterValue } from '../models/counters.interface';

export function getFilteredPmData(
    pmCounters: PmCounter[],
    filterKey: string[],
    filterValue: string
): PmCounter[] {
    const filterValueCaseSensitive = filterValue.toLowerCase();

    return pmCounters.filter((counter: PmCounter) =>
        filterKey.some((key: string) => checkFilterValue(counter, key, filterValueCaseSensitive))
    );
}

export function sortPmCounterValuesByStartTime(order: 'asc' | 'desc', pmCounters: PmCounter[]): PmCounter[] {
    return pmCounters.map((pmCounter: PmCounter) => {
        pmCounter.counterValues = getSortedValuesByTimestamp(order, pmCounter.counterValues);

        return pmCounter;
    });
}

function getSortedValuesByTimestamp(order: 'asc' | 'desc', counterValues: PmCounterValue[]): PmCounterValue[] {
    return counterValues.slice().sort((counter1: PmCounterValue, counter2: PmCounterValue) => {
        const startTime1 = counter1.startTime.split('/').reverse().join('');
        const startTime2 = counter2.startTime.split('/').reverse().join('');

        return order === 'desc' ? compareStartTimesDescendant(startTime1, startTime2) :
            compareStartTimesAscendant(startTime1, startTime2);
    });
}

export function compareStartTimesAscendant(startTime1: string, startTime2: string): -1 | 0 | 1 {
    return startTime1 > startTime2 ? 1 : startTime1 < startTime2 ? -1 : 0;
}

export function compareStartTimesDescendant(startTime1: string, startTime2: string): -1 | 0 | 1 {
    return startTime1 < startTime2 ? 1 : startTime1 > startTime2 ? -1 : 0;
}

function checkFilterValue(pmCounter: PmCounter, filterKey: string, filterValue: string): boolean {
    if (filterKey === 'measurementAbbreviation' || filterKey === 'dimensionDistName') {
        return checkFilterForFullPath(pmCounter, filterValue);
    }
    if (filterKey === 'counterValues') {
        return pmCounter.counterValues.some((pmCounterValue: PmCounterValue) => {
            const valueToSearch = pmCounterValue.value.toLowerCase();
            const timestampsToSearch = pmCounterValue.startTime.toLowerCase();

            return valueToSearch.toString().includes(filterValue) || timestampsToSearch.includes(filterValue);
        });
    }
    const modelValueForKey = computeModelValueForKey(filterKey, pmCounter);
    if (modelValueForKey) {
        return modelValueForKey.includes(filterValue);
    }

    return true;
}

function checkFilterForFullPath(pmCounter: PmCounter, filterValue: string): boolean {
    const modelValueForKey: string = `${pmCounter.dimensionDistName}/${pmCounter.measurementAbbreviation}`.toLowerCase();

    if (modelValueForKey) {
        return modelValueForKey.includes(filterValue);
    }

    return true;
}

function computeModelValueForKey(filterKey: string, pmCounter: PmCounter): string {
    return filterKey === 'counter' ?
        `${pmCounter.counterId} (${pmCounter.counterAbbreviation})`.toLowerCase() :
        pmCounter[filterKey] && (pmCounter[filterKey]).toLowerCase();
}
