import { createAction } from '@ngrx/store';

export enum CountersPmServiceActionTypes {
    PmServerDown = '[PMV2] [PM Sever] Pm server is down',
    PmServerAvailable = '[PMV2] [PM Sever] Pm server is available',
    SubscribeToPmServiceState = '[PMV2] [PM Sever] Subscribe to PM Service state',
    RequestPmServiceAction = '[PMV2] [PM Sever] Request PM Service state'
}

export const pmServerDown = createAction(
    CountersPmServiceActionTypes.PmServerDown
);

export const pmServerAvailable = createAction(
    CountersPmServiceActionTypes.PmServerAvailable
);

export const subscribeToPmServiceState = createAction(
    CountersPmServiceActionTypes.SubscribeToPmServiceState
);

export const requestPmServiceAction = createAction(
    CountersPmServiceActionTypes.RequestPmServiceAction
);
