import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonLength } from '../../../button/model/button-length.enum';

@Component({
    selector: 'ui-webem-lock-open-button',
    templateUrl: './lock-open-button.component.html',
    styleUrls: ['./lock-open-button.component.scss']
})
export class LockOpenButtonComponent implements OnInit {

    @Input() buttonTitle: string;
    @Input() disabled: boolean;
    @Output() lockOpenClick = new EventEmitter<void>();

    buttonModel: ButtonModel;

    constructor() {
    }

    ngOnInit() {
        this.buttonModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-lock-open-variant-outline',
            title: this.buttonTitle,
            length: ButtonLength.Long
        };
    }

}
