import { Pipe, PipeTransform } from '@angular/core';
import { DatetimeService } from '@oam/core/lib/services/datetime.service';
import { getValueIfAvailable } from '@oam/core/lib/utils/shared-utils';

@Pipe({
    name: 'formatFromISO2GMTTime'
})
export class FormatFromISO2GMTTime implements PipeTransform {
    constructor(private readonly datetimeService: DatetimeService) {
    }

    transform(value: string): string {
        const formatedDate = this.datetimeService.formatFromISO2GMTTime(value);

        return formatedDate === 'Invalid date' ? getValueIfAvailable(value) : formatedDate;
    }
}
