import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ServerConnectionNotificationComponent } from './components/server-connection-notification/server-connection-notification.component';
import { FlexModule } from '@angular/flex-layout';
import { StoreModule } from '@ngrx/store';
import * as fromServerConnectionReducer from './core/reducers/server-connection.reducer';

@NgModule({
    declarations: [ServerConnectionNotificationComponent],
    exports: [
        ServerConnectionNotificationComponent
    ],
    imports: [
        CommonModule,
        FlexModule,
        StoreModule.forFeature(
            fromServerConnectionReducer.serverConnectionFeatureKey,
            fromServerConnectionReducer.reducer
        )
    ]
})
export class ServerConnectionCheckerModule {
}
