import { Subject } from 'rxjs';
import { selectUrl } from '@oam/core/lib/selectors/router.selectors';
import { skip, take, takeUntil } from 'rxjs/operators';
import { OnDestroy, OnInit, Directive, Component } from '@angular/core';
import { Store } from '@ngrx/store';
import { DynamicDialogRef } from 'primeng/dynamicdialog';

@Component({
    template: ''
})
export class BaseDialogComponent implements OnInit, OnDestroy {
    protected readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    constructor(
        protected readonly store: Store,
        public readonly ref: DynamicDialogRef,
        public shouldCloseWhenRouteChanged: Boolean = false) {
    }

    closeDialog(): void {
        this.ref.close();
    }

    ngOnInit(): void {
        if (this.shouldCloseWhenRouteChanged) {
            this.initCloseOnRouteChangeSubscription();
        }
    }

    ngOnDestroy(): void {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    initCloseOnRouteChangeSubscription(): void {
        this.store.select(selectUrl).pipe(
            takeUntil(this.ngUnsubscribe),
            skip(1),
            take(1),
        ).subscribe(() => {
            this.closeDialog();
        });
    }
}
