import { find, some, isEmpty } from 'lodash';
import { SPLIT_VIEW_LOCATION, SplitViewConfig, SplitViewRoutePath } from '../models';
import { NavigationService } from '@oam/navigation/lib/navigation/service/navigation.service';
import { groupNavigations } from '@oam/navigation/lib/navigation/core/utility/navigation-transformer';
import { navigationRoot, statusGroup, alarmGroup } from '@oam/navigation/lib/navigation/core/models/navigation-group.model';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { GroupOfViews } from '@oam/split-view/lib/core/models/group-of-views.interface';

export class SplitViewRestrictionsService {
    splitViewConfig: SplitViewConfig;
    private navigationService: NavigationService;

    constructor(config: SplitViewConfig, navigationService: NavigationService) {
        this.splitViewConfig = config;
        this.navigationService = navigationService;
    }

    getAvailableViews() {
        const availableViews = groupNavigations(this.navigationService.getAvailableNavigations(), item => {
            const isNavigationRoot = item.isGroup && item.nav.navigationGroup.id === navigationRoot.id;

            return item.isGroup ? getGroupProperties() : getItemProperties();

            function getItemProperties() {
                const childProperties = {
                    disabled: false,
                    disabledBy: 'none',
                    value: item.nav.routerLink.replace(/^\//, ''),
                    label: item.nav.label
                };

                return item.isItem ? {...childProperties, children: item.nav.children} : childProperties;
            }

            function getGroupProperties() {
                const properties = isNavigationRoot? {icon: item.nav.icon, label: item.nav.label} : {icon: item.nav.navigationGroup.icon, label: item.nav.navigationGroup.label};
                properties['items'] = item.nav['items'].flat();

                return properties;
            }
        }, (nav) => {
            return ![
                navigationRoot,
                statusGroup,
                alarmGroup
            ].some(group => nav.navigationGroup.id === group.id && nav.routerLink !== RouterLink.performance);
        });

        return (availableViews as GroupOfViews[]).map((group: GroupOfViews) => {
            const availableItems = group.items.reduce((flattenItems: any, item: any) => {
                if (!isEmpty(item.children)) {
                    flattenItems.push(...item.children);
                } else {
                    flattenItems.push(item);
                }

                return flattenItems;
            }, []);

            return {...group, items: availableItems};
        });
    }

    getViewRefreshLocation(topView: SplitViewRoutePath, bottomView: SplitViewRoutePath): SPLIT_VIEW_LOCATION {
        const views = [
            {
                value: topView,
                refreshLocation: SPLIT_VIEW_LOCATION.BOTTOM
            },
            {
                value: bottomView,
                refreshLocation: SPLIT_VIEW_LOCATION.TOP
            }
        ];

        const viewToRefresh = find(views, (view) => {
            return some(this.splitViewConfig.REFRESHING_VIEWS, (refreshingView) => refreshingView === view.value);
        });

        return viewToRefresh ? viewToRefresh.refreshLocation : SPLIT_VIEW_LOCATION.NONE;
    }

    isUrlInRestrictionGroup(url: string | undefined): boolean {
        return !!url && this.splitViewConfig.RESTRICTED_PATTERNS.views.includes(url);
    }

    notInSplitViews(destination: string[] | undefined): boolean {
        if (!destination) return false;

        return !this.splitViewConfig.RESTRICTED_PATTERNS.splitViews.includes(destination.join('/'));
    }

    isSplitBottomView(url: string): boolean {
        return this.splitViewConfig.RESTRICTED_PATTERNS.splitBottomViews.includes(url);
    }
}

