/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { CertMgtService } from './services/cert-mgt.service';
import { AsyncOperationsService } from './services/async-operations.service';
import { TraceConfigurationService } from './services/trace-configuration.service';
import { VduInterfacesService } from './services/vdu-interfaces.service';
import { DiagnosticService } from './services/diagnostic.service';
import { StatusService } from './services/status.service';
import { NotificationsService } from './services/notifications.service';
import { SecurityService } from './services/security.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    CertMgtService,
    AsyncOperationsService,
    TraceConfigurationService,
    VduInterfacesService,
    DiagnosticService,
    StatusService,
    NotificationsService,
    SecurityService,
    ApiConfiguration
  ],
})
export class CuTrsApiModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuTrsApiModule> {
    return {
      ngModule: CuTrsApiModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuTrsApiModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuTrsApiModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
