import { AlarmSeverityTypeColor } from '../model/alarm-severity-type-color.definition';
import { AlarmSeverityTypeLabel } from '../model/alarm-severity-type-label.definition';
import { AlarmSeverityType } from '../model/alarm-severity-type.definition';
import { AlarmSeverities } from '../model/alarm-severities.definition';

export type AlarmSeverityMap = {
    [k in keyof typeof AlarmSeverities]: AlarmSeverityType;
};

export const alarmSeverity: AlarmSeverityMap = {
    critical: { color: AlarmSeverityTypeColor.critical, label: AlarmSeverityTypeLabel.critical },
    major: { color: AlarmSeverityTypeColor.major, label: AlarmSeverityTypeLabel.major },
    minor: { color: AlarmSeverityTypeColor.minor, label: AlarmSeverityTypeLabel.minor },
    warning: { color: AlarmSeverityTypeColor.warning, label: AlarmSeverityTypeLabel.warning },
    indeterminate: { color: AlarmSeverityTypeColor.indeterminate, label: AlarmSeverityTypeLabel.indeterminate }
} as AlarmSeverityMap;
