import { Pipe, PipeTransform } from '@angular/core';
import { Alarm } from '@oam/cu-asm-api/models';
import { AlarmSeverityType } from '../model/alarm-severity-type.definition';
import { maxBy } from 'lodash';
import { alarmSeverity } from '../model/alarm-severity.definitions';

const mapSeverityToNumber = {
    critical: 5,
    major: 4,
    minor: 3,
    warning: 2,
    indeterminate: 1
};

@Pipe({
    name: 'getHighestAlarmSeverity',
    pure: true
})
export class GetHighestAlarmSeverityPipe implements PipeTransform {
    transform(alarms: Alarm[] | null): AlarmSeverityType | undefined {
        const highestSeverityAlarm = maxBy(alarms, (alarm: Alarm) => mapSeverityToNumber[alarm.severity]);
        if (highestSeverityAlarm) {
            return alarmSeverity[highestSeverityAlarm.severity];
        }

        return undefined;
    }
}
