import { Action, createFeatureSelector, createReducer, createSelector, on, select } from '@ngrx/store';
import * as CuActions from '../actions/cu.actions';
import { Cu, NgLinkStatus, NullableOperationalState } from '@oam/cu-asm-api/models';
import { pipe } from 'rxjs';
import { skip } from 'rxjs/operators';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';

export const featureKey = 'Cu';

export interface CuState {
    cu: Cu
}

export const initialState: CuState = {
    cu: {
        amfs: [{
            name: EMPTY_PLACEHOLDER,
            ipAddress: EMPTY_PLACEHOLDER,
            ngLinkStatus: NgLinkStatus.Inactive
        }],
        btsName: EMPTY_PLACEHOLDER,
        id: EMPTY_PLACEHOLDER,
        ipAddresses: {
            vnfIpAddress: EMPTY_PLACEHOLDER,
            mPlaneIpAddress: {},
            f1Interface: {
                cPlaneIpAddress: [{}],
                uPlaneIpAddress: [{}]
            },
            ngInterface: {
                cPlaneIpAddress: [{}],
                uPlaneIpAddress: [{}]
            },
            x2Interface: {
                cPlaneIpAddress: [{}],
                uPlaneIpAddress: [{}]
            },
            xnInterface: {
                cPlaneIpAddress: [{}],
                uPlaneIpAddress: [{}]
            }
        },
        mcc: -1,
        mnc: -1,
        mncLength: -1,
        mrbtsId: EMPTY_PLACEHOLDER,
        name: EMPTY_PLACEHOLDER,
        numberOfAlarms: -1,
        operationalState: NullableOperationalState.Disabled,
        serverIpAddressPrimary: EMPTY_PLACEHOLDER,
        serverIpAddressSecondary: EMPTY_PLACEHOLDER,
        softwareVersion: EMPTY_PLACEHOLDER,
        timeZone: null,
        readyForCommissioning: undefined,
        type: 'SA'
    }
};

const cuReducer = createReducer(
    initialState,
    on(CuActions.setCu, (state, { payload: cu }) => ({ ...state, cu })),
);

export function reducer(state: CuState | undefined, action: Action) {
    return cuReducer(state, action);
}

export const selectCuState = createFeatureSelector<CuState>(featureKey);
export const selectCu = createSelector(selectCuState, (state: CuState) => state.cu);
export const selectRecentCu = pipe(
    select(selectCu),
    skip(1)
);

export const selectCuStatusInfo = createSelector(
    selectCuState,
    (state: CuState) => {
        return {
            mrbtsId: state.cu.mrbtsId,
            btsName: state.cu.btsName,
            operationalState: state.cu.operationalState,
            softwareVersion: state.cu.softwareVersion,
            timeZone: state.cu.timeZone

    };
});

export const selectReadyForCommission = createSelector(
    selectCuState,
    (state: CuState) => {
        return state?.cu?.readyForCommissioning;
    }
);

export const selectCuAlarmsNumber = createSelector(
    selectCuState,
    (state: CuState) => {
        return {
            numberOfAlarms: state.cu.numberOfAlarms
        };
    });
