import { Action, createReducer, on } from '@ngrx/store';
import { setConnectionStatus } from '../actions/server-connection.actions';

export const serverConnectionFeatureKey = 'serverConnection';

export interface ServerConnectionState {
    isConnected: boolean
}

export const initialState: ServerConnectionState = {
    isConnected: true
};

const serverConnectionReducer = createReducer(
    initialState,
    on(setConnectionStatus, (state, { isConnected }) => ({ ...state, isConnected }))
);

export function reducer(state: ServerConnectionState | undefined, action: Action) {
    return serverConnectionReducer(state, action);
}
