import { Injectable } from '@angular/core';
import { HistoricalAlarm } from '@oam/cu-fm-api/models';
import { ngxCsv } from 'ngx-csv/ngx-csv';
import { DatetimeService } from './datetime.service';
import { CloudInfoService } from './cloud-info.service';
import { DateTime } from '../classes/date-time';
import { FaultTogglingRowModel } from '@oam/cu-ui/lib/fault-toggling-table/model';
import { sanitizeWithUnderScore } from '@oam/core/lib/utils/shared-utils';
import { AlarmType } from './AlarmType';

@Injectable({
    providedIn: 'root'
})
export class ExportService {
    constructor(private readonly datetimeService: DatetimeService,
                private readonly cloudInfoService: CloudInfoService) {
    }

    public exportAlarmsAsCsv(data: any, modelState: any, isHistoricalAlarms = false): ngxCsv {
        let headers = ['Severity', 'Appeared time', 'Alarm ID', 'Alarm name',
                       'Fault ID', 'Fault name', 'Alarming object'];
        if (isHistoricalAlarms) {
            headers = ['Severity', 'Appeared time', 'Cleared time', 'Alarm ID', 'Alarm name',
                       'Fault ID', 'Fault name', 'Alarming object'];
        }

        const name = isHistoricalAlarms ? AlarmType.HISTORICAL_ALARMS : AlarmType.CURRENT_ALARMS;
        const filename = this.getExportFilenameForAlarms(name, modelState);
        const alarms = data.map((alarm: HistoricalAlarm) => {
            const response = {};
            response['severity'] = alarm.severity || '';
            response['appearTime'] = this.datetimeService.formatFromISO8601(alarm.appearTime, true);
            if (isHistoricalAlarms) response['clearedTime'] = this.datetimeService.formatFromISO8601(alarm.clearedTime, true);
            response['alarmId'] = alarm.alarmId || alarm.alarmId === 0 ? alarm.alarmId : '';
            response['alarmName'] = alarm.alarmName || '';
            response['faultId'] = alarm.faultId || '';
            response['faultName'] = alarm.faultName || '';
            response['alarmingObject'] = alarm.alarmingObject || '';

            return response;
        });

        return new ngxCsv(alarms, filename, {headers});
    }

    public exportFaultTogglingHistoryCsv(data: FaultTogglingRowModel[], modelState: any): ngxCsv {
        const headers = ['State', 'Appeared', 'Fault ID', 'Fault Name',
                         'Fault Source', 'Node ID', 'Last 10 s', 'Last minute', 'Last hour', 'Last day', 'All'];

        const name = AlarmType.FAULT_TOGGLING_HISTORY;
        const filename = this.getExportFilenameForAlarms(name, modelState);
        const alarms = data.map((fault: FaultTogglingRowModel) => {
            const response = {};
            response['state'] = fault.state;
            response['appearTime'] = fault.togglingAlarmRaiseTime || '';
            response['faultId'] = fault.faultId || '';
            response['faultName'] = fault.faultName || '';
            response['faultSource'] = fault.faultSource || '';
            response['nodeId'] = fault.nodeId || '';
            response['togglingAlarmsLast10seconds'] = fault.togglingAlarmsLast10seconds || '';
            response['togglingAlarmsLastMinute'] = fault.togglingAlarmsLastMinute || '';
            response['togglingAlarmsLastHour'] = fault.togglingAlarmsLastHour || '';
            response['togglingAlarmsLastDay'] = fault.togglingAlarmsLastDay || '';
            response['togglingAlarmsSinceStartup'] = fault.togglingAlarmsSinceStartup || '';

            return response;
        });

        return new ngxCsv(alarms, filename, {headers});
    }

    public getExportFilenameWithExtension(name: string, fileFormat: string) {
        return `${this.getExportFilename(name)}.${fileFormat}`;
    }

    public getExportFilenameForAlarms(name: AlarmType, modelState: any) {
        const mrbtsId = modelState.mrbtsId;
        const btsName = modelState.btsName;
        const sanitizeBtsName = sanitizeWithUnderScore(btsName);
        const currentDateTime = new DateTime().toFormattedFileDateTime();

        return `MRBTS-${mrbtsId}_${sanitizeBtsName}_${name}_${currentDateTime}`;
    }

    public getExportFilenameForPmView(modelState: any) {
        const mrbtsId = modelState.mrbtsId;
        const btsName = modelState.btsName;
        const sanitizeBtsName = sanitizeWithUnderScore(btsName);
        const currentDateTime = new DateTime().toFormattedFileDateTime();

        return `PM_MRBTS-${mrbtsId}_${sanitizeBtsName}_${currentDateTime}`;
    }

    public getExportFilename(name: string) {
        const cloudName = this.cloudInfoService.getCloudName();
        const cloudId = this.cloudInfoService.getCloudId();
        const currentDateTime = new DateTime().toFormattedFileDateTime();

        return `${cloudName}_${cloudId}_${name}_${currentDateTime}`;
    }
}
