import { OperationResponse as CmOperationResponse, OperationResult } from '@oam/cu-cm-api/models';
import {
    Operation,
    UploadActionResponse as AsmUploadActionResponse
} from '@oam/cu-asm-api/models';
import { ResponseStatus, SerializedActionResponse } from '@oam/core/lib/services/model';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { CmService } from '@oam/cu-cm-api/services';

export function mapActivationResponse(result: Operation): SerializedActionResponse {
    return {
        actionId: result.id,
        status: result.status as ResponseStatus,
        errorMessage: result.errorMessage
    };
}

export function mapStartActivationCmResponseToAsm(result: CmOperationResponse): Operation {
    if (!result.isCompleted) {
        return {
            id: result.id,
            status: 'ongoing',
            links: result.links
        } as Operation;
    }

    return {
        id: result.id,
        status: 'success',
        links: result.links
    } as Operation;
}

export function mapActivationResultsCmResponseToAsm(cmService: CmService, result: CmOperationResponse, planId: string, operationId: string): Observable<Operation> {
    if (!result.isCompleted) {
        return of({
            id: result.id,
            status: 'ongoing',
            links: result.links
        } as Operation);
    }

    return cmService.getActivationResult({ planId, operationId }).pipe(
        map((activationResult: OperationResult) => {
            return {
                id: operationId,
                status: activationResult.status === 'not_ok' ? 'failure' : 'success',
                errorMessage: activationResult.result
            } as Operation;
        }),
        catchError(error => of({id: operationId, status: error.status, errorMessage: error.statusText}))
    );
}

export function mapCmResponseToAsmUploadActionResponse(result: CmOperationResponse): AsmUploadActionResponse {
    if (!result.isCompleted) {
        return {
            actionId: result.id,
            status: ResponseStatus.Ongoing,
            links: result.links
        } as AsmUploadActionResponse;
    }

    return {
        actionId: result.id,
        status: ResponseStatus.Success,
        links: result.links
    } as AsmUploadActionResponse;
}
