import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, CanLoad, Route, UrlSegment } from '@angular/router';
import { NgFeatureFlagModuleConfig } from '@oam/ng-feature-flag';
import { FeatureFlag } from '@oam/feature-flag';


@Injectable({
    providedIn: 'root'
})
export class FeatureFlagGuard implements CanActivate, CanLoad {
    private mapping: Map<string, string>;

    constructor(private ngFeatureFlagModuleConfig: NgFeatureFlagModuleConfig, private featureFlag: FeatureFlag) {
        this.mapping = this.ngFeatureFlagModuleConfig.featureFlagGuardRules.reduce((sum, rule) => {

            sum.set(rule.path, rule.featureFlag);

            return sum;
        }, new Map<string, string>());
    }

    canLoad(route: Route, segments: UrlSegment[]) {
        return this.isPathEnabled(route.path);
    }

    isPathEnabled(path = ''): boolean {
        const featureFlag = this.mapping.get(path);

        return this.featureFlag.isFeatureEnabled(featureFlag);
    }

    canActivate(
        route: ActivatedRouteSnapshot) {

        return this.isPathEnabled(route.routeConfig?.path);
    }

}
