export interface SideBarMenuModel {
    items: SideBarMenuGroup[];
}

export interface SideBarMenuItem {
    label: string;
    isActive?: boolean;
    data?: any;
    keyboardShortcut?: string;
    children?: ThirdMenuItem[];
}

export interface SideBarMenuAction extends SideBarMenuItem {
    foo?: any
}

export interface SideBarMenuGroup extends SideBarMenuItem {
    items: SideBarMenuAction[];
    icon: string;
    iconType: IconTypes;
}

export enum IconTypes {
    IMAGE_ICON = 'image-icon',
    MATERIAL_ICON = 'material_icon'
}


export interface ThirdMenuItem {
    label: string;
    isActive?: boolean;
    data: any;
    keyboardShortcut?: string;
}
