import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Alarm } from '@oam/cu-fm-api/models/alarm';
import { alarmSeverity } from '../../../alarm-severity-icon/model/alarm-severity.definitions';

import { Table } from 'primeng/table';
import { LazyLoadEvent } from 'primeng/api';
import { applyPrimeTablePatch } from '../../../prime-table-workaround/prime-table-workaround';
import { omit } from 'lodash';
import { Observable } from 'rxjs';
import { SPINNER_CLASS } from '@oam/cu-ui/lib/spinner/models/spinner-class-constants';


@Component({
    selector: 'ui-webem-alarms-table',
    templateUrl: './alarms-table.component.html',
    styleUrls: ['./alarms-table.component.scss']
})
export class AlarmsTableComponent implements OnInit {
    event: LazyLoadEvent;
    @Input() alarms: Alarm[];
    @Input() totalNumberOfAlarms$: Observable<number>;
    @Input() loading: boolean;
    @Input() totalRecords: number;
    @Input() itemsPerPage: number;
    @Input() alarmType: string;

    @ViewChild('table', { static: true }) table: Table;

    @Output()
    PageChanged = new EventEmitter();

    @Output()
    AlarmSelected = new EventEmitter<Alarm>();

    @Output()
    clearSelection = new EventEmitter();

    alarmSeverity = alarmSeverity;

    readonly SPINNER_CLASS = SPINNER_CLASS;

    constructor() {
    }

    ngOnInit() {
        applyPrimeTablePatch(this.table);
    }

    onAlarmSelect({ data }: { data: Alarm }) {
        this.AlarmSelected.emit(data);
    }

    onRowUnselect() {
        this.clearSelection.emit();
    }

    onLoadNewPage($event: LazyLoadEvent) {
        const partialEvent = omit($event, ['first']);
        this.event = { ...this.event, ...partialEvent};
        this.event.first = this.event.first || 1;
        this.PageChanged.emit(this.event);
    }

    onPageChange(pageNumber: number) {
        this.event = { ...this.event, first: pageNumber };
        this.PageChanged.emit(this.event);
    }

    isHistoricalAlarm() {
        return this.alarmType === 'historical';
    }
}
