import { PlansInfo } from '../model';
import { Action, createReducer, on } from '@ngrx/store';
import * as PlanInfoActions from '../actions/plans-info.actions';

export type PlansInfoState = {
    stagedPlans: PlansInfo[],
    deltaPlans: PlansInfo[],
    inactivePlans: PlansInfo[],
    activePlans: PlansInfo[],
};

export const plansInfoInitialState: PlansInfoState = {
    stagedPlans: [],
    deltaPlans: [],
    inactivePlans: [],
    activePlans: [],
};

const plansInfoReducer = createReducer(
    plansInfoInitialState,
    on(PlanInfoActions.updateStagedPlans, (state, {plans}) => ({ ...state, stagedPlans: [...plans] })),
    on(PlanInfoActions.updateDeltaPlans, (state, {plans}) => ({ ...state, deltaPlans: [...plans] })),
    on(PlanInfoActions.updateInactivePlans, (state, {plans}) => ({ ...state, inactivePlans: [...plans] })),
    on(PlanInfoActions.updateActivePlans, (state, {plans}) => ({ ...state, activePlans: [...plans] })),
);

export function reducer(state: PlansInfoState, action: Action) {
    return plansInfoReducer(state, action);
}

export const planBasicInfoFeatureKey = 'plans';
