import { Injectable } from '@angular/core';
import { FaultKeys } from './model';

type ParameterNameMapper = {
    [k in FaultKeys]: string
};
const parameterNames = {
    faultName: 'Fault name',
    meaning: 'Meaning',
    instruction: 'Instruction'
} as ParameterNameMapper;

@Injectable({
    providedIn: 'root'
})
export class UiFaultParametersMapperService {

    constructor() {
    }

    getUiName(abbreviationName: FaultKeys): string | undefined {
        return parameterNames[abbreviationName];
    }
}
