import { Component, Input, OnChanges, SimpleChanges, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { ChartDataSetConfig } from '../../../chart/model/chart-data-set.type';
import { WebemChartData } from '../../../chart/model/webem-chart.data';
import { isEmpty } from 'lodash';
import { ChartOptions } from 'chart.js';
import { CustomLegendItem } from '../../core/models/custom-legend-item.definition';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonThickness } from '../../../button/model/button-thickness.enum';
import { ChartComponent } from '../../../chart/components/chart/chart.component';

@Component({
    selector: 'ui-webem-line-chart',
    templateUrl: './line-chart.component.html',
    styleUrls: ['./line-chart.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class LineChartComponent implements OnChanges {
    @Input() dataSetConfigs: ChartDataSetConfig[];
    @Input() data: WebemChartData[];
    @Input() options: ChartOptions;
    @Input() maxItems: number;
    @Input() labels: string[];
    @Input() loading: boolean;
    @ViewChild('chart') chart: ChartComponent;

    expandButtonModel: ButtonModel = {
        icon: 'mdi mdi-chevron-up',
        theme: ButtonTheme.Secondary,
        thickness: ButtonThickness.Thin,
        title: 'Show full legend'
    };
    collapseButtonModel: ButtonModel = {
        icon: 'mdi mdi-chevron-down',
        theme: ButtonTheme.Secondary,
        thickness: ButtonThickness.Thin,
        title: 'Hide full legend'
    };
    expanded = false;
    customLegendItems: CustomLegendItem[] = [];
    filteredDataSetConfigs: ChartDataSetConfig[] = [];
    filteredData: WebemChartData[] = [];
    isChartEmpty: boolean;

    constructor() { }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.dataSetConfigs && this.didDataSetConfigsChange(changes.dataSetConfigs.previousValue, this.dataSetConfigs)) {
                this.updateChartInfo();
            }

        if (changes.dataSetConfigs || changes.data) {
            this.filterData();
            this.isChartEmpty = isEmpty(this.data);
        }
    }

    toggleVisibility(item: CustomLegendItem) {
        item.visible = !item.visible;
        this.filterData();
    }

    savePmChartAsPicture(fileName: string) {
        this.chart.saveChartAsPicture(fileName);
    }

    filterData() {
        const visibleItemsIndices = this.customLegendItems
            .filter(item => item.visible)
            .map(item => item.index);

        this.filteredData = this.data.filter((value, index) => visibleItemsIndices.includes(index));
        this.filteredDataSetConfigs = this.dataSetConfigs.filter((value, index) => visibleItemsIndices.includes(index));
    }

    private updateChartInfo(): void {
        this.filteredData = this.data;
        this.filteredDataSetConfigs = this.dataSetConfigs;
        this.customLegendItems = this.generateCustomLegendItems(this.dataSetConfigs);
    }

    private didDataSetConfigsChange(previous: undefined | ChartDataSetConfig[], current: ChartDataSetConfig[]): boolean {
        return !previous || previous.map(dsc => dsc.label).join('') !== current.map(dsc => dsc.label).join('');
    }

    private generateCustomLegendItems(dataSetConfigs: ChartDataSetConfig[]): CustomLegendItem[] {
        return dataSetConfigs.map((dataSetConfig, index) => ({
            color: dataSetConfig.borderColor as string,
            label: dataSetConfig.label as string,
            visible: true,
            index
        }));
    }
}
