import { Component, Input } from '@angular/core';

@Component({
    selector: 'ui-webem-confirm-dialog',
    templateUrl: './confirm-dialog.component.html'
})
export class ConfirmDialogComponent {
    @Input() header: string | null = null;
    @Input() message: string | null = null;
    @Input() key: string;
    @Input() icon: string | null = null;
    @Input() acceptLabel = 'OK';
    @Input() acceptIcon = '';
    @Input() acceptVisible = true;
    @Input() rejectLabel = 'Cancel';
    @Input() rejectIcon = '';
    @Input() rejectVisible = true;
    @Input() closeOnEscape = true;
    @Input() rtl = false;
    @Input() closable = false;
    @Input() appendTo: any;
    @Input() acceptButtonStyleClass: string;
    @Input() rejectButtonStyleClass: string;
    @Input() baseZIndex = 0;
    @Input() autoZIndex = true;
    @Input() transitionOptions = '400ms cubic-bezier(0.25, 0.8, 0.25, 1)';
}
