import { createAction, props } from '@ngrx/store';
import { HttpErrorResponse } from '@angular/common/http';
import { TokenInfo } from '@oam/cu-api-gw-api';

export enum TokenInfoActionsTypes {
    SubscribeTokenInfoWatch = '[Token Info] Subscribe token info watch',
    UnsubscribeTokenInfoWatch = '[Token Info] Unsubscribe token info watch',
    SubscribeTokenInfoWatchError = '[Token Info] Unsubscribe token info watch error',
    TokenInfoRequest = '[Token Info] token info request',
    SetTokenInfo = '[Token Info] Set token info',
    SetTokenInfoInitialValues = '[Token Info] Set token info to initial values',
    POST_TOKEN_INFO = '[Token Info] Post token info',
    UnsubscribeUserOperationWatch = '[Token Info] Unsubscribe User Operation Watch',
    SubscribeUserOperationWatch = '[Token Info] Subscribe User Operation Watch',
    ExpiredPollingErrorAction = '[Token Info] Expired polling error',
    RenewOperationalAction = '[Token Info] Renew Operation Action',
}

export const subscribeTokenInfoWatch = createAction(
    TokenInfoActionsTypes.SubscribeTokenInfoWatch
);

export const unsubscribeTokenInfoWatch = createAction(
    TokenInfoActionsTypes.UnsubscribeTokenInfoWatch
);

export const subscribeTokenInfoWatchError = createAction(
    TokenInfoActionsTypes.SubscribeTokenInfoWatchError, props<{ error: HttpErrorResponse }>()
);

export const tokenInfoRequest = createAction(
    TokenInfoActionsTypes.TokenInfoRequest
);

export const setTokenInfo = createAction(
    TokenInfoActionsTypes.SetTokenInfo,
    props<{tokenInfo: TokenInfo}>()
);

export const setTokenInfoInitialValues = createAction(
    TokenInfoActionsTypes.SetTokenInfoInitialValues
);

export const PostTokenInfo = createAction(
    TokenInfoActionsTypes.POST_TOKEN_INFO
);

export const UnsubscribeUserOperationWatch = createAction(TokenInfoActionsTypes.UnsubscribeUserOperationWatch);
export const ExpiredPollingErrorAction = createAction(
    TokenInfoActionsTypes.ExpiredPollingErrorAction,
    props<{ error?: HttpErrorResponse, message?: string, name?: string }>());
export const RenewOperationedAction = createAction(TokenInfoActionsTypes.RenewOperationalAction, props<{ OperationTimestamp: boolean}>());
export const SubscribeUserOperationWatch = createAction(TokenInfoActionsTypes.SubscribeUserOperationWatch);
