import {registerDecorator, ValidationArguments, ValidationOptions, ValidatorConstraint, ValidatorConstraintInterface} from 'class-validator';

export function Match(property: string, comparisonArr: Array<Object>, validationOptions?: ValidationOptions) {
    return (object: any, propertyName: string) => {
        registerDecorator({
            target: object.constructor,
            propertyName,
            options: validationOptions,
            constraints: [property, comparisonArr],
            validator: MatchConstraint,
        });
    };
}

@ValidatorConstraint({name: 'Match'})
export class MatchConstraint implements ValidatorConstraintInterface {

    validate(value: any, args: ValidationArguments) {
        const [relatedPropertyName, comparisonArr] = args.constraints;
        const findRelateItem = comparisonArr.find((o: any) => o.type === (args.object as any).type);
        const relatedValue = findRelateItem && findRelateItem[relatedPropertyName];

        return value === relatedValue;
    }
}
