import { Pipe, PipeTransform } from '@angular/core';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';

export const EMPTY_PLACEHOLDER_TITLE = 'not available';

@Pipe({
    name: 'formatTitle',
    pure: true
})
export class FormatTitlePipe implements PipeTransform {

    transform(title: string): string {
        return title === EMPTY_PLACEHOLDER ? EMPTY_PLACEHOLDER_TITLE : title;
    }

}
