import { Component, ElementRef, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActionsMenu, MenuAction } from '../../../../lib/button-with-menu/model/menu-action';
import { ButtonOptions } from '../../../button/model/button-options.definition';

@Component({
  selector: 'ui-webem-detail-data-row-with-actions-menu',
  templateUrl: './detail-data-row-with-actions-menu.component.html',
  styleUrls: ['./detail-data-row-with-actions-menu.component.scss']
})
export class DetailDataRowWithActionsMenuComponent implements OnInit {
    readonly buttonOptions: ButtonOptions  = {
        forceHeight: '22px',
        forceWidth: '26px'
    };

    @Input()
    itemKey: string;

    @Input()
    itemValue: string;

    @Input()
    actionsMenu?: ActionsMenu;

    @Input()
    container?: ElementRef;

    @Output()
    menuAction = new EventEmitter<string>();

    readonly emitAction = (action: string) => this.menuAction.emit(action);

    initMenuActions(): MenuAction[] | undefined {
        if (!this.actionsMenu) return;
        this.actionsMenu.menuOptions.forEach((action: MenuAction) => action.command = () => this.emitAction(action.label));
    }

    constructor() { }

    ngOnInit() {
        this.initMenuActions();
  }

}
