import { NavGroup, Navigation } from '@oam/navigation/lib/navigation/core/models/navigation.interface';
import { navigationRoot } from '@oam/navigation/lib/navigation/core/models/navigation-group.model';

export function groupNavigations<T extends Navigation = Navigation, B extends NavGroup = NavGroup, R = any>(
    entryModels: T[],
    derivedProperties: (groupOrItem: { isGroup: boolean, isItem: boolean, nav: T | B }) => R = (x) => (x.nav as unknown as R) ,
    shouldExclude: (a: T) => boolean = () => false
): R[] {

    const navigationGroupMap = entryModels.reduce((entryModelGroup, entryModelItem) => {
        const groupKey = getGroupKey(entryModelItem);
        const group = entryModelGroup[groupKey] || { ...entryModelItem, items: [] };

        if (shouldExclude(entryModelItem)) return entryModelGroup;

        group.items.push({...entryModelItem});
        entryModelGroup[groupKey] = group;

        return entryModelGroup;
    }, {} as NavGroup);

    return Object.values(navigationGroupMap).map(navGroup => {
        const navItemsGroup = navGroup.items.reduce((itemGroup: any, item: any) => {
            if (!item.parentKey) {
                itemGroup[item.label] = derivedProperties({ isGroup: false, isItem: true, nav: item });
            } else {

                if (!itemGroup[item.parentKey]) {
                    itemGroup[item.parentKey] = derivedProperties({ isGroup: false, isItem: true, nav: {
                            label: item.parentKey,
                            routerLink: item.routerLink,
                            navigationGroup: item.navigationGroup,
                            children: [derivedProperties({ isGroup: false, isItem: false, nav: item })]
                        } as any
                    });
                } else {
                    itemGroup[item.parentKey].children.push(derivedProperties({ isGroup: false, isItem: false, nav: item }));
                }
            }

            return itemGroup;
        }, {});

        navGroup.items = Object.values(navItemsGroup);

        return derivedProperties({ isGroup: true, isItem: false, nav: navGroup });
    });

    function getGroupKey(entryModel: T) {
        const isRootNavigation = entryModel.navigationGroup === navigationRoot;

        return isRootNavigation ? entryModel.label : entryModel.navigationGroup?.id;
    }
}
