import { UiModelSettingsType, ModifyType } from '@oam/core/lib/services/model';
import { DashboardUiSettingsModel } from '@oam/dashboard/lib/core/models/dashboard-ui-settings-model';
import { LayoutBase } from '@oam/core/lib/functions/layout-base';
import { IsDefined, IsEnum } from 'class-validator';
import { Match } from '@oam/shared/lib/decorators/match.decorator';


export enum VduTypeEnum {
    VduCellsStatus = 'vdu-cells-status',
    RmodsStatus = 'rmods-status',
    Alarms = 'alarms',
    Operations = 'operations',
    VnfcStatus = 'vnfc-status',
    NetworkSlicing = 'network-slicing',
    Counters1 = 'counters1',
    Counters2 = 'counters2',
    Counters3 = 'counters3',
    Counters4 = 'counters4'
}

export const vduDashboardUiSettingsDefault: ModifyType<UiModelSettingsType, { layoutConfig: DashboardUiSettingsModel[] }> = {
    name: 'vdu-dashboard',
    layoutConfig: [
        { w: 8, h: 2, y: 0, x: 8, wXl: 8, hXl: 2, yXl: 0, xXl: 0, type: VduTypeEnum.RmodsStatus },
        { w: 8, h: 2, y: 0, x: 8, wXl: 8, hXl: 2, yXl: 0, xXl: 8, type: VduTypeEnum.VduCellsStatus },

        { w: 3, h: 2, y: 2, x: 0, wXl: 3, hXl: 2, yXl: 2, xXl: 0, type: VduTypeEnum.NetworkSlicing },

        { w: 2, h: 3, y: 4, x: 0, wXl: 2, hXl: 3, yXl: 4, xXl: 0, type: VduTypeEnum.Operations },
        { w: 8, h: 3, y: 4, x: 2, wXl: 8, hXl: 3, yXl: 4, xXl: 2, type: VduTypeEnum.Alarms },
        { w: 6, h: 3, y: 4, x: 10, wXl: 6, hXl: 3, yXl: 4, xXl: 10, type: VduTypeEnum.VnfcStatus },

        { w: 4, h: 3, y: 7, x: 0, wXl: 4, hXl: 5, yXl: 7, xXl: 0, type: VduTypeEnum.Counters1 },
        { w: 4, h: 3, y: 7, x: 4, wXl: 4, hXl: 5, yXl: 7, xXl: 4, type: VduTypeEnum.Counters2 },
        { w: 4, h: 3, y: 7, x: 8, wXl: 4, hXl: 5, yXl: 7, xXl: 8, type: VduTypeEnum.Counters3 },
        { w: 4, h: 3, y: 7, x: 12, wXl: 4, hXl: 5, yXl: 7, xXl: 12, type: VduTypeEnum.Counters4 },
    ]
};


export class VduLayout extends LayoutBase {
    @IsEnum(VduTypeEnum)
    @IsDefined()
    type: VduTypeEnum;

    @Match('w', vduDashboardUiSettingsDefault.layoutConfig)
    @IsDefined()
    w: number;

    @Match('h', vduDashboardUiSettingsDefault.layoutConfig)
    @IsDefined()
    h: number;

    constructor(layout: any) {
        super(layout);
    }
}
