import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { ButtonTheme } from '../../../button/model/button-theme.enum';
import { ButtonLength } from '../../../button/model/button-length.enum';

@Component({
    selector: 'ui-webem-lock-button',
    templateUrl: './lock-button.component.html',
    styleUrls: ['./lock-button.component.scss']
})
export class LockButtonComponent implements OnInit {

    @Input() buttonTitle: string;
    @Input() disabled = false;
    @Output() lockClick = new EventEmitter<void>();

    buttonModel: ButtonModel;

    constructor() {
    }

    ngOnInit() {
        this.buttonModel = {
            theme: ButtonTheme.Secondary,
            icon: 'mdi mdi-lock',
            title: this.buttonTitle,
            length: ButtonLength.Long
        };
    }

}
