import { Injectable } from '@angular/core';
import { PeService } from '@oam/cu-asm-api/services';
import { PlanOrigin } from '../model/plan-origin';
import { CmService } from '@oam/cu-cm-api/services';
import { Observable, of, throwError } from 'rxjs';
import { map, mergeMap } from 'rxjs/operators';
import {
    ActionStatePlanInformation,
    ActivationAdditionalData,
    DeleteManagedObjectActionRequestEndpoint,
    DeleteManagedObjectRequestEndpoint,
    GlobalSearchInformation,
    NotSupportedPlanCreationOption,
    NotSupportedPlanOriginError,
    PlanDeletionInformation,
    PlansInfo
} from '../model';
import {
    FileDownloadGeneratePlanResponse,
    GeneratePlanResponse,
    ManagedObjectCopySources,
    ManagedObjectData,
    Operation,
    SearchParameterResponse, ValidationActionResponse,
    ValidationErrorType
} from '@oam/cu-asm-api/models';
import { UploadActionResponse } from '@oam/cu-asm-api/models/upload-action-response';
import { ClientType } from '@oam/cu-cm-api/models/client-type';
import {
    mapActivationResultsCmResponseToAsm,
    mapCmResponseToAsmUploadActionResponse,
    mapStartActivationCmResponseToAsm
} from '@oam/core/lib/functions/api-mappings';

import {
    AddManagedObjectActionRequestEndpoint,
    AddManagedObjectRequestEndpoint
} from '../model/add-managed-object-endpoints';
import { PlanCreationOption } from '../model/plan-creation-option';
import { PlanBase } from '@oam/configuration-shared/lib/core/model/plan-request-base';
import { PaginationObject } from '@oam/configuration-shared/lib/core/model/pagination-object';
import { SortObject } from '@oam/configuration-shared/lib/core/model/sort-object';
import {
    FixErrorsActionRequestEndpoint,
    FixErrorsRequestEndpoint
} from '@oam/configuration-shared/lib/core/model/fix-errors-endpoints';
import { ReceivePlanRequest } from '@oam/configuration-shared/lib/core/model/receive-plan-request';
import {
    CopyManagedObjectActionRequestEndpoint,
    CopyManagedObjectRequestEndpoint
} from '@oam/configuration-shared/lib/core/model/copy-managed-object-endpoints';
import { getAsmUploadRequestBody, getCmUploadRequestBody } from '../functions/receive-plan';
import { ManagedObjectCopySourcesRequest } from '../model/managed-object-copy-sources-request';
import { OperationResponse } from '@oam/cu-cm-api';

const INVALID_PLAN_ORIGIN = 'Invalid PlanOrigin in validate plan operator';
type PlanTreeUpdateInformation = PlanBase & { revision?: string };

@Injectable()
export class PlanApiEndpointsService {
    constructor(private readonly peService: PeService, private readonly cmService: CmService) {
    }

    getDeleteManagedObjectRequestEndpoint(planOrigin: PlanOrigin): DeleteManagedObjectRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.deleteManagedObject(request);
            case PlanOrigin.DELTA:
                return request => this.peService.deleteManagedObjectOfDeltaPlan(request);
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    getDeleteManagedObjectActionRequestEndpoint(planOrigin: PlanOrigin, planId: string):
        DeleteManagedObjectActionRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.getDeleteManagedObjectAction({
                    planId,
                    actionId: request.actionId
                });
            case PlanOrigin.DELTA:
                return request => this.peService.getDeleteManagedObjectOfDeltaPlanAction({
                    planId,
                    actionId: request.actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    getDeleteActionOfDeleteManagedObjectEndpoint(planOrigin: PlanOrigin, planId: string, actionId: string): Observable<void | never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteActionOfDeleteManagedObject({
                    planId, actionId
                });
            case PlanOrigin.DELTA:
                return this.peService.deleteActionOfDeleteManagedObjectOfDeltaPlan({
                    planId, actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    deletePlan({ planId, planOrigin }: PlanDeletionInformation): Observable<void | never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteStagedPlan({ planId });

            case PlanOrigin.DELTA:
                return this.peService.deleteDeltaPlan({ planId });

            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    receivePlanFile({ planId, from, saveVariant }: ReceivePlanRequest): () => Observable<UploadActionResponse | OperationResponse> {
        switch (from) {
            case PlanOrigin.STAGE:
                return () => this.peService.generatePlanFileForUploading({ planId, body: getAsmUploadRequestBody(saveVariant) });

            case PlanOrigin.DELTA:
                return () => this.peService.generateDeltaPlanFileForUploading({ planId, body: getAsmUploadRequestBody(saveVariant) });

            case PlanOrigin.ACTIVE:
                return () => this.cmService.startUpload({ xOamClientType: ClientType.Em, body: getCmUploadRequestBody(saveVariant) })
                    .pipe(map(mapCmResponseToAsmUploadActionResponse));
        }

        return () => of<UploadActionResponse>();
    }

    getReceivePlanFileAction({ planId, from }: PlanBase): (params: any) =>
        Observable<UploadActionResponse | OperationResponse> {
        switch (from) {
            case PlanOrigin.STAGE:
                return ({ actionId }: any) => this.peService.getActionStateOfUploadingPlanFile({ actionId, planId });

            case PlanOrigin.DELTA:
                return ({ actionId }: any) => this.peService.getActionStateOfUploadingDeltaPlanFile({
                    actionId,
                    planId
                });

            case PlanOrigin.ACTIVE:
                return ({ actionId }) => this.cmService.getUpload({ operationId: actionId })
                    .pipe(map(mapCmResponseToAsmUploadActionResponse));
        }

        return () => of<UploadActionResponse>();
    }

    deleteReceivePlanFileAction({ actionId, planId, from }: ActionStatePlanInformation): Observable<void> {
        switch (from) {
            case PlanOrigin.STAGE:
                return this.peService.deleteActionStateOfUploadingPlanFile({ actionId, planId });

            case PlanOrigin.DELTA:
                return this.peService.deleteActionStateOfUploadingDeltaPlanFile({ actionId, planId });

            case PlanOrigin.ACTIVE:
                return this.cmService.deleteUpload({ operationId: actionId });
        }

        return of<void>();
    }

    uploadReceivePlanFile({ actionId, fileName, from }:
                              { actionId: string, fileName: string, from: PlanOrigin }): Observable<Blob> {
        switch (from) {
            case PlanOrigin.STAGE:
            case PlanOrigin.DELTA:
                return this.peService.uploadFile({ fileName });

            case PlanOrigin.ACTIVE:
                return this.cmService.getUploadFile({ operationId: actionId });
        }

        return of<Blob>();
    }

    getTreeOfPlan({ planId, from, revision }: PlanTreeUpdateInformation) {
        switch (from) {
            case PlanOrigin.STAGE:
                return this.peService.getTreeOfStagedPlan({ planId, oldRevision: revision });

            case PlanOrigin.DELTA:
                return this.peService.getTreeOfDeltaPlan({ planId, oldRevision: revision });

            case PlanOrigin.ACTIVE:
                return this.peService.getTreeOfCurrentPlan({ oldRevision: revision });
        }

        return throwError(new NotSupportedPlanOriginError(from));
    }

    isNotSupportedPlanOriginError(error: any): boolean {
        return error instanceof NotSupportedPlanOriginError;
    }

    getManagedObject({ moUuid, planId, fromOrigin }: any): Observable<ManagedObjectData | void> {
        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.getManagedObjectOfStagedPlan({ moUuid, planId });

            case PlanOrigin.DELTA:
                return this.peService.getManagedObjectOfDeltaPlan({ moUuid, planId });

            case PlanOrigin.ACTIVE:
                return this.peService.getManagedObjectOfCurrentPlan({ moUuid });
        }

        return of<void>();
    }

    sendPlanFile(option: PlanOrigin): (request: any) => Observable<FileDownloadGeneratePlanResponse> {
        if (option === PlanOrigin.STAGE) {
            return (request: any) => this.peService.generatePlanByDownloadScfFile(request);
        }

        return () => of<FileDownloadGeneratePlanResponse>();
    }

    getSendPlanFileAction(option: PlanOrigin): (request: any) => Observable<FileDownloadGeneratePlanResponse> {
        if (option === PlanOrigin.STAGE) {
            return (request: any) => this.peService.getActionStateOfGeneratingPlanByScf(request);
        }

        return () => of<FileDownloadGeneratePlanResponse>();
    }

    deleteSendPlanFileAction(option: PlanOrigin): (request: any) => Observable<void> {
        if (option === PlanOrigin.STAGE) {
            return (request: any) => this.peService.deleteActionStateOfGeneratingPlanByScf(request);
        }

        return () => of<void>();
    }

    searchInPlan({ planId, fromOrigin, pageNumber, itemsPerPage, sortOrder, sortedBy, filterByName }: GlobalSearchInformation): Observable<SearchParameterResponse> {
        if (!!planId && !!fromOrigin) {
            const searchParametersProps = {
                planId,
                fromOrigin,
                pageNumber,
                itemsPerPage,
                sortOrder,
                sortedBy,
                filterByName
            };
            switch (fromOrigin) {
                case PlanOrigin.STAGE:
                    return this.peService.searchParametersOfStagedPlan(searchParametersProps);

                case PlanOrigin.DELTA:
                    return this.peService.searchParametersOfDeltaPlan(searchParametersProps);
            }
        }
        if (fromOrigin === PlanOrigin.ACTIVE) {
            return this.peService.searchParametersOfCurrentPlan(
                { pageNumber, itemsPerPage, sortOrder, sortedBy, filterByName }
            );
        }

        return of<SearchParameterResponse>();
    }

    getCreatePlanRequestEndpoint(option: PlanCreationOption): (request: any) => Observable<GeneratePlanResponse> {
        switch (option) {
            case PlanCreationOption.EMPTY:
                return (request: any) => this.peService.generateNewPlan(request);
            case PlanCreationOption.COPY_ACTIVE:
                return (request: any) => this.peService.generatePlanByCopyCurrentPlan(request);
            case PlanCreationOption.COPY_STAGED:
                return (request: any) => this.peService.generatePlanByCopyStagedPlan(request);
            case PlanCreationOption.COPY_DELTA:
                return (request: any) => this.peService.generatePlanByCopyDeltaPlan(request);
            case PlanCreationOption.EDIT:
                return (request: any) => this.peService.generateDeltaPlan(request);
            default:
                throw new NotSupportedPlanCreationOption(
                    `Not supported plan creation option: ${option} in plan request endpoint`
                );
        }
    }

    getCreatePlanPollingEndpoint(option: PlanCreationOption): (request: any) => Observable<GeneratePlanResponse> {
        switch (option) {
            case PlanCreationOption.EMPTY:
                return (request: any) => this.peService.getActionStateOfGeneratingNewPlan(request);
            case PlanCreationOption.COPY_ACTIVE:
                return (request: any) => this.peService.getActionStateOfCopyingCurrentPlan(request);
            case PlanCreationOption.COPY_STAGED:
                return (request: any) => this.peService.getActionStateOfCopyingStagedPlan(request);
            case PlanCreationOption.COPY_DELTA:
                return (request: any) => this.peService.getActionStateOfCopyingDeltaPlan(request);
            case PlanCreationOption.EDIT:
                return (request: any) => this.peService.getActionStateOfGeneratingDeltaPlan(request);
            default:
                throw new NotSupportedPlanCreationOption(
                    `Not supported plan creation option: ${option} in plan polling endpoint`
                );
        }
    }

    getCreatePlanDeleteEndpoint(option: PlanCreationOption): (request: any) => Observable<void> {
        switch (option) {
            case PlanCreationOption.EMPTY:
                return (request: any) => this.peService.deleteActionStateOfGeneratingNewPlan(request);
            case PlanCreationOption.COPY_ACTIVE:
                return (request: any) => this.peService.deleteActionStateOfCopyingCurrentPlan(request);
            case PlanCreationOption.COPY_STAGED:
                return (request: any) => this.peService.deleteActionStateOfCopyingStagedPlan(request);
            case PlanCreationOption.COPY_DELTA:
                return (request: any) => this.peService.deleteActionStateOfCopyingDeltaPlan(request);
            case PlanCreationOption.EDIT:
                return (request: any) => this.peService.deleteActionStateOfGeneratingDeltaPlan(request);
            default:
                throw new NotSupportedPlanCreationOption(
                    `Not supported plan creation option: ${option} in plan delete endpoint`
                );
        }
    }

    createActivatePlanEndpoint(plan: PlansInfo, autoLockCells: boolean, forceReset: boolean): () => Observable<Operation> {
        switch (plan.from) {
            case PlanOrigin.STAGE:
                return () => this.peService.activateStagedPlan({ planId: plan.planId });

            case PlanOrigin.INACTIVE:
                return () => this.cmService.startActivation({ planId: plan.planId, xOamClientType: ClientType.Em })
                    .pipe(map(mapStartActivationCmResponseToAsm));

            case PlanOrigin.DELTA:
                return () => this.peService.activateDeltaPlan({
                    planId: plan.planId,
                    body: { autoLockCells, forceReset }
                });
        }
        throw Error('Invalid PlanOrigin in createActivatePlanEndpoint');
    }

    createDeployPlanEndpoint({ from, planId }: PlanBase): () => Observable<Operation> {
        switch (from) {
            case PlanOrigin.STAGE:
                return () => this.peService.preActivateOperationForStagedPlan({ planId });
            case PlanOrigin.DELTA:
                return () => this.peService.preActivateOperationForDeltaPlan({ planId });
        }

        throw Error('Invalid PlanOrigin in createDeployPollingEndpoint');
    }

    createDeployPollingEndpoint({ from, planId }: PlanBase): (params: any) => Observable<Operation> {
        switch (from) {
            case PlanOrigin.STAGE:
                return ({ actionId }: any) =>
                    this.peService.getPreActivateOperationForStagedPlan({ operationId: actionId, planId });

            case PlanOrigin.DELTA:
                return ({ actionId }: any) => this.peService.getPreActivateOperationForDeltaPlan({
                    operationId: actionId,
                    planId
                });
        }

        throw Error('Invalid PlanOrigin in createDeployPollingEndpoint');
    }

    createActivatePollingEndpoint({ from, planId }: PlanBase):
        (params: any) => Observable<Operation> {
        switch (from) {
            case PlanOrigin.STAGE:
                return ({ actionId }: any) =>
                    this.peService.getOperationStateOfActivatingStagedPlan({ operationId: actionId, planId });

            case PlanOrigin.INACTIVE:
                return ({ actionId }: any) =>
                    this.cmService.getActivation({ operationId: actionId, planId })
                        .pipe(
                            mergeMap((result) => {
                                return mapActivationResultsCmResponseToAsm(this.cmService, result, planId, actionId);
                            })
                        );

            case PlanOrigin.DELTA:
                return ({ actionId }: any) =>
                    this.peService.getOperationStateOfActivatingDeltaPlan({ operationId: actionId, planId });
        }
        throw Error('Invalid PlanOrigin in createActivatePollingEndpoint');
    }

    deleteDeployOperation(response: Operation, additionalData: ActivationAdditionalData) {
        switch (additionalData.fromOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deletePreActivateOperationForStagedPlan({
                    planId: additionalData.planId,
                    operationId: response.id
                });

            case PlanOrigin.DELTA:
                return this.peService.deletePreActivateOperationForDeltaPlan({
                    planId: additionalData.planId,
                    operationId: response.id
                });

            default:
                return throwError(new NotSupportedPlanOriginError(additionalData.fromOrigin));
        }
    }

    deleteActivationOperation(response: Operation, additionalData: ActivationAdditionalData) {
        const fromOrigin = additionalData.fromOrigin;
        const planId = additionalData.planId;
        const operationId = response.id;

        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteOperationStateOfActivatingStagedPlan({ planId, operationId });

            case PlanOrigin.INACTIVE:
                return this.cmService.deleteActivation({ planId, operationId });

            case PlanOrigin.DELTA:
                return this.peService.deleteOperationStateOfActivatingDeltaPlan({ planId, operationId });

            default:
                return throwError(new NotSupportedPlanOriginError(fromOrigin));
        }
    }

    getPossibleManagedObjectsToCopy(request: ManagedObjectCopySourcesRequest): Observable<ManagedObjectCopySources[]> {
        const { targetPlan, sourcePlan, moUuid, childNiddId } = request;

        switch (targetPlan.from) {
            case PlanOrigin.STAGE:
                return this.peService.getPossibleManagedObjectsToCopyForStagedPlan({
                    targetPlanId: targetPlan.planId,
                    sourcePlanId: sourcePlan.planId,
                    moUuid,
                    childNiddId
                });
            case PlanOrigin.DELTA:
                return this.peService.getPossibleManagedObjectsToCopyForDeltaPlans({
                    targetPlanId: targetPlan.planId,
                    sourcePlanId: sourcePlan.planId,
                    moUuid,
                    childNiddId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(targetPlan.from));
        }
    }

    getCopyManagedObjectRequestEndpoint(
        planOrigin: PlanOrigin
    ): CopyManagedObjectRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.copyManagedObjectsIntoStagedPlan(request);
            case PlanOrigin.DELTA:
                return request => this.peService.copyManagedObjectsIntoDeltaPlan(request);
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    getCopyManagedObjectActionRequestEndpoint(
        planOrigin: PlanOrigin,
        planId: string
    ): CopyManagedObjectActionRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.getCopyManagedObjectsIntoStagedPlanAction({
                    planId,
                    actionId: request.actionId
                });
            case PlanOrigin.DELTA:
                return request => this.peService.getCopyManagedObjectsIntoDeltaPlanAction({
                    planId,
                    actionId: request.actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    deleteCopyManagedObjectAction(
        planOrigin: PlanOrigin,
        planId: string,
        actionId: string
    ): Observable<void | never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteActionOfCopyManagedObjectsIntoStagedPlan({
                    planId, actionId
                });
            case PlanOrigin.DELTA:
                return this.peService.deleteActionOfCopyManagedObjectsIntoDeltaPlan({
                    planId, actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    getAddManagedObjectRequestEndpoint(
        planOrigin: PlanOrigin
    ): AddManagedObjectRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.createManagedObject(request);
            case PlanOrigin.DELTA:
                return request => this.peService.createManagedObjectOfDeltaPlan(request);
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    getAddManagedObjectActionRequestEndpoint(
        planOrigin: PlanOrigin,
        planId: string
    ): AddManagedObjectActionRequestEndpoint | Observable<never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.getCreateManagedObjectAction({
                    planId,
                    actionId: request.actionId
                });
            case PlanOrigin.DELTA:
                return request => this.peService.getCreateManagedObjectOfDeltaPlanAction({
                    planId,
                    actionId: request.actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    deleteAddManagedObjectAction(
        planOrigin: PlanOrigin,
        planId: string,
        actionId: string
    ): Observable<void | never> {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return this.peService.deleteCreateManagedObjectAction({
                    planId, actionId
                });
            case PlanOrigin.DELTA:
                return this.peService.deleteCreateManagedObjectInDeltaPlanAction({
                    planId, actionId
                });
            default:
                return throwError(new NotSupportedPlanOriginError(planOrigin));
        }
    }

    createValidationPlanEndPoint(planBase: PlanBase): (params: any) => Observable<ValidationActionResponse> {
        const fromOrigin = planBase.from;
        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return () => this.peService.validateStagedPlan({ planId: planBase.planId });

            case PlanOrigin.DELTA:
                return () => this.peService.validateDeltaPlan({ planId: planBase.planId });

        }

        throw Error(INVALID_PLAN_ORIGIN);
    }

    createValidationPlanPollingEndPoint(planBase: PlanBase) {
        const fromOrigin = planBase.from;
        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return ({ actionId }: any) => {
                    return this.peService.getValidateStagedPlanAction({ actionId, planId: planBase.planId });
                };

            case PlanOrigin.DELTA:
                return ({ actionId }: any) => {
                    return this.peService.getValidateDeltaPlanAction({ actionId, planId: planBase.planId });
                };
        }

        throw Error(INVALID_PLAN_ORIGIN);
    }

    createDownloadValidationPlanErrorEndPoint(plansInfo: PlanBase | undefined, validationId: string, additionalData: {
        pagination: PaginationObject, sort: SortObject, validationErrorType: ValidationErrorType
    }) {
        const fromOrigin = (plansInfo as PlanBase).from;

        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return this.peService
                    .getValidationResultOfStagedPlan({
                        validationId,
                        planId: (plansInfo as PlanBase).planId,
                        filterByErrorType: [additionalData.validationErrorType],
                        pageNumber: additionalData.pagination.pageNumber,
                        itemsPerPage: additionalData.pagination.itemsPerPage,
                        sortedBy: additionalData.sort.sortBy,
                        sortOrder: additionalData.sort.sortOrder
                    });

            case PlanOrigin.DELTA:
                return this.peService
                    .getValidationResultOfDeltaPlan({
                        validationId,
                        planId: (plansInfo as PlanBase).planId,
                        filterByErrorType: [additionalData.validationErrorType],
                        pageNumber: additionalData.pagination.pageNumber,
                        itemsPerPage: additionalData.pagination.itemsPerPage,
                        sortedBy: additionalData.sort.sortBy,
                        sortOrder: additionalData.sort.sortOrder
                    });

        }

        throw Error(INVALID_PLAN_ORIGIN);
    }

    createDownloadLatestValidationEndpoint(plansInfo: PlanBase | undefined, additionalData: {
        pagination: PaginationObject, sort: SortObject, validationErrorType: ValidationErrorType
    }) {
        const fromOrigin = (plansInfo as PlanBase).from;
        switch (fromOrigin) {
            case PlanOrigin.STAGE:
                return this.peService
                    .getLatestValidationResultOfStagedPlan({
                        planId: (plansInfo as PlanBase).planId,
                        filterByErrorType: [additionalData.validationErrorType],
                        pageNumber: additionalData.pagination.pageNumber,
                        itemsPerPage: additionalData.pagination.itemsPerPage,
                        sortedBy: additionalData.sort.sortBy,
                        sortOrder: additionalData.sort.sortOrder
                    });

            case PlanOrigin.DELTA:
                return this.peService
                    .getLatestValidationResultOfDeltaPlan({
                        planId: (plansInfo as PlanBase).planId,
                        filterByErrorType: [additionalData.validationErrorType],
                        pageNumber: additionalData.pagination.pageNumber,
                        itemsPerPage: additionalData.pagination.itemsPerPage,
                        sortedBy: additionalData.sort.sortBy,
                        sortOrder: additionalData.sort.sortOrder
                    });

        }

        throw Error(INVALID_PLAN_ORIGIN);
    }

    getRelationErrorsDetailsObject(plansInfo: PlansInfo, errorIndex: string) {
        switch (plansInfo.from) {
            case PlanOrigin.STAGE:
                return this.peService.getRelationErrorRelevantObjectListOfStagedPlan({
                    errorIndex,
                    planId: plansInfo.planId
                });
            case PlanOrigin.DELTA:
                return this.peService.getRelationErrorRelevantObjectListOfDeltaPlan({
                    errorIndex,
                    planId: plansInfo.planId
                });
            default:
                throw Error('Invalid PlanOrigin in getRelationErrorsDetailsObject');
        }
    }

    fixErrors(planOrigin: PlanOrigin, planId: string): FixErrorsRequestEndpoint {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.fixStagedPlan({
                    body: {
                        addMissingObjects: request.body.addMissingObjects,
                        fixMandatoryParameters: request.body.fixMandatoryParameters
                    },
                    planId
                });
            case PlanOrigin.DELTA:
                return request => this.peService.fixDeltaPlan({
                    body: {
                        addMissingObjects: request.body.addMissingObjects,
                        fixMandatoryParameters: request.body.fixMandatoryParameters
                    },
                    planId
                });
            default:
                throw Error('Fixing errors not possible');
        }
    }

    getFixedErrors(planOrigin: PlanOrigin, planId: string): FixErrorsActionRequestEndpoint {
        switch (planOrigin) {
            case PlanOrigin.STAGE:
                return request => this.peService.getFixStagedPlanAction({
                    actionId: request.actionId,
                    planId
                });

            case PlanOrigin.DELTA:
                return request => this.peService.getFixDeltaPlanAction({
                    actionId: request.actionId,
                    planId
                });
            default:
                throw Error('Fixing errors not possible');
        }
    }

    deleteFixErrorsAction({ actionId, planId, from }: ActionStatePlanInformation): Observable<void> {
        switch (from) {
            case PlanOrigin.STAGE:
                return this.peService.deleteActionOfFixStagedPlan({ actionId, planId });

            case PlanOrigin.DELTA:
                return this.peService.deleteActionOfFixDeltaPlan({ actionId, planId });
        }

        return of<void>();
    }
}
