import { Injectable, Output, Directive } from '@angular/core';
import { Subject } from 'rxjs';
import { ARROW_DOWN, ARROW_UP, ENTER } from '../../constants/keyboard-keys.constants';

@Injectable({
    providedIn: 'root'
})
export class NavigationSearchKeyboardService {
    @Output() handleKeyboardEvent = new Subject();

    handleKeyUp(event: KeyboardEvent): void {
        if ([ARROW_UP, ARROW_DOWN, ENTER].includes(event.key)) {
            this.handleKeyboardEvent.next(event);
        }
    }
}
