import { isArray, isEmpty, isNil } from 'lodash';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';
import sanitize from 'sanitize-filename';
import { loginFailed } from '@oam/auth/lib/auth/core/actions/login.actions';

export function getValueIfAvailable<T>(value: T): string {
    if (isNil(value) || isEmpty(`${value}`)) return EMPTY_PLACEHOLDER;

    return isArray(value) ?  value.join(', ') : `${value}`;
}


export function sanitizeWithUnderScore(input: string): string {
    const sanitizeName = sanitize(input, {replacement: '_'});

    return sanitizeName;
}

export function derToPemConverter(arrayBuffer: ArrayBuffer): any {
    const LF = '\n';
    const PEM_VALID = {
        BEGIN: '-----BEGIN CERTIFICATE-----',
        END: '-----END CERTIFICATE-----'
    };
    let pemBuffer;
    if (!isEmpty(arrayBuffer) || arrayBuffer.byteLength > 0) {
        pemBuffer = [PEM_VALID.BEGIN, `${getBase64Buffer(arrayBuffer)}`, PEM_VALID.END].join(LF);
    }

    return pemBuffer;

    function getBase64Buffer(arrBuffer: ArrayBuffer) {
        const MAX_LINE = 64;
        const outputBuffer = [];
        let base64Buffer = btoa(String.fromCharCode(...new Uint8Array(arrBuffer)));
        while (base64Buffer.length > MAX_LINE) {
            outputBuffer.push(`${base64Buffer.substr(0, MAX_LINE)}${LF}`);
            base64Buffer = base64Buffer.substr(MAX_LINE);
        }
        outputBuffer.push(base64Buffer);

        return outputBuffer.join('');
    }
}
