import { Component, HostListener, OnInit } from '@angular/core';
import {
    ButtonModel,
    ButtonTheme,
    ButtonLength
} from '@oam/cu-ui';
import { FullScreenService } from '@oam/layouts/lib/modules/page-header/full-screen.service';
import { chrome, firefox } from '@oam/layouts/lib/models/browsers-names';

@Component({
  selector: 'webem-full-screen',
  templateUrl: './full-screen.component.html',
  styleUrls: ['./full-screen.component.scss']
})
export class FullScreenComponent implements OnInit {

    isInFullScreenByAPI: boolean;
    isFirefoxInFullScreenByOS: boolean;
    disableFullScreenButton = false;
    browser: string;
    fullScreenButtonModel: ButtonModel;

    readonly f11Warning = 'Exit full screen by keyboard (F11) or firefox menu';
    readonly fullScreenEnterButtonModel: ButtonModel = {
        icon: 'mdi mdi-fullscreen',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long,
        title: 'Full screen mode'
    };
    readonly fullScreenExitButtonModel: ButtonModel = {
        icon: 'mdi mdi-fullscreen-exit',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long,
        title: 'Exit full screen mode'
    };
    readonly firefoxF11ButtonModel: ButtonModel = {
        icon: 'mdi mdi-fullscreen-exit',
        theme: ButtonTheme.Secondary,
        length: ButtonLength.Long,
        title: this.f11Warning
    };

    constructor(private readonly fullScreenService: FullScreenService) { }


    ngOnInit() {
        this.checkBrowser();
        this.checkFullScreenByAPI();
        this.updateFullScreenButton();
    }

    @HostListener('window:keydown', ['$event'])
    onKeyDown(event: KeyboardEvent) {
        if (event.key === 'F11' && this.browser === chrome) {
            event.preventDefault();
            this.toggleFullScreen();
        }
    }

    @HostListener('window:resize', [])
    onResize() {
        this.checkFullScreenByAPI();
        this.updateFullScreenButton();
    }

    toggleFullScreen(): void {
        this.fullScreenService.toggleFullScreen();
    }

    checkFullScreenByAPI(): void {
        this.isInFullScreenByAPI = this.fullScreenService.isInFullScreenModeByAPI();
    }

    updateFullScreenButton(): void {
        this.updateFullScreenButtonByAPI();
        if (this.browser === firefox) {
            this.checkFirefoxFullScreenByOS();
            this.updateFirefoxFullScreenButton();
        }
    }


    updateFirefoxFullScreenButton(): void {
        if (this.isFirefoxInFullScreenByOS) this.fullScreenButtonModel = this.firefoxF11ButtonModel;
        this.disableFullScreenButton = this.isFirefoxInFullScreenByOS;
    }

    checkFirefoxFullScreenByOS() {
        this.isFirefoxInFullScreenByOS = this.fullScreenService.isFirefoxInFullScreenModeByOS();
    }

    updateFullScreenButtonByAPI(): void {
        this.fullScreenButtonModel = this.isInFullScreenByAPI ? this.fullScreenExitButtonModel : this.fullScreenEnterButtonModel;
        this.disableFullScreenButton = false;
    }

    checkBrowser() {
        this.browser = this.fullScreenService.getBrowser();
    }
}
