/* tslint:disable */
import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ApiConfiguration, ApiConfigurationParams } from './api-configuration';

import { StatusService } from './services/status.service';
import { TracingService } from './services/tracing.service';
import { NotificationsService } from './services/notifications.service';
import { FhConfigService } from './services/fh-config.service';
import { TransportService } from './services/transport.service';
import { TestModelsService } from './services/test-models.service';

/**
 * Module that provides all services and configuration.
 */
@NgModule({
  imports: [],
  exports: [],
  declarations: [],
  providers: [
    StatusService,
    TracingService,
    NotificationsService,
    FhConfigService,
    TransportService,
    TestModelsService,
    ApiConfiguration
  ],
})
export class CuCpConfigClientModule {
  static forRoot(params: ApiConfigurationParams): ModuleWithProviders<CuCpConfigClientModule> {
    return {
      ngModule: CuCpConfigClientModule,
      providers: [
        {
          provide: ApiConfiguration,
          useValue: params
        }
      ]
    }
  }

  constructor( 
    @Optional() @SkipSelf() parentModule: CuCpConfigClientModule,
    @Optional() http: HttpClient
  ) {
    if (parentModule) {
      throw new Error('CuCpConfigClientModule is already loaded. Import in your base AppModule only.');
    }
    if (!http) {
      throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
      'See also https://github.com/angular/angular/issues/20575');
    }
  }
}
