import { RootStoreConfig } from '@ngrx/store/src/store_module';
import { WebemStore } from '@oam/webem/app/core/store/webem-store';
import { Environment } from '@oam/core/lib/environment-variables/core/models';

const runtimeCheckConfiguration: RootStoreConfig<WebemStore> = {
    runtimeChecks: {
        strictStateImmutability: true,
        // must be changed temporary to false due to issues:
        // - https://github.com/ngrx/platform/issues/2109
        // - https://github.com/ngrx/platform/issues/2404
        strictActionImmutability: false,
        strictStateSerializability: true,
        strictActionSerializability: false
    }
};

export const getRuntimeChecksConfiguration =
    (environment: Environment): RootStoreConfig<WebemStore> => (!environment.production ? runtimeCheckConfiguration : {});
