import { Injectable, NgZone } from '@angular/core';
import { Observable, SchedulerLike, timer, asyncScheduler } from 'rxjs';
import { leaveZone } from '@oam/core/lib/functions/leave-zone-scheduler';
import { enterZone } from '@oam/core/lib/functions/enter-zone-scheduler';
import { select, Store } from '@ngrx/store';
import { observeOn, switchMap, filter, map, distinctUntilChanged } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class TimerServiceService {

    constructor(private readonly ngZone: NgZone, private store: Store) {
    }

    public getTimer(scheduler: SchedulerLike, dueTime = 0, delay = 5000): Observable<number> {
        return timer(0, delay, leaveZone(this.ngZone, scheduler))
            .pipe(
                observeOn(enterZone(this.ngZone, scheduler))
            );
    }

    public setSnapshotDurationTimer(selector: any, scheduler = asyncScheduler) {
        return this.store.pipe(
            select(selector),
            filter(startedAt => !!startedAt || startedAt === 0),
            switchMap((startedAt: number) =>
                this.getTimer(scheduler, 0, 1000).pipe(
                    map(() => Date.now() - startedAt)
                )
            )
        );
    }

    public setSnapshotDuration(selector: any) {
        return this.store.pipe(
            select(selector),
            filter((duration: number) => !!duration && duration >= 0),
            distinctUntilChanged(),
            map((durationMs: number) => durationMs)
        );
    }
}
