import { DynamicFlatNode } from './dynamic-flat-node';
import { CountersFilter } from '../pm-tree.component';

export class DynamicDatabase {
    private readonly DISTNAME_SEPARATOR = '/';
    private readonly DELIMITER = '*-*';
    dataMap: Map<string, string[]>;
    rootLevelNodes: string[];

    constructor() {}

    setDatabase(database: Map<string, string[]>) {
        if (database.size) {
            this.dataMap = database;
            this.rootLevelNodes = Array.from(database)
                .map((item: any) => item[0])
                .filter((item: any) => !(item.includes(this.DELIMITER) ||
                    item.includes(this.DISTNAME_SEPARATOR))
                );
        } else {
            this.dataMap = new Map<string, string[]>();
            this.rootLevelNodes = [];
        }
    }

    initialData(): DynamicFlatNode[] {
        return (this.rootLevelNodes || []).map(name => {
            const children = this.getChildren(name);
            const hasChildren = children && !!children.length;

            return new DynamicFlatNode(name, 0, hasChildren);
        });
    }

    initialDataExtendedByFilter(filter: CountersFilter): DynamicFlatNode[] {
        const nodesList: DynamicFlatNode[] = [];
        (this.rootLevelNodes || []).forEach(name => this.addNode(name, 0, filter, nodesList));

        return nodesList;
    }

    addNode(name: string, level: number, filter: CountersFilter, nodesList: DynamicFlatNode[]) {
        const shortName = this.getNodeShortName(name);
        const nodeShortNameType = this.getNodeShortNameType(shortName, name);
        const isMatchingFilter = this.isRawNodeMatchingFilter(nodeShortNameType, shortName, filter);

        const children = this.getChildren(name) || [];
        const hasChildren = children && !!children.length;
        nodesList.push(new DynamicFlatNode(name, level, hasChildren, !isMatchingFilter));

        if (!isMatchingFilter) {
            children.forEach((child: string) => this.addNode(child, level + 1, filter, nodesList));
        }
    }

    private getNodeShortNameType(node: string, name: string): string {
        const partTypes = ['distName', 'measurementAbbreviation', 'counterId'];
        if (name.includes(this.DELIMITER)) {
            const parts = name.split(this.DELIMITER);

            return partTypes[parts.indexOf(node)];
        }

        return partTypes[0];
    }

    private isRawNodeMatchingFilter(nodeShortNameType: string, node: string, filterValue: CountersFilter): boolean {
        if (filterValue[nodeShortNameType] && node) {
            if (!nodeShortNameType.toLowerCase().includes(this.getLastTypeWithValueFromFilter(filterValue).toLowerCase())) {
                return false;
            }

            return node.toLowerCase().includes(filterValue[nodeShortNameType].toLowerCase());
        } else {
            return false;
        }
    }

    getLastTypeWithValueFromFilter(filterValue: CountersFilter): string {
        let lastValue = '';

        Object.entries(filterValue).forEach(item => {
            if (item[1]) {
                lastValue = item[0];
            }
        });

        return lastValue;
    }

    getNodeShortName(name: string): string {
        if (name.includes(this.DELIMITER)) {
            const parts = name.split(this.DELIMITER);

            return parts[parts.length - 1];
        }

        return name;
    }

    getChildren(node: string): string[] | undefined {
        return this.dataMap.get(node);
    }

    getParentId(node: string): string | undefined {
        const index = node.lastIndexOf(this.DELIMITER);
        if (index > 0) {
            return node.slice(0, index);
        } else {
            const distnameSeparatorIndex = node.lastIndexOf(this.DISTNAME_SEPARATOR);
            if (distnameSeparatorIndex > 0) {
                return node.slice(0, distnameSeparatorIndex);
            }
        }

        return;
    }

    isExpandable(node: string): boolean {
        return this.dataMap.has(node);
    }
}
