import { Injectable, Type } from '@angular/core';
import { DynamicMessageDialogConfig, WebemMessageDialogSeverity } from '../models';
import { DynamicMessageDialogComponent } from '../../components/dynamic-message-dialog/dynamic-message-dialog.component';
import { ConfirmationService, Confirmation } from 'primeng/api';
import { DialogService, DynamicDialogComponent, DynamicDialogConfig } from 'primeng/dynamicdialog';

@Injectable({
    providedIn: 'root'
})
export class WebemOverlayService {

    constructor(
        private readonly dialogService: DialogService,
        private readonly confirmationService: ConfirmationService
    ) {}

    openDialog(componentType: Type<any>, config: DynamicDialogConfig) {
        const DIALOG_DEFAULTS = {
            contentStyle: {
                margin: 0,
                padding: 0
            }
        };

        const newConfig = {
            ...DIALOG_DEFAULTS,
            ...config,
            contentStyle: {...DIALOG_DEFAULTS.contentStyle, ...config.contentStyle}
        };

        this.dialogService.open(componentType, newConfig);
    }

    showMessage(config: DynamicMessageDialogConfig) {
        const MESSAGE_DIALOG_DEFAULTS = {
            dismissableMask: true,
            width: '280px',
            contentStyle: {
                margin: 0,
                padding: 0
            },
            styleClass: '',
            closable: false,
            severity: WebemMessageDialogSeverity.INFO
        };

        const newConfig = {
            ...MESSAGE_DIALOG_DEFAULTS,
            ...config,
            contentStyle: {...MESSAGE_DIALOG_DEFAULTS.contentStyle, ...config.contentStyle}
        };
        newConfig.styleClass += ` ui-message-dialog-${newConfig.severity}`;

        this.dialogService.open(DynamicMessageDialogComponent, newConfig);
    }

    confirm(confirmation: Confirmation) {
        const defaultConfirmation: Confirmation = {
            header: 'Confirmation',
            acceptVisible: true,
            rejectVisible: true,
            acceptLabel: 'OK',
            rejectLabel: 'Cancel'
        };

        const confirmationModel: Confirmation = Object.assign({}, defaultConfirmation, confirmation);
        this.confirmationService.confirm(confirmationModel);
    }

    closeDialogsByName(closedComponent: any): void {
        const dialogs = this.dialogService.dialogComponentRefMap;
        dialogs.forEach(function(componentRef, dynamicDialogRef) {
            if (componentRef.instance.childComponentType === closedComponent) {
                dynamicDialogRef.close();
            }
        });
    }

    closeAllDialogs(): void {
        const dialogs = this.dialogService.dialogComponentRefMap;
        dialogs.forEach((componentRef, dynamicDialogRef) =>
                dynamicDialogRef.close()
        );
    }
}
