export type FlagsType = { [key: string]: boolean };
export type FeatureFlagProperty = {
    featureFlag?: string
};

export class FeatureFlag {

    private readonly flags: FlagsType;

    constructor(flags: FlagsType) {
        this.flags = flags;
    }

    isFeatureEnabled(flag: string | undefined): boolean {
        if (flag === undefined || this.flags[flag] === undefined) return true;

        return this.flags[flag];
    }
}
