import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CuResetButtonState } from '../../model/cu-reset-button-state';
import { SaveConfigurationState } from '../../model/save-button-state';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { selectCommissioningFlagStatus } from '@oam/commissioning-flag-store/lib/core/selectors/select-commissioning-status';
import { VnfcSharedService } from '../../../../../../vnfc-shared/src/lib/vnfc-store/services/vnfc-shared.service';

@Component({
    selector: 'ui-webem-operations-control-panel',
    templateUrl: './operations-control-panel.component.html',
    styleUrls: ['./operations-control-panel.component.scss']
})
export class OperationsControlPanelComponent implements OnInit {
    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();

    @Input() cuResetState: CuResetButtonState;
    @Input() saveConfigurationState: SaveConfigurationState;

    @Output() resetCuClicked = new EventEmitter<void>();
    @Output() saveConfigurationClicked = new EventEmitter<void>();
    @Output() duLockClicked = new EventEmitter<void>();
    @Output() duLockOpenClicked = new EventEmitter<void>();
    @Output() cellsLockClicked = new EventEmitter<void>();
    @Output() cellsLockOpenClicked = new EventEmitter<void>();
    @Output() x2LinksLockClicked = new EventEmitter<void>();
    @Output() x2LinksLockOpenClicked = new EventEmitter<void>();
    @Output() xnLinksLockClicked = new EventEmitter<void>();
    @Output() xnLinksLockOpenClicked = new EventEmitter<void>();
    @Output() ngLinksLockClicked = new EventEmitter<void>();
    @Output() ngLinksLockOpenClicked = new EventEmitter<void>();
    @Output() networkSlicingLockClicked = new EventEmitter<void>();
    @Output() networkSlicingLockOpenClicked = new EventEmitter<void>();

    private commissioningSubscription: Subscription;
    isCommissioningInProgress: boolean;
    isXnNgNsOperationsDisplay: boolean;

    constructor(private readonly store: Store,
                vnfcSharedService: VnfcSharedService) {
        this.isXnNgNsOperationsDisplay = vnfcSharedService.isVcu();
    }

    ngOnInit() {
        this.commissioningSubscription = this.store.pipe(
          takeUntil(this.ngUnsubscribe),
          select(selectCommissioningFlagStatus)
        )
          .subscribe(data => {
              this.isCommissioningInProgress = data;
          });
    }

}
