import { AlarmSeverities } from '@oam/cu-ui/lib/alarm-severity-icon/model/alarm-severities.definition';
import { RaisedAlarm } from '@oam/cu-ui/lib/raised-alarms/model/raised-alarm';
import { Alarm } from '@oam/cu-asm-api/models';

export function toRaisedAlarm({ alarmId, alarmName, severity, faultId, faultName }: Alarm): RaisedAlarm {
    return {
        alarmId: `${alarmId}`,
        alarmName,
        severity: severity as AlarmSeverities,
        faultId: `${faultId}`,
        faultName
    } as RaisedAlarm;
}
