import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Table } from 'primeng/table';
import { applyPrimeTablePatch } from '../../../prime-table-workaround/prime-table-workaround';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { includes, isEmpty, pickBy, isUndefined } from 'lodash';
import { CrlInfo } from '@oam/cu-trs-api/models/crl-info';
import { takeUntil } from 'rxjs/operators';

interface CrlInformationFiltersInterface {
    issuer: string;
    numOfRevCert: string;
    distributionPointURI: string;
    distributionPointType: any;
    updateFailureReason?: string;
}

const LDAP_BIND_FAILED = 'LdapBindFailed';
const VISIBLE_LDAP_BIND_FAILED = 'LDAP Bind Failed';
@Component({
    selector: 'ui-webem-crl-information-table',
    templateUrl: './crl-information-table.component.html',
    styleUrls: ['./crl-information-table.component.scss']
})

export class CrlInformationTableComponent implements OnInit, OnDestroy {
    @Input() crlInformation: Observable<CrlInfo[]>;
    @Input() set totalNumberOfCrl(value: number) {
        this.totalNumberOfCrlInfo.next(value);
    }
    @Input() itemsPerPage$: Observable<number>;
    @Input() filtersReceived: CrlInformationFiltersInterface;
    @Input() isLoadingCrlData$: Observable<boolean>;

    latestCrlInformation: CrlInfo[];
    filteredCrlInformation: CrlInfo[];
    currentPageCrlInformation: CrlInfo[];
    totalNumberOfCrlInfo: BehaviorSubject<number> = new BehaviorSubject<number>(0);
    distributionPointTypeOptions = [
        {
            label: 'Certificate_extension',
            value: 0
        },
        {
            label: 'Primary_manual',
            value: 1
        },
        {
            label: 'Secondary_manual',
            value: 2
        }
    ];

    filters: CrlInformationFiltersInterface;

    private readonly ngUnsubscribe: Subject<void> = new Subject<void>();
    pageNumber = 1;
    itemsPerPage: number;

    @Output()
    CrlCertificateSelected = new EventEmitter<number>();

    @Output()
    ClearSelection = new EventEmitter();

    @Output()
    SetFilters = new EventEmitter<CrlInformationFiltersInterface>();

    @ViewChild('table', { static: true }) table: Table;

    constructor() {
    }

    ngOnInit() {
        applyPrimeTablePatch(this.table);
        this.filters = { ...this.filtersReceived };
        this.crlInformation.pipe(takeUntil(this.ngUnsubscribe)).subscribe((crlInfo: CrlInfo[]) => {
            this.latestCrlInformation = crlInfo;
            this.filteredCrlInformation = [...crlInfo];
            this.totalNumberOfCrlInfo.next(crlInfo.length);
            this.onFilterChange();
            this.onPageChange(this.pageNumber);
        });
        this.itemsPerPage$.pipe(takeUntil(this.ngUnsubscribe)).subscribe((numberPerPage: number) => {
            this.itemsPerPage = numberPerPage;
            this.onPageChange(this.pageNumber);
        });
    }

    ngOnDestroy() {
        this.SetFilters.emit(this.filters);
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    isTableEmpty() {
        return this.latestCrlInformation.length > 0 && this.filteredCrlInformation.length === 0;
    }

    isTableNoData() {
        return this.latestCrlInformation.length === 0;
    }

    onSelectCrlCertificate({ data }: { data: CrlInfo }) {
        this.CrlCertificateSelected.emit(data.crlinfoRId);
    }

    onRowUnselect() {
        this.ClearSelection.emit();
    }

    onPageChange(pageNumber: number) {
        const start = (pageNumber - 1) * this.itemsPerPage;
        this.pageNumber = pageNumber;
        this.currentPageCrlInformation = [...this.filteredCrlInformation].splice(start, this.itemsPerPage);
    }

    onFilterSet(event: any, field: string) {
        this.filters[field] = event;
        this.onFilterChange();
    }

    mapReason(reason: string): string {
        return reason === LDAP_BIND_FAILED ? VISIBLE_LDAP_BIND_FAILED : reason;
    }

    onFilterChange() {
        const filterOptions = pickBy(this.filters, filterValue => !!filterValue);

        if (!isEmpty(filterOptions)) {
            this.filteredCrlInformation = this.latestCrlInformation.filter(certificate => {
                return Object.keys(filterOptions).every(key => {
                    if (key === 'distributionPointType' && filterOptions.distributionPointType) {
                        if (!filterOptions.distributionPointType.length) return true;

                        return includes(filterOptions.distributionPointType, certificate[key]?.value);
                    }

                    if (key === 'updateFailureReason' && !!filterOptions.updateFailureReason && certificate[key] === LDAP_BIND_FAILED) {
                        return includes(VISIBLE_LDAP_BIND_FAILED.toLowerCase(), filterOptions.updateFailureReason.toString().toLowerCase());
                    }

                    return !isUndefined(certificate[key]) &&
                        includes(certificate[key].toString().toLowerCase(), filterOptions[key].toString().toLowerCase());
                });
            });
        } else {
            this.filteredCrlInformation = [...this.latestCrlInformation];
        }
        this.onPageChange(1);
        this.totalNumberOfCrlInfo.next(this.filteredCrlInformation.length);
    }
}
