import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'formatGeneralStatusTimeZone',
    pure: true
})
export class FormatGeneralStatusTimeZonePipe implements PipeTransform {
    timeZoneRegex = new RegExp('([A-Z]{3})([+-]?)([0-9]{1,4})');

    transform(timeZone: string | undefined): string {
        if (!timeZone) return '-';

        const [, zone , sign, diff]: RegExpMatchArray = Object.assign(
            [],
            timeZone.match(this.timeZoneRegex) as RegExpMatchArray
        );

        if (!zone || !sign || !diff) return '-';

        const time = parseInt(diff.substring(0, 2), 10).toString();

        return `${zone}${sign}${time}`;
    }
}
