import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';

@Component({
    selector: 'ui-webem-no-selected-message',
    templateUrl: './no-selected-message.component.html',
    styleUrls: ['./no-selected-message.component.scss']
})
export class NoSelectedMessageComponent implements OnInit, OnChanges {
    @Input() message: string;
    @Input() isLoading: boolean;

    iconClasses: string[];

    readonly CLASSES = {
        INFO: 'mdi-information',
        LOADING: 'mdi-loading',
        SPIN: 'mdi-spin'
    };

    constructor() {
    }

    ngOnInit(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.isLoading = changes.isLoading?.currentValue;
        this.iconClasses = this.isLoading
            ? [this.CLASSES.LOADING, this.CLASSES.SPIN]
            : [this.CLASSES.INFO];
    }
}
