import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { AlarmCountBySeverity } from './AlarmCountBySeverity';

const normalFontSizeRange = 9999;
const nextFontSizeRange = 9999999;

@Component({
    selector: 'ui-webem-alarm-severity-dashboard-panel',
    templateUrl: './alarm-severity-dashboard-panel.component.html',
    styleUrls: ['./alarm-severity-dashboard-panel.component.scss']
})
export class AlarmSeverityDashboardPanelComponent implements OnInit, OnChanges {
    @Input() backgroundColorClass: string;
    @Input() severity: string;
    @Input() amount: AlarmCountBySeverity;
    @Input() fontColor: 'black' | 'white';

    fontSize = 22;

    constructor() {}

    ngOnInit() {}

    ngOnChanges(): void {
        if (typeof this.amount === 'number' && this.amount > normalFontSizeRange && this.amount < nextFontSizeRange) {
            this.fontSize = 15;
        } else if (typeof this.amount === 'number' && this.amount > nextFontSizeRange) {
            this.fontSize = 11;
        }
    }
}
