import { Actions, createEffect, ofType } from '@ngrx/effects';
import { setVisibleDuByIdAction } from '@oam/core/lib/actions/status.actions';
import { map } from 'rxjs/operators';
import { changeFilter } from '@oam/cells-status/lib/core/actions/cells-status.actions';
import { Injectable } from '@angular/core';

@Injectable()
export class CellsStatusSetFilterEffects {
    constructor(private readonly actions$: Actions) {
    }

    setVisibleDuByIdAction$ = createEffect(() =>
        this.actions$.pipe(
            ofType(setVisibleDuByIdAction),
            map(({payload}) => changeFilter({payload: {servingDuId: payload}}))
        )
    );
}
