import { Subscription } from 'rxjs';
import { SchedulerLike } from 'rxjs/internal/types';
import { NgZone } from '@angular/core';

class EnterZoneScheduler implements SchedulerLike {
    constructor(private readonly zone: NgZone, private readonly scheduler: SchedulerLike) {
    }

    schedule(...args: any[]): Subscription {
        const [work, delay, state] = args;

        return this.zone.run(() => this.scheduler.schedule(work, delay, state));
    }

    now(): number {
        return this.scheduler.now();
    }
}

export function enterZone(zone: NgZone, scheduler: SchedulerLike): SchedulerLike {
    return new EnterZoneScheduler(zone, scheduler);
}
