import { createAction, props } from '@ngrx/store';
import { HttpErrorResponse } from '@angular/common/http';
import { Cu } from '@oam/cu-asm-api/models';

enum CuActionTypes {
    UnsubscribeCuWatch = '[Cu Status] UnsubscribeCuWatch',
    SubscribeCuWatch = '[Cu Status] SubscribeCuWatch',
    SubscribeCuError = '[Cu Status] SubscribeCuError',
    CuRequest = '[Cu Status] CuRequest',
    SetCu = '[Cu Status] SetCu',
}

export const subscribeCuWatch = createAction(CuActionTypes.SubscribeCuWatch);
export const unsubscribeCuWatch = createAction(CuActionTypes.UnsubscribeCuWatch);
export const subscribeCuError = createAction(CuActionTypes.SubscribeCuError, props<{ error: HttpErrorResponse }>());
export const cuRequest = createAction(CuActionTypes.CuRequest);
export const setCu = createAction(CuActionTypes.SetCu, props<{ payload: Cu }>());
