import { ModuleWithProviders, NgModule } from '@angular/core';
import { CellsInfoConfig } from './core/models/cells-info-config';

@NgModule({
    declarations: [],
    imports: [],
    exports: []
})
export class CellsInfoConfigModule {
    static forRoot(config: CellsInfoConfig): ModuleWithProviders<CellsInfoConfigModule> {
        return {
            ngModule: CellsInfoConfigModule,
            providers: [
                {
                    provide: CellsInfoConfig,
                    useValue: config,
                }
            ]
        };
    }
}
