export class CuLayout extends LayoutBase{
    @IsEnum(CuTypeEnum)
    @IsDefined()
    type: CuTypeEnum;

    @Match('w', dashboardUiSettingsDefault.layoutConfig)
    @IsDefined()
    w: number;

    @Match('h', dashboardUiSettingsDefault.layoutConfig)
    @IsDefined()
    h: number;

    constructor(layout: any) {
        super(layout);
    }
}

export class VmCuLayout extends LayoutBase{
    @IsEnum(CuTypeEnum)
    @IsDefined()
    type: CuTypeEnum;

    @Match('w', dashboardUiSettingsVmCU.layoutConfig)
    @IsDefined()
    w: number;

    @Match('h', dashboardUiSettingsVmCU.layoutConfig)
    @IsDefined()
    h: number;

    constructor(layout: any) {
        super(layout);
    }
}
