import { ChartOptions, ChartDataSets } from 'chart.js';

export const DEFAULT_LABEL_COLOR = '#98A2AE';
const FONT_COLOR = '#1B1B1B';
const NOKIA_FONT_FAMILY = 'Nokia Pure';

export const matchBackgroundColor = (context: any) => {
    const index = context.dataIndex;
    const backgroundColors = context.dataset && (context.dataset as ChartDataSets).backgroundColor;

    return (backgroundColors && backgroundColors[index])
        ? backgroundColors[index]
        : DEFAULT_LABEL_COLOR;
};

export const chartBarOptions: ChartOptions = {
    plugins: {
        datalabels: {
            align: 'end',
            anchor: 'end',
            offset: -3,
            color: matchBackgroundColor,
            font: {
                size: 10,
                weight: 'bold',
                family: NOKIA_FONT_FAMILY
            }
        }
    },
    layout: {
        padding: {
            top: -5,
            left: 5,
            right: 5
        }
    },
    tooltips: {
        enabled: false
    },
    title: {
        position: 'top',
        display: true,
        text: '',
        fontSize: 10,
        fullWidth: true,
        padding: 13,
        fontFamily: NOKIA_FONT_FAMILY,
        fontColor: FONT_COLOR
    },
    animation: {
        duration: 2000
    },
    maintainAspectRatio: false,
    responsive: true,
    legend: {
        display: false
    },
    scales: {
        xAxes: [{
            gridLines: {
                tickMarkLength: 1,
                display: false,
                color: 'rgba(0, 0, 0, 0)'
            },
            ticks: {
                beginAtZero: true,
                fontSize: 9,
                padding: 2,
                fontFamily: NOKIA_FONT_FAMILY,
                fontColor: FONT_COLOR
            }
        }],
        yAxes: [{
            gridLines: {
                display: false,
                color: 'rgba(0, 0, 0, 0)'
            },
            display: false,
            ticks: {
                padding: 0,
                beginAtZero: true,
                fontFamily: NOKIA_FONT_FAMILY
            }
        }]
    }
};
