import {
    PasswordValidationOptions, UsernameValidationOptions, ValidationRules,
    ValidationStatus
} from '../interface/user-information-change.interface';
import { ValidationRule } from '@oam/cu-ui/lib/validation-rules-details/models/ValidationRule';

export function getPasswordMessages(validationOptions: PasswordValidationOptions, passwordErrors: ValidationStatus['password'],
                                    isRoleOperationPage = false) {
    const passwordMessages: ValidationRule[] = [
        {
            value: getCharacterNumberMessage(validationOptions),
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.MIN_LENGTH]) && !(passwordErrors[ValidationRules.MAX_LENGTH])
        },
        {
            value: 'There must be at least 1 upper case letter (A-Z)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.UPPER_CASE])
        },
        {
            value: 'There must be at least 1 lower case letter (a-z)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.LOWER_CASE])
        },
        {
            value: `There must be at least ${validationOptions.minNumberOfDigits} ` +
            `${validationOptions.minNumberOfDigits === 1 ? 'number' : 'numbers'} (0-9)`,
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.DIGIT_NUMBER])
        },
        {
            value: 'There should not be two consecutive identical characters',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.CONSECUTIVE_CHAR])
        },
        {
            value: 'Only the following characters are allowed: A-Za-z0-9!#$%&\'()*+,-.;=?@[]_~/',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.DISALLOWED_ASCII])
        }
    ];

    if (isRoleOperationPage) {
        passwordMessages.push({
            value: 'The password can not same as selected role\'s username in straight or reversed form (check is case insensitive)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.PASSWORD_NOT_SAME_AS_USERNAME])
        }, {
            value: 'The password can not contain selected role\'s username in straight or reversed form (check is case insensitive)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.PASSWORD_NOT_CONTAIN_USERNAME])
        });
    } else {
        passwordMessages.push({
            value: 'The password can not same as username in straight or reversed form (check is case insensitive)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.PASSWORD_NOT_SAME_AS_USERNAME])
        }, {
            value: 'The password can not contain username in straight or reversed form (check is case insensitive)',
            isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.PASSWORD_NOT_CONTAIN_USERNAME])
        });
    }

    const nonAlphaNumericalCheck: ValidationRule = {
        value: `There must be at least ${validationOptions.minNonAlphaNumerical} ` +
            `special ${validationOptions.minNonAlphaNumerical === 1 ? 'character' : 'characters'} (e.g. @#$)`,
        isRulePassed: !(passwordErrors && passwordErrors[ValidationRules.NON_ALPHA_NUMBER])
    };

    return !validationOptions.minNonAlphaNumerical
        ? passwordMessages
        : addNonAlphaNumericalCheck(passwordMessages, nonAlphaNumericalCheck);
}

export function getUsernameMessages(validationOptions: UsernameValidationOptions, usernameErrors: ValidationStatus['username']) {
    return [
        {
            value: `The username should have ${validationOptions.minLength}-${validationOptions.maxLength} characters`,
            isRulePassed: !(usernameErrors && usernameErrors[ValidationRules.MIN_LENGTH]) &&
            !(usernameErrors && usernameErrors[ValidationRules.MAX_LENGTH])
        },
        {
            value: 'Only the following characters are allowed: A-Za-z0-9._-',
            isRulePassed: !(usernameErrors && usernameErrors[ValidationRules.DISALLOWED_ASCII])
        }
    ];
}

function addNonAlphaNumericalCheck(passwordMessages: ValidationRule[], nonAlphaNumericalCheck: ValidationRule): ValidationRule[] {
    return [...passwordMessages.slice(0, 4), nonAlphaNumericalCheck, ...passwordMessages.slice(4)];
}

function getCharacterNumberMessage(validationOptions: PasswordValidationOptions) {
    return validationOptions.minLength === validationOptions.maxLength
        ? `The password should have ${validationOptions.minLength} characters`
        : `The password should have ${validationOptions.minLength}-${validationOptions.maxLength} characters`;
}
