import { Action, createReducer, on } from '@ngrx/store';
import { GenericState } from '@oam/status-shared/lib/toolbar/core/model/generic-state';
import { ViewType } from '@oam/status-shared/lib/generic-view/core/model/view-toggle-input.interface';
import { CellsSortType } from '@oam/status-shared/lib/cells/core/models/cells-sort-type';
import { CellsFilterType } from '@oam/status-shared/lib/cells/core/models/cells-filter-type';
import { CellsFilterState } from '@oam/status-shared/lib/cells/core/models/cells-filter-state';
import { VisibleCuCellWidgetParameters } from '@oam/cu-ui';
import * as cellsStatusActions from '../actions/cells-status.actions';
import { NullableOperationalState, AdministrativeState } from '@oam/cu-asm-api/models';
import { isNil } from 'lodash';

export const featureKey = 'cellsStatus';
const EMPTY_SELECTION_ID = -1;

export interface CellsStatusState extends GenericState {
    displayConfiguration: VisibleCuCellWidgetParameters;
    sortType: CellsSortType;
    filterState: CellsFilterState;
}

export const initialState: CellsStatusState = {
    viewType: ViewType.TILE,
    freeSearchText: '',
    selectedId: EMPTY_SELECTION_ID,
    sortType: CellsSortType.BY_NR_CELL_ID,
    filterState: {
        [CellsFilterType.BY_NRCELL_ID]: '',
        [CellsFilterType.BY_CELL_NAME]: '',
        [CellsFilterType.BY_SERVING_DU_ID]: '',
        [CellsFilterType.BY_OPERATIONAL_STATE]: [],
        [CellsFilterType.BY_ADMINISTRATIVE_STATE]: [],
    },
    displayConfiguration: {
        nrCellIdentity: true,
        physicalLayerCellID: true,
        cellName: true,
        type: true,
        nrarfcn: true,
        cellGroupId: true,
        duName: true,
        actions: true,
        technology: true,
    },
};

const cellsStatusReducer = createReducer(
    initialState,
    on(cellsStatusActions.setView, (state, { payload: viewType }) => ({ ...state, viewType })),
    on(cellsStatusActions.selectCell, (state, { payload: selectedId }) => ({
        ...state,
        selectedId: isNil(selectedId) ? EMPTY_SELECTION_ID : selectedId })),
    on(cellsStatusActions.setDisplayConfiguration, (state, { payload: displayConfiguration }) => ({ ...state, displayConfiguration })),
    on(cellsStatusActions.changeSortType, (state, { payload: sortType }) => ({ ...state, sortType })),
    on(cellsStatusActions.setFreeSearchText, (state, { payload: freeSearchText }) => ({ ...state, freeSearchText })),
    on(cellsStatusActions.changeFilter, (state, { payload: filter }) => ({
            ...state,
            filterState: {
                ...state.filterState,
                ...filter
            }
    })),
    on(cellsStatusActions.restoreView, state => ({ ...initialState, viewType: state.viewType, selectedId: state.selectedId })),
);

export function reducer(state: CellsStatusState | undefined, action: Action) {
    return cellsStatusReducer(state, action);
}
