import { Component, OnInit } from '@angular/core';
import { ButtonModel } from '../../../button/model/button-model.definition';
import { DynamicMessageDialogConfig } from '../../core/models';
import { DynamicDialogConfig, DynamicDialogRef } from 'primeng/dynamicdialog';

@Component({
    selector: 'ui-webem-dynamic-message-dialog',
    templateUrl: './dynamic-message-dialog.component.html'
})
export class DynamicMessageDialogComponent implements OnInit {
    readonly okButtonModel: ButtonModel = {
        label: 'OK'
    };

    header?: string;
    message = '';

    constructor(private readonly ref: DynamicDialogRef, private readonly config: DynamicDialogConfig) {
    }

    ngOnInit() {
        const props = this.config as DynamicMessageDialogConfig;
        this.header = props.header;
        this.message = props.message;
    }

    close() {
        this.ref.close();
    }

}
