import {
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    ViewChild,
    ChangeDetectorRef
} from '@angular/core';
import { OverlayPanel } from 'primeng/overlaypanel';

@Component({
    selector: 'ui-webem-overlay-panel',
    templateUrl: './overlay-panel.component.html'
})
export class OverlayPanelComponent {
    @ViewChild('overlayPanel', { static: true }) overlayPanel: OverlayPanel;
    @ViewChild('pDialog') pDialog: ElementRef;
    @Input() dismissable = true;
    @Input() showCloseIcon = false;
    @Input() style: string | null = null;
    @Input() styleClass: string | null	= null;
    @Input() appendTo: any = 'body';
    @Input() baseZIndex	= 0;
    @Input() autoZIndex = true;
    @Input() showTransitionOptions = '225ms ease-out';
    @Input() hideTransitionOptions	= '195ms ease-in';
    @Input() header: string;
    @Input() componentsToHideBeforeOverlayPanel: object[] = [];

    constructor(
        public changeDetectorRef: ChangeDetectorRef) {
    }

    @Output() hideEvent = new EventEmitter();
    show($event: any, target?: any) {
        this.overlayPanel.show($event, target);
    }

    toggle($event: any) {
        this.overlayPanel.toggle($event);
    }

    hide() {
        this.overlayPanel.hide();
    }

    onHide(event: any) {
        this.hideEvent.emit(event);
        this.componentsToHideBeforeOverlayPanel.forEach((component: any) => {
            if (component.overlayVisible) {
                component.overlayVisible = false;
            }
        });
        this.changeDetectorRef.detectChanges();
    }

}
