import { createFeatureSelector, createSelector } from '@ngrx/store';
import { OverlayState } from '../models';

export const selectOverlay = createFeatureSelector<OverlayState>('overlay');

export const selectIsOutdatedDeltaPlanDialogOpened = createSelector(selectOverlay,
    (state: OverlayState) => state.isOutdatedDeltaPlanDialogOpen);
