import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserMenuComponent } from './components/user-menu/user-menu.component';
import { NgbDropdownModule } from '@ng-bootstrap/ng-bootstrap';
import { OverlayMenuBarModule } from '../overlay-menu-bar/overlay-menu-bar.module';
import { NotificationComponent } from './components/notification/notification.component';
import { NotificationPanelComponent } from './components/notification-panel/notification-panel.component';
import { UserMenuOverlayComponent } from './components/user-menu-overlay/user-menu-overlay.component';
import { ClickOutsideModule } from 'ng-click-outside';
import { WebemButtonModule } from '../button/button.module';
import { RenewSessionDialogComponent } from './components/renew-session-dialog/renew-session-dialog.component';
import { DowngradedPrivilegesDialogComponent } from './components/downgraded-privileges-dialog/downgraded-privileges-dialog.component';
import { ReactiveFormsModule } from '@angular/forms';
import { TrackCapsLockModule } from '@oam/cu-ui/lib/track-caps-lock/track-caps-lock.module';
import { ShowPasswordButtonModule } from '@oam/cu-ui/lib/show-password-button/show-password-button.module';
import { EffectsModule } from '@ngrx/effects';
import { StoreModule } from '@ngrx/store';
import * as fromSessionTime from './components/session-time/core/reducers/session-token-time.reducer';
import { SessionTimeComponent } from './components/session-time/session-time.component';
import { SessionTokenTimeEffects } from '@oam/cu-ui/lib/user-menu/components/session-time/core/effects/session-token-time.effects';
import { SharedModule } from 'primeng/api';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { OverlayPanelModule } from 'primeng/overlaypanel';
import { WebemCommonModule } from '@oam/shared/lib/modules/webem-common';

@NgModule({
    declarations: [
        UserMenuComponent,
        NotificationComponent,
        NotificationPanelComponent,
        UserMenuOverlayComponent,
        RenewSessionDialogComponent,
        DowngradedPrivilegesDialogComponent,
        SessionTimeComponent
    ],
    exports: [UserMenuComponent],
    imports: [
        CommonModule,
        NgbDropdownModule,
        OverlayMenuBarModule,
        WebemButtonModule,
        ButtonModule,
        OverlayPanelModule,
        ClickOutsideModule,
        SharedModule,
        TrackCapsLockModule,
        ShowPasswordButtonModule,
        ReactiveFormsModule,
        InputTextModule,
        EffectsModule.forFeature([SessionTokenTimeEffects]),
        StoreModule.forFeature(fromSessionTime.sessionTokenTimeFeatureKey, fromSessionTime.reducer),
        WebemCommonModule
  ]
})
export class UserMenuModule {
}
