import { camelCase } from 'lodash';
import { EMPTY_PLACEHOLDER } from '@oam/shared/lib/modules/webem-common/core/pipes/nullable-value.pipe';

export function convertUrlIntoKey(url: string | undefined): string {
    return camelCase(url);
}

export function filterTreeObject(filterPhrase: string, data: any) {
    const value = !data.value && data.value!== 0 ? EMPTY_PLACEHOLDER : data.value;
    const fullName = data.fullName || '';
    const name = data.name || '';

    return filterString(filterPhrase, value) || filterString(filterPhrase, name) || filterString(filterPhrase, fullName);
}

export function filterString(filterPhrase: string, treeObject: string) {
    treeObject = treeObject.toString().toLowerCase();
    filterPhrase = filterPhrase.toString().toLowerCase();

    const treeObjectLng = treeObject.length;
    const filterPhraseLng = filterPhrase.length;

    if (filterPhraseLng > treeObjectLng) {
        return false;
    }
    if (filterPhraseLng === treeObjectLng) {
        return filterPhrase === treeObject;
    }

    return treeObject.includes(filterPhrase);
}
