import { Injectable } from '@angular/core';
import { TestModelsService } from '@oam/cu-cp-config-api/services';
import { PollingService } from '@oam/core/lib/services';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { catchError, filter, map, mergeMap, takeUntil } from 'rxjs/operators';
import { asyncScheduler, of } from 'rxjs';
import {
    runningTestModelsRequest, runningTestModelsRequestError,
    startRunningTestModelsPolling, stopRunningTestModelsPolling, updateRunningTestModels
} from '@oam/diagnostic-shared/lib/core/actions/running-test-models.actions';
import { DiagnosticTestmodelService } from '@oam/diagnostic-shared/lib/services/diagnostic-testmodel.service';

@Injectable()
export class RunningTestModelsEffects {
    constructor(
        private readonly pollingService: PollingService,
        private readonly actions$: Actions,
        private readonly testModelsService: TestModelsService,
        private readonly diagnosticTestmodelService: DiagnosticTestmodelService
    ) {
    }

    startRunningTestModelsPolling$ = createEffect(() => ({ scheduler = asyncScheduler } = {}) =>
        this.actions$.pipe(
            ofType(startRunningTestModelsPolling),
            mergeMap(() => this.pollingService.getTimer(scheduler).pipe(
                takeUntil(this.stopRunningTestModelsPolling$),
                map(() => runningTestModelsRequest())
            ))
        )
    );

    stopRunningTestModelsPolling$ = createEffect(() =>
            this.actions$.pipe(
                ofType(stopRunningTestModelsPolling)
            ),
        { dispatch: false }
    );

    runningTestModelsRequest$ = createEffect(() =>
        this.actions$.pipe(
            ofType(runningTestModelsRequest),
            filter(() => this.diagnosticTestmodelService.isTDSState()),
            mergeMap(() => this.testModelsService.testModelsRunningTestModelsGet().pipe(
                mergeMap(runningTestModels => ([
                    updateRunningTestModels({ payload: runningTestModels })
                ])),
                catchError(err => of(runningTestModelsRequestError(err)))
            ))
        )
    );
}
