import { Pipe, PipeTransform } from '@angular/core';
import { GroupOfViews } from '../core/models/group-of-views.interface';


@Pipe({
  name: 'disabledGroupHeaderIfRestricted'
})
export class DisabledGroupHeaderIfRestrictedPipe implements PipeTransform {
    transform(group: GroupOfViews, restrictions = false): string {
         if (group.label === 'Configuration' && restrictions) {
             return 'disable-group-header';
         } else {
             return '';
         }
    }

}

