import { mapKeys } from 'lodash';
import { MultiValue } from '@oam/cu-pmv2-api/models/multi-value';
import { Value } from '@oam/cu-pmv2-api/models';

export function getCounterDistNameFromUniqueId(uniqueCounterId: string): string {
    const [, dimensionDistName] = uniqueCounterId.split(':');

    return dimensionDistName;
}

export function getCounterIdFromUniqueId(uniqueCounterId: string): string {
    const [counterId, ] = uniqueCounterId.split(':');

    return counterId;
}

export function getMeasurementIdFromUniqueId(uniqueCounterId: string): string {
    const [counterId, ] = uniqueCounterId.split(':');

    return counterId.split('C')[0].slice(1);
}

export function removeCounterKeyPrefix(values: MultiValue): MultiValue {
    const prefix = 'PLMN-PLMN/';

    return mapKeys(values, (value: Value[], key: string) => key.replace(prefix, ''));
}

export function filterCounterKey(previousValues: Value[], newValues: Value[]): Value[] {

    return Object.values([...previousValues, ...newValues].reduce((result, {
        startTime,
        ...rest
    }) => {
        result[startTime] = {
            ...(result[startTime] || {}),
            startTime,
            ...rest
        };

        return result;
    }, {}));
}
