import { Injectable, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { get } from 'lodash';
import { chrome, firefox } from '@oam/layouts/lib/models/browsers-names';

@Injectable({
    providedIn: 'root'
})
export class FullScreenService {
    constructor(@Inject(DOCUMENT) private readonly document: Document) {}

    requestFullScreen(): Promise<void> {
        return this.document.documentElement.requestFullscreen();
    }

    isInFullScreenModeByAPI() {
        return !!this.document.fullscreenElement;
    }

    isFirefoxInFullScreen(): boolean {
        return get(window, 'fullScreen');
    }

    isFirefoxInFullScreenModeByOS(): boolean {
        return this.isFirefoxInFullScreen() && !this.isInFullScreenModeByAPI();
    }

    exitFullScreen(): Promise<void> {
        return this.document.exitFullscreen();
    }

    toggleFullScreen(): void {
        if (this.isInFullScreenModeByAPI()) {
            this.exitFullScreen();
        } else {
            this.requestFullScreen();
        }
    }

    getBrowser(): string {
        let browser = '';
        if (this.checkUserAgent(chrome)) {
            browser = chrome;
        } else if (this.checkUserAgent(firefox)) {
            browser = firefox;
        }

        return browser;
    }

    checkUserAgent(browser: string): boolean {
        return navigator.userAgent.indexOf(browser) !== -1;
    }
}
