import { createAction, props } from '@ngrx/store';
import { ViewType } from '@oam/status-shared/lib/generic-view/core/model/view-toggle-input.interface';
import { CellsSortType } from '@oam/status-shared/lib/cells/core/models/cells-sort-type';
import { FilterState } from '@oam/status-shared/lib/generic-view/core/model/value-filter-input.interface';
import { VisibleCuCellWidgetParameters } from '@oam/cu-ui';

enum ActionTypes {
    SetView = '[User] [Cells Status] Set view',
    SelectCell = '[User] [Cells Status] Select cell',
    setDisplayConfiguration = '[User] [Cells Status] Set visible cell parameters',
    ChangeSortType = '[User] [Cells Status] Change sort type',
    ChangeFilter = '[User] [Cells Status] Change filter',
    RestoreView = '[User] [Cells Status] Restore view',
    ExportToCsv = '[User] [Cells Status] Export to csv',
    SetFreeSearchText = '[User] [Cells Status] Set free search text',
}

type Payload<T> = { payload: T };

export const setView = createAction(ActionTypes.SetView, props<Payload<ViewType>>());
export const selectCell = createAction(ActionTypes.SelectCell, props<Payload<number | null>>());
export const setDisplayConfiguration = createAction(ActionTypes.setDisplayConfiguration, props<Payload<VisibleCuCellWidgetParameters>>());
export const changeSortType = createAction(ActionTypes.ChangeSortType, props<Payload<CellsSortType>>());
export const changeFilter = createAction(ActionTypes.ChangeFilter, props<Payload<FilterState>>());
export const restoreView = createAction(ActionTypes.RestoreView);
export const exportToCsv = createAction(ActionTypes.ExportToCsv);
export const setFreeSearchText = createAction(ActionTypes.SetFreeSearchText, props<Payload<string>>());
