import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges
} from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { AbstractControl, FormControl, FormGroup, Validators } from '@angular/forms';
import { debounceTime, distinctUntilChanged, map, share, tap } from 'rxjs/operators';
import {
    FormParameters,
    InformationChangeStatusTypes,
    PasswordValidationOptions,
    UserInformationChange,
    UserInformationChangeOptions,
    UserInformationChangeStatus,
    UsernameValidationOptions,
    ValidationRules,
    ValidationStatus
} from './interface/user-information-change.interface';
import { UserInformationValidator } from './validators/user-information.validator';
import { isNull, filter } from 'lodash';
import { CURRENT_PASSWORD_TOOLTIP_CONTENT, FIELDS_DESCRIPTION } from './constants/user-information-change.constants';
import { SelectItem } from 'primeng/api';
import { ButtonLength } from '../button/model/button-length.enum';
import { ButtonModel } from '../button/model/button-model.definition';
import { ButtonOptions } from '../button/model/button-options.definition';
import { ButtonTheme } from '../button/model/button-theme.enum';
import { ServiceUserTypePropertie } from '@oam/cu-asm-api';
import { VnfcSharedService } from '../../../../vnfc-shared/src/lib/vnfc-store/services/vnfc-shared.service';
import {
    ACTIVE_DEACTIVE, DEACTIVE_ACTIVE, ACTIVE, ROLE_USERNAME_MAPPING
} from '@oam/shared/lib/constants/role-access-constants';
import {
    judgeForActionStatusChangeOrNot,
    judgeForCurrentPassword,
    judgeForUsernameAndCurrentPassword
} from './utils/validation.utils';

@Component({
    selector: 'ui-webem-user-information-change',
    templateUrl: './user-information-change.component.html',
    styleUrls: ['./user-information-change.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class UserInformationChangeComponent implements OnInit, OnChanges, OnDestroy {
    private static readonly DISABLED = 'disabled';
    private static readonly SUBMIT = 'Save';

    @Input() passwordValidationOptions$: Observable<PasswordValidationOptions>;
    @Input() usernameValidationOptions: UsernameValidationOptions;
    @Input() username: string;
    @Input() isDisabled: boolean;
    @Input() usersList: Array<ServiceUserTypePropertie>;

    @Input() isRoleOperationPage = false;
    @Input() isRoleDropdownShow = false;
    @Input() isActivationStatusShow = false;
    @Input() isUserNameShow = true;
    @Input() isCurrentPasswordShow = true;
    @Input() isActivationStatusChange: boolean;
    @Input() availableRoles: string[];
    @Input() availableActivationStatus: string[];
    @Input() selectRoleName: string;
    @Input() currentRoleActivationStatus: string;
    @Input() selectActivationStatus: string;

    @Input() changeStatus: UserInformationChangeStatus;
    @Input() changeOptions: UserInformationChangeOptions[];
    @Output() validationChange: EventEmitter<ValidationStatus> = new EventEmitter();
    @Output() informationChange: EventEmitter<UserInformationChange> = new EventEmitter();
    @Output() clearInformationResponse: EventEmitter<any> = new EventEmitter();
    @Output() changeOptionSelectionChange: EventEmitter<UserInformationChangeOptions> = new EventEmitter();

    @Output() roleNameChange: EventEmitter<string> = new EventEmitter();
    @Output() activationStatusChange: EventEmitter<string> = new EventEmitter();

    passwordValidationOptions: PasswordValidationOptions;
    passwordValidationOptionsSubscription: Subscription;
    updateAccountForm: FormGroup;
    isPasswordMatchError$: Observable<boolean>;
    canChangeInformation$: Observable<boolean>;
    inProgress: boolean;
    disabledFields: { [field in FormParameters]: string | null};
    selectToChangeOptions: SelectItem[];
    isOptionSelected = false;
    showCurrentPassword: boolean;
    showNewPassword: boolean;
    showConfirmPassword: boolean;
    submitButtonLabel = UserInformationChangeComponent.SUBMIT;
    currentPasswordCapsLockOn: boolean;
    newPasswordCapsLockOn: boolean;
    confirmPasswordCapsLockOn: boolean;
    tooltipContent = CURRENT_PASSWORD_TOOLTIP_CONTENT;
    isGeoresiliencyUser = false;

    isConfigVmCu: boolean;

    readonly  buttonOptions: ButtonOptions = {
        forceWidth: '76px'
    };
    readonly buttonModel: ButtonModel = {
        theme: ButtonTheme.Accent,
        label: this.submitButtonLabel,
        length: ButtonLength.Long
    };

    readonly buttonModelInProgress: ButtonModel = {
        icon: 'mdi mdi-loading mdi-spin',
        theme: ButtonTheme.Accent,
        length: ButtonLength.Long
    };
    private selectedOption: UserInformationChangeOptions;

    constructor(private readonly vnfcSharedService: VnfcSharedService) {}

    ngOnInit(): void {
        this.isConfigVmCu = this.vnfcSharedService.isVmcu();

        this.selectToChangeOptions = this.changeOptions.map((option: UserInformationChangeOptions) => ({
            label: option,
            value: option
        }));
        this.updateAccountForm = new FormGroup({
            [FormParameters.USERNAME]: this.isServiceAccountOption(this.changeOptions) ?
                new FormControl('', [Validators.required]) :
                new FormControl('', [
                    Validators.required,
                    UserInformationValidator.minLength(this.usernameValidationOptions.minLength),
                    UserInformationValidator.maxLength(this.usernameValidationOptions.maxLength),
                    UserInformationValidator.disallowedUserNameAsciiCharacters(),
                ]),
            [FormParameters.PASSWORD]: new FormControl('', [ Validators.required ]),
            [FormParameters.NEW_PASSWORD]: new FormControl('', []),
            [FormParameters.ACTIVATION_STATUS]: new FormControl('', []),
            [FormParameters.ROLE_NAME]: new FormControl('', []),
            [FormParameters.CONFIRM_PASSWORD]: new FormControl('', [ Validators.required ])
        }, [
            UserInformationValidator.passwordsMatch(),
            control => UserInformationValidator.passwordNotSameAsUsername(this.username)(control),
            control => UserInformationValidator.passwordNotContainUsername(this.username)(control)
        ]);
        this.passwordValidationOptionsSubscription = this.passwordValidationOptions$
            .subscribe((passwordValidationOptions: PasswordValidationOptions) => {
                this.setValidatorsForPassword(passwordValidationOptions);
            });
        const valueChange$ = this.updateAccountForm.valueChanges.pipe(
            debounceTime(100),
            distinctUntilChanged(),
            tap(() => this.emitValidationStatus()),
            tap((formInputs: any) => {
                if ( formInputs.username ) {
                    this.setGeoresiliencyUser(formInputs.username);
                }
                if ( formInputs.username || formInputs.password  || formInputs.newPassword || formInputs.confirmPassword ) {
                    this.clearInformationResponse.emit();
                }
            }),
            share()
        );
        this.isPasswordMatchError$ = valueChange$.pipe(map(() => this.doesPasswordMatch()));
        this.canChangeInformation$ = valueChange$.pipe(map(() => this.isFormValid()));
        this.emitValidationStatus();
        this.initFormFields(this.changeOptions[0]);

        if (!this.isConfigVmCu && this.isRoleOperationPage) {
            if (this.isUserNameShow) {
                this.changeOptionSelectionChange.emit(UserInformationChangeOptions.PASSWORD_AND_USERNAME);
            } else {
                this.changeOptionSelectionChange.emit(UserInformationChangeOptions.PASSWORD_ONLY);
            }
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.changeStatus && this.changeStatus) {
            this.inProgress = this.changeStatus.status === InformationChangeStatusTypes.IN_PROGRESS;

            // we want all inputs to be disabled after password changed successful when password expires
            if (this.changeStatus.status === InformationChangeStatusTypes.SUCCESSFUL
                && this.changeOptions[0] === UserInformationChangeOptions.PASSWORD_EXPIRATION_CHANGE) {
                this.changeOptions = [];
                this.updateDisabledFields(this.changeOptions[0]);
            }

            if (this.changeStatus.status === InformationChangeStatusTypes.SUCCESSFUL) {
                this.updateAccountForm?.controls?.roleName.setValue(this.selectRoleName);
            }
        }

        if (changes.changeStatus && changes.changeStatus.currentValue) {
            this.submitButtonLabel = changes.changeStatus.currentValue.isOperationInProgress ? '' :
                UserInformationChangeComponent.SUBMIT;
        }

        if (this.isGeoresiliencyUser) {
            const password = this.updateAccountForm?.get(FormParameters.PASSWORD)?.value;
            if (password) {
                this.updateAccountForm.controls[FormParameters.PASSWORD].setValue('');
            }
        }

        if ((changes.currentRoleActivationStatus && changes.currentRoleActivationStatus.currentValue) ||
            (changes.isActivationStatusChange && !changes.isActivationStatusChange.currentValue)
            || changes.selectRoleName) {
            this.updateAccountForm?.controls?.activationStatus.setValue(this.currentRoleActivationStatus);
        }

        if (changes.isActivationStatusChange && changes.isActivationStatusChange.currentValue) {
            this.updateAccountForm?.controls?.activationStatus.setValue(this.selectActivationStatus);
        }
    }

    ngOnDestroy(): void {
        this.passwordValidationOptionsSubscription?.unsubscribe();
    }

    onSubmit(): void {
        const username = (this.updateAccountForm.get(FormParameters.USERNAME) as AbstractControl).value;
        const password = (this.updateAccountForm.get(FormParameters.PASSWORD) as AbstractControl).value;
        const newPassword = (this.updateAccountForm.get(FormParameters.NEW_PASSWORD) as AbstractControl).value;

        this.informationChange.emit({
            username: this.username,
            password,
            newPassword,
            newUsername: username
        });
        this.updateAccountForm.reset({
            [FormParameters.USERNAME]: '',
            [FormParameters.PASSWORD]: '',
            [FormParameters.NEW_PASSWORD]: '',
            [FormParameters.CONFIRM_PASSWORD]: ''
        });

        // reset user name, so that isGeoresiliencyUser is setting to false
        this.setGeoresiliencyUser('');
    }

    setAllCapsLockStates(capsLockState: boolean): void {
        this.currentPasswordCapsLockOn = capsLockState;
        this.newPasswordCapsLockOn = capsLockState;
        this.confirmPasswordCapsLockOn = capsLockState;
    }

    setCurrentPasswordCapsLockState(event: MouseEvent): void {
        this.currentPasswordCapsLockOn = event.getModifierState('CapsLock');
    }

    setNewPasswordCapsLockState(event: MouseEvent): void {
        this.newPasswordCapsLockOn = event.getModifierState('CapsLock');
    }

    setConfirmPasswordCapsLockState(event: MouseEvent): void {
        this.confirmPasswordCapsLockOn = event.getModifierState('CapsLock');
    }

    updateRoleName(roleName: string): void {
        this.updateAccountForm.reset({
            [FormParameters.USERNAME]: '',
            [FormParameters.PASSWORD]: '',
            [FormParameters.NEW_PASSWORD]: '',
            [FormParameters.CONFIRM_PASSWORD]: ''
        });
        this.username = ROLE_USERNAME_MAPPING[roleName];
        const newRoles: string[] = filter(this.availableRoles, (itemRoleName) => {
            return itemRoleName !== roleName;
        });
        this.availableRoles = [roleName, ...newRoles];
        this.roleNameChange.emit(roleName);
    }

    updateActivateStatus(activeStatus: string): void {
        this.availableActivationStatus = activeStatus === ACTIVE ?
            ACTIVE_DEACTIVE : DEACTIVE_ACTIVE;
        this.activationStatusChange.emit(activeStatus);
    }

    updateDisabledFields(selectedOption: UserInformationChangeOptions): void {
        this.disabledFields = {
            [FormParameters.USERNAME]: FIELDS_DESCRIPTION[FormParameters.USERNAME].includes(selectedOption) ?
                null :
                UserInformationChangeComponent.DISABLED,
            [FormParameters.PASSWORD]: FIELDS_DESCRIPTION[FormParameters.PASSWORD].includes(selectedOption) ?
                null :
                UserInformationChangeComponent.DISABLED,
            [FormParameters.ACTIVATION_STATUS]: null,
            [FormParameters.ROLE_NAME]: null,
            [FormParameters.NEW_PASSWORD]: FIELDS_DESCRIPTION[FormParameters.NEW_PASSWORD].includes(selectedOption) ?
                null :
                UserInformationChangeComponent.DISABLED,
            [FormParameters.CONFIRM_PASSWORD]: FIELDS_DESCRIPTION[FormParameters.CONFIRM_PASSWORD].includes(selectedOption) ?
                null :
                UserInformationChangeComponent.DISABLED
        };

        if (!this.isConfigVmCu && this.isRoleOperationPage) {
            this.disabledFields = {
                [FormParameters.USERNAME]: null,
                [FormParameters.PASSWORD]: null,
                [FormParameters.ACTIVATION_STATUS]: null,
                [FormParameters.ROLE_NAME]: null,
                [FormParameters.NEW_PASSWORD]: null,
                [FormParameters.CONFIRM_PASSWORD]: null
            };
        }

        this.selectedOption = selectedOption;
        this.updateAccountForm.reset({
            [FormParameters.USERNAME]: '',
            [FormParameters.PASSWORD]: '',
            [FormParameters.NEW_PASSWORD]: '',
            [FormParameters.CONFIRM_PASSWORD]: ''
        });
        this.changeOptionSelectionChange.emit(selectedOption);
    }

    private initFormFields(selectedOption: UserInformationChangeOptions): void {
        this.isOptionSelected = true;
        this.clearInformationResponse.emit();
        this.updateDisabledFields(selectedOption);
    }

    private setValidatorsForPassword(passwordValidationOptions: PasswordValidationOptions) {
        this.passwordValidationOptions = passwordValidationOptions;
        (this.updateAccountForm.get(FormParameters.NEW_PASSWORD) as AbstractControl).setValidators([
            Validators.required,
            UserInformationValidator.minLength(this.passwordValidationOptions.minLength),
            UserInformationValidator.maxLength(this.passwordValidationOptions.maxLength),
            UserInformationValidator.minimumDigitNumbers(this.passwordValidationOptions.minNumberOfDigits),
            UserInformationValidator.minNonAlphaNumerical(this.passwordValidationOptions.minNonAlphaNumerical),
            UserInformationValidator.noConsecutiveCharacters(),
            UserInformationValidator.upperCase(),
            UserInformationValidator.lowerCase(),
            UserInformationValidator.disallowedPasswordAsciiCharacters()
        ]);
        (this.updateAccountForm.get(FormParameters.NEW_PASSWORD) as AbstractControl).updateValueAndValidity();
    }

    private emitValidationStatus() {
        const username = this.updateAccountForm.get(FormParameters.USERNAME) as AbstractControl;
        const password = this.updateAccountForm.get(FormParameters.NEW_PASSWORD) as AbstractControl;
        const formErrors = this.updateAccountForm.errors;
        const validationStatus: ValidationStatus = {
            username: username.errors || {},
            usernameOptions: this.usernameValidationOptions,
            password: password.errors || formErrors ?
                {
                    ...password.errors,
                    ...formErrors
                } :
                {},
            passwordOptions: this.passwordValidationOptions
        };
        this.validationChange.emit(validationStatus);
    }

    judgeStatusForSave(username: AbstractControl,
                       newPassword: AbstractControl,
                       currentPassword: AbstractControl,
                       isUsernameValid: boolean,
                       isPasswordValid: boolean,
                       confirmPassword: AbstractControl) {
        if (this.isUserNameShow && this.isCurrentPasswordShow) {
            return judgeForUsernameAndCurrentPassword(username, newPassword, confirmPassword,
                currentPassword, isUsernameValid, isPasswordValid);
        } else if (!this.isUserNameShow && this.isCurrentPasswordShow) {
            return judgeForCurrentPassword(this.selectRoleName, newPassword, confirmPassword, isPasswordValid, currentPassword,
                this.isActivationStatusShow, this.isActivationStatusChange);
        } else if (!this.isUserNameShow && !this.isCurrentPasswordShow) {
            return judgeForActionStatusChangeOrNot(newPassword, confirmPassword, isPasswordValid,
                this.isActivationStatusChange);
        } else {
            return true;
        }
    }

    private isFormValid(): boolean {
        const username = this.updateAccountForm.get(FormParameters.USERNAME) as AbstractControl;
        const currentPassword = this.updateAccountForm.get(FormParameters.PASSWORD) as AbstractControl;
        const newPassword = this.updateAccountForm.get(FormParameters.NEW_PASSWORD) as AbstractControl;
        const confirmPassword = this.updateAccountForm.get(FormParameters.CONFIRM_PASSWORD) as AbstractControl;
        const newPasswordErrors = newPassword.errors;
        const usernameErrors = username.errors;
        const formErrors = this.updateAccountForm.errors;
        const isUsernameValid = username.value && this.isGeoresiliencyUser ? true : currentPassword.value && isNull(usernameErrors);
        const isPasswordValid = newPassword.value && confirmPassword.value && isNull(newPasswordErrors) && isNull(formErrors);

        if (!this.isConfigVmCu && this.isRoleOperationPage) {
            return this.judgeStatusForSave(username, newPassword, currentPassword,
                isUsernameValid, isPasswordValid, confirmPassword);
        } else {
            return this.isOptionSelected && (
                this.isPasswordAndUserNameFormValid(isUsernameValid, isPasswordValid) ||
                this.isPasswordOnlyFormValid(isPasswordValid, currentPassword.value) ||
                this.isUserNameOnlyFormValid(isUsernameValid) ||
                this.isPasswordServiceFormValid(isUsernameValid, isPasswordValid) ||
                this.isPasswordExpirationFormValid(isPasswordValid, currentPassword.valid)
            );
        }
    }

    private isPasswordAndUserNameFormValid(isUsernameValid: boolean, isPasswordValid: boolean) {
        return this.selectedOption === UserInformationChangeOptions.PASSWORD_AND_USERNAME && isUsernameValid && isPasswordValid;
    }

    private isPasswordOnlyFormValid(isPasswordValid: boolean, currentPassword: boolean) {
        return this.selectedOption === UserInformationChangeOptions.PASSWORD_ONLY && isPasswordValid && currentPassword;
    }

    private isUserNameOnlyFormValid(isUsernameValid: boolean) {
        return this.selectedOption === UserInformationChangeOptions.USERNAME_ONLY && isUsernameValid;
    }

    private isPasswordExpirationFormValid(isPasswordValid: boolean, isCurrentPasswordValid: boolean) {
        return this.selectedOption === UserInformationChangeOptions.PASSWORD_EXPIRATION_CHANGE && isPasswordValid && isCurrentPasswordValid;
    }

    private isPasswordServiceFormValid(isUsernameValid: boolean, isPasswordValid: boolean) {
        return this.selectedOption === UserInformationChangeOptions.PASSWORD_SERVICE && isUsernameValid && isPasswordValid;
    }

    private doesPasswordMatch(): boolean {
        const formErrors = this.updateAccountForm.errors;

        return formErrors && formErrors[ValidationRules.PASSWORDS_MATCH];
    }

    setCurrentPasswordVisible(isVisible: boolean) {
        this.showCurrentPassword = isVisible;
    }

    setNewPasswordVisible(isVisible: boolean) {
        this.showNewPassword = isVisible;
    }

    setConfirmPasswordVisible(isVisible: boolean) {
        this.showConfirmPassword = isVisible;
    }

    private isServiceAccountOption(changeOptions: any): boolean {
         return changeOptions.length === 1 && changeOptions[0] === UserInformationChangeOptions.PASSWORD_SERVICE;
    }

    setGeoresiliencyUser(username: string): void {
        this.isGeoresiliencyUser = !!this.usersList?.find(user => user?.username === username && user?.UserType === 'Georesiliency');
    }
}
