import { createFeatureSelector, createSelector } from '@ngrx/store';
import {
    commissioningFlagFeatureKey,
    FlagState
} from '../reducers/commissioning-flag.reducer';

export const selectCommissioningFlagStatusState = createFeatureSelector<FlagState>(commissioningFlagFeatureKey);

export const selectCommissioningFlagStatus = createSelector(
    selectCommissioningFlagStatusState, (state: FlagState) => state.isCommissioningInProgress
);
