import { Pipe, PipeTransform } from '@angular/core';
import { Du } from '@oam/cu-asm-api/models/du';
import { F1LinkStatus } from '@oam/cu-asm-api/models/f-1-link-status';

@Pipe({
  name: 'getDuClass',
  pure: true
})
export class GetDuClassPipe implements PipeTransform {

  transform(du: Du, isSelected: boolean): string {

      let duClass = (du.f1LinkStatus === F1LinkStatus.Available ? 'green' : 'gray');
      if (isSelected) duClass += ' selected';

      return duClass;
  }

}
