import { ChannelMapping } from '@oam/cu-asm-api/models';

export function mapRmodsIds (channels: ChannelMapping[]): string {
    const rmodsIds = channels.map((channel) => channel.rmodRId);
    const uniqueRmods = [...new Set(rmodsIds)];

    return uniqueRmods.reduce((acc, cur, index) => {
        const row = `[RMOD_R-${cur}]`;

        return index !== uniqueRmods.length - 1 ?
            `${acc}${row}\n` :
            `${acc}${row}`;
    }, '') || 'none';
}
