import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    ViewChild
} from '@angular/core';
import { ButtonModel } from '../../model/button-model.definition';
import { ButtonOptions } from '../../model/button-options.definition';
import { ButtonEvent } from '../../model/button-event.definition';
import { ButtonTheme } from '../../model/button-theme.enum';
import { ButtonLength } from '../../model/button-length.enum';
import { ButtonThickness } from '../../model/button-thickness.enum';

@Component({
    selector: 'ui-webem-button',
    templateUrl: './button.component.html',
    styleUrls: ['./button.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ButtonComponent implements OnInit, OnChanges {
    @Input() model: ButtonModel;
    @Input() disabled = false;
    @Input() showTitle = true;
    @Input() title = '';
    @Input() id = '';
    @Input() options?: ButtonOptions;
    @Input() type: string;
    @Output() action = new EventEmitter<ButtonEvent>();
    @Output() mousedown = new EventEmitter<MouseEvent>();
    @Output() mouseup = new EventEmitter<MouseEvent>();
    @Output() mouseout = new EventEmitter<MouseEvent>();

    @ViewChild('uploader') uploader: ElementRef<HTMLInputElement>;

    filePath: string;
    styles = {};
    classes = '';

    ngOnInit(): void {
        this.update();
    }

    ngOnChanges(): void {
        this.update();
    }

    update() {
        this.setStyles();
        this.setClasses();
    }

    setClasses() {
        const classes: string[] = [
            'custom-button',
            this.model.theme || ButtonTheme.Default,
            this.model.length || ButtonLength.Default,
            this.model.thickness || ButtonThickness.Default
        ];

        if (this.options) {
            const conditions: [string, boolean][] = [
                ['no-horizontal-paddings', !!(this.options.forceWidth || this.options.noPaddings)],
                ['no-vertical-paddings', !!(this.options.forceHeight || this.options.noPaddings)],
                ['inherit-width', !!this.options.forceWidth],
                ['inherit-height', !!this.options.forceHeight],
                ['inherit-min-width', !!this.options.minWidth],
                ['inherit-min-height', !!this.options.minHeight]
            ];

            conditions.filter(condition => condition[1]).forEach(([cl]) => classes.push(cl));
        }

        if (this.model.label && this.model.icon) {
            const iconPositionClass = this.model.iconPosition === 'right' ? 'button-icon-right' : 'button-icon-left';
            classes.push(iconPositionClass);
        }

        this.classes = classes.join(' ');
    }

    setStyles() {
        this.styles['width'] = this.options && this.options.forceWidth ? this.options.forceWidth : 'auto';
        this.styles['height'] = this.options && this.options.forceHeight ? this.options.forceHeight : 'auto';
        this.styles['minWidth'] = this.options && this.options.minWidth ? this.options.minWidth : 0;
        this.styles['minHeight'] = this.options && this.options.minHeight ? this.options.minHeight : 0;
    }

    onClick(event: MouseEvent): void {
        if (this.disabled) return;

        if (this.options) {
            if (this.options.blur) this.blur(event);
            if (this.options.fileUpload) return this.uploader.nativeElement.click();
        }

        return this.action.emit({ event });
    }

    onMouseDown(event: MouseEvent) {
        if (this.disabled) {
            event.stopPropagation();

            return;
        }

        this.mousedown.emit(event);
    }

    onMouseUp(event: MouseEvent) {
        if (this.disabled) {
            event.stopPropagation();

            return;
        }

        this.mouseup.emit(event);
    }

    onMouseOut(event: MouseEvent) {
        if (this.disabled) {
            event.stopPropagation();

            return;
        }

        this.mouseout.emit(event);
    }

    uploadFile(event: Event): void {
        this.filePath = '';
        if (event && event.target) {
            const input = event.target as HTMLInputElement;
            if (input.files) {
                this.action.emit({ file: input.files[0], event });
            }
        }
    }

    private blur(event: MouseEvent): void {
        if (event.currentTarget) (event.currentTarget as HTMLElement).blur();
    }

}
