import {  createAction, props } from '@ngrx/store';
import { CountersViews } from '../models/counters-values.interface';
import { CounterValues } from '../store/counter-values';
import { HttpErrorResponse } from '@angular/common/http';
import { MultiValue } from '@oam/cu-pmv2-api/models';

export enum CountersValuesActionTypes {
    SetCountersValues = '[PMV2] [PM Counters Values] Set counters',
    AppendCountersValues = '[PMV2] [PM Counters Values] Update counters values',
    ClearCountersValues = '[PMV2] [PM Counters Values] Clear counters',
    SetStartTime = '[PMV2] [PM Counters  Timestamp Values] Add start time values',
    SetEndTime = '[PMV2] [PM Counters Timestamp Values] Add end time values',
    ClearStartTime = '[PMV2] [PM Counters Timestamp Values] Remove start time values',
    ClearEndTime = '[PMV2] [PM Counters Timestamp Values] Remove end time values',
    SetCountersFilter = '[PMV2] [User] [Counters] Set Counters Filter',
    ChangeCountersView = '[PMV2] [PM Counters Selection] Change counters view',
    ExportCountersTable = '[PMV2] [PM Counters Selection] Export counters table',
    ExportCountersChart = '[PMV2] [PM Counters Selection] Export counters chart',
    CounterValuesNotModified = '[PMV2] [PM Counters Values] Counters values not modified',
    CounterValuesHttpError = '[PMV2] [PM Counters Values] Counters values http error',
    FetchCounters = '[PMV2] [PM Counters Values] Fetch counters',
    CountersValuesFetchingSuccess = '[PMV2] [PM Counters Values] Counters values fetching success',
    SetLatestEtag = '[PMV2] [PM] Set Latest Etag',
    RetrievingCounterValuesFailed = '[PMV2] [PM Counters Values] Failed to retrieve counter values from server',
    RetrievingCounterValuesSucceed = '[PMV2] [PM Counters Values] Counter values successfully retrieved from the server',
}

export const setCountersValues = createAction(
    CountersValuesActionTypes.SetCountersValues,
    props<{ values: CounterValues[] }>()
);

export const appendCountersValues = createAction(
    CountersValuesActionTypes.AppendCountersValues,
    props<{ values: CounterValues[], isOldRequest: boolean }>()
);

export const clearCountersValues = createAction(
    CountersValuesActionTypes.ClearCountersValues
);

export const setCountersFilter = createAction(
    CountersValuesActionTypes.SetCountersFilter,
    props<{filter: string}>()
);

export const setStartTime = createAction(
    CountersValuesActionTypes.SetStartTime,
    props<{timestamp: string}>()
);

export const clearStartTime = createAction(
    CountersValuesActionTypes.ClearStartTime
);

export const setEndTime = createAction(
    CountersValuesActionTypes.SetEndTime,
    props<{timestamp: string}>()
);

export const setLatestEtag = createAction(
    CountersValuesActionTypes.SetLatestEtag,
    props<{etag: string | undefined}>()
);

export const clearEndTime = createAction(
    CountersValuesActionTypes.ClearEndTime
);

export const changeCountersView = createAction(
    CountersValuesActionTypes.ChangeCountersView,
    props<{viewType: CountersViews}>()
);

export const exportCountersTable = createAction(
    CountersValuesActionTypes.ExportCountersTable,
    props<{filename: string}>()
);

export const exportCountersChart = createAction(
    CountersValuesActionTypes.ExportCountersChart,
    props<{filename: string}>()
);

export const fetchCountersValues = createAction(
    CountersValuesActionTypes.FetchCounters
);

export const countersValuesNotModified = createAction(
    CountersValuesActionTypes.CounterValuesNotModified,
);

export const countersFetchingError = createAction(
    CountersValuesActionTypes.CounterValuesHttpError,
    props<{error: HttpErrorResponse}>()
);

export const countersValuesFetchingSuccess = createAction(
    CountersValuesActionTypes.CountersValuesFetchingSuccess,
    props<{values: MultiValue, etag: string | undefined, isOldRequest?: boolean}>()
);

export const retrievingCounterValuesFailed = createAction(
    CountersValuesActionTypes.RetrievingCounterValuesFailed
);

export const retrievingCounterValuesSucceed = createAction(
    CountersValuesActionTypes.RetrievingCounterValuesSucceed
);
