import { addNotification } from '@oam/core/lib/actions/toast-message.actions';
import {
    SIZE_LIMIT
} from '@oam/cu-ui';
import { Store } from '@ngrx/store';
import { Injectable } from '@angular/core';
import { importUiSettings } from '@oam/core/lib/actions/app-settings.actions';

@Injectable({
    providedIn: 'root'
})
export class DroppedItemService {

    constructor(private readonly store: Store) {}

    handleItem(item: File | Error): void {

        switch (item.constructor) {
            case File:
                this.store.dispatch(importUiSettings({ file: item as File}));
                break;
            case Error:
                this.notifyError((item as Error).message);
        }
    }

    notifyError(message: string): void {
        let errorSummary: string;
        let errorDetails = '';
        if (message.includes(SIZE_LIMIT)) {
            errorSummary = 'File size too large';
            errorDetails = message;
        } else {
            errorSummary = message;
        }

        this.store.dispatch(addNotification({
            payload: {
                detail: errorDetails,
                severity: 'error',
                summary: errorSummary
            }
        }));
    }
}
