import { OverlayState } from '../models';
import { Action, createReducer, on } from '@ngrx/store';
import { closeOutdatedDeltaPlanDialog, openDialog } from '../actions/overlay.actions';

export const overlayStateFeatureKey = 'overlay';

export const initialState: OverlayState = {
    isOutdatedDeltaPlanDialogOpen: false
};

const setOverlayReducer = createReducer(
    initialState,
    on(openDialog, (state, action) => {
        if (action.config.header === 'Outdated delta plan') {
            return { isOutdatedDeltaPlanDialogOpen: true } as OverlayState;
        } else {
            return { ...state };
        }
    }),
    on(closeOutdatedDeltaPlanDialog, (state, action) => {
        return { isOutdatedDeltaPlanDialogOpen: false } as OverlayState;
    })
);

export function reducer(state: OverlayState | undefined, action: Action) {
    return setOverlayReducer(state, action);
}
