import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { select, Store } from '@ngrx/store';
import { Observable, Subject } from 'rxjs';
import { login, setLoginInitialValues } from '../../core/actions/login.actions';
import { OperationStatus } from '@oam/core/lib/models/operation.interface';
import { LoginMessages } from './login-messages';
import { getLoginStatus } from '../../core/reducers/login.reducer';
import { redirect } from '@oam/core/lib/actions/router.actions';
import { RouterLink } from '@oam/navigation/lib/navigation/core/models/router-link';
import { ActivatedRoute } from '@angular/router';
import { ButtonModel, ButtonThickness, ButtonTheme, ButtonEvent, ButtonOptions } from '@oam/cu-ui';
import { TokenType } from '@oam/core/lib/store/user-state';

@Component({
    selector: 'webem-login-page',
    templateUrl: './login-page.component.html',
    styleUrls: ['./login-page.component.scss']
})
export class LoginPageComponent implements OnInit {
    isLoggedIn: boolean;
    applicationType: string;
    form: FormGroup;
    loginStatus$: Observable<OperationStatus>;
    chromeVersionText = LoginMessages.chromeVersionText;
    copyrightText = LoginMessages.copyrightText;
    showPassword: boolean;
    bearerToken: string | null;
    capsLockOn: boolean;
    ngUnsubscribe = new Subject();

    submitButton = {
        thickness: ButtonThickness.Default,
        theme: ButtonTheme.Accent,
        label: 'Login',
    } as ButtonModel;

    buttonOptions = {
        forceWidth: '100%'
    } as ButtonOptions;

    cancelButton = {
        thickness: ButtonThickness.Default,
        theme: ButtonTheme.Accent,
        label: 'Cancel',
    } as ButtonModel;

    constructor(private readonly store: Store,
                private readonly formBuilder: FormBuilder,
                private readonly route: ActivatedRoute) {
    }

    ngOnInit() {
        this.isLoggedIn = false;
        this.bearerToken = sessionStorage.getItem(`${TokenType.BEARER}`);
        if (this.bearerToken) {
            this.store.dispatch(redirect({routerLink: RouterLink.dashboard, activatedRoute: this.route}));
        }
        this.applicationType = this.route.snapshot.data.title;
        this.loginStatus$ = this.store.pipe(select(getLoginStatus));
        this.form = this.formBuilder.group({
            username: ['', Validators.required],
            password: ['', Validators.required],
            readOnlyChecked: [false]
        });
        this.showPassword = false;
    }

    ngOnDestroy() {
        this.ngUnsubscribe.next();
        this.ngUnsubscribe.complete();
    }

    onClickLogin() {
        this.store.dispatch(login({
            payload: {
                username: this.form.value.username, password: this.form.value.password,
                readOnlyChecked: this.form.value.readOnlyChecked
            }
        }));

        this.loginStatus$.subscribe((status) => {
            this.isLoggedIn = status.successful;
            if (status.successful) {
                this.form.reset({
                    password: ''
                });
            }
        });
    }

    resetLoginStoreValues() {
        this.store.dispatch(setLoginInitialValues());
    }

    setPasswordVisible(isVisible: boolean) {
        this.showPassword = isVisible;
    }

    getCapsLockState(event: MouseEvent): void {
        this.capsLockOn = event.getModifierState('CapsLock');
    }
}
